import Big from "big.js";
import classNames from "classnames";
import React, { ChangeEvent, useContext, useState } from "react";

import { useCopy } from "../../../extensions";
import { NumericInput } from "../../NumericInput/NumericInput";
import { PricingCalculatorContext } from "../../PricingCalculator/PricingCalculatorContext";
import { RevenueEntryRow } from "../../PricingCalculator/types";
import styles from "../RevenueCalculator.module.scss";
import { RevenueTable } from "../RevenueTable/RevenueTable";
import { PanelProps } from "./types";

const currencies = ["AUD", "EUR", "GBP", "CAD", "USD"];
const getValues = (localListPrice: number) =>
  currencies.map((c) => ({ currency: c, amount: localListPrice }));

export const LuluDirectPanel = ({
  locale,
  onListPriceChange,
  pricing,
  baseCurrency,
  loading,
  initialPriceAmount,
}: PanelProps) => {
  const { productType, getDisplayValue } = useContext(PricingCalculatorContext);
  const [localListPrice, setLocalListPrice] = useState<string>(
    initialPriceAmount?.toString() ?? ""
  );

  const {
    _textLD_header_1,
    _textLD_header_2,
    _textLD_header_3,
    _textYourListPrice,
    _textLD_footer,
    _textLD_footerLink,
    _textGrossProfit,
  } = useCopy("RevenueCalculator");

  const handleListPriceInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    // we only need to request the BE once to get the fulfillment fee
    // as the user can enter any price (and the BE is currently tied to min/max)
    // we calculate the revenue locally
    onListPriceChange(parseFloat(newValue));
    setLocalListPrice(newValue);
  };

  const allowedRows = ["PRINT_COST", "FULFILLMENT_FEE"];
  let revenueEntryRows = pricing?.revenue?.find(({ channel }) => channel === "LULU_DIRECT")?.data;
  revenueEntryRows = revenueEntryRows?.filter((row) => allowedRows.includes(row.label ?? ""));

  if (revenueEntryRows && localListPrice && !Number.isNaN(localListPrice)) {
    const printCostAmount = revenueEntryRows
      .find((r) => r.label === "PRINT_COST")
      ?.values?.find((v) => v.currency === baseCurrency)?.amount;

    const fulfillmentFeeAmount = revenueEntryRows
      .find((r) => r.label === "FULFILLMENT_FEE")
      ?.values?.find((v) => v.currency === baseCurrency)?.amount;

    if (printCostAmount && fulfillmentFeeAmount) {
      const listPriceRow: RevenueEntryRow = {
        label: "LIST_PRICE",
        values: getValues(parseFloat(localListPrice)),
      };

      const authorRevenueRow: RevenueEntryRow = {
        label: "AUTHOR_REVENUE",
        values: getValues(
          Big(localListPrice)
            .minus(Big(printCostAmount))
            .minus(Big(fulfillmentFeeAmount))
            .toNumber()
        ),
      };

      revenueEntryRows = [listPriceRow, ...revenueEntryRows, authorRevenueRow];
    }
  }

  return (
    <div className={styles.content}>
      <div className={classNames(styles.text, styles.contentHeader)}>
        <p className={styles.text}>{`${_textLD_header_1} ${getDisplayValue(
          productType
        )} ${_textLD_header_2}`}</p>
        <p className={styles.text}>{_textLD_header_3}</p>
      </div>
      <NumericInput
        isMoney
        maxLength={12}
        currency={baseCurrency}
        label={_textYourListPrice}
        onChange={handleListPriceInputChange}
        wrapperClassName={styles.listPriceInput}
        value={localListPrice}
        dataTestId="list-price-input"
      />
      <div className={styles.revenueTableWrapper}>
        <RevenueTable
          revenueEntryRows={localListPrice && revenueEntryRows ? revenueEntryRows : []}
          summary={_textGrossProfit}
          baseCurrency={baseCurrency}
          locale={locale}
          loading={loading}
        />
      </div>
      <p className={styles.footer}>
        {_textLD_footer}{" "}
        <a
          target="_blank"
          rel="noopener noreferrer"
          href={`https://help.luludirect.lulu.com/${locale}/support/solutions/articles/64000270795-how-are-lulu-direct-prices-calculated-and-what-will-i-pay-`}
        >
          {_textLD_footerLink}
        </a>
      </p>
    </div>
  );
};
