import classNames from "classnames";
import React, { CSSProperties } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { isEnterOrSpaceKeyEvent } from "../../utils/isEnterOrSpaceKeyEvent";
import { Icon } from "../Icon/Icon";
import styles from "./AccordionForm.module.scss";

export interface AccordionFormProps {
  className?: string;
  collapsed?: boolean;
  dataTestId?: string;
  title: string | React.ReactElement;
  titleAccessory?: string | React.ReactElement;
  value?: string;
  name?: string;
  type?: "checkbox" | "radio" | "noCheck";
  description?: string | React.ReactElement;
  helper?: string;
  helperLink?: string;
  checked: boolean;
  disabled?: boolean;
  rightElement?: React.ReactNode;
  labelStyle?: CSSProperties;
  icon?: { color?: Colors; name: Icons };
  variant?: "default" | "pricing";
  onChange?: () => void;
  onClick?: () => void;
  logo?: string;
  condensedLogo?: boolean;
  fixedHeight?: number;
  children?: React.ReactNode;
  inputId?: string;
}

export const AccordionForm: React.FC<AccordionFormProps> = ({
  dataTestId,
  className,
  collapsed,
  type = "radio",
  title,
  titleAccessory,
  value,
  name,
  description,
  checked,
  children,
  helper,
  helperLink,
  disabled = false,
  onChange,
  onClick,
  rightElement,
  labelStyle,
  icon,
  logo,
  condensedLogo,
  variant = "default",
  fixedHeight = "default",
  inputId,
}) => {
  const [open, setOpen] = React.useState(checked || variant === "pricing");

  React.useEffect(() => {
    if (variant === "default") {
      setOpen(checked);
    }
  }, [checked, variant]);

  const checkable = type !== "noCheck";
  const boxClassName = classNames(
    styles.accordionForm,
    {
      [styles.collapsed]: collapsed,
      [styles.disabled]: disabled,
      [styles.open]: open,
      [styles.withChildren]: children,
      [styles.checked]: checked,
      [styles.pricing]: variant === "pricing",
    },
    className
  );

  const resolvedInputId = inputId || name || "accordion-input";

  return (
    <div className={boxClassName} data-testid={dataTestId}>
      <div
        role="presentation"
        tabIndex={disabled ? -1 : 0}
        className={styles.content}
        style={fixedHeight ? { height: fixedHeight } : {}}
        onClick={() => !disabled && onChange?.()}
        onKeyDown={(e) => {
          if (!disabled && isEnterOrSpaceKeyEvent(e)) {
            onChange?.();
          }
        }}
      >
        <div className={styles.inputSection}>
          {checkable && (
            <>
              {type === "radio" ? (
                <div className={styles.radioInput}>
                  {checked && <div className={styles.radioInputSelected} />}
                  <input
                    id={resolvedInputId}
                    type="radio"
                    name={name}
                    value={value}
                    checked={checked}
                    readOnly={true}
                    disabled={disabled}
                    data-testid={dataTestId ? `${dataTestId}-input` : undefined}
                  />
                </div>
              ) : (
                <input
                  id={resolvedInputId}
                  type={type}
                  name={name}
                  value={value}
                  checked={checked}
                  readOnly={true}
                  className={styles.input}
                  disabled={disabled}
                  data-testid={dataTestId ? `${dataTestId}-input` : undefined}
                />
              )}
            </>
          )}
        </div>
        <div className={styles.main}>
          <div className={styles.heading}>
            {icon && <Icon iconName={icon.name} color={icon.color} size={24} />}
            <div className={styles.headingContentWrapper}>
              <div className={styles.titleContainer}>
                {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-noninteractive-element-interactions */}
                <label
                  onClick={(e) => e.stopPropagation()}
                  className={styles.label}
                  style={labelStyle}
                  htmlFor={resolvedInputId}
                >
                  {title}
                </label>
                {titleAccessory && (
                  <span className={styles.titleAccessoryContainer}>{titleAccessory}</span>
                )}
              </div>
              {description && <p className={styles.description}>{description}</p>}
              {helper && (
                <a
                  href={helperLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  className={styles.helper}
                >
                  {helper}
                </a>
              )}
            </div>
          </div>
          {logo && (
            <img
              className={classNames(styles.logo, {
                [styles.condensedLogo]: condensedLogo,
              })}
              src={logo}
              alt="logo"
            />
          )}
          {children && (
            // eslint-disable-next-line jsx-a11y/click-events-have-key-events
            <div
              role="button"
              tabIndex={disabled ? -1 : 0}
              aria-label="accordionToggle"
              className={classNames(styles.icon, {
                [styles.collapsed]: collapsed,
              })}
              onClick={(e) => {
                if (!disabled && variant === "pricing") {
                  e.stopPropagation();
                  onClick?.();
                }
                if (!disabled && variant === "default") {
                  setOpen(!open);
                }
              }}
            >
              <Icon
                className={classNames({ [styles.collapsed]: collapsed })}
                iconName={Icons.ArrowDownMedium}
                size={24}
              />
            </div>
          )}
          {rightElement && !children && <div className={styles.rightElement}>{rightElement}</div>}
        </div>
      </div>

      <div
        className={classNames(styles.dropdown, {
          [styles.open]: (collapsed || open) && children,
        })}
      >
        {children}
      </div>
    </div>
  );
};
