import React from "react";

import { useCopy } from "../../extensions";
import { Address } from "./types";

interface AddressDisplayProps {
  address: Address;
  countryName: string;
}
export const AddressDisplay: React.FC<AddressDisplayProps> = ({ address, countryName }) => {
  const { _textRecipientTaxId } = useCopy("OrderDetails");

  return (
    <>
      <p>{address.name}</p>
      {address.organization && <p>{address.organization}</p>}
      <p>{address.addressLine1}</p>
      <p>{address.addressLine2}</p>
      <p>
        {address.city}
        {address.state && `, ${address.state}`} {address.postalCode}
      </p>
      {address.country && <p>{countryName}</p>}
      {address.recipientTaxId && (
        <p>
          {_textRecipientTaxId}: <br />
          <span style={{ whiteSpace: "nowrap" }}>{address.recipientTaxId}</span>
        </p>
      )}
    </>
  );
};
