export const safeLocalStorage = {
  setItem: (key: string, value: string, onError?: (error: unknown) => void) => {
    try {
      window.localStorage.setItem(key, value);
    } catch (error) {
      console.error("Error while saving to localStorage", error);
      onError?.(error);
    }
  },
  getItem: (key: string, onError?: (error: unknown) => void) => {
    try {
      return window.localStorage.getItem(key);
    } catch (error) {
      console.error("Error while reading from localStorage", error);
      onError?.(error);
      return null;
    }
  },
  removeItem: (key: string, onError?: (error: unknown) => void) => {
    try {
      window.localStorage.removeItem(key);
    } catch (error) {
      console.error("Error while removing item from localStorage", error);
      onError?.(error);
    }
  },
};
