import classNames from "classnames";
import React, { useContext } from "react";

import { Colors } from "../../../../constants";
import { useCopy } from "../../../../extensions";
import { Icons } from "../../../../icon-types";
import { NakedButton } from "../../../../shared/components/NakedButton/NakedButton";
import { Icon } from "../../../Icon/Icon";
import { MoneyDisplay } from "../../../MoneyDisplay/MoneyDisplay";
import { PricingCalculatorContext } from "../../PricingCalculatorContext";
import styles from "./TotalBar.module.scss";

interface Props {
  expanded?: boolean;
  onClick?: (event: React.MouseEvent<HTMLButtonElement>) => void;
}
export const TotalBar: React.FC<Props> = ({ expanded, onClick }) => {
  const { _textCostPer } = useCopy("PricingCalculator");
  const {
    manufacturingCostQuery,
    getDisplayValue,
    productType,
    locale,
    podPackageId,
    state,
    baseCurrency,
  } = useContext(PricingCalculatorContext);

  const isEbook = productType === "EBOOK";

  const money =
    podPackageId && state.pageCount && manufacturingCostQuery.data
      ? manufacturingCostQuery.data
      : {
          amount: 0,
          currency: baseCurrency ?? "USD",
        };

  const isIntegratedVersion = expanded === undefined && onClick === undefined;

  const testIdPrefix = "total-bar";

  return (
    <NakedButton
      className={classNames(styles.totalBar, {
        [styles.integrated]: isIntegratedVersion,
        [styles.ebook]: isEbook,
        [styles.expanded]: expanded,
      })}
      onClick={onClick}
      data-testid={testIdPrefix}
    >
      <div
        className={classNames(styles.wrapper, { [styles.loading]: manufacturingCostQuery.loading })}
        data-testid={`${testIdPrefix}-wrapper`}
      >
        {isEbook ? (
          isIntegratedVersion ? null : (
            <span className={styles.ebookHeadline}>Ebook Details</span>
          )
        ) : (
          <>
            <MoneyDisplay money={money} locale={locale} className={styles.total} />
            <span className={styles.per}>{` ${_textCostPer} ${getDisplayValue(productType)}`}</span>
          </>
        )}
        {expanded !== undefined && (
          <Icon
            iconName={expanded ? Icons.ArrowDownMedium : Icons.ArrowUpMedium}
            size={24}
            color={Colors.Indigo}
          />
        )}
      </div>
    </NakedButton>
  );
};
