import classNames from "classnames";
import React from "react";

import { mapContentfulColorToStyleProp } from "../../utils/colors";
import styles from "./ContentLinkTextBlock.module.scss";

export interface ContentLinkTextBlockProps {
  dataTestId?: string;
  text: string;
  backgroundColor?: string;
  textColor?: string;
  centeredText?: boolean;
  idAttribute?: string;
}

export const ContentLinkTextBlock: React.FC<ContentLinkTextBlockProps> = ({
  dataTestId,
  text,
  backgroundColor,
  textColor,
  centeredText,
  idAttribute,
}) => {
  const textClasses = classNames("max-width-container", "large", {
    "centered-text": centeredText,
  });
  return (
    <section
      data-testid={dataTestId}
      id={idAttribute}
      className={styles.contentLinkTextBlockContainer}
      style={
        backgroundColor ? { background: mapContentfulColorToStyleProp(backgroundColor) } : undefined
      }
    >
      <h2
        className={textClasses}
        style={textColor ? { color: mapContentfulColorToStyleProp(textColor) } : undefined}
      >
        {text}
      </h2>
    </section>
  );
};
