import classNames from "classnames";
import React, { useContext, useRef, useState } from "react";

import { Colors } from "../../../constants";
import { Icons } from "../../../icon-types";
import { ReactComponent as Gradient } from "../../../styles/assets/images/scroll-gradient.svg";
import { Icon } from "../../Icon/Icon";
import { PricingCalculatorContext } from "../PricingCalculatorContext";
import { ConfiguratorActionType } from "../reducer";
import { ProductTypeEnum } from "../types";
import { ProductTypeOption } from "./ProductTypeOption";
import styles from "./ProductTypeSelector.module.scss";

interface Props {
  onChange: (printableType: ProductTypeEnum) => void;
}

export const ProductTypeSelector: React.FC<Props> = ({ onChange }) => {
  const { availableProductTypes, productType, setProductType, dispatch } =
    useContext(PricingCalculatorContext);
  const productTypeSelectorRef = useRef<HTMLDivElement>(null);
  const [displayCaret, setDisplayCaret] = useState<"LEFT" | "RIGHT" | null>("RIGHT");

  const handleClick = (value: string) => {
    dispatch({
      type: ConfiguratorActionType.RESET_CONFIGURATION,
    });

    onChange(value as ProductTypeEnum);
    setProductType(value as ProductTypeEnum);
  };

  const handleScroll = (e: React.UIEvent<HTMLDivElement>) => {
    const targetElement = e.target as HTMLDivElement;
    if (targetElement.scrollLeft === 0) {
      setDisplayCaret("RIGHT");
    } else if (targetElement.scrollLeft + targetElement.clientWidth === targetElement.scrollWidth) {
      setDisplayCaret("LEFT");
    } else {
      setDisplayCaret(null);
    }
  };

  const testIdPrefix = "product-type-selector";

  return (
    <div className={styles.selector} data-testid={testIdPrefix}>
      <Gradient
        className={classNames(styles.gradient, styles.gradientLeft, {
          [styles.displayElement]: displayCaret !== "RIGHT",
        })}
      />
      <Icon
        iconName={Icons.ArrowDownLarge}
        color={Colors.Indigo}
        size={32}
        className={classNames(styles.caret, styles.caretLeft, {
          [styles.displayElement]: displayCaret === "LEFT",
        })}
        dataTestId={`${testIdPrefix}-left-icon`}
      />
      <div className={styles.optionContainer} ref={productTypeSelectorRef} onScroll={handleScroll}>
        {availableProductTypes.map(({ image, value, label }) => (
          <ProductTypeOption
            key={value}
            isSelected={productType === value}
            label={label}
            image={image}
            value={value}
            onClick={handleClick}
          />
        ))}
      </div>
      <Gradient
        className={classNames(styles.gradient, styles.gradientRight, {
          [styles.displayElement]: displayCaret !== "LEFT",
        })}
      />
      <Icon
        iconName={Icons.ArrowDownLarge}
        color={Colors.Indigo}
        size={32}
        className={classNames(styles.caret, styles.caretRight, {
          [styles.displayElement]: displayCaret === "RIGHT",
        })}
        dataTestId={`${testIdPrefix}-right-icon`}
      />
      <div className={styles.radioBorderSelection} />
    </div>
  );
};
