import React from "react";

type Images = {
  imageUrl?: string;
  mobileImageUrl?: string;
  mobileRetinaImageUrl?: string;
  desktopRetinaImageUrl?: string;
};

export const imageMimeTypes = {
  jpe: "image/jpeg",
  jpeg: "image/jpeg",
  jpg: "image/jpeg",
  svg: "image/svg+xml",
  ico: "image/x-icon",
  webp: "image/webp",
};

const isImageFromGoogleCloud = (url?: string) =>
  url?.startsWith("https://assets.lulu.com/media") ||
  url?.startsWith("https://storage.googleapis.com/upp-media-upp-prod/");

export const getWebpPicture = (url?: string) => {
  if (!url) return;

  if (isImageFromGoogleCloud(url)) {
    return url.replace(".png", ".webp").replace(".jpg", ".webp");
  } else {
    const baseUrl = url.split("&fm=")[0];

    return baseUrl ? `${baseUrl}&fm=webp` : undefined;
  }
};

export const checkIsVectorImage = (imageUrl?: string) => {
  if (!imageUrl) return;

  return imageUrl.includes(".svg");
};

export const prepareSVGImage = (imageUrl?: string) => {
  if (!imageUrl) return;

  return imageUrl.split("?")[0];
};

export const getImageExtension = (url: string) => {
  const splitUrl = url.split("/");
  const filename = splitUrl[splitUrl.length - 1].split("?")[0];

  return filename.split(".")[1] as keyof typeof imageMimeTypes;
};

// To add the ability to load retina images from different hosts, we should modify this function to load the appropriate graphics and rename this function
export const getRetinaUrlForGoogleCloudImages = (url?: string) =>
  isImageFromGoogleCloud(url) ? url?.replace("-1x.", "-2x.").replace("/1x/", "/2x/") : url;

export const getRetinaSourceSet = (url?: string, retinaUrl?: string) => {
  if (isImageFromGoogleCloud(url)) {
    return `${url}${retinaUrl ? ` 1x, ${getRetinaUrlForGoogleCloudImages(retinaUrl)} 1.3x` : ""}`;
  }
  return `${url}${retinaUrl ? ` 1x, ${retinaUrl} 1.3x` : ""}`;
};

export const getSourceProps = (url?: string, retinaUrl?: string) => {
  if (!url) return {};

  const isContentfulImage = url?.includes("images.ctfassets");
  const isCloudBucket = isImageFromGoogleCloud(url);
  const isVectorImage = checkIsVectorImage(url);

  if (isVectorImage)
    return {
      srcSet: prepareSVGImage(url),
      type: imageMimeTypes.svg,
    };

  if (isContentfulImage)
    return {
      srcSet: getRetinaSourceSet(getWebpPicture(url), getWebpPicture(retinaUrl)),
      type: imageMimeTypes.webp,
    };

  if (isCloudBucket) {
    return {
      srcSet: getRetinaSourceSet(
        getWebpPicture(url),
        getRetinaUrlForGoogleCloudImages(getWebpPicture(retinaUrl))
      ),
      type: imageMimeTypes.webp,
    };
  }

  return {
    srcSet: getRetinaSourceSet(url, retinaUrl),
    type: imageMimeTypes[getImageExtension(url)],
  };
};

export const getImageSources = ({
  imageUrl,
  mobileImageUrl,
  mobileRetinaImageUrl,
  desktopRetinaImageUrl,
}: Images) => {
  if (!imageUrl) return;

  return (
    <>
      {mobileImageUrl && (
        <source
          {...getSourceProps(mobileImageUrl, mobileRetinaImageUrl)}
          media="(max-width: 600px)"
        />
      )}
      <source {...getSourceProps(imageUrl, desktopRetinaImageUrl)} />
    </>
  );
};

export const IMAGES_BASE_URL = "https://assets.lulu.com/media/";
