import React from "react";
export interface ColumnProps {
  dataTestId?: string;
  className?: string;
  children?: React.ReactNode;
}

export const Column: React.FC<ColumnProps> = ({ dataTestId, className, children }) => {
  return (
    <div className={className} data-testid={dataTestId} style={{ position: "relative" }}>
      {children}
    </div>
  );
};
