import classNames from "classnames";
import useSlider from "embla-carousel-react";
import React, { useEffect, useState } from "react";

import { Colors } from "../../constants";
import { useCopy } from "../../extensions";
import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../hooks";
import { Icons } from "../../icon-types";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { Icon } from "../Icon/Icon";
import { ImageWithMetadata } from "../ImageWithMetadata/ImageWithMetadata";
import { OptionsViewProps } from "./ImageSlider";
import styles from "./ImageSlider.module.scss";

export const MobileAndTabletImageSelectors = ({
  activeIndex,
  options,
  setActiveIndex,
  handlePrevious,
  handleNext,
}: OptionsViewProps) => {
  const { _previewImageAriaLabel } = useCopy("ImageSlider");
  const [sliderRef, sliderActions] = useSlider();
  const windowWidth = useWindowWidth();
  const [isMobile, setIsMobile] = useState<boolean>();

  const previous = () => {
    sliderActions?.scrollPrev();
    handlePrevious?.();
  };
  const next = () => {
    sliderActions?.scrollNext();
    handleNext?.();
  };

  useEffect(() => {
    setIsMobile(windowWidth < MEDIA_BREAKPOINTS.M_BIG);
  }, [windowWidth]);

  useEffect(() => {
    sliderActions?.scrollTo(activeIndex);
  }, [isMobile, activeIndex, sliderActions]);

  return (
    <div className={styles.previewMobileImagesContainer}>
      {isMobile && (
        <div className={styles.buttons}>
          {activeIndex !== 0 && (
            <button
              className={styles.buttonLeft}
              onClick={previous}
              data-testid="ImageSlider-buttonPrevious"
            >
              <span style={{ transform: "rotate(90deg)" }}>
                <Icon iconName={Icons.ArrowDownSmall} size={20} color={Colors.Indigo} />
              </span>
            </button>
          )}
          {activeIndex !== options.length - 1 && (
            <button
              className={styles.buttonRight}
              onClick={next}
              data-testid="ImageSlider-buttonNext"
            >
              <span style={{ transform: "rotate(-90deg)" }}>
                <Icon iconName={Icons.ArrowDownSmall} size={20} color={Colors.Indigo} />
              </span>
            </button>
          )}
        </div>
      )}
      <div ref={sliderRef}>
        <div className={styles.sliderElements}>
          {options.map((item, index) => (
            <NakedButton
              key={`previewImage-${index}`}
              onClick={() => setActiveIndex(index)}
              className={classNames(styles.sliderItem, {
                [styles.selectedItem]: index === activeIndex,
              })}
              data-testid="ImageSlider-optionItem"
              aria-label={`${_previewImageAriaLabel} ${index + 1}`}
            >
              <ImageWithMetadata {...item.image} />
            </NakedButton>
          ))}
        </div>
      </div>
    </div>
  );
};
