import debounce from "just-debounce-it";
import React from "react";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { IconButton } from "../IconButton/IconButton";
import styles from "./Pagination.module.scss";

export interface PaginationProps {
  dataTestId?: string;
  pageNumber: number;
  pageCount: number;
  onPageChange: (page: number) => void;
  info?: string;
  fullWidth?: boolean;
  hierarchy?: "primary" | "secondary" | "tertiary";
}

const debounceUpdate = debounce((fn: () => void) => fn(), 800);

export const Pagination: React.FC<PaginationProps> = ({
  dataTestId,
  pageNumber,
  pageCount,
  onPageChange,
  info,
  fullWidth,
  hierarchy = "secondary",
}) => {
  const [inputValue, setInputValue] = React.useState(pageNumber.toString());

  React.useEffect(() => setInputValue(pageNumber.toString()), [pageNumber]);

  const {
    _firstPageAriaLabel,
    _previousPageAriaLabel,
    _currentPageAriaLabel,
    _nextPageAriaLabel,
    _lastPageAriaLabel,
  } = useCopy("Pagination");

  return (
    <div
      className={fullWidth ? styles.paginationFullWidth : styles.pagination}
      data-testid={dataTestId}
    >
      {info ? <div className={styles.info}>{info}</div> : null}
      <div className={styles.links}>
        <div>
          <IconButton
            aria-label={_firstPageAriaLabel}
            className={styles.button}
            disabled={pageNumber === 1}
            onClick={() => onPageChange(1)}
            icon={Icons.ArrowPagingBackward}
            buttonSize="medium"
            hierarchy={hierarchy}
            dataTestId="first-page-button"
          />
          <IconButton
            aria-label={_previousPageAriaLabel}
            className={styles.button}
            disabled={pageNumber === 1}
            onClick={() => onPageChange(pageNumber - 1)}
            icon={Icons.ArrowLinkLeftMedium}
            buttonSize="medium"
            hierarchy={hierarchy}
            dataTestId="previous-page-button"
          />
        </div>
        <input
          aria-label={_currentPageAriaLabel}
          className={styles.currentPage}
          value={inputValue}
          onChange={(e) => {
            setInputValue(e.target.value);
            const newPage = parseInt(e.target.value, 10);
            if (newPage && newPage > 0) {
              if (newPage < pageCount) {
                debounceUpdate(() => onPageChange(newPage));
              } else {
                debounceUpdate(() => onPageChange(pageCount));
              }
            } else if (newPage < 1) {
              debounceUpdate(() => onPageChange(1));
            }
          }}
          data-testid="current-page-input"
        />
        <div>
          <IconButton
            aria-label={_nextPageAriaLabel}
            className={styles.button}
            disabled={pageNumber === pageCount || pageNumber > pageCount}
            onClick={() => onPageChange(pageNumber + 1)}
            icon={Icons.ArrowLinkRightMedium}
            buttonSize="medium"
            hierarchy={hierarchy}
            dataTestId="next-page-button"
          />
          <IconButton
            aria-label={_lastPageAriaLabel}
            className={styles.button}
            disabled={pageNumber === pageCount || pageNumber > pageCount}
            onClick={() => onPageChange(pageCount)}
            icon={Icons.ArrowPagingForward}
            buttonSize="medium"
            hierarchy={hierarchy}
            dataTestId="last-page-button"
          />
        </div>
      </div>
    </div>
  );
};
