import classNames from "classnames";
import React, { useState } from "react";

import { useCopy } from "../../../extensions";
import { Checkbox } from "../../Checkbox/Checkbox";
import { InputDropdown } from "../../InputDropdown/InputDropdown";
import { LoadingContainer } from "../../LoadingContainer/LoadingContainer";
import { MoneyDisplay } from "../../MoneyDisplay/MoneyDisplay";
import { RevenueEntryRow } from "../../PricingCalculator/types";
import styles from "./RevenueTable.module.scss";

interface RevenueTableProps {
  revenueEntryRows: RevenueEntryRow[];
  summary: string;
  baseCurrency: string;
  conversion?: boolean;
  locale: string;
  loading?: boolean;
  title?: string;
  theme?: "default" | "naked";
  showZeroValueRows?: boolean;
  summaryOnly?: boolean;
}

export const RevenueTable = ({
  revenueEntryRows,
  summary,
  baseCurrency,
  conversion,
  locale,
  loading = false,
  title,
  theme = "default",
  showZeroValueRows = false,
  summaryOnly = false,
}: RevenueTableProps) => {
  const [conversionToggle, setConversionToggle] = useState(false);
  const [currencyConversion, setCurrencyConversion] = useState("");

  const {
    _textCurrencyConversion,
    _textPrintCost,
    _textGrossProfit,
    _textLuluShare,
    _textFulfillmentFee,
    _textDistributionFee,
    _textListPrice,
  } = useCopy("RevenueCalculator");

  const REVENUE_ENTRY_ROW: Record<string, { label: string; sign?: string }> = {
    LIST_PRICE: { label: _textListPrice },
    PRINT_COST: { sign: "-", label: _textPrintCost },
    DISTRIBUTION_FEES: { sign: "-", label: _textDistributionFee },
    LULU_SHARE: { sign: "-", label: _textLuluShare },
    FULFILLMENT_FEE: { sign: "-", label: _textFulfillmentFee },
    GROSS_PROFIT: { sign: "=", label: _textGrossProfit },
  };

  const currencies = revenueEntryRows[0]?.values
    ? revenueEntryRows[0].values.map(({ currency }) => currency)
    : [];

  const authorRevenueRow = revenueEntryRows.find(({ label }) => label === "AUTHOR_REVENUE")?.values;
  const authorRevenueRowAmount = authorRevenueRow?.find(
    ({ currency }) => currency === baseCurrency
  )?.amount;
  const authorRevenueRowConversionAmount = authorRevenueRow?.find(
    ({ currency }) => currency === currencyConversion
  )?.amount;

  const testIdPrefix = "revenue-table";
  const disabled = !revenueEntryRows?.length;

  return (
    <LoadingContainer loading={loading} dataTestId={`${testIdPrefix}-loading-container`}>
      <div
        className={classNames(styles.container, {
          [styles.disabled]: disabled,
          [styles.naked]: theme === "naked",
        })}
      >
        {title && <h4 className={styles.title}>{title}</h4>}
        <table className={styles.table}>
          <colgroup>
            <col className={styles.nameColumn} />
            <col style={{ width: 0 }} />
            <col style={{ width: 0 }} />
            <col style={{ width: 0 }} />
          </colgroup>
          {conversion && (
            <thead>
              <tr>
                <td className={classNames(styles.cellHeader)}>
                  <Checkbox
                    checked={conversionToggle}
                    onChange={() => {
                      if (conversionToggle) setCurrencyConversion("");
                      setConversionToggle(!conversionToggle);
                    }}
                    disabled={disabled}
                    label={_textCurrencyConversion}
                    className={styles.currencyConversion}
                    dataTestId={`${testIdPrefix}-conversion-checkbox`}
                  />
                </td>
                <td
                  className={classNames(styles.cellHeader, styles.cellHeaderCurrency, {
                    [styles.baseCurrencyHeaderCell]: conversionToggle,
                    [styles.paddingRight]: !conversionToggle,
                  })}
                  colSpan={2}
                >
                  {baseCurrency}
                </td>
                {conversionToggle && (
                  <td
                    className={classNames(styles.cellHeader, styles.fitContent, styles.paddingLeft)}
                  >
                    <InputDropdown
                      options={currencies.map((key) => ({
                        value: key,
                        label: key,
                      }))}
                      name="currency"
                      label=""
                      value={currencyConversion}
                      onChange={setCurrencyConversion}
                      wrapperClassName={styles.currencySelectorWrapper}
                      inputClassName={styles.currencyConversionInput}
                      dataTestId={`${testIdPrefix}-currency-input`}
                    />
                  </td>
                )}
              </tr>
              {conversionToggle && (
                <tr className={styles.headerCurrencies}>
                  <td></td>
                  <td></td>
                  <td
                    className={classNames(styles.cell, styles.cellHeader, styles.cellSecondHeader)}
                  >
                    {baseCurrency}
                  </td>
                  {conversion && (
                    <td
                      className={classNames(
                        styles.cell,
                        styles.cellHeader,
                        styles.cellSecondHeader
                      )}
                    >
                      {currencyConversion}
                    </td>
                  )}
                </tr>
              )}
            </thead>
          )}
          <tbody>
            {revenueEntryRows.map((revenueEntryRow, index) => {
              if (
                !revenueEntryRow.label ||
                revenueEntryRow.label === "AUTHOR_REVENUE" ||
                summaryOnly
              )
                return null;

              const amount = revenueEntryRow.values?.find(
                ({ currency }) => currency === baseCurrency
              )?.amount;
              const conversionAmount = conversionToggle
                ? revenueEntryRow.values?.find(({ currency }) => currency === currencyConversion)
                    ?.amount
                : undefined;

              const IS_ADDITIONAL_STYLE = !index && conversion;

              const revenueEntry = REVENUE_ENTRY_ROW[revenueEntryRow.label];

              if ((!revenueEntry || !amount) && !showZeroValueRows) return null;

              return (
                <tr
                  key={revenueEntryRow.label}
                  data-testid={`revenue-entry-row-${revenueEntryRow.label}`}
                >
                  <td
                    className={classNames(styles.cell, {
                      [styles.firstRowCell]: IS_ADDITIONAL_STYLE,
                    })}
                  >
                    {revenueEntry.label}
                  </td>
                  <td
                    className={classNames(styles.cell, {
                      [styles.firstRowCell]: IS_ADDITIONAL_STYLE,
                    })}
                  >
                    {revenueEntry.sign && revenueEntry.sign}
                  </td>
                  <td
                    className={classNames(styles.cell, styles.fitContent, {
                      [styles.firstRowCell]: IS_ADDITIONAL_STYLE,
                    })}
                    colSpan={1}
                  >
                    <MoneyDisplay
                      money={{
                        amount,
                        currency: "",
                      }}
                      locale={locale}
                      onlyAmount
                    />
                  </td>
                  {conversionToggle && (
                    <td
                      className={classNames(styles.cell, {
                        [styles.firstRowCell]: IS_ADDITIONAL_STYLE,
                      })}
                    >
                      <MoneyDisplay
                        money={{
                          amount: conversionAmount,
                          currency: "",
                        }}
                        locale={locale}
                        onlyAmount
                      />
                    </td>
                  )}
                </tr>
              );
            })}
          </tbody>
          <tfoot>
            <tr className={styles.separator} />
            <tr className={styles.summaryRow} data-testid="revenue-entry-row-summary">
              <td className={classNames(styles.cell, styles.summaryCell, styles.summaryCellFirst)}>
                {summary}
              </td>
              <td />
              <td
                className={classNames(styles.cell, styles.summaryCell, {
                  [styles.summaryCellFlex]: !conversion,
                })}
                colSpan={!conversion ? 2 : 1}
              >
                <div className={classNames({ [styles.summaryAmountAbs]: !conversion })}>
                  <MoneyDisplay
                    money={{ amount: authorRevenueRowAmount, currency: "USD" }}
                    locale={locale}
                    fallback="---"
                    onlyAmount
                  />
                  {!conversion && <span className={styles.summaryCurrency}>{baseCurrency}</span>}
                </div>
              </td>
              {conversionToggle && (
                <td className={classNames(styles.cell, styles.summaryCell)}>
                  <MoneyDisplay
                    money={{
                      amount: authorRevenueRowConversionAmount,
                      currency: "",
                    }}
                    locale={locale}
                    onlyAmount
                  />
                </td>
              )}
            </tr>
          </tfoot>
        </table>
      </div>
    </LoadingContainer>
  );
};
