import { KeyboardEvent } from "react";
import { tabbable } from "tabbable";

type KeyboardDropdownControlsProps = {
  trigger: React.RefObject<HTMLElement> | HTMLElement | undefined | null;
  wrapper: React.RefObject<HTMLElement> | HTMLElement | undefined | null;
  onClose: () => void;
};

const resolveElement = (
  elementOrRef: HTMLElement | React.RefObject<HTMLElement> | null | undefined
) => {
  if (!elementOrRef) return null;
  return "current" in elementOrRef ? elementOrRef.current : elementOrRef;
};

export const getKeyboardDropdownControlsHandler = ({
  trigger,
  wrapper,
  onClose,
}: KeyboardDropdownControlsProps) => {
  const handleEscape = () => {
    resolveElement(trigger)?.focus();
    onClose();
  };

  const handleTab = (event: KeyboardEvent<HTMLDivElement>) => {
    const wrapperElement = resolveElement(wrapper);
    const target = event.target as HTMLElement;
    const focusableElements = wrapperElement ? tabbable(wrapperElement) : [];

    if (!event.shiftKey && target === focusableElements.at(-1)) {
      focusableElements[0].focus();
      event.preventDefault();
      event.stopPropagation();
      return;
    }

    if (event.shiftKey && target === focusableElements[0]) {
      focusableElements.at(-1)?.focus();
      event.preventDefault();
      event.stopPropagation();
    }
  };

  return (event: KeyboardEvent<HTMLDivElement>) =>
    event.key === "Escape" ? handleEscape() : event.key === "Tab" && handleTab(event);
};
