import classNames from "classnames";
import React from "react";

import { useCopy } from "../../../extensions";
import { Money } from "../../../types";
import { OptionProps } from "../../InputDropdown/InputDropdown";
import { MoneyDisplay } from "../../MoneyDisplay/MoneyDisplay";
import styles from "./ShippingMethodOption.module.scss";

export interface ShippingMethodOptionProps extends OptionProps {
  description?: string;
  active?: boolean;
  money?: Money;
  locale?: string;
}

export const ShippingMethodOption = ({
  label,
  money,
  description,
  active,
  locale,
}: ShippingMethodOptionProps) => {
  const { _textStartingAt } = useCopy("ShippingCalculator");

  if (!money || !locale) return null;

  return (
    <div className={classNames(styles.container)}>
      <div>
        <p
          className={classNames(styles.header, {
            [styles.activeHeader]: active,
          })}
        >
          {label}
        </p>
        <p className={styles.description}>{description}</p>
      </div>
      <div className={styles.startingAtColumn}>
        <p className={styles.startingAt}>{_textStartingAt}</p>
        <p className={styles.money}>
          {money.currency && money.amount && (
            <MoneyDisplay
              money={{ currency: money.currency, amount: money.amount }}
              locale={locale}
            />
          )}
        </p>
      </div>
    </div>
  );
};
