import React, { useContext } from "react";

import { RevenueCalculator } from "../RevenueCalculator/RevenueCalculator";
import { PricingCalculatorContext } from "./PricingCalculatorContext";
import { isEligibleForDistro } from "./utils";

export const RevenueCalculatorAdapter = () => {
  const {
    podPackageId,
    state,
    productType,
    locale,
    priceMapQuery,
    listPriceBoundsQuery,
    manufacturingCostQuery,
    onRequestListPriceBounds,
    onRequestPriceMap,
    baseCurrency,
  } = useContext(PricingCalculatorContext);

  if (!podPackageId || !state.pageCount) return null;

  const selectedPodPackageIsEligibleForDistro = isEligibleForDistro(
    podPackageId,
    state.podPackages
  );

  return (
    <RevenueCalculator
      locale={locale}
      podPackageId={podPackageId}
      podPackageIsEligibleForGlobalDistro={selectedPodPackageIsEligibleForDistro}
      pageCount={state.pageCount}
      productType={productType}
      baseCurrency={baseCurrency || "USD"}
      priceMapQuery={priceMapQuery}
      manufacturingCostQuery={manufacturingCostQuery}
      listPriceBoundsQuery={listPriceBoundsQuery}
      onRequestListPriceBounds={onRequestListPriceBounds}
      onRequestPriceMap={onRequestPriceMap}
    />
  );
};
