import { ConfiguratorState } from "./reducer";
import { ConfigurationOptions, PodPackage, PodPackageOptions, ProductTypeEnum } from "./types";

export const getTrimSizeFromId = (id: string) => id.slice(0, 9);
export const getBindingTypeFromId = (id: string) => id.slice(14, 16);
export const getCoverFinishFromId = (id: string) => id.slice(24, 25);
export const getInteriorInkFromId = (id: string) => id.slice(9, 14);
export const getInteriorPaperFromId = (id: string) => id.slice(16, 21);
export const getLinenMaterialFromId = (id: string) => id.slice(25, 26);
export const getFoilStampFromId = (id: string) => id.slice(26, 27);

export const filterPodsByTrimSizeAndPageCount =
  (trimSize: string, pageCount: number) => (pod: PodPackage) =>
    pod.trimSize?.key === trimSize &&
    (pod.minPages ?? 0) <= pageCount &&
    (pod.maxPages ?? 0) >= pageCount;

export const filterPodsByInteriorInk = (interiorInk: string) => (pod: PodPackage) =>
  getInteriorInkFromId(pod.id ?? "") === interiorInk;

export const filterPodsByInteriorPaper = (interiorPaper: string) => (pod: PodPackage) =>
  getInteriorPaperFromId(pod.id ?? "") === interiorPaper;

export const filterPodsByBindingType = (bindingType: string) => (pod: PodPackage) =>
  getBindingTypeFromId(pod.id ?? "") === bindingType;

export const filterPodsByCoverFinish = (coverFinish: string) => (pod: PodPackage) =>
  getCoverFinishFromId(pod.id ?? "") === coverFinish;

export const sameLinenPodPackages = (podPackages: PodPackage[] = []) => {
  if (podPackages.length === 0) {
    return false;
  }

  const prefix = podPackages?.[0].id?.slice(0, 25);

  return (
    prefix &&
    podPackages.every((pod) => pod.id?.startsWith(prefix) && getLinenMaterialFromId(pod.id) !== "I")
  );
};

export const getAllOptionsFromPodPackages = (podPackages: PodPackage[]): PodPackageOptions => ({
  bindingTypeOptions: Array.from(
    new Set(podPackages.map((p: PodPackage) => getBindingTypeFromId(p.id ?? "")))
  ),
  coverFinishOptions: Array.from(
    new Set(podPackages.map((p: PodPackage) => getCoverFinishFromId(p.id ?? "")))
  ),
  foilStampOptions: Array.from(
    new Set(
      podPackages
        .map((p: PodPackage) => getFoilStampFromId(p.id ?? ""))
        .filter((option: string) => option !== "X")
    )
  ),
  interiorInkOptions: Array.from(
    new Set(podPackages.map((p: PodPackage) => getInteriorInkFromId(p.id ?? "")))
  ),
  interiorPaperOptions: Array.from(
    new Set(podPackages.map((p: PodPackage) => getInteriorPaperFromId(p.id ?? "")))
  ),
  linenMaterialOptions: Array.from(
    new Set(
      podPackages
        .map((p: PodPackage) => getLinenMaterialFromId(p.id ?? ""))
        .filter((option: string) => option !== "X")
    )
  ),
  trimSizes: Array.from(new Set(podPackages.map((p: PodPackage) => getTrimSizeFromId(p.id ?? "")))),
});

export const getFilteredOptions = (
  podPackages: PodPackage[] = [],
  trimSize: string | null = null,
  pageCount: number | null = null,
  distributionSelected = false,
  bindingType: string | null = null,
  interiorInk: string | null = null,
  interiorPaper: string | null = null
): ConfigurationOptions => {
  const distroEligiblePodPackages = podPackages.filter((p) => p.distributionEligible);
  const validPodPackages = distributionSelected ? distroEligiblePodPackages : podPackages;
  const filteredOptionsResult: ConfigurationOptions = {
    bindingTypeEnabledOptions: [],
    bindingTypeDistroEligibleOptions: [],
    coverFinishEnabledOptions: [],
    coverFinishDistroEligibleOptions: [],
    interiorInkEnabledOptions: [],
    interiorInkDistroEligibleOptions: [],
    interiorPaperEnabledOptions: [],
    interiorPaperDistroEligibleOptions: [],
    trimSizesDistroEligible: getAllOptionsFromPodPackages(
      podPackages.filter((p) => p.distributionEligible)
    ).trimSizes,
  };

  if (
    !trimSize ||
    !pageCount ||
    (distributionSelected && !filteredOptionsResult.trimSizesDistroEligible?.includes(trimSize))
  ) {
    return filteredOptionsResult;
  }

  filteredOptionsResult.pageCountDistroEligible =
    distroEligiblePodPackages.filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount)).length >
    0;

  if (distributionSelected && !filteredOptionsResult.pageCountDistroEligible)
    return filteredOptionsResult;

  filteredOptionsResult.bindingTypeEnabledOptions = getAllOptionsFromPodPackages(
    validPodPackages.filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
  ).bindingTypeOptions;

  filteredOptionsResult.bindingTypeDistroEligibleOptions = distributionSelected
    ? filteredOptionsResult.bindingTypeEnabledOptions
    : getAllOptionsFromPodPackages(
        distroEligiblePodPackages.filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
      ).bindingTypeOptions;

  if (!bindingType) {
    return filteredOptionsResult;
  }

  filteredOptionsResult.interiorInkEnabledOptions = getAllOptionsFromPodPackages(
    validPodPackages
      .filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
      .filter(filterPodsByBindingType(bindingType))
  ).interiorInkOptions;

  filteredOptionsResult.interiorInkDistroEligibleOptions = distributionSelected
    ? filteredOptionsResult.interiorInkEnabledOptions
    : getAllOptionsFromPodPackages(
        distroEligiblePodPackages
          .filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
          .filter(filterPodsByBindingType(bindingType))
      ).interiorInkOptions;

  if (!interiorInk) {
    return filteredOptionsResult;
  }

  filteredOptionsResult.interiorPaperEnabledOptions = getAllOptionsFromPodPackages(
    validPodPackages
      .filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
      .filter(filterPodsByInteriorInk(interiorInk))
      .filter(filterPodsByBindingType(bindingType))
  ).interiorPaperOptions;

  filteredOptionsResult.interiorPaperDistroEligibleOptions = distributionSelected
    ? filteredOptionsResult.interiorPaperEnabledOptions
    : getAllOptionsFromPodPackages(
        distroEligiblePodPackages
          .filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
          .filter(filterPodsByInteriorInk(interiorInk))
          .filter(filterPodsByBindingType(bindingType))
      ).interiorPaperOptions;

  if (!interiorPaper) {
    return filteredOptionsResult;
  }

  filteredOptionsResult.coverFinishEnabledOptions = getAllOptionsFromPodPackages(
    validPodPackages
      .filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
      .filter(filterPodsByInteriorInk(interiorInk))
      .filter(filterPodsByInteriorPaper(interiorPaper))
      .filter(filterPodsByBindingType(bindingType))
  ).coverFinishOptions;

  filteredOptionsResult.coverFinishDistroEligibleOptions = distributionSelected
    ? filteredOptionsResult.coverFinishEnabledOptions
    : getAllOptionsFromPodPackages(
        distroEligiblePodPackages
          .filter(filterPodsByTrimSizeAndPageCount(trimSize, pageCount))
          .filter(filterPodsByInteriorInk(interiorInk))
          .filter(filterPodsByInteriorPaper(interiorPaper))
          .filter(filterPodsByBindingType(bindingType))
      ).coverFinishOptions;

  return filteredOptionsResult;
};

export const getSelectedPodPackageId = (
  podPackages: PodPackage[] = [],
  trimSize: string | null = null,
  pageCount: number | null = null,
  distributionSelected = false,
  interiorInk: string | null = null,
  interiorPaper: string | null = null,
  bindingType: string | null = null,
  coverFinish: string | null = null
): string | null => {
  const pods = distributionSelected
    ? podPackages.filter((p) => p.distributionEligible)
    : podPackages;
  const filteredList = pods
    .filter(filterPodsByTrimSizeAndPageCount(trimSize ?? "", pageCount ?? 0))
    .filter(filterPodsByInteriorInk(interiorInk ?? ""))
    .filter(filterPodsByInteriorPaper(interiorPaper ?? ""))
    .filter(filterPodsByBindingType(bindingType ?? ""))
    .filter(filterPodsByCoverFinish(coverFinish ?? ""));

  if (filteredList.length === 1) {
    return filteredList[0].id ?? null;
  }

  // INFO: set initial linen/foil stamp color selections for linen wrap projects
  if (filteredList.length > 1 && bindingType === "LW" && sameLinenPodPackages(filteredList)) {
    // NG = Navy linen with Gold foil stamp text
    return filteredList.find((pod) => pod.id?.endsWith("NG"))?.id ?? null;
  }

  return null;
};

export const imageUrlBuilder = (filename: string) =>
  `https://assets.lulu.com/media/pricing-calculator/${filename}`;

export const isEligibleForDistro = (podPackageId: string, podPackages: PodPackage[]) =>
  podPackages.find((pp) => pp.id === podPackageId)?.distributionEligible ?? false;

export const getResetChoices = (
  state: ConfiguratorState,
  filteredOptions: ConfigurationOptions
): Partial<ConfiguratorState> => {
  const resetChoices: Partial<ConfiguratorState> = {};

  const options = ["coverFinish", "interiorPaper", "interiorInk", "bindingType"] as const;

  type Option = (typeof options)[number];

  type EnabledOptions = `${Option}EnabledOptions`;

  options.forEach((option) => {
    const selectedOption = state[option];
    if (
      selectedOption &&
      !filteredOptions[(option + "EnabledOptions") as EnabledOptions]?.includes(selectedOption)
    ) {
      resetChoices[option] = null;
    }
  });

  return resetChoices;
};

const LowerPageLimit: Record<string, number> = {
  COMIC_BOOK: 4,
  MAGAZINE: 4,
  YEARBOOK: 4,
  PREMIUM_PHOTO_BOOK: 24,
};

export const maxPageCountLimit = 800;

export const getMinPageCountByProductType = (productType?: ProductTypeEnum) =>
  LowerPageLimit[productType || ""] || 2;

export const getPagesLimitByProductType = (productType?: ProductTypeEnum) => {
  return `${getMinPageCountByProductType(productType)}-${maxPageCountLimit}`;
};
