import React, { useContext, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../../../../extensions";
import { displayCalendarTotalPageCount } from "../../../../../utils";
import { Error } from "../../../../Error/Error";
import { FloatingLabelInput } from "../../../../Input/FloatingLabelInput";
import { PricingCalculatorContext } from "../../../PricingCalculatorContext";
import { ConfiguratorActionType } from "../../../reducer";
import { SectionHeader } from "../../SectionHeader/SectionHeader";
import styles from "./CalendarSizeSelect.module.scss";

export const CalendarSizeSelect = () => {
  const { dispatch, getDisplayValue, state } = useContext(PricingCalculatorContext);

  const [monthCount, setMonthCount] = useState<number>();
  const [monthCountError, setMonthCountError] = useState(false);
  const calendarPageCount =
    monthCountError || !monthCount ? "" : displayCalendarTotalPageCount(monthCount * 2 - 2);

  const handleMonthCountChange = useDebouncedCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const inputValue = event.target.value;

      if (!/^[0-9]+$/.test(inputValue) || !inputValue) {
        setMonthCountError(true);
        setMonthCount(undefined);
        dispatch({
          payload: { value: null },
          type: ConfiguratorActionType.CHANGE_PAGE_COUNT,
        });
        return;
      }

      setMonthCountError(false);
      const newMonthCount = Number(inputValue);
      setMonthCount(newMonthCount);

      let newCount: number | null = newMonthCount * 2 - 2;

      if (newMonthCount < 2 || newMonthCount > 18) {
        setMonthCountError(true);
        newCount = null;
      }
      dispatch({
        payload: { value: newCount },
        type: ConfiguratorActionType.CHANGE_PAGE_COUNT,
      });
    },
    500
  );

  const {
    _textCalendarMonths,
    _textCalendarError,
    _textTotalPageCount,
    _textCalendarSize,
    _textCalendarSizeTitle,
  } = useCopy("PricingCalculator");

  const testIdPrefix = "calendar-size-select";

  return (
    <div className={styles.container} data-testid={testIdPrefix}>
      <SectionHeader title={_textCalendarSizeTitle} />
      <div className={styles.selectors}>
        <FloatingLabelInput
          label={_textCalendarMonths}
          onChange={handleMonthCountChange}
          defaultValue={monthCount?.toString() || ""}
          data-testid={`${testIdPrefix}-month-count`}
        />
        <FloatingLabelInput
          label={_textTotalPageCount}
          onChange={() => {}}
          value={calendarPageCount}
          disabled
          alternateDisabledStyle
          data-testid={`${testIdPrefix}-page-count`}
        />
      </div>
      <div className={styles.errorContainer}>
        <Error error={monthCountError ? _textCalendarError : undefined} />
      </div>
      <div className={styles.calendarSizeContainer}>
        <span className={styles.calendarSizeValue}>{getDisplayValue(state.trimSize ?? "")}</span>
        <span className={styles.calendarSizeLabel}>{_textCalendarSize}</span>
      </div>
    </div>
  );
};
