import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./StarRating.module.scss";

export interface StarRatingProps {
  dataTestId?: string;
  currentRating: number;
  submitRating?: (rating: number) => void;
  disabled?: boolean;
  maxRating?: number;
}

export const StarRating: React.FC<StarRatingProps> = ({
  dataTestId,
  currentRating,
  submitRating,
  maxRating = 5,
  disabled = false,
}) => (
  <div className={styles.starRating} data-testid={dataTestId}>
    <div className={disabled ? styles.starsDisabled : styles.stars}>
      {Array.from({ length: maxRating }, (_, index) => (
        <Icon
          key={index}
          ariaLabel={`Choose ${index + 1} stars`}
          ariaRole="button"
          iconName={Icons.ShopRatingFilledMedium}
          className={classNames({
            [styles.checked]: index < currentRating,
          })}
          onClick={() => {
            if (!disabled) {
              submitRating?.(index + 1);
            }
          }}
        />
      ))}
    </div>
  </div>
);
