import classNames from "classnames";
import React from "react";

import {
  checkIsVectorImage,
  getImageSources,
  getRetinaSourceSet,
  getRetinaUrlForGoogleCloudImages,
  prepareSVGImage,
} from "../../utils";
import { Link } from "../Link/Link";
import styles from "./ImageWithMetadata.module.scss";

export interface ImageWithMetadataProps {
  dataTestId?: string;
  image: {
    url: string;
    retina?: string;
  };
  imageForMobile?: {
    url?: string;
    retina?: string;
  };
  imageLinkUrl?: string;
  imageLinkPage?: {
    url?: string;
  };
  imageAltText?: string;
  newTab: boolean;
  highPriority?: boolean;
  imageClasses?: string;
  CustomImageComponent?: JSX.Element;
  externalImageUrl?: string;
  externalMobileImageUrl?: string;
}

export const ImageWithMetadata: React.FC<ImageWithMetadataProps> = ({
  dataTestId,
  image,
  imageForMobile,
  imageLinkUrl,
  imageLinkPage,
  imageAltText,
  newTab,
  imageClasses,
  CustomImageComponent,
  highPriority = false,
  externalImageUrl,
  externalMobileImageUrl,
}) => {
  const isVectorImage = checkIsVectorImage(image?.url);

  const ImageComponent = () => (
    <picture>
      {getImageSources({
        imageUrl: externalImageUrl || image?.url,
        mobileImageUrl: externalMobileImageUrl || imageForMobile?.url,
        desktopRetinaImageUrl: getRetinaUrlForGoogleCloudImages(externalImageUrl) || image?.retina,
        mobileRetinaImageUrl:
          getRetinaUrlForGoogleCloudImages(externalMobileImageUrl) || imageForMobile?.retina,
      })}
      <img
        srcSet={
          isVectorImage
            ? prepareSVGImage(image?.url)
            : getRetinaSourceSet(externalImageUrl || image?.url, externalImageUrl || image?.retina)
        }
        src={isVectorImage ? prepareSVGImage(image?.url) : externalImageUrl || image?.url}
        alt={imageAltText}
        data-testid={dataTestId}
        className={classNames(styles.metadataImage, imageClasses)}
        // @ts-ignore - modern browsers supports this prop
        fetchpriority={highPriority ? "high" : "auto"}
      />
    </picture>
  );

  if (!imageLinkPage && !imageLinkUrl) {
    return <>{CustomImageComponent ?? <ImageComponent />}</>;
  }

  return (
    <Link page={imageLinkPage} url={imageLinkUrl} newTab={newTab}>
      {CustomImageComponent ?? <ImageComponent />}
    </Link>
  );
};
