import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { printMoney, validAmount, validInput } from "../../utils";
import styles from "./ListPriceForm.module.scss";

export interface ListPriceFormProps {
  dataTestId?: string;
  active?: boolean;
  prices: ListPriceData[];
  disabled?: boolean;
  onChange: (listPrices: { amount: number; currency: string }[]) => void;
  onInvalidInput?: () => void;
}

export interface ListPriceData {
  currency: string;
  listPrice: string;
  minimumPrice: string;
  maximumPrice?: string | null;
  referencePrice?: string | null;
}

const formatPrices = (prices: ListPriceData[]) =>
  prices.map(({ currency, listPrice, minimumPrice, maximumPrice, referencePrice }) => ({
    currency,
    listPrice: printMoney(listPrice) ?? "",
    minimumPrice: printMoney(minimumPrice) ?? "",
    maximumPrice: maximumPrice ? printMoney(maximumPrice) : null,
    referencePrice: printMoney(referencePrice ?? ""),
  }));

const havePricesChanged = (curPrices: ListPriceData[], newPrices: ListPriceData[]) =>
  curPrices.some(
    (price) =>
      price.listPrice !== newPrices.find((p) => p.currency === price.currency)?.listPrice ||
      price.minimumPrice !== newPrices.find((p) => p.currency === price.currency)?.minimumPrice
  );

export const ListPriceForm: React.FC<ListPriceFormProps> = ({
  dataTestId,
  active,
  disabled = false,
  onChange,
  onInvalidInput,
  prices,
}) => {
  const { _textPriceTooLow, _textPriceTooHigh } = useCopy("ListPriceForm");
  const isInitialPropsEffect = React.useRef(true);

  const [priceFields, setPriceFields] = React.useState(formatPrices(prices));

  React.useEffect(() => {
    if (isInitialPropsEffect.current) {
      isInitialPropsEffect.current = false;
      if (!prices.every((price) => validatePriceField(price))) {
        return onInvalidInput?.();
      }
      return;
    }

    if (havePricesChanged(priceFields, prices)) {
      setPriceFields(formatPrices(prices));
    }
    //ToDo: figure out how to fix the dependencies
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prices]);

  const updatePriceFields = (currency: string, value: string) => {
    if (priceFields.find((price) => price.currency === currency)?.listPrice === value) {
      return;
    }

    if (validInput(value)) {
      const newFields = priceFields.map((field) =>
        field.currency === currency ? { ...field, listPrice: value } : field
      );

      setPriceFields(newFields);

      if (newFields.every((price) => validatePriceField(price))) {
        const formattedOutput = newFields.map(({ listPrice, currency }) => ({
          amount: Number(listPrice),
          currency,
        }));
        onChange(formattedOutput);
      } else {
        return onInvalidInput?.();
      }
    }
  };

  const handleMoneyInputChange =
    (currency: string) =>
    ({ target: { value } }: React.ChangeEvent<HTMLInputElement>) => {
      updatePriceFields(currency, value);
    };

  const formatMoneyInputValue =
    (currency: string) =>
    ({ target: { value } }: React.ChangeEvent<HTMLInputElement>) => {
      updatePriceFields(currency, printMoney(value) ?? "");
    };

  const validatePriceField = (field: ListPriceData) =>
    validAmount(field.listPrice) &&
    Number(field.listPrice) >= Number(field.minimumPrice) &&
    (field.maximumPrice ? Number(field.listPrice) <= Number(field.maximumPrice) : true);

  const labelClasses = classNames("field__label", "field__label--visible", {
    enabled: !active,
  });

  return (
    <div className={styles.listPriceForm} data-test-id={dataTestId}>
      {priceFields.map((price) => {
        const showError = price.listPrice && !validatePriceField(price);
        const fieldClasses = classNames("field", { "field--error": showError });

        return (
          <div className={styles.fieldContainer} key={price.currency}>
            <div className={fieldClasses}>
              <div className="field__input-wrapper">
                <label className={labelClasses} htmlFor={price.currency}>
                  {price.currency}
                </label>
                <input
                  className="field__input"
                  placeholder="_.__"
                  value={price.listPrice ?? ""}
                  onChange={handleMoneyInputChange(price.currency)}
                  onBlur={formatMoneyInputValue(price.currency)}
                  disabled={disabled}
                  id={price.currency}
                />
              </div>
              {showError && (
                <p className="field__message field__message--error" id="error-for-last_name">
                  {Number(price.listPrice) < Number(price.minimumPrice) ? (
                    <>{_textPriceTooLow}</>
                  ) : (
                    <>
                      {_textPriceTooHigh}
                      <br />
                      <span className={styles.minPriceLabel}>
                        Max: {printMoney(price.maximumPrice ?? undefined)}
                      </span>
                    </>
                  )}
                </p>
              )}
            </div>
            {price.referencePrice && (
              <div className={styles.minPriceLabel}>
                {price.currency} {printMoney(price.referencePrice)}
              </div>
            )}
            <div className={styles.minPriceLabel}>
              {price.currency} {printMoney(price.minimumPrice)}
            </div>
          </div>
        );
      })}
    </div>
  );
};
