import classNames from "classnames";
import React from "react";

import { ClientOnly } from "../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./Button.module.scss";

export interface ButtonProps extends React.HTMLProps<HTMLButtonElement> {
  name?: string;
  dataTestId?: string;
  downloadHref?: string;
  downloadTitle?: string;
  block?: boolean;
  responsive?: boolean;
  disabled?: boolean;
  loading?: boolean;
  download?: boolean;
  hasDownloadIcon?: boolean;
  isVerticallyStretched?: boolean;
  buttonSize?: "large" | "medium" | "small" | "extra-small";
  hierarchy?: "primary" | "secondary" | "tertiary" | "tertiary-white";
  type?: "button" | "submit" | "reset";
  icon?: Icons;
  progress?: number;
  downloadForceNewTab?: boolean;
  hasModalStyles?: boolean;
}

export const Button: React.FunctionComponent<ButtonProps> = ({
  children,
  hierarchy = "secondary",
  buttonSize = "medium",
  block = false,
  responsive = true,
  loading,
  icon,
  onClick,
  type = "button",
  disabled = false,
  dataTestId,
  download = false,
  downloadHref,
  downloadTitle,
  isVerticallyStretched = false,
  hasDownloadIcon = true,
  progress,
  downloadForceNewTab,
  hasModalStyles,
  ...props
}) => {
  const buttonClass = classNames(
    styles.button,
    styles[hierarchy],
    styles[buttonSize],
    {
      [styles.fullWidth]: block,
      [styles.responsive]: responsive,
      [styles.modalStyles]: hasModalStyles,
      [styles.isVerticallyStretched]: isVerticallyStretched,
    },
    props.className
  );

  const displayProgress = typeof progress === "number";

  const button = (iconName: Icons | undefined) => (
    <ClientOnly>
      <button
        {...props}
        data-testid={dataTestId}
        disabled={disabled}
        type={type}
        className={buttonClass}
        onClick={onClick}
        data-progress={displayProgress}
      >
        <span className={loading ? "invisible" : undefined}>
          {iconName && (
            <Icon iconName={iconName} className={children ? styles.iconLeft : undefined} />
          )}
          {children}
        </span>
        {loading &&
          (displayProgress ? (
            <>
              <div className={classNames(styles.progress, styles.gradient)}></div>
              <div
                className={classNames(styles.progress, styles.bar, styles[hierarchy])}
                style={{ width: `${100 - progress!}%` }}
              ></div>
              <div className={classNames(styles.progress, styles.percentage)}>{`${progress}%`}</div>
            </>
          ) : (
            <Icon iconName={Icons.DataProcessLarge} className={styles.loadingIcon} />
          ))}
      </button>
    </ClientOnly>
  );

  return download ? (
    <a
      href={downloadHref}
      download={downloadTitle || true}
      className={classNames(styles.downloadLink, { [styles.disabled]: disabled })}
      target={downloadForceNewTab ? "_blank" : "_self"}
      rel="noopener noreferrer"
    >
      {hasDownloadIcon ? button(Icons.DataDownloadLarge) : button(icon)}
    </a>
  ) : (
    button(icon)
  );
};
