import React, { useCallback, useEffect, useState } from "react";

import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../hooks";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import styles from "./Video.module.scss";

export interface VideoProps {
  dataTestId?: string;
  videoEmbedCode?: string;
  videoAsset?: {
    url?: string;
  };
  autoplay?: boolean;
  controls?: boolean;
  muted?: boolean;
  loop?: boolean;
}
interface EmbeddedVideoProps {
  dataTestId?: string;
  videoEmbedCode: string;
  muted: boolean;
  loop: boolean;
  autoPlay: boolean;
  controls: boolean;
}

const EmbeddedVideo: React.FC<EmbeddedVideoProps> = ({
  dataTestId,
  videoEmbedCode,
  muted,
  loop,
  autoPlay,
  controls,
}) => {
  const [isUserInitialInteraction, setIsUserInitialInteraction] = React.useState(false);

  const [displayVideo, setDisplayVideo] = useState(false);
  const isYoutubeVideo = videoEmbedCode.includes("youtube");

  let videoUrl: string | undefined;
  let videoId: string | undefined;
  let isFromCloudBucket = false;

  if (
    videoEmbedCode.startsWith("https://storage.googleapis.com/upp-media-upp-prod/") ||
    videoEmbedCode.startsWith("https://assets.lulu.com/media/")
  ) {
    videoUrl = videoEmbedCode;
    isFromCloudBucket = true;
  } else {
    videoUrl = videoEmbedCode.match(/src=".*?(?=[?"])/)?.[0].split('src="')[1];
    const splitedVideoUrl = videoUrl?.split("/");
    videoId = splitedVideoUrl?.[splitedVideoUrl.length - 1];
  }

  const videoThumbnailSrc = isYoutubeVideo
    ? `https://img.youtube.com/vi/${videoId}/maxresdefault.jpg`
    : `https://vumbnail.com/${videoId}.jpg`;

  const windowWidth = useWindowWidth();

  const handleUserInitialInteraction = useCallback(() => {
    if (!isUserInitialInteraction) setIsUserInitialInteraction(true);
  }, [isUserInitialInteraction]);

  const handleVimeoPlayer = useCallback(() => {
    try {
      // @ts-ignore - Vimeo is from script imported from Vimeo iframe api
      const player = new Vimeo.Player(`player${videoId}`, {
        url: "https://player.vimeo.com/video/" + videoId,
        width: 800,
        autoplay: true,
        muted: windowWidth <= MEDIA_BREAKPOINTS.S,
        playsinline: true,
      });

      player.on("play", () => {
        player.setMuted(false);
      });
    } catch (err) {
      console.warn("Vimeo Player SDK is not yet loaded");
      setTimeout(handleVimeoPlayer, 500);
    }
  }, [videoId, windowWidth]);

  const handleYoutubePlayer = useCallback(() => {
    const onPlayerReady = (event: YT.PlayerEvent) => {
      if (event?.target?.mute && event?.target?.playVideo) {
        windowWidth <= MEDIA_BREAKPOINTS.S && event.target.mute();
        event.target.playVideo();
      }
    };

    let isMuted = true;
    const onPlayerStateChange = (event: YT.OnStateChangeEvent) => {
      if (isMuted) {
        if (event?.target?.unMute) {
          setTimeout(() => event.target.unMute(), 100);
          isMuted = false;
        }
      }
    };

    try {
      new YT.Player(`player${videoId}`, {
        width: "100%",
        videoId: videoId,
        events: {
          onReady: onPlayerReady,
          onStateChange: onPlayerStateChange,
        },
      });
    } catch (err) {
      console.warn("Youtube Iframe API is not yet loaded");
      setTimeout(handleYoutubePlayer, 500);
    }
  }, [videoId, windowWidth]);

  useEffect(() => {
    const script = document.createElement("script");
    script.src = isYoutubeVideo
      ? "https://www.youtube.com/iframe_api"
      : "https://player.vimeo.com/api/player.js";
    script.async = true;
    script.type = "text/javascript";

    if (isUserInitialInteraction) document.head.appendChild(script);

    document.addEventListener("scroll", handleUserInitialInteraction);
    document.addEventListener("mousemove", handleUserInitialInteraction);
  }, [isYoutubeVideo, isUserInitialInteraction, handleUserInitialInteraction]);

  useEffect(() => {
    if (!displayVideo) return;

    isYoutubeVideo ? handleYoutubePlayer() : handleVimeoPlayer();
  }, [displayVideo, isYoutubeVideo, handleYoutubePlayer, handleVimeoPlayer]);

  return (
    <>
      {isFromCloudBucket ? (
        <video
          controls={controls}
          autoPlay={autoPlay}
          muted={muted}
          loop={loop}
          className={styles.gCloudVideo}
        >
          <source src={videoUrl} type="video/mp4" />
        </video>
      ) : (
        <div className={styles.videoContainer} data-testid={dataTestId}>
          {displayVideo ? (
            <span className={styles.playButtonWrapper}>
              <img alt="Loading video" src={videoThumbnailSrc} />
              <div id={`player${videoId}`} data-testid="videoWrapper" />
            </span>
          ) : (
            <NakedButton
              onClick={() => {
                setDisplayVideo(true);
              }}
              className={styles.playButtonWrapper}
              aria-label="Load video"
            >
              <span className={styles.playButtonWrapper_playButton}>
                <svg viewBox="0 0 24 24">
                  <path d="M8,5.14V19.14L19,12.14L8,5.14Z" />
                </svg>
              </span>
              <img alt="Video thumbnail" src={videoThumbnailSrc} />
            </NakedButton>
          )}
        </div>
      )}
    </>
  );
};

export const Video: React.FC<VideoProps> = ({
  dataTestId,
  videoEmbedCode,
  videoAsset,
  autoplay = false,
  controls = false,
  muted = false,
  loop = false,
}) => {
  if (videoEmbedCode) {
    return (
      <EmbeddedVideo
        dataTestId={dataTestId}
        videoEmbedCode={videoEmbedCode}
        muted={muted}
        loop={loop}
        autoPlay={autoplay}
        controls={controls}
      />
    );
  }

  if (videoAsset) {
    return (
      <video
        data-testid={dataTestId}
        muted={muted}
        loop={loop}
        autoPlay={autoplay}
        controls={controls}
        className={styles.videoPlayer}
      >
        <source src={videoAsset.url} type="video/mp4" />
      </video>
    );
  }

  return null;
};
