import React from "react";
import Media from "react-media";

import { ClientOnly } from "../../extensions";
import { BookFormatSelectionButton } from "./BookFormatSelectionButton/BookFormatSelectionButton";
import { BookFormatSelectionDropdown } from "./BookFormatSelectionDropdown/BookFormatSelectionDropdown";

export interface BookFormatSelectionProps {
  dataTestId?: string;
  options: FormatOption[];
  multipleOptions: boolean;
}

export interface FormatOption {
  iconName?: string;
  value?: string;
  label: string | React.ReactElement;
  price: string;
  priceBeforeDiscount?: string;
}

export const BookFormatSelectionContainer: React.FC<BookFormatSelectionProps> = ({
  dataTestId,
  options,
  multipleOptions,
}) => {
  const [selectedBookFormat, setFormat] = React.useState(options[0] || null);
  const mediaQueries = {
    largeScreen: 1024,
    mediumBigScreen: 840,
    smallScreen: 480,
  };
  const formattedOptions = multipleOptions ? options : [...options.slice(0, 1)];
  return (
    <div className="book-format-selection" data-testid={dataTestId}>
      <ClientOnly>
        <Media
          query={`(min-width: ${mediaQueries.largeScreen}px)`}
          render={() => (
            <BookFormatSelectionButton
              bookFormatButtonSize="large"
              selectedBookFormat={selectedBookFormat}
              setFormat={setFormat}
              options={formattedOptions}
            />
          )}
        />
        <Media
          query={`(min-width: ${mediaQueries.mediumBigScreen}px) and (max-width: ${
            mediaQueries.largeScreen - 1
          }px)`}
          render={() => (
            <BookFormatSelectionButton
              bookFormatButtonSize="medium-big"
              selectedBookFormat={selectedBookFormat}
              setFormat={setFormat}
              options={formattedOptions}
            />
          )}
        />
        {formattedOptions.length > 1 ? (
          <>
            <Media
              query={`(min-width: ${mediaQueries.smallScreen}px) and (max-width: ${
                mediaQueries.mediumBigScreen - 1
              }px)`}
              render={() => (
                <BookFormatSelectionDropdown
                  selectedBookFormat={selectedBookFormat}
                  setFormat={setFormat}
                  bookFormatDropdownSize="medium-small"
                  options={formattedOptions}
                />
              )}
            />
            <Media
              query={`(max-width: ${mediaQueries.smallScreen - 1}px)`}
              render={() => (
                <BookFormatSelectionDropdown
                  selectedBookFormat={selectedBookFormat}
                  setFormat={setFormat}
                  bookFormatDropdownSize="extra-small"
                  options={formattedOptions}
                />
              )}
            />
          </>
        ) : (
          <>
            <Media
              query={`(min-width: ${mediaQueries.smallScreen}px)
              and (max-width: ${mediaQueries.mediumBigScreen - 1}px)`}
              render={() => (
                <BookFormatSelectionButton
                  bookFormatButtonSize="large"
                  selectedBookFormat={selectedBookFormat}
                  setFormat={setFormat}
                  options={formattedOptions}
                />
              )}
            />
            <Media
              query={`(max-width: ${mediaQueries.smallScreen - 1}px)`}
              render={() => (
                <BookFormatSelectionButton
                  bookFormatButtonSize="large"
                  selectedBookFormat={selectedBookFormat}
                  setFormat={setFormat}
                  options={formattedOptions}
                />
              )}
            />
          </>
        )}
      </ClientOnly>
    </div>
  );
};
