import classNames from "classnames";
import React from "react";

import { LinkedIconAndText, LinkedIconAndTextProps } from "../LinkedIconAndText/LinkedIconAndText";
import { Video, VideoProps } from "../Video/Video";
import styles from "./ContentLinkImageCard.module.scss";

export interface ContentLinkImageCardProps {
  dataTestId?: string;
  videoRef?: VideoProps;
  heading: string;
  description: string;
  classes?: string;
  linksCollection?: { items: LinkedIconAndTextProps[] };
}

export const ContentLinkImageCard: React.FC<ContentLinkImageCardProps> = ({
  dataTestId,
  videoRef,
  heading,
  description,
  classes,
  linksCollection: { items } = {},
}) => (
  <div className={classes || "col-4"} data-testid={dataTestId}>
    <div className={styles.contentLinkImageCard}>
      {videoRef && <Video {...videoRef} />}
      <div className={styles.content}>
        <div>
          <div className={styles.headerBottomBorder}>
            <div>
              <h4 className={classNames(styles.heading, "contentful-h4")}>{heading}</h4>
            </div>
          </div>
          <p className={classNames(styles.description, "contentful-body")}>{description}</p>
          {items &&
            items.map((link: LinkedIconAndTextProps, index: number) => (
              <div className={styles.linkedIconAndText} key={`link-${index}`}>
                <LinkedIconAndText {...link} />
              </div>
            ))}
        </div>
      </div>
    </div>
  </div>
);
