import React from "react";

import styles from "./ColorPicker.module.scss";

interface Props {
  active?: boolean;
  children: string;
  onClick: () => void;
}

export const ColorPickerButton: React.FC<Props> = ({ active, children, onClick }) => (
  <div className={styles.buttonContainer}>
    <button className={active ? styles.buttonActive : styles.button} onClick={() => onClick()}>
      {children}
    </button>
    <div className={active ? styles.buttonArrowActive : styles.buttonArrow} />
  </div>
);
