import classNames from "classnames";
import React, { FC, useContext, useEffect, useRef, useState } from "react";

import { Icon as AmberIcon } from "../../amber/components/Icon/Icon";
import { Icons as AmberIcons } from "../../amber/icon-types";
import { CopyContext } from "../../extensions";
import { useMediaQuery, useMediaQuerySmallerThan } from "../../hooks";
import { isEnterOrSpaceKeyEvent } from "../../utils/isEnterOrSpaceKeyEvent";
import styles from "./Footer.module.scss";

interface FooterSelectProps {
  label: string;
  name: string;
  onClick: (arg: string) => void;
  options: {
    label: string;
    value: string;
  }[];
  selectedValue: string;
}

export const FooterSelect: FC<FooterSelectProps> = ({
  label,
  name,
  onClick,
  options,
  selectedValue,
}) => {
  const { _textLanguage } = useContext(CopyContext).Footer;
  const triggerRef = useRef<HTMLDivElement>(null);
  const dropdownRef = useRef<HTMLDivElement>(null);
  const accordionRef = useRef<HTMLDivElement>(null);
  const [showLinks, setShowLinks] = useState(false);
  const [shouldDisplayDropdownAbove, setShouldDisplayDropdownAbove] = useState(true);
  const [isTabletOrMobile, setIsTabletOrMobile] = useState(false);
  const isSmallerThanM = useMediaQuerySmallerThan("M");
  const isTouchScreenDevice = useMediaQuery("(pointer: coarse)");

  useEffect(() => {
    const handleResize = () => {
      if (isSmallerThanM) setIsTabletOrMobile(true);
      else {
        setIsTabletOrMobile(false);
        setShowLinks(false);
        setShouldDisplayDropdownAbove(true);
      }
    };
    handleResize();

    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, [isSmallerThanM]);

  useEffect(() => {
    const handleClickOutside = (event: Event) => {
      if (triggerRef?.current && !triggerRef.current.contains(event.target as Node) && showLinks) {
        setShowLinks(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showLinks]);

  const checkIfNotEnoughSpaceBelow = () => {
    if (triggerRef.current && dropdownRef.current) {
      const selectRect = triggerRef.current.getBoundingClientRect();
      const dropdownHeight = dropdownRef.current.offsetHeight;
      const spaceBelow = window.innerHeight - selectRect.bottom;
      setShouldDisplayDropdownAbove(spaceBelow < dropdownHeight);
    }
  };

  return (
    // eslint-disable-next-line jsx-a11y/no-static-element-interactions,jsx-a11y/click-events-have-key-events
    <div
      className={classNames(styles.footerSelect, {
        [styles.active]: showLinks,
      })}
      aria-label="footerSelect"
      onMouseOver={() => {
        if (isTabletOrMobile || isTouchScreenDevice) return;
        setShowLinks(true);
        checkIfNotEnoughSpaceBelow();
      }}
      onMouseOut={() => {
        if (isTabletOrMobile || isTouchScreenDevice) return;
        setShowLinks(false);
      }}
      onClick={() => {
        if (isTabletOrMobile || isTouchScreenDevice) setShowLinks((prev) => !prev);
      }}
      ref={triggerRef}
    >
      <div className={styles.footerSelected}>
        <div
          className={classNames(styles.footerLink, styles.mainNavLink, styles.link, {
            [styles.activeLink]: showLinks,
            [styles.active]: isTabletOrMobile && showLinks,
          })}
          data-testid={`${name}-selector`}
        >
          {isTabletOrMobile && _textLanguage} {selectedValue}
        </div>
        <AmberIcon iconName={AmberIcons.NavArrowDown} size={24} />
      </div>
      {isTabletOrMobile ? (
        <div
          className={classNames(styles.accordionContent, {
            [styles.active]: showLinks,
          })}
          style={showLinks ? { maxHeight: accordionRef.current?.scrollHeight } : undefined}
          ref={accordionRef}
        >
          {options.map(({ label, value }, index) => (
            <div
              role="presentation"
              key={index}
              onClick={() => onClick(value)}
              data-testid={`${name}-selector-${label.toLowerCase()}`}
            >
              {label}
            </div>
          ))}
        </div>
      ) : (
        <div
          className={classNames(styles.dropdownMenu, {
            [styles.up]: shouldDisplayDropdownAbove,
          })}
          style={{ visibility: showLinks ? "visible" : "hidden" }}
          ref={dropdownRef}
        >
          <div className={styles.content}>
            <div className={classNames(styles.title)}>{label}</div>
            {options.map(({ label, value }, index) => (
              <div
                role="button"
                tabIndex={0}
                className={styles.link}
                key={index}
                onClick={() => onClick(value)}
                data-testid={`${name}-selector-${label.toLowerCase()}`}
                onKeyDown={(e) => {
                  if (isEnterOrSpaceKeyEvent(e)) {
                    onClick(value);
                  }
                }}
              >
                {label}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};
