import { useEffect, useState } from "react";

export const useScrollToSection = () => {
  const [activeHash, setActiveHash] = useState("");

  useEffect(() => {
    const htmlTag = document.querySelector("html");
    if (htmlTag) {
      htmlTag.style.scrollBehavior = "smooth";
    }
    return () => {
      if (htmlTag) htmlTag.style.scrollBehavior = "auto";
    };
  }, []);

  useEffect(() => {
    const handleHashChange = () => {
      const hashLetter = window.location.hash.replace("#", "");
      if (hashLetter) {
        setActiveHash(hashLetter);
      }
    };
    handleHashChange();
    window.addEventListener("hashchange", handleHashChange);
    return () => window.removeEventListener("hashchange", handleHashChange);
  }, []);

  useEffect(() => {
    if (activeHash !== "") document.querySelector(`#${activeHash}`)?.scrollIntoView();
  }, [activeHash]);

  return { activeHash };
};
