import React, { useContext, useRef, useState } from "react";

import { Colors } from "../../../constants";
import { useCopy } from "../../../extensions";
import { MEDIA_BREAKPOINTS, useMediaQuery, useMediaQueryLargerThan } from "../../../hooks";
import { Icons } from "../../../icon-types";
import { displayCalendarMonthCount, displayCalendarTotalPageCount } from "../../../utils";
import { Button } from "../../Button/Button";
import { ButtonLink } from "../../ButtonLink/ButtonLink";
import { Icon } from "../../Icon/Icon";
import { PricingCalculatorContext } from "../PricingCalculatorContext";
import { TotalBar } from "../PricingConfigurator/TotalBar/TotalBar";
import { getTemplateFilename } from "../templateFileHelper";
import { ProductTypeEnum } from "../types";
import { useDownloadCoverTemplate } from "../useDownloadCoverTemplate";
import * as utils from "../utils";
import { imageUrlBuilder } from "../utils";
import styles from "./ProductSpecsPanel.module.scss";

const productTypeUrlMap: Record<ProductTypeEnum, string> = {
  CALENDAR: "calendar",
  COMIC_BOOK: "comic",
  EBOOK: "ebook",
  MAGAZINE: "magazine",
  PREMIUM_PHOTO_BOOK: "photo-book",
  PRINTED_BOOK: "print-book",
  YEARBOOK: "yearbook",
  BUNDLE: "bundle",
};

function getImage(
  podPackageId: string | null,
  productType: ProductTypeEnum,
  trimSize: string | null,
  bindingType: string | null
) {
  if (!podPackageId || !trimSize || !bindingType) {
    // fallback image
    switch (productType) {
      case "CALENDAR":
        return imageUrlBuilder(`ProductPreview/Calendar/lulu-calendar-1100X0850WO`);
      case "EBOOK":
        return imageUrlBuilder(`ProductPreview/Ebook/lulu-ebook-preview`);
      default:
        return imageUrlBuilder(`ProductPreview/Book/lulu-book-0850X1100PB`);
    }
  }

  let productTypePath;
  switch (productType) {
    case "CALENDAR":
      productTypePath = "Calendar/lulu-calendar";
      break;
    case "EBOOK":
      productTypePath = "Ebook/lulu-ebook-preview";
      break;
    default:
      productTypePath = "Book/lulu-book";
  }

  return imageUrlBuilder(`ProductPreview/${productTypePath}-${trimSize}${bindingType}`);
}

export const ProductSpecsPanel: React.FC = () => {
  const {
    getDisplayValue,
    productType,
    podPackageId,
    state,
    locale,
    wizardUrl: wizardUrlBase,
    isDeveloperPortalVersion,
  } = useContext(PricingCalculatorContext);

  const {
    _textCreateButton,
    _textDistributionEligibleTitle,
    _textDistributionEligibleYes,
    _textDistributionEligibleNo,
    _textSelectAllProductOptions,
    _textFileFormat,
    _textFileFormatEpubOrPdf,
    _textBookSize,
    _textBindingTypeTitle,
    _textPageCount,
    _textTotalPageCount,
    _textInteriorPageCount,
    _textCoverPageCount,
    _text2PageCoverDescription,
    _textInteriorColor,
    _textPaperType,
    _textCoverFinish,
    _textBookTemplates,
    _textCalendarTemplates,
    _textEbookCreationGuide,
    _textCustomCoverTemplate,
    _textCalendarMonths,
    _textCalendarSize,
  } = useCopy("PricingCalculator");

  const { _textCALENDAR, _textEBOOK, _textCW, _textLW, _textWO, _textSS, _textCO, _textPB } =
    useCopy("CartItem");

  const { downloadCoverTemplate, downloadingCoverTemplate } = useDownloadCoverTemplate(
    productType,
    podPackageId,
    state.pageCount
  );

  const [expanded, setExpanded] = useState(false);
  const ref = useRef<HTMLDivElement>(null);
  const isLargeScreen = useMediaQueryLargerThan("L");
  const isMediumBigScreenMin = useMediaQueryLargerThan("M_BIG");
  const isMediumBigScreenMax = useMediaQuery(`(max-width: ${MEDIA_BREAKPOINTS["L"] - 1}px)`);
  const isMediumBigScreen = isMediumBigScreenMin && isMediumBigScreenMax;

  const isCalendar = productType === "CALENDAR";
  const is2PageCover = productType === "COMIC_BOOK" || productType === "MAGAZINE";
  const isEbook = productType === "EBOOK";

  let wizardUrl;

  if (wizardUrlBase) {
    wizardUrl =
      isEbook || isCalendar
        ? `${wizardUrlBase}/${productTypeUrlMap[productType]}`
        : `${wizardUrlBase}/${productTypeUrlMap[productType]}?pcPodPackageId=${podPackageId}`;
  }

  const getActiveStyles = () => {
    if (!ref.current) {
      return {
        bottom: "-1000px",
      };
    }

    if (expanded || isLargeScreen) return {};

    return {
      bottom: `-${ref.current.offsetHeight}px`,
    };
  };

  const selectedPodPackageIsEligibleForDistro = podPackageId
    ? utils.isEligibleForDistro(podPackageId, state.podPackages)
    : isEbook;

  const templateDownloadFile =
    state.trimSize || isEbook
      ? getTemplateFilename(productType, state.trimSize, state.pageCount, locale)
      : undefined;

  const image = getImage(podPackageId, productType, state.trimSize, state.bindingType);

  const productLabelMap = new Map([
    ["CALENDAR", _textCALENDAR],
    ["EBOOK", _textEBOOK],
    ["CW", _textCW],
    ["LW", _textLW],
    ["WO", _textWO],
    ["SS", _textSS],
    ["CO", _textCO],
    ["PB", _textPB],
  ]);

  return (
    <>
      <div className={styles.specsPanel} ref={ref} style={getActiveStyles()}>
        <div>
          <picture className={styles.previewImage}>
            <source type="image/webp" srcSet={`${image}-1x.webp, ${image}-2x.webp 2x`} />
            <img
              className={styles.image}
              alt="Preview"
              src={image + "-1x.jpg"}
              srcSet={`${image}-1x.jpg, ${image}-2x.jpg 2x`}
            />
            {!podPackageId && !isEbook && (
              <div className={styles.unfinishedHint}>
                <span>{_textSelectAllProductOptions}</span>
              </div>
            )}
          </picture>
          {isMediumBigScreen && !isDeveloperPortalVersion && wizardUrl && (
            <ButtonLink
              block
              responsive={false}
              buttonSize="large"
              hierarchy="primary"
              disabled={!podPackageId && !isEbook}
              url={wizardUrl}
              dataTestId="create-button"
            >
              {`${_textCreateButton} ${getDisplayValue(productType)}`}
            </ButtonLink>
          )}
        </div>
        <div className={styles.specsAndActions}>
          <div className={styles.specs}>
            {!isDeveloperPortalVersion && (
              <div className={styles.title}>
                <h6>{_textDistributionEligibleTitle}</h6>
                <Icon
                  iconName={
                    !podPackageId && !isEbook
                      ? Icons.DistributionPending
                      : selectedPodPackageIsEligibleForDistro
                      ? Icons.DistributionYes
                      : Icons.DistributionNo
                  }
                  size={20}
                  color={
                    selectedPodPackageIsEligibleForDistro || isEbook
                      ? Colors.GreenDark
                      : Colors.DarkGray1
                  }
                />
                {(podPackageId || isEbook) && (
                  <span>
                    {selectedPodPackageIsEligibleForDistro
                      ? isEbook
                        ? "EPUB"
                        : _textDistributionEligibleYes
                      : _textDistributionEligibleNo}
                  </span>
                )}
              </div>
            )}
            <dl className={styles.specsTable}>
              {isEbook ? (
                <div className={styles.ebookFileFormat}>
                  <dt>{_textFileFormat}</dt>
                  <dd>{_textFileFormatEpubOrPdf}</dd>
                </div>
              ) : isCalendar ? (
                <>
                  <div>
                    <dt>{_textCalendarMonths}</dt>
                    <dd>{displayCalendarMonthCount(state.pageCount) ?? ""}</dd>
                  </div>
                  <div>
                    <dt>{_textTotalPageCount}</dt>
                    <dd>{state.pageCount ? displayCalendarTotalPageCount(state.pageCount) : ""}</dd>
                  </div>
                  <div>
                    <dt>{_textCalendarSize}</dt>
                    <dd>{state.trimSize ? getDisplayValue(`${state.trimSize}_short`) : ""}</dd>
                  </div>
                  <div>
                    <dt>{_textInteriorColor}</dt>
                    <dd>{state.interiorInk ? getDisplayValue(state.interiorInk) : ""}</dd>
                  </div>
                  <div>
                    <dt>{_textBindingTypeTitle}</dt>
                    <dd>
                      {state.bindingType
                        ? `${
                            locale.toLowerCase() === "en"
                              ? productLabelMap.get(state.bindingType) + " "
                              : ""
                          } ${getDisplayValue(state.bindingType)}`
                        : ""}
                    </dd>
                  </div>
                  <div>
                    <dt>{_textPaperType}</dt>
                    <dd>{state.interiorPaper ? getDisplayValue(state.interiorPaper) : ""}</dd>
                  </div>
                </>
              ) : (
                <>
                  <div>
                    <dt>{_textBookSize}</dt>
                    <dd>{state.trimSize ? getDisplayValue(`${state.trimSize}_short`) : ""}</dd>
                  </div>
                  <div>
                    <dt>{is2PageCover ? _textInteriorPageCount : _textPageCount}</dt>
                    <dd>{state.pageCount ? state.pageCount : ""}</dd>
                  </div>
                  <div>
                    <dt>{_textBindingTypeTitle}</dt>
                    <dd>
                      {state.bindingType
                        ? `${
                            locale.toLowerCase() === "en"
                              ? productLabelMap.get(state.bindingType) + " "
                              : ""
                          } ${getDisplayValue(state.bindingType)}`
                        : ""}
                    </dd>
                  </div>
                  {is2PageCover && (
                    <div>
                      <dt>{_textCoverPageCount}</dt>
                      <dd>{_text2PageCoverDescription}</dd>
                    </div>
                  )}
                  {is2PageCover ? (
                    <>
                      <div>
                        <dt>{_textPaperType}</dt>
                        <dd>{state.interiorPaper ? getDisplayValue(state.interiorPaper) : ""}</dd>
                      </div>
                      <div>
                        <dt>{_textInteriorColor}</dt>
                        <dd>{state.interiorInk ? getDisplayValue(state.interiorInk) : ""}</dd>
                      </div>
                    </>
                  ) : (
                    <>
                      <div>
                        <dt>{_textInteriorColor}</dt>
                        <dd>{state.interiorInk ? getDisplayValue(state.interiorInk) : ""}</dd>
                      </div>
                      <div>
                        <dt>{_textPaperType}</dt>
                        <dd>{state.interiorPaper ? getDisplayValue(state.interiorPaper) : ""}</dd>
                      </div>
                    </>
                  )}
                  <div>
                    <dt>{_textCoverFinish}</dt>
                    <dd>{state.coverFinish ? getDisplayValue(state.coverFinish) : ""}</dd>
                  </div>
                </>
              )}
            </dl>
          </div>
          <div className={styles.actions}>
            <div className={styles.subActions}>
              <Button
                block={!isMediumBigScreen}
                hierarchy="tertiary-white"
                icon={Icons.DataDownloadLarge}
                disabled={(!state.trimSize || !templateDownloadFile) && !isEbook}
                download
                downloadForceNewTab={isEbook}
                downloadHref={templateDownloadFile}
                downloadTitle={templateDownloadFile}
                dataTestId="template-download-button"
              >
                {isCalendar
                  ? _textCalendarTemplates
                  : isEbook
                  ? _textEbookCreationGuide
                  : _textBookTemplates}
              </Button>
              {!isCalendar && !isEbook && (
                <div>
                  <Button
                    block={!isMediumBigScreen}
                    hierarchy="tertiary-white"
                    icon={Icons.DataDownloadLarge}
                    disabled={!podPackageId}
                    loading={downloadingCoverTemplate}
                    onClick={() => downloadCoverTemplate()}
                    dataTestId="cover-download-button"
                  >
                    {_textCustomCoverTemplate}
                  </Button>
                </div>
              )}
            </div>
            {isLargeScreen && <TotalBar />}
            {!isMediumBigScreen && !isDeveloperPortalVersion && wizardUrl && (
              <ButtonLink
                block
                responsive={false}
                buttonSize="medium"
                hierarchy="primary"
                disabled={!podPackageId && !isEbook}
                url={wizardUrl}
                dataTestId="create-button"
              >
                {`${_textCreateButton} ${getDisplayValue(productType)}`}
              </ButtonLink>
            )}
          </div>
        </div>
      </div>
      {!isLargeScreen && (
        <TotalBar expanded={expanded} onClick={() => setExpanded((prev) => !prev)} />
      )}
    </>
  );
};
