import classNames from "classnames";
import React, { useState } from "react";

import { useScrollToSection } from "../../hooks/useScrollToSection";
import { Icons } from "../../icon-types";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { mapContentfulColorToStyleProp } from "../../utils";
import { Icon } from "../Icon/Icon";
import styles from "./QuickLinkCollection.module.scss";

export type QuickLinkTextSize = "H1" | "H2" | "H3" | "H4" | "H5" | "H6";
export interface QuickLinkCollectionProps {
  separator: string;
  headingCopy: string;
  quickLinksCollection: {
    items: {
      idToScroll: string;
      text: string;
      textSize?: QuickLinkTextSize;
    }[];
  };
  classes?: string;
  children?: React.ReactNode;
  textColor?: string;
}

export const QuickLinkCollection: React.FC<QuickLinkCollectionProps> = ({
  headingCopy,
  separator,
  quickLinksCollection: { items },
  classes,
  children,
  textColor,
}) => {
  const { activeHash } = useScrollToSection();
  const [isDrawerExpanded, setIsDrawerExpanded] = useState(false);

  if (children) {
    return (
      <div className={styles.quickLinkOnSidebar}>
        <nav className={styles.sidebarContainer}>
          <div className={styles.desktopStickyContainer}>
            <NakedButton className={styles.header} onClick={() => setIsDrawerExpanded((v) => !v)}>
              <h5
                className={classNames("contentful-h5")}
                style={{
                  color: mapContentfulColorToStyleProp(textColor || "Lulu Almost Black 90%"),
                }}
              >
                {headingCopy}
              </h5>
              <Icon
                iconName={isDrawerExpanded ? Icons.ArrowUpMedium : Icons.ArrowDownMedium}
                size={24}
              />
            </NakedButton>
            <div
              className={styles.links}
              style={
                isDrawerExpanded
                  ? {
                      height: "100%",
                    }
                  : {
                      height: 0,
                      padding: 0,
                    }
              }
            >
              {items.map((item, index) => (
                <div
                  className={classNames(styles.item, {
                    [styles.active]: activeHash === item.idToScroll,
                  })}
                  key={index}
                >
                  {activeHash === item.idToScroll && (
                    <Icon iconName={Icons.ArrowLinkRightSmall} size={24} />
                  )}
                  <a
                    href={`#${item.idToScroll}`}
                    target="_self"
                    onClick={() => setIsDrawerExpanded(false)}
                    className={`contentful-${(item.textSize || "H5").toLowerCase()}`}
                  >
                    {item.text}
                  </a>
                </div>
              ))}
            </div>
          </div>
        </nav>
        <div className={styles.content}>
          {children}
          <div className={styles.backToTopButton}>
            <button onClick={() => window.scrollTo(0, 0)}>
              <Icon iconName={Icons.ArrowUpMedium} size={24} />
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <section className={classes}>
      <h4
        className={classNames(styles.alignCenter, "contentful-h4")}
        style={{ color: mapContentfulColorToStyleProp(textColor || "Lulu Magenta") }}
      >
        {headingCopy}
      </h4>
      <div className={classNames(styles.alignCenter, "headline-700")}>
        {items.map((item, index) => (
          <React.Fragment key={index}>
            <a
              href={`#${item.idToScroll}`}
              target="_self"
              className={`contentful-${(item.textSize || "H5").toLowerCase()}`}
            >
              {item.text}
            </a>
            {index !== items.length - 1 ? ` ${separator} ` : ""}
          </React.Fragment>
        ))}
      </div>
    </section>
  );
};
