import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { isEnterOrSpaceKeyEvent } from "../../utils/isEnterOrSpaceKeyEvent";
import { Icon } from "../Icon/Icon";
import styles from "./Notification.module.scss";

export type NotificationType = "success" | "warning" | "error" | "neutral" | "marketing";
export interface NotificationProps {
  dataTestId?: string;
  type?: NotificationType;
  title?: string;
  marginBottom?: string;
  onClose?: () => void;
  fullWidth?: boolean;
  children?: React.ReactNode;
  transparentBackground?: boolean;
}

export const Notification: React.FC<NotificationProps> = ({
  children,
  dataTestId,
  type = "neutral",
  title,
  marginBottom,
  onClose,
  fullWidth,
  transparentBackground,
}) => {
  const icon: Record<NotificationType, Icons> = {
    error: Icons.NotificationError,
    neutral: Icons.NotificationNeutral,
    success: Icons.NotificationSuccess,
    warning: Icons.NotificationWarning,
    marketing: Icons.DiscountMediumCircle,
  };
  const iconColor: Record<NotificationType, Colors> = {
    error: Colors.RedDark,
    neutral: Colors.Purple,
    success: Colors.GreenDark,
    warning: Colors.OrangeDark,
    marketing: Colors.Magenta,
  };

  return (
    <div
      data-testid={dataTestId}
      className={classNames(`${styles.message} ${styles[type]}`, {
        [styles.transparent]: transparentBackground,
      })}
      style={{ marginBottom, width: fullWidth ? "100%" : "auto" }}
    >
      {onClose && (
        <div
          role="button"
          aria-label="close"
          className={styles.close}
          onClick={onClose}
          tabIndex={0}
          onKeyDown={(e) => {
            if (isEnterOrSpaceKeyEvent(e)) {
              onClose();
            }
          }}
        >
          <Icon iconName={Icons.DisplayCloseLarge} color={Colors.Indigo} />
        </div>
      )}
      <Icon iconName={icon[type]} color={iconColor[type]} size={28} className={styles.icon} />
      <div>
        {title && (
          <p className={styles.messageTitle}>
            <b>{title}</b>
          </p>
        )}
        {children}
      </div>
    </div>
  );
};
