import React from "react";

import styles from "./Toolbar.module.scss";

export interface ToolbarProps {
  dataTestId?: string;
  leftComponent?: React.ReactNode;
  rightComponent?: React.ReactNode;
  reversedAfterBreak?: boolean;
}

export const Toolbar: React.FC<ToolbarProps> = ({
  dataTestId,
  leftComponent,
  rightComponent,
  reversedAfterBreak = false,
}) => (
  <div
    data-testid={dataTestId}
    className={reversedAfterBreak ? styles.toolbarReversedMaxMedium : styles.toolbar}
  >
    <div className={styles.toolbarLeft}>{leftComponent}</div>
    <div className={styles.toolbarRight}>{rightComponent}</div>
  </div>
);
