import classNames from "classnames";
import React, { useEffect, useRef, useState } from "react";

import { Icon as AmberIcon } from "../../amber/components/Icon/Icon";
import { Icons as AmberIcons } from "../../amber/icon-types";
import { useMediaQuery, useMediaQuerySmallerThan } from "../../hooks";
import { Flyout } from "../Flyout/Flyout";
import { Link } from "../Link/Link";
import { LinkType } from "./Footer";
import styles from "./Footer.module.scss";

type FooterLinkCollectionProps = {
  rootLink?: LinkType;
  linkClassnames?: string;
  subLinks: LinkType[];
  name: string;
};

export const FooterLinkCollection: React.FC<FooterLinkCollectionProps> = ({
  linkClassnames,
  rootLink,
  subLinks,
  name,
}) => {
  const [showLinks, setShowLinks] = useState(false);
  const [isTabletOrMobile, setIsTabletOrMobile] = useState(false);
  const triggerRef = useRef<HTMLDivElement>(null);
  const accordionRef = useRef<HTMLDivElement>(null);
  const isSmallerThanM = useMediaQuerySmallerThan("M");
  const isTouchScreenDevice = useMediaQuery("(pointer: coarse)");

  useEffect(() => {
    const handleResize = () => {
      if (isSmallerThanM) setIsTabletOrMobile(true);
      else {
        setIsTabletOrMobile(false);
        setShowLinks(false);
      }
    };
    handleResize();

    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, [isSmallerThanM]);

  useEffect(() => {
    const handleClickOutside = (event: Event) => {
      if (triggerRef?.current && !triggerRef.current.contains(event.target as Node) && showLinks) {
        setShowLinks(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showLinks]);

  return (
    <div
      role="presentation"
      className={classNames(styles.footerLinkCollection, {
        [styles.expandedFooterLinkCollection]: showLinks,
      })}
      ref={triggerRef}
      onMouseOver={() => {
        if (isTabletOrMobile || isTouchScreenDevice) return;
        setShowLinks(true);
      }}
      onMouseOut={() => {
        if (isTabletOrMobile || isTouchScreenDevice) return;
        setShowLinks(false);
      }}
      onClick={() => {
        if (isTabletOrMobile || isTouchScreenDevice) setShowLinks((prev) => !prev);
      }}
    >
      <div
        className={classNames(styles.linkCollection, {
          [styles.active]: showLinks,
        })}
      >
        {rootLink ? (
          <Link
            className={classNames(linkClassnames, styles.footerLink, {
              [styles.activeLink]: showLinks,
            })}
            {...rootLink}
          />
        ) : (
          <span className={classNames(linkClassnames, styles.footerLink)}>{name}</span>
        )}
        <AmberIcon iconName={AmberIcons.NavArrowDown} size={24} />
      </div>
      {isTabletOrMobile ? (
        <div
          className={classNames(styles.accordionContent, {
            [styles.active]: showLinks,
          })}
          style={showLinks ? { maxHeight: accordionRef.current?.scrollHeight } : undefined}
          ref={accordionRef}
        >
          {subLinks.map((link, index) => (
            <Link
              className={classNames(linkClassnames, styles.accordionLink)}
              key={`${link.name}${index}`}
              {...link}
            />
          ))}
        </div>
      ) : (
        <Flyout
          show={showLinks}
          size="small"
          triggerRef={triggerRef}
          customPosition={{
            x: 0,
            y: triggerRef.current?.getBoundingClientRect().height || 40, // 40 - default height of the link
          }}
          className={styles.flyout}
          noSpacing
        >
          {subLinks.map((link, index) => (
            <Link
              className={classNames(linkClassnames, styles.flyoutLink)}
              key={`${link.name}${index}`}
              {...link}
            />
          ))}
        </Flyout>
      )}
    </div>
  );
};
