import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { Link } from "../Link/Link";
import { MoneyDisplay } from "../MoneyDisplay/MoneyDisplay";
import { ProductTypeEnum } from "../PricingCalculator/types";
import {
  getBindingTypeFromId,
  getCoverFinishFromId,
  getInteriorInkFromId,
  getInteriorPaperFromId,
  getTrimSizeFromId,
} from "../PricingCalculator/utils";
import { ProjectFields, ProjectLineOfBusiness } from "../ProjectSelect/types";
import { CoverThumbnail } from "./CoverThumbnail/CoverThumbnail";
import styles from "./ProjectTile.module.scss";

export interface ProjectTileProps extends ProjectFields {
  getDisplayValue: (rawString?: string | null) => string | undefined;
  luluAppUrl?: string;
  selectable?: boolean;
  displaySpecification?: boolean;
  dataTestIdPrefix?: string;
  displayVersionNumber?: boolean;
  whiteBackground?: boolean;
  lineOfBusiness: ProjectLineOfBusiness;
  ProjectStatus?: React.FC;
  displayPrintCost?: boolean;
  luluAdminUrl?: string;
  optionValues?: { option: string; value: string }[];
}

export const ProjectTile: React.FC<ProjectTileProps> = ({
  id,
  projectTitle,
  manufacturingCostPerChannel,
  productType,
  thumbnailUrl,
  podPackageId,
  selectable = true,
  displaySpecification = true,
  displayPrintCost = true,
  dataTestIdPrefix,
  luluAppUrl,
  luluAdminUrl,
  getDisplayValue,
  bundledProjectsCount,
  displayVersionNumber,
  latestVersionNumber,
  whiteBackground,
  lineOfBusiness,
  ProjectStatus,
  optionValues,
}) => {
  const {
    _textLuluProjectId,
    _textPrintCost,
    ProjectTile: { _textIncludes, _textLulu, _textProjects, _textProject, _textVersion },
  } = useCopy("ProjectSelect");
  const manufacturingCost = displayPrintCost
    ? manufacturingCostPerChannel?.find(
        (channelManufacturingCost) => channelManufacturingCost.channel === lineOfBusiness
      )?.manufacturingCost
    : null;

  return (
    <div
      data-testid={`${`${dataTestIdPrefix ? `${dataTestIdPrefix}-` : ""}project-tile`}${
        selectable ? "-option" : ""
      }`}
      className={classNames(styles.projectCard, {
        [styles.whiteBackground]: whiteBackground,
      })}
      style={{
        cursor: selectable ? "pointer" : "auto",
      }}
    >
      <CoverThumbnail url={thumbnailUrl ?? undefined} containerClasses={styles.projectThumbnail} />
      <div className={styles.contentContainer}>
        <div className={styles.detailsLeftCol}>
          <h3 className={styles.projectTitle}>
            {projectTitle}{" "}
            {(!!luluAdminUrl || !!luluAppUrl) && (
              <Link
                className={styles.projectLink}
                url={luluAdminUrl ?? `${luluAppUrl}account/projects/${id}`}
                newTab={!luluAdminUrl}
              >
                <Icon iconName={Icons.ArrowLinkRightSmall} size={24} />
              </Link>
            )}
          </h3>
          <div className={styles.detailRow}>
            {displayVersionNumber ? (
              `${_textVersion} ${latestVersionNumber} | ID ${id}`
            ) : (
              <>
                {_textLuluProjectId} <span>{id}</span>
              </>
            )}
          </div>
          {!!manufacturingCost && (
            <div className={styles.detailRow}>
              {_textPrintCost}{" "}
              <span>
                <MoneyDisplay money={manufacturingCost} />
              </span>
            </div>
          )}
          {!!optionValues?.length &&
            optionValues.map((optionValue, index) => (
              <div className={classNames(styles.detailRow, styles.optionValue)} key={index}>
                {optionValue.value}
              </div>
            ))}
        </div>
        {displaySpecification && (
          <div className={styles.detailsRightCol}>
            {!!productType && (
              <div className={styles.projectType}>
                <FormatProductType productType={productType} />
              </div>
            )}
            {productType === "BUNDLE" ? (
              <div
                className={styles.podPackageDetails}
              >{`${_textIncludes} ${bundledProjectsCount} ${_textLulu} ${
                bundledProjectsCount === 1 ? _textProject : _textProjects
              }`}</div>
            ) : (
              !!podPackageId && (
                <div className={styles.podPackageDetails}>
                  {getPodPackageDescription(getDisplayValue, podPackageId)}
                </div>
              )
            )}
          </div>
        )}
        {!!ProjectStatus && (
          <div className={styles.statusContainer}>
            <ProjectStatus />
          </div>
        )}
      </div>
    </div>
  );
};

const FormatProductType: React.FC<{ productType: ProductTypeEnum }> = ({ productType }) => {
  const { ProductTypeFormat } = useCopy("ProjectSelect");

  const Format: Record<ProductTypeEnum, string> = {
    CALENDAR: ProductTypeFormat._textCalendar,
    EBOOK: ProductTypeFormat._textEbook,
    PRINTED_BOOK: ProductTypeFormat._textPrintedBook,
    PREMIUM_PHOTO_BOOK: ProductTypeFormat._textPremiumPhotoBook,
    MAGAZINE: ProductTypeFormat._textMagazine,
    COMIC_BOOK: ProductTypeFormat._textComicBook,
    YEARBOOK: ProductTypeFormat._textYearBook,
    BUNDLE: ProductTypeFormat._textBundle,
  };
  return Format[productType];
};

const getPodPackageDescription = (getDisplayValue: Function, podPackageId: string) =>
  [
    getDisplayValue(getTrimSizeFromId(podPackageId)),
    getDisplayValue(getInteriorInkFromId(podPackageId)),
    getDisplayValue(getInteriorPaperFromId(podPackageId)),
    getDisplayValue(getBindingTypeFromId(podPackageId)),
    getDisplayValue(getCoverFinishFromId(podPackageId)),
  ]
    .filter((item) => item !== undefined)
    .join(", ");
