import classNames from "classnames";
import React, { useState } from "react";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./FileUpload.module.scss";

interface Props {
  uploading: boolean;
  progress: number | null;
  status?: string;
  fileName?: string;
  customUploadMessage?: string;
  forceLoadingProgress?: boolean;
  preventProgressDecrementation?: boolean;
}

export const FileUploadProgress: React.FC<Props> = ({
  uploading,
  progress: _progress,
  fileName,
  status = "UNKNOWN",
  customUploadMessage,
  forceLoadingProgress,
  preventProgressDecrementation,
}) => {
  const {
    _textFile,
    _textYour,
    _textIs,
    _textUploading,
    _textValidating,
    _textNormalizing,
    _textProcessing,
  } = useCopy("FileUpload");
  const [oldProgress, setOldProgress] = useState(0);

  const showProgressBar = ["VALIDATING", "NORMALIZING"].includes(status) || forceLoadingProgress;
  const progress = _progress ?? 0;

  const getProgress = () => {
    if (!preventProgressDecrementation) {
      return `${progress}%`;
    }

    if ((progress ?? 0) > oldProgress) {
      setOldProgress(progress);

      return `${progress}%`;
    }

    return `${oldProgress}%`;
  };

  const getUploadMessage = () => {
    if (customUploadMessage) {
      return customUploadMessage;
    }

    let statusText;
    switch (status) {
      case "VALIDATING":
        statusText = _textValidating;
        break;
      case "NORMALIZING":
        statusText = _textNormalizing;
        break;
      default:
        statusText = _textProcessing;
    }

    return `${_textYour} ${_textFile} ${_textIs} ${uploading ? _textUploading : statusText}`;
  };

  return (
    <div className={styles.loading}>
      <p className={styles.dropzoneDescription}>
        <span>
          <Icon iconName={uploading ? Icons.DataUploadLarge : Icons.DataValidateLarge} />
          {getUploadMessage()}
        </span>
        <span className={styles.dropzonePercentage}>{!uploading && getProgress()}</span>
      </p>
      <div className={styles.loadingBar} data-testid="file-upload-progress-loading-bar-container">
        <span
          data-testid="file-upload-progress-loading-bar"
          className={classNames(styles.loadingContent, {
            [styles.loadingContentProgress]: showProgressBar,
          })}
          style={showProgressBar && !uploading ? { width: getProgress() } : {}}
        />
      </div>
      <p className={styles.fileName}>{fileName}</p>
    </div>
  );
};
