import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Checkbox } from "../Checkbox/Checkbox";
import { Icon } from "../Icon/Icon";
import { ImageUploadBoxProps } from "./ImageUploadBox";
import styles from "./ImageUploadBox.module.scss";

export const CornerAction: React.FC<Partial<ImageUploadBoxProps>> = ({
  select,
  deleting,
  deleteImage,
  dataTestId,
}) => (
  <div className={styles.cornerActionWrapper}>
    {!!select
      ? renderCheckbox(select, dataTestId)
      : deleting
      ? renderDeleting(dataTestId)
      : renderDeleteIcon(deleteImage, dataTestId)}
  </div>
);

const renderCheckbox = (
  select: ImageUploadBoxProps["select"],
  dataTestId: ImageUploadBoxProps["dataTestId"]
) => {
  if (!select) return null;

  return (
    <Checkbox
      resetLabelStyles={true}
      dataTestId={!!dataTestId ? `${dataTestId}-selectCheckbox` : undefined}
      className={classNames(styles.checkbox, styles.icon)}
      onChange={() => select.onSelect()}
      checked={select.isSelected}
    />
  );
};

const renderDeleting = (dataTestId: ImageUploadBoxProps["dataTestId"]) => (
  <div
    className={styles.deleting}
    data-testid={!!dataTestId ? `${dataTestId}-deleting` : undefined}
  >
    <span>
      <Icon iconName={Icons.DataProcessLarge} />
    </span>
  </div>
);

const renderDeleteIcon = (
  deleteImage: ImageUploadBoxProps["deleteImage"],
  dataTestId: ImageUploadBoxProps["dataTestId"]
) => (
  <Icon
    dataTestId={!!dataTestId ? `${dataTestId}-deleteIcon` : undefined}
    ariaLabel="Delete"
    iconName={Icons.DataDeleteLarge}
    className={classNames(styles.delete, styles.icon)}
    onClick={(e) => {
      e.stopPropagation();
      if (deleteImage) {
        deleteImage();
      }
    }}
  />
);
