import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { Link, LinkProps } from "../Link/Link";
import styles from "./LinkedIconAndText.module.scss";

export interface LinkedIconAndTextProps {
  dataTestId?: string;
  className?: string;
  iconSize?: number;
  linkRef: LinkProps;
  icon?: Icons;
}

export const LinkedIconAndText: React.FC<LinkedIconAndTextProps> = ({
  dataTestId,
  linkRef,
  icon,
  iconSize,
  className,
}) => (
  <div
    className={classNames(styles.linkedIconAndTextContainer, className)}
    data-testid={dataTestId}
  >
    {icon && <Icon iconName={icon} className={styles.icon} size={iconSize} />}
    <Link {...linkRef} className={classNames(linkRef.className, "contentful-body")} />
  </div>
);
