import React, { ReactNode } from "react";
import { useParams } from "react-router-dom";
import { components, MenuProps, SingleValue, SingleValueProps } from "react-select";

import { useCopy } from "../../extensions";
import { CustomSelect, CustomSelectProps, Option } from "../CustomSelect/CustomSelect";
import { ProjectTile, ProjectTileProps } from "../ProjectTile/ProjectTile";
import styles from "./ProjectSelect.module.scss";
import { ProjectFields } from "./types";

export interface ProjectSelectProps {
  selectedValue: ProjectFields | null;
  onChange: (value: SingleValue<SelectOption<ProjectFields>>) => void;
  fetchProjects: (input?: string) => void;
  projects: ProjectFields[];
  projectsLoading: boolean;
  label?: string;
  placeholder?: string;
  displaySelectedValue?: boolean;
  error?: string;
  stopFetchingProjects?: boolean;
  projectIdsToOmit?: string[];
  defaultValue?: ProjectFields | null;
  dataTestIdPrefix?: string;
  projectTileOptions: Pick<
    ProjectTileProps,
    | "luluAppUrl"
    | "lineOfBusiness"
    | "displayVersionNumber"
    | "getDisplayValue"
    | "displayPrintCost"
  >;
}

export const ProjectSelect: React.FC<ProjectSelectProps> = ({
  label,
  placeholder,
  displaySelectedValue = true,
  selectedValue,
  onChange,
  error,
  stopFetchingProjects,
  projectIdsToOmit,
  defaultValue,
  dataTestIdPrefix,
  projects,
  projectsLoading,
  fetchProjects,
  projectTileOptions,
}) => {
  const { storeId } = useParams<{ storeId: string }>();
  const { _textPublishedProjects, _textPlaceholder, _textLabel } = useCopy("ProjectSelect");

  const projectsToDisplay = getProjectsToDisplay(projects, projectIdsToOmit);

  const defaultProject = defaultValue
    ? { ...defaultValue, value: defaultValue.id!, label: defaultValue.projectTitle! }
    : undefined;

  React.useEffect(() => {
    if (stopFetchingProjects) return;
    fetchProjects();
  }, [stopFetchingProjects, fetchProjects, storeId]);

  return (
    <Select<ProjectFields>
      dataTestId={`${dataTestIdPrefix ? `${dataTestIdPrefix}-` : ""}project-select`}
      defaultValue={defaultProject}
      label={label ?? _textLabel}
      placeholder={placeholder || _textPlaceholder}
      helperText={_textPublishedProjects}
      displaySelectedValue={displaySelectedValue}
      selectedValue={selectedValue}
      ListItem={(props) => (
        <ProjectTile dataTestIdPrefix={dataTestIdPrefix} {...props} {...projectTileOptions} />
      )}
      isLoading={projectsLoading}
      onChange={onChange}
      error={error}
      options={
        projectsToDisplay.map(
          (project) =>
            ({
              ...project,
              value: project.id,
              label: project.projectTitle,
            } as SelectOption<ProjectFields>)
        ) ?? []
      }
      onInputChange={fetchProjects}
    />
  );
};

const getProjectsToDisplay = (projects: ProjectFields[], projectIdsToOmit?: string[]) => {
  const filteredProjects = (
    projectIdsToOmit?.length
      ? projects.filter(
          (project) => !projectIdsToOmit.some((projectIdToOmit) => projectIdToOmit === project?.id)
        )
      : projects
  ).filter(Boolean);

  const uniqueProjects = new Set();

  return filteredProjects.filter((project) => {
    const stringifiedProject = JSON.stringify(project);

    if (!uniqueProjects.has(stringifiedProject)) {
      uniqueProjects.add(stringifiedProject);
      return true;
    }
    return false;
  });
};

type SelectOption<SelectType> = SelectType & Option;

function SelectSingleValue<T>({ children, ...props }: SingleValueProps<SelectOption<T>>) {
  return <components.SingleValue {...props}>{props.data.label}</components.SingleValue>;
}

type SelectMenuProps<T> = MenuProps<T> & { helperText?: ReactNode; hasHeader?: boolean };

const SelectMenu = <T extends Option>({
  children,
  helperText,
  hasHeader = true,
  ...props
}: SelectMenuProps<T>) => {
  const {
    ProjectSelect: { _textResultsForPage, _textResults },
  } = useCopy();

  return (
    <components.Menu {...props}>
      {hasHeader && (
        <span className={`${styles.selectMenuHeader} almost-black-text-60`}>
          {helperText
            ? helperText
            : !!props.selectProps.inputValue
            ? `${_textResultsForPage} "${props.selectProps.inputValue}"`
            : _textResults}
        </span>
      )}
      {children}
    </components.Menu>
  );
};

type SelectInputProps<T> = Pick<
  CustomSelectProps<T>,
  | "options"
  | "onInputChange"
  | "isLoading"
  | "label"
  | "placeholder"
  | "disabled"
  | "isClearable"
  | "isSearchable"
  | "defaultValue"
  | "dataTestId"
> &
  Pick<SelectMenuProps<T>, "helperText"> & {
    onChange: (value: SingleValue<SelectOption<T>>) => void;
    error?: string;
    ListItem: React.FunctionComponent<T & { selectable?: boolean; whiteBackground?: boolean }>;
    displaySelectedValue?: boolean;
    hasHeader?: boolean;
  };

const SelectInput = <T,>({
  onChange,
  ListItem,
  helperText,
  hasHeader,
  isClearable,
  isSearchable,
  ...customSelectProps
}: SelectInputProps<T>) => {
  return (
    <CustomSelect<SelectOption<T>>
      {...customSelectProps}
      isSearchable={isSearchable}
      isClearable={isClearable}
      formatOptionLabel={ListItem}
      onChange={(value) => {
        onChange(value as SelectOption<T>);
      }}
      components={{
        SingleValue: SelectSingleValue,
        Menu: (props) => (
          <SelectMenu<SelectOption<T>> {...props} helperText={helperText} hasHeader={hasHeader} />
        ),
      }}
    />
  );
};

const Select = <T,>({
  displaySelectedValue = true,
  selectedValue,
  ListItem,
  ...customSelectProps
}: SelectInputProps<T> & { selectedValue?: T | null }) => {
  return (
    <div className={styles.selectContainer}>
      <SelectInput ListItem={ListItem} {...customSelectProps} />
      {displaySelectedValue && selectedValue && (
        <ListItem {...selectedValue} selectable={false} whiteBackground={true} />
      )}
    </div>
  );
};
