import classNames from "classnames";
import { useMemo } from "react";
import React from "react";
import { Link } from "react-router-dom";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { formatDateAndTime } from "../../utils";
import { getOrderNumber, useStatusIcons } from "../../utils";
import { Button } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import { LinkedIconAndText } from "../LinkedIconAndText/LinkedIconAndText";
import { MoneyDisplay } from "../MoneyDisplay/MoneyDisplay";
import { StatusInfo } from "../StatusInfo/StatusInfo";
import { AddressDisplay } from "./AddressDisplay";
import styles from "./OrderDetails.module.scss";
import { OrderItemProduct } from "./OrderItemProduct";
import { PaymentMethodDetails } from "./PaymentMethodDetails";
import { Order, OrderLineItem, ProductLink } from "./types";
import { showGiftReceipt } from "./utils";

export const ORDER_TRACKING_LINK_TEST_ID = "order-tracking-link";

export interface OrderDetailsProps {
  goBackUrl?: string;
  locale: string;
  onCancelOrder?: () => void;
  onDownloadInvoice?: () => void;
  onShowGiftReceipt?: () => void;
  order: Order;
  renderProductLink: (productLinkAttributes: ProductLink) => React.ReactNode;
  getDisplayValue: (rawString?: string, plural?: boolean) => string | undefined;
  getCountryName: (name: string | number, lang: string) => string;
  __debug__ignoreGiftReciptDateConstraints?: boolean;
}

export const OrderDetails: React.FunctionComponent<OrderDetailsProps> = ({
  goBackUrl,
  onShowGiftReceipt,
  onCancelOrder,
  onDownloadInvoice,
  renderProductLink,
  order,
  locale,
  getDisplayValue,
  getCountryName,
  __debug__ignoreGiftReciptDateConstraints,
}) => {
  const allProducts = useMemo(() => {
    const products: OrderLineItem[] = [];
    const productsWithErrors: OrderLineItem[] = [];

    if (order && order.id) {
      order.lineItems?.forEach((item: OrderLineItem) => {
        if (item.status === "ERROR") {
          productsWithErrors.push(item);
        } else {
          products.push(item);
        }
      });
    }

    return { products, productsWithErrors };
  }, [order]);

  const hasDispatchEstimates =
    order?.shippingMethod?.minDispatchDate && order?.shippingMethod?.maxDispatchDate;
  const hasDeliveryEstimates =
    order?.shippingMethod?.minDeliveryDate && order?.shippingMethod?.maxDeliveryDate;

  const phoneNumber = order.shippingAddress?.phone || order.billingAddress?.phone;

  const isGiftReceiptAvailable =
    showGiftReceipt(order, __debug__ignoreGiftReciptDateConstraints) && !!onShowGiftReceipt;

  const {
    _textShippingMethod,
    _textOrderTime,
    _textShipDate,
    _textArrivalDate,
    _textTracking,
    _textOrderTracking,
    _textShippingAddress,
    _textEmail,
    _textPhone,
    _textBillingAddress,
    _textItemSubtotal,
    _textExcludingShippingTax,
    _textShippingHandling,
    _textExcludingTax,
    _textSalesTax,
    _textPaymentTotal,
    _textOrderNumber,
  } = useCopy("OrderDetails");

  const statusIcon = useStatusIcons()[order.status || "ERROR"];

  return (
    <div className={styles.baseContainer}>
      <div className={styles.orderDetails}>
        <div className={styles.header}>
          {!!goBackUrl && <GoBack url={goBackUrl} />}
          <div
            className={classNames(styles.headerContainer, {
              [styles.containerMultipleButtons]: isGiftReceiptAvailable && !!onDownloadInvoice,
            })}
          >
            <div className={styles.status}>
              <h1>
                {_textOrderNumber}: {getOrderNumber(order.id, order.referenceNo)}
              </h1>
              <StatusInfo {...statusIcon} />
            </div>
            <div className={styles.actionsContainer}>
              {isGiftReceiptAvailable && onShowGiftReceipt && (
                <ShowGiftReceipt onClick={onShowGiftReceipt} />
              )}
              {onDownloadInvoice && <DownloadInvoice onClick={onDownloadInvoice} />}
            </div>
          </div>
          {order.dateCreated && (
            <h3 className="subhead">
              {_textOrderTime} {formatDateAndTime(locale, order.dateCreated).join(", ")}
            </h3>
          )}
        </div>
        <div className={styles.infoBar}>
          <div>
            {order.shippingMethod && (
              <div className={styles.infoColumn}>
                <p>{_textShippingMethod}</p>
                <p>
                  {order.carrierName
                    ? `${order.shippingMethod?.name} - ${order.carrierName}`
                    : order.shippingMethod?.name}
                </p>
              </div>
            )}
            {hasDispatchEstimates && order.status !== "COMPLETE" && (
              <div className={styles.infoColumn}>
                <p>{_textShipDate}</p>
                <p>
                  {formatDateAndTime(locale, order.shippingMethod!.minDispatchDate!)[0]}
                  {" - "}
                  {formatDateAndTime(locale, order.shippingMethod!.maxDispatchDate!)[0]}
                </p>
              </div>
            )}
            {hasDeliveryEstimates && (
              <div className={styles.infoColumn}>
                <p>{_textArrivalDate}</p>
                <p>
                  {formatDateAndTime(locale, order.shippingMethod!.minDeliveryDate!)[0]}
                  {" - "}
                  {formatDateAndTime(locale, order.shippingMethod!.maxDeliveryDate!)[0]}
                </p>
              </div>
            )}
            {order.shippingMethod?.traceable && order.trackingUrl && (
              <>
                <div className={styles.infoColumn}>
                  <p>{_textTracking}</p>
                </div>
                <div className={styles.infoColumn}>
                  <LinkedIconAndText
                    dataTestId={ORDER_TRACKING_LINK_TEST_ID}
                    linkRef={{
                      url: order.trackingUrl,
                      name: _textOrderTracking,
                      newTab: true,
                    }}
                    icon={Icons.ShippingTrackable}
                    iconSize={24}
                  />
                </div>
              </>
            )}
          </div>
          <div>
            <div className={styles.infoColumn}>
              <p>{_textShippingAddress}</p>
            </div>
            <div className={styles.infoColumn}>
              {order.shippingMethod && order.shippingAddress ? (
                <AddressDisplay
                  address={order.shippingAddress}
                  countryName={getCountryName(order.shippingAddress.country || "", locale)}
                />
              ) : (
                <p>-</p>
              )}
              <br />
              {order.contactEmail && (
                <p>
                  {_textEmail} {order.contactEmail}
                </p>
              )}
              {phoneNumber && (
                <p>
                  {_textPhone} {phoneNumber}
                </p>
              )}
            </div>
          </div>
          <PaymentMethodDetails order={order} />
          <div>
            <div className={styles.infoColumn}>
              <p>{_textBillingAddress}</p>
            </div>
            <div className={styles.infoColumn}>
              {order.billingAddress && (
                <AddressDisplay
                  address={order.billingAddress}
                  countryName={getCountryName(order.billingAddress.country || "", locale)}
                />
              )}
            </div>
          </div>
        </div>
        {allProducts.productsWithErrors.map((item) => {
          const productTypeName = getDisplayValue(item.product?.type || "") || "";

          return (
            <OrderItemProduct
              {...item}
              renderProductLink={renderProductLink}
              key={item.id}
              error
              locale={locale}
              orderId={order.id!}
              productTypeName={productTypeName}
            />
          );
        })}
        {allProducts.products.map((item) => {
          const productTypeName = getDisplayValue(item?.product?.type || "") || "";

          return (
            <OrderItemProduct
              {...item}
              renderProductLink={renderProductLink}
              key={item.id}
              locale={locale}
              orderId={order.id!}
              productTypeName={productTypeName}
            />
          );
        })}
        <div className={styles.row} style={{ border: "none" }}>
          <div className={styles.priceTotal}>
            <div>
              <h3>{_textItemSubtotal}</h3>
              <div>
                <h3 className={styles.price}>
                  <MoneyDisplay money={order.itemsNetSubtotalWithDiscounts} locale={locale} />
                </h3>
                <div>
                  <h4 className="subhead">{_textExcludingShippingTax}</h4>
                </div>
              </div>
            </div>
            <div>
              <div>
                <h3 className="almost-black-text">{_textShippingHandling}</h3>
                <h4 className="subhead">{order.shippingMethod?.name}</h4>
              </div>
              <div>
                <h3 className={styles.price}>
                  <MoneyDisplay money={order.shippingAndHandlingAmount} locale={locale} />
                </h3>
                <h4 className="subhead">{_textExcludingTax}</h4>
              </div>
            </div>
            <div>
              <h3 className="almost-black-text">{_textSalesTax}</h3>
              <h3 className={styles.price}>
                <MoneyDisplay money={order.totalTax} locale={locale} />
              </h3>
            </div>
          </div>
        </div>
        <div className={classNames(styles.row, styles.dark)}>
          <div className={styles.priceTotal}>
            <div>
              <h2 className={styles.semiBold}>{_textPaymentTotal}</h2>
              <h2>
                <MoneyDisplay money={order.amount} locale={locale} />
              </h2>
            </div>
          </div>
        </div>
        {onCancelOrder && order.canBeCanceled && (
          <div className={styles.orderActions}>
            <CancelOrder onClick={onCancelOrder} />
          </div>
        )}
      </div>
    </div>
  );
};

interface GoBackProps {
  url: string;
}
const GoBack = ({ url }: GoBackProps) => {
  const { _textGoBack } = useCopy("GoBack");

  return (
    <Link className={styles.goBack} to={url}>
      <Icon iconName={Icons.ArrowLinkLeftMedium} />
      <span>{_textGoBack}</span>
    </Link>
  );
};

interface ShowGiftReceiptProps {
  onClick: () => void;
}
const ShowGiftReceipt = ({ onClick }: ShowGiftReceiptProps) => {
  const { _textDownload } = useCopy("ShowGiftReceipt");

  return (
    <Button onClick={onClick} dataTestId="download-gift-receipts-button">
      {_textDownload}
    </Button>
  );
};

interface CancelOrderProps {
  onClick: () => void;
}
const CancelOrder = ({ onClick }: CancelOrderProps) => {
  const { _textCancel } = useCopy("CancelOrder");

  return (
    <Button onClick={onClick} hierarchy="primary">
      {_textCancel}
    </Button>
  );
};

interface DownloadInvoiceProps {
  onClick: () => void;
}
const DownloadInvoice = ({ onClick }: DownloadInvoiceProps) => {
  const { _textDownloadInvoice } = useCopy("DownloadInvoice");

  return (
    <Button onClick={onClick} dataTestId="download-invoice-button">
      {_textDownloadInvoice}
    </Button>
  );
};
