import { useState } from "react";

import { ProductTypeEnum } from "./types";
import { getBindingTypeFromId, getInteriorPaperFromId, getTrimSizeFromId } from "./utils";

const pagesPerInch: Record<string, number> = {
  "060UW": 444,
  "060UC": 444,
  "070CW": 460,
  "080CW": 444,
  "100CW": 200,
};

const baseUrl = "https://api.lulu.com/cover/api/v1/template/?";

const downloadImageFromFetchResponse = async (imageData: Response) => {
  const coverBlob = await imageData.blob();
  const url = URL.createObjectURL(coverBlob);
  const a = document.createElement("a");
  a.style.display = "none";
  a.href = url;
  a.download = "cover_template.pdf";
  document.body.appendChild(a);
  a.click();
  window.URL.revokeObjectURL(url);
};

export const useDownloadCoverTemplate = (
  productType: ProductTypeEnum,
  podPackageId: string | null,
  pageCount: number | null
) => {
  const [downloading, setDownloading] = useState(false);

  const bindingType = getBindingTypeFromId(podPackageId ?? "");
  const interiorPaper = getInteriorPaperFromId(podPackageId ?? "");
  const trimSize = getTrimSizeFromId(podPackageId ?? "");
  const interiorWidth = trimSize ? (parseInt(trimSize.split("X")[0]) / 100).toString() : null;
  const interiorHeight = trimSize ? (parseInt(trimSize.split("X")[1]) / 100).toString() : null;

  const urlSearchParams = new URLSearchParams({
    binding_type: bindingType,
    mode: ["COMIC_BOOK", "MAGAZINE"].includes(productType) ? "full_inside" : "full",
    interior_height: String(interiorHeight),
    interior_width: String(interiorWidth),
    num_pages: String(pageCount),
    pages_per_inch: String(pagesPerInch[interiorPaper]),
    target: "print",
    theme: "lulu2",
  });

  const fetchOptions = {
    headers: {
      "Content-Type": "application/pdf",
    },
  };

  const downloadCoverTemplate = async () => {
    try {
      setDownloading(true);
      const coverData = await fetch(baseUrl + urlSearchParams, fetchOptions);

      downloadImageFromFetchResponse(coverData);
    } catch (error) {
      if (error instanceof Error) {
        throw Error(error.message);
      }
    } finally {
      setDownloading(false);
    }
  };

  return {
    downloadCoverTemplate,
    downloadingCoverTemplate: downloading,
  };
};
