import classNames from "classnames";
import React from "react";

import { ClientOnly } from "../../extensions";
import { useWindowWidth } from "../../hooks";
import { mapContentfulColorToStyleProp } from "../../utils";
import { ContentfulButtonLinkProps, LinkedButton } from "../ButtonLink/ButtonLink";
import { ImageWithMetadata, ImageWithMetadataProps } from "../ImageWithMetadata/ImageWithMetadata";
import { RichTextContainer, RichTextContainerProps } from "../RichTextContainer/RichTextContainer";
import styles from "./MainBanner.module.scss";

export interface MainBannerProps {
  dataTestId?: string;
  backgroundColor?: string;
  contentBlockClassNames: string;
  idForQuickLink?: string;
  fullWidthImage: boolean;
  isVerticallyCentered: boolean;
  unsetTopPadding?: boolean;
  highPriority?: boolean;
  containerHeight: "short" | "medium" | "tall";
  containerHeightForMobile?: "short" | "medium" | "tall";
  backgroundVideo?: {
    url: string;
  };
  backgroundImageRef: ImageWithMetadataProps;
  buttonLinkRef?: ContentfulButtonLinkProps;
  richTextContainerRef: RichTextContainerProps;
  CustomImageComponent?: JSX.Element;
  externalVideoUrl?: string;
}

export const MainBanner: React.FC<MainBannerProps> = ({
  dataTestId,
  backgroundColor,
  backgroundImageRef,
  backgroundVideo,
  buttonLinkRef,
  containerHeight,
  containerHeightForMobile,
  contentBlockClassNames,
  fullWidthImage,
  isVerticallyCentered = false,
  richTextContainerRef,
  unsetTopPadding = false,
  CustomImageComponent,
  highPriority,
  idForQuickLink,
  externalVideoUrl,
}) => {
  // TODO: remove useWindowWidth solution. I suggest merging `containerHeightForMobile` and `containerHeight` into one prop, and let component consumer chose variant based on it's own window width check
  const mainBannerContainerHeight =
    useWindowWidth() <= 600 ? containerHeightForMobile || containerHeight : containerHeight;
  const mainBannerImageWrapperClasses = classNames(styles.mainBannerImageWrapper, {
    [styles.shortHeight]: mainBannerContainerHeight === "short",
    [styles.mediumHeight]: mainBannerContainerHeight === "medium",
    [styles.tallHeight]: mainBannerContainerHeight === "tall",
    "grid-1 grid-align-center": isVerticallyCentered,
  });
  const contentBlockClasses = classNames(
    styles.mainBannerContentContainer,
    {
      [styles.shortContainer]: mainBannerContainerHeight === "short",
      [styles.mediumContainer]: mainBannerContainerHeight === "medium",
      [styles.tallContainer]: mainBannerContainerHeight === "tall",
      [styles.noTopPadding]: isVerticallyCentered || unsetTopPadding,
    },
    contentBlockClassNames
  );
  const buttonLinkContainerClasses = classNames(
    {
      [`text-${buttonLinkRef?.buttonAlignment}`]: buttonLinkRef?.buttonAlignment,
    },
    buttonLinkRef?.linkedButtonClassNames || styles.buttonWrapper
  );

  return (
    <section
      className={styles.mainBannerWrapper}
      data-testid={dataTestId}
      id={idForQuickLink}
      style={
        backgroundColor
          ? {
              background: mapContentfulColorToStyleProp(backgroundColor),
            }
          : undefined
      }
    >
      <ClientOnly>
        {fullWidthImage && (
          <ImageWithMetadata {...backgroundImageRef} CustomImageComponent={CustomImageComponent} />
        )}
        {(backgroundVideo || externalVideoUrl) && (
          <video
            data-testid={dataTestId ? `${dataTestId}-video` : undefined}
            poster={backgroundImageRef.externalImageUrl || backgroundImageRef.image.url}
            muted
            loop
            autoPlay
          >
            <source src={externalVideoUrl || backgroundVideo?.url} type="video/mp4" />
          </video>
        )}
        <div className={`max-width-container ${mainBannerImageWrapperClasses}`}>
          {!fullWidthImage && !backgroundVideo && (
            <ImageWithMetadata
              {...backgroundImageRef}
              CustomImageComponent={CustomImageComponent}
              highPriority={highPriority}
            />
          )}
          <div className="grid-12">
            <div className={contentBlockClasses}>
              {richTextContainerRef && (
                <RichTextContainer
                  standaloneEntry={false}
                  richTextContainerClassNames={richTextContainerRef.richTextContainerClassNames}
                  richTextFieldsCollection={richTextContainerRef.richTextFieldsCollection}
                />
              )}
              {buttonLinkRef && (
                <div className={buttonLinkContainerClasses}>
                  <LinkedButton
                    buttonSize={buttonLinkRef.buttonSize}
                    hierarchy={buttonLinkRef.hierarchy}
                    linkRef={buttonLinkRef.linkRef.page || buttonLinkRef.linkRef}
                    buttonAlignment={buttonLinkRef.buttonAlignment}
                    buttonText={buttonLinkRef.buttonText}
                  />
                </div>
              )}
            </div>
          </div>
        </div>
      </ClientOnly>
    </section>
  );
};
