import {
  ChangeEvent,
  DragEvent,
  HTMLAttributes,
  InputHTMLAttributes,
  RefObject,
  useRef,
  useState,
} from "react";

type FileError = { message: string; code: string };
type FileRejection = {
  file: File;
  errors: FileError[];
};
type UseFileUploadProps = {
  acceptedFileTypes: string[];
  onDrop: (acceptedFile: File[]) => void;
  maxSize?: number;
  multiple?: boolean;
  onDropRejected?: (fileRejections: FileRejection[]) => void;
};

interface InputExtendedProps extends InputHTMLAttributes<HTMLInputElement> {
  ref: RefObject<HTMLInputElement>;
}
interface DivExtendedProps extends HTMLAttributes<HTMLDivElement> {
  ref: RefObject<HTMLDivElement>;
  onDrop: (evt: DragEvent) => void;
}

export const useFileUpload = ({
  acceptedFileTypes,
  onDrop,
  maxSize,
  multiple = false,
  onDropRejected,
}: UseFileUploadProps) => {
  const [acceptedFiles, setAcceptedFiles] = useState<File[]>([]);
  const [error, setError] = useState<boolean>(false);
  const inputRef = useRef<HTMLInputElement>(null);
  const rootRef = useRef<HTMLDivElement>(null);

  const validateFiles = (files: File[]) => {
    const validFiles: File[] = [];
    const rejectedFiles: { file: File; errors: { message: string; code: string }[] }[] = [];

    files.forEach((file) => {
      const errors: { message: string; code: string }[] = [];
      if (acceptedFileTypes.length > 0 && !acceptedFileTypes.includes(file.type)) {
        errors.push({ message: "Incorrect file type", code: "file-invalid-type" });
      }
      if (maxSize && file.size > maxSize) {
        errors.push({ message: "File too large", code: "file-too-large" });
      }
      if (errors.length > 0) {
        rejectedFiles.push({ file, errors });
      } else {
        validFiles.push(file);
      }
    });

    if (rejectedFiles.length > 0) {
      setError(true);
      onDropRejected?.(rejectedFiles);
    }

    return multiple ? validFiles : validFiles.slice(0, 1);
  };

  const handleDragOver = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
  };

  const handleDrop = (e: DragEvent) => {
    e.preventDefault();
    setError(false);
    if (!e.dataTransfer) return;

    const files = Array.from(e.dataTransfer.files);

    const validFiles = validateFiles(files);

    setAcceptedFiles(validFiles);

    if (validFiles.length > 0) {
      if (inputRef.current) {
        onDrop(validFiles);
      }
    }
  };

  const handleInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    setError(false);
    if (!e.target.files) return;

    const files = Array.from(e.target.files);

    const validFiles = validateFiles(files);

    setAcceptedFiles(validFiles);

    if (validFiles.length > 0) {
      if (inputRef.current) {
        onDrop(validFiles);
      }
    }
  };

  const handleClick = () => {
    inputRef.current?.click();
  };

  const getInputProps = (): InputExtendedProps => ({
    accept: acceptedFileTypes.filter(Boolean).join(","),
    multiple,
    type: "file",
    tabIndex: -1,
    onChange: handleInputChange,
    style: { display: "none" },
    ref: inputRef,
  });

  const getRootProps = (): DivExtendedProps => ({
    onDragOver: handleDragOver,
    onDrop: handleDrop,
    role: "presentation",
    ref: rootRef,
    tabIndex: 0,
    onClick: handleClick,
  });

  return {
    acceptedFiles,
    fileWasRejected: error,
    getInputProps,
    getRootProps,
  };
};
