import classNames from "classnames";
import React, { ForwardedRef } from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";

export type InputProps = {
  autoComplete?: string;
  autoFocus?: boolean;
  cols?: number;
  dataTestId?: string;
  disabled?: boolean;
  endAdornment?: React.ReactNode;
  error?: string;
  info?: string;
  helperText?: React.ReactNode;
  icon?: Icons;
  iconPlacement?: "right";
  id?: string;
  label?: string | React.ReactElement;
  maxLength?: number;
  name?: string;
  placeholder?: string;
  readOnly?: boolean;
  required?: boolean;
  rows?: number;
  theme?: "dark" | "light";
  type?: string;
  tooltipContent?: React.ReactNode;
  value?: string | number;
  withTooltip?: boolean;
  onBlur?: React.FocusEventHandler<HTMLInputElement | HTMLTextAreaElement>;
  onChange?: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement>;
  onKeyDown?: React.KeyboardEventHandler<HTMLTextAreaElement | HTMLInputElement>;
  ariaLabel?: string;
};

export const Input = React.forwardRef<HTMLInputElement | HTMLTextAreaElement, InputProps>(
  (
    {
      dataTestId,
      endAdornment,
      error,
      info,
      helperText,
      icon = undefined,
      iconPlacement = undefined,
      label,
      type = "text",
      theme = "light",
      tooltipContent,
      withTooltip = false,
      ...inputProps
    },
    ref
  ) => {
    const fieldClasses = classNames("field", {
      "field--error": error,
      "field--info": info,
    });
    const inputClasses = classNames("field__input-wrapper", {
      "field__input-wrapper--dark": theme === "dark",
      "field__input-wrapper--with-adornment": endAdornment,
      "field__input-wrapper--with-icon": icon,
      "field__input-wrapper--with-icon--right": iconPlacement === "right",
    });

    const labelId = inputProps.id || inputProps.name || "input-label";

    return (
      <div className={fieldClasses}>
        <div className={inputClasses}>
          {label && (
            <label
              className="field__label field__label--visible"
              htmlFor={inputProps.ariaLabel ? undefined : labelId}
            >
              {label}
              {inputProps.required && "*"}
              {helperText && <span className="field__label__helper">{helperText}</span>}
            </label>
          )}

          {type === "textarea" ? (
            <textarea
              ref={ref as ForwardedRef<HTMLTextAreaElement>}
              cols={inputProps.cols}
              className="field__input"
              data-testid={dataTestId}
              disabled={inputProps.disabled}
              id={labelId}
              maxLength={inputProps.maxLength}
              name={inputProps.name}
              onChange={inputProps.onChange}
              placeholder={inputProps.placeholder}
              readOnly={inputProps.readOnly}
              required={inputProps.required}
              rows={inputProps.rows}
              value={inputProps.value ?? ""}
              aria-label={inputProps.ariaLabel}
            />
          ) : (
            <>
              <input
                ref={ref as ForwardedRef<HTMLInputElement>}
                className="field__input"
                data-testid={dataTestId}
                id={labelId}
                required={inputProps.required}
                type={type}
                value={inputProps.value ?? ""}
                aria-label={inputProps.ariaLabel}
                {...inputProps}
              />
              {endAdornment && <div className="field__adorment">{endAdornment}</div>}
            </>
          )}
          {icon && <Icon iconName={icon} className="input-icon" size={19} />}
          {withTooltip && (
            <div className="has-tooltip">
              <span className="tooltip">{tooltipContent}</span>
            </div>
          )}
        </div>
        {error && (
          <p
            className="field__message field__message--error"
            data-testid={`${dataTestId}-error-message`}
          >
            {error}
          </p>
        )}
        {info && (
          <p
            className="field__message field__message--info"
            data-testid={`${dataTestId}-info-message`}
          >
            {info}
          </p>
        )}
      </div>
    );
  }
);
