import classNames from "classnames";
import React from "react";

import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../hooks";
import { Button } from "../Button/Button";
import { ImageWithMetadata, ImageWithMetadataProps } from "../ImageWithMetadata/ImageWithMetadata";
import { Link, LinkProps } from "../Link/Link";
import styles from "./ImageTextBullet.module.scss";

export interface ImageTextBulletProps {
  dataTestId?: string;
  imageRef: ImageWithMetadataProps;
  title: string;
  linkedTitleRef?: LinkProps;
  description: string;
  buttonText: string;
  linkedButtonRef?: LinkProps;
  textAlign?: "center" | "right" | "left";
}

export const ImageTextBullet: React.FC<ImageTextBulletProps> = ({
  dataTestId,
  imageRef,
  title,
  linkedTitleRef,
  description,
  buttonText,
  linkedButtonRef,
  textAlign,
}) => {
  const isMobile = useWindowWidth() <= MEDIA_BREAKPOINTS.M;
  const imageClasses = classNames(styles.image, {
    [styles.smaller]: linkedTitleRef,
  });

  return (
    <div className={styles.imageTextBullet} data-testid={dataTestId}>
      {imageRef && (
        <div className={styles.imageContainer}>
          <ImageWithMetadata {...imageRef} imageClasses={imageClasses} />
        </div>
      )}
      {linkedTitleRef ? (
        <Link {...linkedTitleRef}>
          <h2 className={`contentful-h2 ${styles.title} ${styles.link}`}>{title}</h2>
        </Link>
      ) : (
        <>
          <h3 className={classNames("contentful-h3", styles.title)}>{title}</h3>
          <h3
            className={classNames("contentful-h3 subhead almost-black-text-60", styles.description)}
            style={{ textAlign }}
          >
            {description}
          </h3>
        </>
      )}
      {linkedButtonRef && !linkedTitleRef && (
        <Link {...linkedButtonRef}>
          <Button hierarchy="primary" buttonSize={isMobile ? "small" : "medium"}>
            {buttonText}
          </Button>
        </Link>
      )}
    </div>
  );
};
