import React from "react";

import { useCopy } from "../../extensions/CopyContext";
import { ReactComponent as BCorpLogo } from "../../styles/assets/logos/b-corp-logo.svg";
import { ReactComponent as LuluApiLogo } from "../../styles/assets/logos/lulu-api-logo-black.svg";
import { ReactComponent as LuluJrLogo } from "../../styles/assets/logos/lulu-junior-logo-black.svg";
import { ReactComponent as LuluLogo } from "../../styles/assets/logos/lulu-logo-black.svg";
import { ReactComponent as TheTiltLogo } from "../../styles/assets/logos/the-tilt-logo-black.svg";
import { IconWithMetadata, IconWithMetadataProps } from "../IconWithMetadata/IconWithMetadata";
import { Link, LinkProps } from "../Link/Link";
import styles from "./Footer.module.scss";
import { FooterLinkCollection } from "./FooterLinkCollection";
import { FooterSelect } from "./FooterSelect";

export type LinkType = {
  name: string;
  newTab: boolean;
  page?: { url?: string } | null;
  url?: string | null;
};

type LinkCollectionType = {
  name: string;
  rootLink?: LinkType;
  sublinksCollection: {
    items: LinkType[];
  };
};

type FooterData = {
  apiAltText?: string;
  bCorpAltText?: string;
  copyrightText?: string;
  legalLinksCollection?: Omit<
    LinkProps,
    "className" | "dataTestId" | "description" | "__typename"
  >[];
  luluAltText?: string;
  luluJrAltText?: string;
  theTiltAltText?: string;
  navigationLinksCollection?: (LinkType | LinkCollectionType)[];
  newsletterText?: string;
  socialLinksCollection?: Omit<IconWithMetadataProps, "dataTestId">[];
};

export interface FooterProps {
  footerData?: FooterData;
  locale?: string;
  localeOptions?: {
    label: string;
    value: string;
  }[];
  newsletterForm?: React.ReactNode;
  updateLocale?: (arg: string) => void;
}

export const Footer: React.FC<FooterProps> = ({
  footerData,
  locale,
  localeOptions,
  newsletterForm,
  updateLocale,
}) => {
  const { _localeSelectText } = useCopy("Footer");
  const footerLinkClasses = `${styles.footerLink} ${styles.mainNavLink}`;

  const SocialLinks = ({ classNames }: { classNames: string }) => (
    <div className={classNames}>
      <div className={styles.socialLinks}>
        {footerData?.socialLinksCollection?.map((icon) => (
          <IconWithMetadata key={icon.icon} {...icon} />
        ))}
      </div>
    </div>
  );

  return (
    <footer className={`${styles.footerContainer} white-text-80`}>
      <div className={`max-width-container ${styles.footerTopRow}`}>
        <div>
          <div className={styles.mainNavLinks}>
            {footerData?.navigationLinksCollection?.map((link) => {
              if ("sublinksCollection" in link)
                return (
                  <FooterLinkCollection
                    key={link.name}
                    rootLink={link.rootLink}
                    linkClassnames={footerLinkClasses}
                    subLinks={link.sublinksCollection.items}
                    name={link.name}
                  />
                );

              return <Link className={footerLinkClasses} key={link.name} {...link} />;
            })}
            {locale && !!localeOptions?.length && updateLocale && (
              <FooterSelect
                name="language"
                label={_localeSelectText}
                selectedValue={locale}
                onClick={updateLocale}
                options={localeOptions}
              />
            )}
            {locale && !localeOptions && (
              <div className={styles.footerSelect}>
                <div className={styles.footerSelected}>{locale}</div>
              </div>
            )}
          </div>
          <SocialLinks classNames={`${styles.socialLinksContainer} ${styles.desktopSocialLinks}`} />
        </div>
        <div className={styles.subscribeContainer}>
          <div className={styles.subscribe}>
            <div className={styles.newsletterText}>{footerData?.newsletterText}</div>
            {newsletterForm}
          </div>
        </div>
        <SocialLinks classNames={`${styles.socialLinksContainer} ${styles.mobileSocialLinks}`} />
      </div>
      <div className={styles.footerBottomRow}>
        <div className={`max-width-container ${styles.bottomRowContent}`}>
          <div className={styles.platformLinks}>
            <a
              href="https://www.lulu.com/"
              target="_blank"
              rel="noopener noreferrer"
              title={footerData?.luluAltText}
            >
              <LuluLogo
                alt={footerData?.luluAltText}
                className={`${styles.luluLogo} ${styles.svgLink}`}
              />
            </a>
            <a
              href="https://www.lulu.com/sell/sell-on-your-site/print-api"
              target="_blank"
              rel="noopener noreferrer"
              title={footerData?.apiAltText}
            >
              <LuluApiLogo
                alt={footerData?.apiAltText}
                className={`${styles.apiLogo} ${styles.svgLink}`}
              />
            </a>
            <a
              href="https://www.lulujr.com/"
              target="_blank"
              rel="noopener noreferrer"
              title={footerData?.luluJrAltText}
            >
              <LuluJrLogo
                alt={footerData?.luluJrAltText}
                className={`${styles.jrLogo} ${styles.svgLink}`}
              />
            </a>
            <a
              href="https://www.thetilt.com/"
              target="_blank"
              rel="noopener noreferrer"
              title={footerData?.theTiltAltText}
            >
              <TheTiltLogo
                alt={footerData?.theTiltAltText}
                className={`${styles.theTiltLogo} ${styles.svgLink}`}
              />
            </a>
          </div>
          <div className={styles.legalInfoContainer}>
            <div className={styles.legalInfo}>
              <div className={styles.legalLinks}>
                {footerData?.legalLinksCollection?.map((link) => (
                  <Link
                    key={link.name}
                    className={styles.footerLink}
                    {...link}
                    data-testid={`footer-link-${link.page?.url}`}
                  />
                ))}
              </div>
              <div className={styles.copyrightMessage}>
                Copyright © {`${new Date().getFullYear()} ${footerData?.copyrightText}`}
              </div>
            </div>
            <div>
              <a
                className={styles.footerLink}
                href="https://bcorporation.net/en-us/find-a-b-corp/company/lulu-press-inc"
                rel="noopener noreferrer"
                target="_blank"
                title={footerData?.bCorpAltText}
              >
                <BCorpLogo
                  alt={footerData?.bCorpAltText}
                  className={`${styles.bCorpLogo} ${styles.svgLink}`}
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};
