import classNames from "classnames";
import React from "react";

import styles from "./RadioInput.module.scss";

export interface RadioInputProps extends React.HTMLProps<HTMLInputElement> {
  dataTestId?: string;
  size?: number;
  spacing?: number;
  className?: string;
}

export const RadioInput: React.FC<RadioInputProps> = ({
  size = 16,
  spacing = 12,
  className,
  dataTestId,
  children,
  ...inputProps
}) => {
  const inputId = inputProps.id ?? "radio-input";

  return (
    <label
      className={classNames(
        styles.wrapper,
        {
          [styles.disabled]: !!inputProps.disabled,
        },
        className
      )}
    >
      <div
        className={styles.radioInputWrapper}
        style={
          {
            "--size": `${size}px`,
            marginRight: `${spacing}px`,
          } as React.CSSProperties
        }
      >
        <input id={inputId} data-testid={dataTestId} type="radio" {...inputProps} />
      </div>
      {children}
    </label>
  );
};
