import React, { useEffect, useState } from "react";
import { HexColorInput } from "react-colorful";

import { RGBColor } from "../../utils/colors";
import styles from "./ColorPicker.module.scss";

interface Props {
  color: string | number;
  hex: string;
  label: string;
  rgb: RGBColor;
  onChange: (color: string | RGBColor) => void;
}

export const HelperInput: React.FC<Props> = ({ label, onChange, color, rgb }) => {
  const [value, setValue] = useState(color);

  useEffect(() => setValue(color), [color]);

  const _onInput = (e: React.FormEvent<HTMLInputElement>) => {
    if (e.currentTarget.value.length > 3) {
      e.currentTarget.value = e.currentTarget.value.slice(0, 3);
    }

    if (value === 0) {
      e.currentTarget.value = e.currentTarget.value.slice(1);
    }
  };

  return (
    <div className={styles.helperInput}>
      <div className={styles.helperInputContainer}>
        {label === "hex" ? (
          <>
            <span>#</span>
            <HexColorInput
              color={typeof value === "string" ? value.toUpperCase() : value.toString()}
              onChange={(hex) => {
                if (hex.length < 7) return;
                onChange(hex);
              }}
              id="helper-input"
            />
          </>
        ) : (
          <input
            id="helper-input"
            type="number"
            style={{ textAlign: "center" }}
            value={value}
            onChange={(e) => {
              const value = Number(e.target.value) > 255 ? 255 : Number(e.target.value);

              setValue(value);
              onChange({ ...rgb, [label]: value });
            }}
            onInput={_onInput}
            min={0}
            max={255}
          />
        )}
      </div>
      <label htmlFor="helper-input">{label}</label>
    </div>
  );
};
