import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { ButtonProps } from "../Button/Button";
import styles from "../Button/Button.module.scss";
import { Icon } from "../Icon/Icon";

export interface IconButtonProps extends ButtonProps {
  shape?: "square" | "round" | "naked";
  icon: Icons;
  color?: Colors;
}

export const IconButton: React.FC<IconButtonProps> = ({
  dataTestId,
  children,
  hierarchy = "secondary",
  buttonSize = "medium",
  shape = "square",
  icon,
  block,
  responsive,
  color,
  size,
  // we need to destructure all non-HTMLButtonElement props (even if we don't use them)
  // as these rest props are spread on the <button> element. Passing incompatible props would lead to warnings
  loading: _,
  ...props
}) => {
  const buttonClass = classNames(
    styles.button,
    styles[hierarchy],
    styles[buttonSize],
    {
      [styles.fullWidth]: block,
      [styles.responsive]: responsive,
    },
    `icon-${shape}`,
    props.className
  );

  return (
    <button {...props} className={buttonClass} data-testid={dataTestId}>
      {icon && <Icon iconName={icon} color={color} size={size} />}
    </button>
  );
};
