import classNames from "classnames";
import React, { CSSProperties } from "react";

import { Link, LinkProps } from "../Link/Link";
import styles from "./PriceFormContainer.module.scss";

export interface PriceFormContainerProps {
  dataTestId?: string;
  title: string;
  titleStyles?: CSSProperties;
  active?: boolean;
  invalid?: boolean;
  reference?: string | string[];
  footnote?: string;
  helperText?: string;
  helperLink?: LinkProps;
  children?: React.ReactNode;
}

export const PriceFormContainer: React.FC<PriceFormContainerProps> = ({
  dataTestId,
  title,
  titleStyles,
  active,
  invalid,
  reference,
  footnote,
  helperText,
  helperLink,
  children,
}) => {
  const containerClasses = classNames(styles.container, {
    [styles.active]: active,
    [styles.invalid]: invalid,
  });

  const referenceTextClasses = classNames(styles.reference, "almost-black-text");

  return (
    <section data-test-id={dataTestId}>
      <div className={containerClasses}>
        <div className={styles.content}>
          <div className={styles.text}>
            <h3 className={styles.title} style={titleStyles}>
              {title}
            </h3>
            {reference &&
              (Array.isArray(reference) ? (
                <small className={referenceTextClasses}>
                  {reference.map(
                    (line, idx) =>
                      line && (
                        <div key={idx}>
                          {line}
                          <br />
                        </div>
                      )
                  )}
                </small>
              ) : (
                <small className={referenceTextClasses}>
                  {reference}
                  {footnote && "*"}
                </small>
              ))}
          </div>
          <div className={styles.form}>{children}</div>
        </div>
      </div>
      {footnote && (
        <div className={styles.info}>
          {footnote}
          {helperText && (
            <span>
              <br />
              {helperText}
            </span>
          )}
        </div>
      )}
      {helperLink && <Link className={styles.link} {...helperLink} />}
    </section>
  );
};
