import classNames from "classnames";
import React from "react";

import { useCopy } from "../../../extensions";
import styles from "./CoverThumbnail.module.scss";

interface CoverThumbnailProps {
  url?: string;
  containerClasses?: string;
}

export const CoverThumbnail: React.FC<CoverThumbnailProps> = ({ url, containerClasses }) => {
  const { _textNoCover } = useCopy("ProjectSelect");

  return (
    <div
      className={classNames(styles.thumbnail, containerClasses, {
        [styles.whiteBackground]: !!url,
      })}
    >
      {url ? (
        <img src={url} alt={"cover thumbnail"} />
      ) : (
        <div className={styles.fallback}>{_textNoCover}</div>
      )}
    </div>
  );
};
