import classNames from "classnames";
import React from "react";

import { isEnterOrSpaceKeyEvent } from "../../utils/isEnterOrSpaceKeyEvent";
import { Checkbox } from "../Checkbox/Checkbox";
import styles from "./CheckboxBox.module.scss";

export interface CheckboxBoxProps {
  dataTestId?: string;
  title: React.ReactNode;
  description?: string | React.ReactNode;
  disabled?: boolean;
  value: string;
  name: string;
  checked: boolean;
  onChange: () => void;
  titleColor?: "indigo" | "black";
  theme?: "classic" | "modern";
}

export const CheckboxBox: React.FC<CheckboxBoxProps> = ({
  dataTestId,
  title,
  description,
  checked,
  value,
  name,
  disabled,
  onChange,
  titleColor = "indigo",
  theme = "classic",
}) => {
  const checkboxBoxClass = classNames(styles.checkbox, {
    [styles.modern]: theme === "modern",
    [styles.checked]: checked,
    [styles.disabled]: disabled,
  });

  const checkboxBoxTitleClass = classNames(styles.title, {
    [styles.indigo]: titleColor === "indigo",
    [styles.black]: titleColor === "black",
  });

  return (
    <div
      role="presentation"
      tabIndex={disabled ? -1 : 0}
      className={checkboxBoxClass}
      onClick={() => !disabled && onChange?.()}
      data-testid={`${dataTestId}-container`}
      onKeyDown={(e) => {
        if (!disabled && isEnterOrSpaceKeyEvent(e)) {
          onChange?.();
        }
      }}
    >
      <div className={styles.checkboxWrapper}>
        <Checkbox
          value={value}
          name={name}
          checked={checked}
          disabled={disabled}
          dataTestId={dataTestId}
        />
      </div>
      <div>
        <div className={styles.content}>
          <span className={checkboxBoxTitleClass}>{title}</span>
          {description && <p className={styles.description}>{description}</p>}
        </div>
      </div>
    </div>
  );
};
