import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { useFileUpload } from "../../hooks/useFileUpload";
import { Icons } from "../../icon-types";
import { Button } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import { Notification } from "../Notification/Notification";
import styles from "./FileUpload.module.scss";
import { FileUploadProgress } from "./FileUploadProgress";

export interface FileUploadProps {
  dataTestId?: string;
  file?: FileObject;
  uploading: boolean;
  requirements?: string | React.ReactNode;
  type: FileType;
  accept?: string[];
  success?: boolean;
  successMessage?: string;
  successIcon?: Icons;
  fileCopy: string;
  fileCopyPrefix?: string;
  formatErrorCopy: string;
  uploadError?: boolean;
  UploadWarning?: React.FC | null;
  uploadErrorCopy?: string;
  fileStalledMessage?: string;
  warningAction?: MessageActionType;
  errorAction?: MessageActionType;
  successAction?: MessageActionType;
  label?: string;
  onDrop: (acceptedFile: File) => void;
  onLoadingCancel?: () => void;
  disabled?: boolean;
  customUploadMessage?: string;
  forceLoadingProgress?: boolean;
  height?: string;
  fontSize?: {
    icon?: string;
    text?: string;
  };
  customProgress?: number;
  preventProgressDecrementation?: boolean;
}

export interface FileUploadMessagesProps {
  messages: Message[];
  type: "error" | "warning" | "success";
  action?: MessageActionType;
  icon?: Icons;
}

export interface Message {
  code?: number;
  message?: string | JSX.Element;
}

export interface FileObject {
  errors?: Message[];
  filename?: string;
  pageCount?: number;
  progress?: number;
  status?: string;
  warnings?: Message[];
  isStalled?: boolean;
}

export type FileType = "cover" | "interior" | "ebook" | "event" | "orders";

export interface MessageActionType {
  title: string;
  action: () => void;
  loading: boolean;
  error: boolean;
}

const FileUploadMessages: React.FC<FileUploadMessagesProps> = ({
  messages,
  type,
  action,
  icon,
}) => {
  const { _actionError } = useCopy("FileUpload");

  if (messages.length === 0) {
    return null;
  }

  return (
    <Notification type={type} dataTestId={`file-upload-notification-${type}`}>
      <div>
        {messages.map((error, index) => (
          <p className={styles.message} key={index}>
            {error.message}
          </p>
        ))}
        {action && (
          <Button onClick={action.action} loading={action.loading} icon={icon}>
            {action.error ? _actionError : action.title}
          </Button>
        )}
      </div>
    </Notification>
  );
};

export const FileUpload: React.FC<FileUploadProps> = ({
  dataTestId,
  onDrop,
  uploading,
  requirements,
  file,
  type,
  formatErrorCopy,
  success,
  successAction,
  successIcon,
  successMessage,
  uploadError,
  UploadWarning,
  uploadErrorCopy,
  fileStalledMessage,
  warningAction,
  errorAction,
  fileCopy,
  fileCopyPrefix,
  accept = ["application/pdf"],
  label,
  disabled = false,
  customUploadMessage,
  forceLoadingProgress,
  height,
  fontSize,
  customProgress,
  preventProgressDecrementation,
}) => {
  const {
    _textUploadedFile,
    _textReplaceYour,
    _textUploadYour,
    _textDragAndDrop,
    _textRequirements,
    _textPages,
  } = useCopy("FileUpload");
  const [error, setError] = React.useState(false);

  const handleOnDrop = React.useCallback(
    (accepted: File[]) => {
      setError(!accepted.length);

      if (accepted.length) {
        onDrop(accepted[0]);
      }
    },
    [onDrop]
  );

  const { acceptedFiles, fileWasRejected, getRootProps, getInputProps } = useFileUpload({
    acceptedFileTypes: accept,
    onDrop: handleOnDrop,
  });

  const status = file?.status ?? "UNKNOWN";

  const fileCopyLabel = `${
    fileCopyPrefix || (file ? _textReplaceYour : _textUploadYour)
  } ${fileCopy}`;

  return (
    <div data-testid={dataTestId}>
      {uploading ||
      (!file?.isStalled && ["VALIDATING", "NORMALIZING", "PROCESSING"].includes(status)) ? (
        <FileUploadProgress
          customUploadMessage={customUploadMessage}
          forceLoadingProgress={forceLoadingProgress}
          uploading={uploading}
          progress={customProgress || file?.progress || 0}
          status={status}
          fileName={uploading && acceptedFiles[0] ? acceptedFiles[0].name : file?.filename}
          preventProgressDecrementation={preventProgressDecrementation}
        />
      ) : (
        <>
          {file && (
            <div className="grid-12">
              <div className="col-4 col-m-12">
                <h4 className={styles.uploadLabel}>{!uploading && (label || _textUploadedFile)}</h4>
              </div>
              <div className="col-8 col-m-12">
                {!uploading && !error && !uploadError && type !== "event" && (
                  <div
                    className={styles.fileInfo}
                    data-testid={dataTestId ? `${dataTestId}-file-info` : undefined}
                  >
                    {file.filename}
                    {type === "interior" && (
                      <div className={styles.filePageCount}>
                        {file.pageCount} {_textPages}
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          )}
          {file?.isStalled && (
            <Notification type="error">
              <p>{fileStalledMessage}</p>
            </Notification>
          )}
          {file?.errors && !error && !uploadError && (
            <FileUploadMessages type="error" messages={file?.errors || []} action={errorAction} />
          )}
          {file?.warnings && (
            <FileUploadMessages
              type="warning"
              messages={file?.warnings || []}
              action={warningAction}
            />
          )}
          {success && !error && !uploadError && (
            <FileUploadMessages
              type="success"
              messages={[{ message: successMessage }]}
              action={successAction}
              icon={successIcon}
            />
          )}
          {UploadWarning && (
            <Notification type="warning">
              <UploadWarning />
            </Notification>
          )}
          {(error || uploadError || fileWasRejected) && (
            <Notification type="error">
              <p>{error || fileWasRejected ? formatErrorCopy : uploadErrorCopy}</p>
              {errorAction && (
                <Button
                  hierarchy="primary"
                  onClick={errorAction.action}
                  loading={errorAction.loading}
                >
                  {errorAction.title}
                </Button>
              )}
            </Notification>
          )}
          <div
            {...getRootProps()}
            className={classNames(styles.dropzone, {
              [styles.disabled]: disabled,
            })}
            aria-disabled={disabled}
          >
            <input
              aria-label={fileCopyLabel}
              disabled={disabled}
              {...getInputProps()}
              data-testid={dataTestId ? `${dataTestId}-input` : undefined}
            />
            {type !== "event" ? (
              <p className={styles.dropzoneDescription} style={{ height }}>
                <Icon iconName={Icons.DataUploadLarge} style={{ fontSize: fontSize?.icon }} />
                <span style={{ fontSize: fontSize?.text }}>
                  <button
                    type="button"
                    className="link"
                    style={{ fontSize: fontSize?.text }}
                    data-testid={dataTestId ? `${dataTestId}-button` : undefined}
                  >
                    {fileCopyLabel}
                  </button>
                  {_textDragAndDrop}
                </span>
              </p>
            ) : (
              <div className={styles.dropzoneDescription}>
                <Button responsive data-testid={dataTestId ? `${dataTestId}-button` : undefined}>
                  <span style={{ fontSize: fontSize?.text }}>{fileCopyLabel}</span>
                </Button>
                <div style={{ justifySelf: "end" }}>
                  <Icon
                    iconName={Icons.DataUploadLarge}
                    style={{ fontSize: fontSize?.icon || "24px", margin: 0 }}
                  />
                </div>
                <span style={{ fontSize: fontSize?.text || "16px" }}>{_textDragAndDrop}</span>
              </div>
            )}
          </div>
          {requirements && (
            <div className={styles.requirements}>
              <span className={styles.requirementsLabel}>{_textRequirements}: </span>
              <span className={styles.requirementsValue}>{requirements}</span>
            </div>
          )}
        </>
      )}
    </div>
  );
};
