import classNames from "classnames";
import React, { useEffect } from "react";

import { Header } from "./Header/Header";
import styles from "./PricingCalculator.module.scss";
import { PricingCalculatorProvider } from "./PricingCalculatorContext";
import { PricingConfigurator } from "./PricingConfigurator/PricingConfigurator";
import configuratorStyles from "./PricingConfigurator/PricingConfigurator.module.scss";
import { ProductTypeSelector } from "./ProductTypeSelector/ProductTypeSelector";
import { PricingCalculatorProps, PrintableTypeEnum } from "./types";

export const PricingCalculator: React.FC<PricingCalculatorProps> = ({
  dataTestId,
  availableProductTypes,
  podPackagesQuery,
  getDisplayValue,
  manufacturingCostQuery,
  onRequestPodPackages,
  productsUrl,
  shippingCountriesQuery,
  shippingMethodsQuery,
  totalPricesQuery,
  listPriceBoundsQuery,
  priceMapQuery,
  priceMapEbookQuery,
  podPackageDimensionsQuery,
  countryStatesQuery,
  getCountryName,
  locale,
  onRequestShippingMethods,
  onRequestTotalPrices,
  onRequestPriceMap,
  onRequestPriceMapEbook,
  onRequestManufacturingCost,
  onRequestShippingCountries,
  onRequestListPriceBounds,
  onRequestPodPackageDimensions,
  onRequestCountryStates,
  baseCurrency,
  wizardUrl,
  isDeveloperPortalVersion,
  initialConfiguration,
  mainColumnAddons,
}) => {
  useEffect(() => {
    onRequestPodPackages(mapProductTypeToPrintableType("PRINTED_BOOK"));
  }, [onRequestPodPackages]);

  return (
    <PricingCalculatorProvider
      availableProductTypes={availableProductTypes}
      podPackagesQuery={podPackagesQuery}
      getDisplayValue={getDisplayValue}
      manufacturingCostQuery={manufacturingCostQuery}
      shippingCountriesQuery={shippingCountriesQuery}
      shippingMethodsQuery={shippingMethodsQuery}
      totalPricesQuery={totalPricesQuery}
      listPriceBoundsQuery={listPriceBoundsQuery}
      priceMapQuery={priceMapQuery}
      priceMapEbookQuery={priceMapEbookQuery}
      podPackageDimensionsQuery={podPackageDimensionsQuery}
      countryStatesQuery={countryStatesQuery}
      getCountryName={getCountryName}
      onRequestShippingMethods={onRequestShippingMethods}
      onRequestTotalPrices={onRequestTotalPrices}
      onRequestPriceMap={onRequestPriceMap}
      onRequestPriceMapEbook={onRequestPriceMapEbook}
      onRequestManufacturingCost={onRequestManufacturingCost}
      onRequestShippingCountries={onRequestShippingCountries}
      onRequestListPriceBounds={onRequestListPriceBounds}
      onRequestPodPackageDimensions={onRequestPodPackageDimensions}
      onRequestCountryStates={onRequestCountryStates}
      initialConfiguration={initialConfiguration}
      baseCurrency={baseCurrency}
      wizardUrl={wizardUrl}
      isDeveloperPortalVersion={isDeveloperPortalVersion}
      locale={locale}
    >
      <div
        data-testid={dataTestId}
        className={classNames(styles.container, {
          isDeveloperPortalVersion: isDeveloperPortalVersion,
        })}
      >
        <Header
          productsUrl={productsUrl}
          isDeveloperPortalVersion={isDeveloperPortalVersion}
          locale={locale}
        />
        <ProductTypeSelector
          onChange={(productType) => {
            if (productType === "EBOOK") {
              return;
            }
            onRequestPodPackages(mapProductTypeToPrintableType(productType));
          }}
        />
        <div className={configuratorStyles.container}>
          <PricingConfigurator mainColumnAddons={mainColumnAddons} />
        </div>
      </div>
    </PricingCalculatorProvider>
  );
};

export const mapProductTypeToPrintableType = (productType: string) => {
  const mappers: Record<string, PrintableTypeEnum> = {
    CALENDAR: "CALENDAR",
    COMIC_BOOK: "COMIC_BOOK",
    MAGAZINE: "MAGAZINE",
    PREMIUM_PHOTO_BOOK: "PREMIUM_PHOTO_BOOK",
    PRINTED_BOOK: "BOOK",
    YEARBOOK: "YEARBOOK",
  };

  return mappers[productType];
};
