import React from "react";

import { useCopy } from "../../extensions/CopyContext";
import { Checkbox } from "../Checkbox/Checkbox";
import styles from "./BulkActionsMenu.module.scss";

export interface BulkActionsMenuArgs<T extends {}> {
  selectedItems: T[];
}

export interface BulkActionsMenuProps<T extends {}> {
  dataTestId?: string;
  actions: (props: BulkActionsMenuArgs<T>) => React.ReactNode;
  allItems: T[];
  id?: string;
  selectedItems: T[];
  onSelectAll: () => void;
  onSelectNone: () => void;
}

export const BulkActionsMenu = <T extends {}>({
  dataTestId,
  actions,
  allItems,
  id = "bulk-root-checkbox",
  selectedItems,
  onSelectAll,
  onSelectNone,
}: BulkActionsMenuProps<T>): React.ReactElement => {
  const { _textSelectAll } = useCopy("BulkActionsMenu");
  const allSelected = allItems.length > 0 && allItems.length === selectedItems.length;

  const toggleSelection = () => {
    if (allSelected) {
      onSelectNone();
    } else {
      onSelectAll();
    }
  };

  return (
    <div className={styles.bulkActionsMenu} data-testid={dataTestId}>
      <div className={allSelected ? styles.labelSelected : styles.label}>
        <Checkbox
          checked={allSelected}
          disabled={allItems.length === 0}
          id={id}
          onChange={toggleSelection}
          label={_textSelectAll}
        />
      </div>
      {selectedItems.length > 0 && (
        <div className={styles.actions}>{actions({ selectedItems })}</div>
      )}
    </div>
  );
};
