import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Error } from "../Error/Error";
import { Icon } from "../Icon/Icon";

export interface SelectProps {
  label: string | React.ReactElement;
  onChange: (event: React.ChangeEvent<HTMLSelectElement>) => void;
  options: OptionField[];
  dataTestId?: string;
  className?: string;
  name?: string;
  value?: number | string;
  icon?: Icons;
  placeholder?: string;
  onBlur?: React.FocusEventHandler<HTMLSelectElement>;
  disabled?: boolean;
  error?: string;
  id?: string;
  ariaLabel?: string;
}

type OptionField = {
  label: string;
  value: number | string;
};

export const Select: React.FC<SelectProps> = ({
  dataTestId,
  className,
  label,
  name,
  onChange,
  options,
  value,
  icon,
  placeholder,
  onBlur,
  disabled,
  error,
  id,
  ariaLabel,
}) => {
  const wrapperClassNames = classNames(
    "field__input-wrapper",
    "field__input-wrapper--select",
    className,
    {
      "field__input-wrapper--with-icon": icon,
      "field--error": error,
    }
  );

  const getName = () => {
    if (name) return name;

    if (typeof label === "string") return label;

    return;
  };

  const selectId = (id || name) ?? "select";

  return (
    <div>
      <div className={wrapperClassNames} data-testid={dataTestId}>
        <label
          className="field__label field__label--visible"
          htmlFor={typeof label === "string" ? selectId : undefined}
        >
          {label}
        </label>
        <select
          aria-label={ariaLabel}
          id={selectId}
          data-testid={`${dataTestId}-select`}
          className="field__input field__input--select"
          name={getName()}
          onChange={onChange}
          value={value}
          onBlur={onBlur}
          disabled={disabled}
        >
          {placeholder && (
            <option value="" disabled>
              {placeholder}
            </option>
          )}
          {options.map((item) => (
            <option value={item.value} key={item.value}>
              {item.label}
            </option>
          ))}
        </select>
        {icon && <Icon iconName={icon} className="input-icon" size={19} />}
        <div className="field__caret" />
      </div>
      <Error error={error} />
    </div>
  );
};
