import classNames from "classnames";
import useSlider from "embla-carousel-react";
import React, { useEffect } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { mapContentfulColorToStyleProp } from "../../utils/colors";
import { BookTeaser, BookTeaserProps } from "../BookTeaser/BookTeaser";
import { ContentfulButtonLinkProps, LinkedButton } from "../ButtonLink/ButtonLink";
import { Icon } from "../Icon/Icon";
import { LoadingContainer } from "../LoadingContainer/LoadingContainer";
import { RichTextContainer, RichTextContainerProps } from "../RichTextContainer/RichTextContainer";
import styles from "./Carousel.module.scss";

export interface CarouselProps {
  backgroundColor?: string;
  dataTestId?: string;
  elementsAligned: boolean;
  galleryBookTeaserClassNames?: string;
  headerTextBlockRef?: RichTextContainerProps;
  isCarousel: boolean;
  linkedButtonRef?: ContentfulButtonLinkProps;
  products: BookTeaserProps[];
  spacingClassNames?: string;
}

export const Carousel: React.FC<CarouselProps> = ({
  backgroundColor,
  dataTestId,
  elementsAligned = true,
  galleryBookTeaserClassNames,
  headerTextBlockRef,
  isCarousel = true,
  linkedButtonRef,
  products,
  spacingClassNames,
}) => {
  const [sliderRef, sliderActions] = useSlider({ loop: true });
  const next = () => sliderActions?.scrollNext();
  const previous = () => sliderActions?.scrollPrev();
  const [totalCarouselImagesLoaded, setTotalCarouselImagesLoaded] = React.useState(0);

  // INFO: allow slider elements to properly align after page load in the NextJs context
  useEffect(() => {
    if (isCarousel && !!sliderActions && totalCarouselImagesLoaded === products.length) {
      setTimeout(() => sliderActions.reInit(), 100);
    }
  }, [isCarousel, sliderActions, elementsAligned, totalCarouselImagesLoaded, products.length]);

  const containerClasses = classNames(
    "max-width-container",
    { "res-top-padding-48 res-bottom-padding-48": !spacingClassNames },
    spacingClassNames
  );
  const galleryBookTeaserClasses = classNames(
    {
      "col-3 col-m-big-6 col-s-12": !galleryBookTeaserClassNames,
    },
    galleryBookTeaserClassNames
  );

  const gradientColor =
    (backgroundColor && mapContentfulColorToStyleProp(backgroundColor)) || Colors.White;

  return (
    <div
      style={
        backgroundColor ? { background: mapContentfulColorToStyleProp(backgroundColor) } : undefined
      }
      data-testid={dataTestId}
    >
      <div className={containerClasses}>
        {headerTextBlockRef && (
          <RichTextContainer
            standaloneEntry={false}
            richTextFieldsCollection={headerTextBlockRef.richTextFieldsCollection}
          />
        )}
        {isCarousel ? (
          <LoadingContainer loading={totalCarouselImagesLoaded < products.length}>
            <div className={styles.carousel}>
              <Icon
                className={styles.arrow}
                onClick={previous}
                iconName={Icons.ArrowLinkLeftLarge}
                color={Colors.Indigo}
                size={32}
                style={{ cursor: "pointer" }}
              />
              <div className={styles.sliderWrapper}>
                <div
                  className={styles.linearGradient}
                  style={{
                    background: `linear-gradient(to right, ${gradientColor}, ${gradientColor}00)`,
                  }}
                ></div>
                <div ref={sliderRef}>
                  <div className={styles.sliderElements}>
                    {products.map((book) => (
                      <BookTeaser
                        {...book}
                        orientation={elementsAligned ? "landscape" : book.orientation}
                        key={book.productUrl}
                        onImageLoaded={() => setTotalCarouselImagesLoaded((prev) => prev + 1)}
                      />
                    ))}
                  </div>
                </div>
                <div
                  className={`${styles.linearGradient} ${styles.linearGradientRight}`}
                  style={{
                    background: `linear-gradient(to left, ${gradientColor}, ${gradientColor}00)`,
                  }}
                ></div>
              </div>
              <Icon
                className={styles.arrow}
                onClick={next}
                iconName={Icons.ArrowLinkRightLarge}
                color={Colors.Indigo}
                size={32}
                style={{ cursor: "pointer" }}
              />
            </div>
          </LoadingContainer>
        ) : (
          <div className="grid-12" style={{ rowGap: "3rem" }}>
            {products.map((book) => (
              <div className={galleryBookTeaserClasses} key={book.productUrl}>
                <BookTeaser
                  {...book}
                  orientation={elementsAligned ? "landscape" : book.orientation}
                />
              </div>
            ))}
          </div>
        )}
        {linkedButtonRef && (
          <LinkedButton
            {...linkedButtonRef}
            linkedButtonClassNames={linkedButtonRef.linkedButtonClassNames || styles.buttonWrapper}
          />
        )}
      </div>
    </div>
  );
};
