import classNames from "classnames";
import React from "react";

import styles from "./DropDownContainer.module.scss";

export interface DropDownContainerProps {
  dataTestId?: string;
  title: string;
  downwardArrow: boolean;
  increasedHorizontalPadding: boolean;
  children?: React.ReactNode;
}

export const DropDownContainer: React.FC<DropDownContainerProps> = ({
  dataTestId,
  title,
  downwardArrow,
  increasedHorizontalPadding,
  children,
}) => {
  const containerClasses = classNames(styles.dropDownContainer, {
    [styles.withArrow]: downwardArrow,
    [styles.increasedHorizontalPadding]: increasedHorizontalPadding,
  });

  return (
    <section data-testid={dataTestId}>
      <div className={containerClasses}>
        <h3 className={classNames(styles.title, "white-text")}>{title}</h3>
        <div className={styles.fieldWrapper}>{children}</div>
      </div>
    </section>
  );
};
