import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { getTotalProductPriceAfterDiscounts } from "../../utils";
import { Button } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import { MoneyDisplay } from "../MoneyDisplay/MoneyDisplay";
import styles from "./OrderDetails.module.scss";
import { OrderLineItem, ProductLink } from "./types";
import { getProductTypeIcon } from "./utils";

interface OrderItemProductProps extends OrderLineItem {
  renderProductLink: (productLinkAttributes: ProductLink) => React.ReactNode;
  orderId: string;
  locale: string;
  error?: boolean;
  productTypeName: string;
}

export const OrderItemProduct: React.FC<OrderItemProductProps> = ({
  renderProductLink,
  title,
  product,
  isEbookDistroFee,
  totalAmount,
  unitAmount,
  discounts,
  quantity,
  locale,
  downloadUrl,
  error,
  productTypeName,
}) => {
  const priceAfterDiscounts = getTotalProductPriceAfterDiscounts(totalAmount, discounts);
  const isbn = product?.isbn;
  const orderClass = classNames(styles.row, {
    [styles.error]: !!error,
  });
  const productClass = classNames(styles.orderItemProduct, {
    [styles.error]: !!error,
  });
  const Wrapper = error ? "div" : React.Fragment;

  const { _textPrinterError, _textUnknownProduct, _textNoCover, _textDownloadEbook, _textBy } =
    useCopy("OrderDetails");

  return (
    <Wrapper {...(error && { className: styles.productError })}>
      {error && (
        <div className={styles.errorNotification}>
          <div>
            <Icon iconName={Icons.NotificationError} />
            <p className="almost-black-text">{_textPrinterError}</p>
          </div>
        </div>
      )}
      <div className={orderClass}>
        <div className={productClass}>
          <div className={styles.productThumbnail}>
            {product?.thumbnailUrl && product?.name ? (
              <img src={product.thumbnailUrl} alt={product.name} />
            ) : (
              <div className={styles.noCover}>{_textNoCover}</div>
            )}
          </div>
          <div className={styles.productTitle}>
            <h4 className={styles.semiBold}>
              {product ? (
                !product.canonicalUrlSlug ? (
                  <span>{product.name}</span>
                ) : (
                  renderProductLink({ ...product, isEbookDistroFee })
                )
              ) : (
                title || _textUnknownProduct
              )}
            </h4>
            {!isEbookDistroFee && (
              <p className="almost-black-text">
                {productTypeName + " "}
                {product?.contributors && product.contributors.length > 0 && (
                  <>
                    | {_textBy}{" "}
                    {product.contributors.slice(0, 2).map((contributor, i) => (
                      <span key={i}>
                        {i === 1 && ", "}
                        {contributor?.name}
                      </span>
                    ))}
                  </>
                )}
              </p>
            )}
            {isbn && <p className="almost-black-text">ISBN: {isbn}</p>}
          </div>
        </div>
        <div className={styles.info}>
          {product?.type && (
            <div className={styles.productIcon}>
              <Icon
                iconName={
                  isEbookDistroFee ? Icons.DistributionEbookYes : getProductTypeIcon(product?.type)
                }
              />
            </div>
          )}
          <div className={styles.alignRight}>
            <h3 className="almost-black-text-60">
              {unitAmount?.amount && <MoneyDisplay money={unitAmount} locale={locale} />}
            </h3>
          </div>
          <div className={styles.alignRight}>
            <h3 className="almost-black-text">x {quantity}</h3>
          </div>
          <div className={styles.alignRight}>
            {priceAfterDiscounts === totalAmount?.amount ? (
              <h3 className={styles.price}>
                <MoneyDisplay money={totalAmount} locale={locale} />
              </h3>
            ) : (
              <h3>
                <h3 className="subhead">
                  <s>
                    {totalAmount?.amount && <MoneyDisplay money={totalAmount} locale={locale} />}
                  </s>
                </h3>
                <h3>
                  <MoneyDisplay
                    money={{
                      amount: priceAfterDiscounts,
                      currency: totalAmount.currency,
                    }}
                    locale={locale}
                    className={styles.price}
                  />
                </h3>
              </h3>
            )}
          </div>
        </div>
        <div className={styles.downloadEbook}>
          {downloadUrl && (
            <Button
              dataTestId="download-ebook-button"
              download
              downloadHref={downloadUrl}
              downloadTitle={title ?? undefined}
              buttonSize="small"
            >
              {_textDownloadEbook}
            </Button>
          )}
        </div>
      </div>
    </Wrapper>
  );
};
