import React, { useContext } from "react";

import { useCopy } from "../../../../extensions";
import { Accordion, AccordionItem } from "../../../Accordion/Accordion";
import { AdvancedProductSpecsTableAdapter } from "../../AdvancedProductSpecsTableAdapter";
import { PricingCalculatorContext } from "../../PricingCalculatorContext";
import { ConfiguratorActionType } from "../../reducer";
import { RevenueCalculatorAdapter } from "../../RevenueCalculatorAdapter";
import { ShippingCalculatorAdapter } from "../../ShippingCalculatorAdapter";
import { imageUrlBuilder } from "../../utils";
import { AttributeOptionValue, AttributeSelector } from "../AttributeSelector/AttributeSelector";
import { BindingType } from "./BindingType";
import { BookSizeSelect } from "./BookSizeSelect/BookSizeSelect";

type OptionType = "bindingType" | "interiorInk" | "interiorPaper" | "coverFinish";

const customOrder: Partial<Record<OptionType, Array<{ id: string; colStart?: number }>>> = {
  interiorInk: [{ id: "BWSTD" }, { id: "BWPRE" }, { id: "FCSTD", colStart: 1 }, { id: "FCPRE" }],
};

export const BookConfigurator: React.FC = () => {
  const {
    getDisplayValue,
    state,
    dispatch,
    availableOptions,
    filteredOptions,
    podPackageId,
    productType,
    isDeveloperPortalVersion,
  } = useContext(PricingCalculatorContext);

  const {
    _textInteriorColor,
    _textPaperType,
    _textCoverFinish,
    _textShippingAndHandlingEstimate,
    _textRevenueEstimates,
    _textLinenWrapDescription,
    _textAdvancedProductSpecifications,
  } = useCopy("PricingCalculator");

  const configurationStarted = Boolean(state.trimSize && state.pageCount);

  const handleTrimSizeChange = (value: string) => {
    dispatch({
      payload: { option: "trimSize", value: value },
      type: ConfiguratorActionType.CHANGE_POD_OPTION,
    });
  };

  const handlePageCountChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const pageCount = /^[0-9]+$/.test(event.target.value) ? parseInt(event.target.value) : null;

    dispatch({
      payload: { value: pageCount },
      type: ConfiguratorActionType.CHANGE_PAGE_COUNT,
    });
  };

  const parseOption = (option: string, optionType: OptionType): AttributeOptionValue => {
    type EnabledOptionsKey = `${typeof optionType}EnabledOptions`;
    type DistroEligibleOptionsKey = `${typeof optionType}DistroEligibleOptions`;

    const customOrderForOptionType = customOrder[optionType];

    return {
      unavailable:
        !filteredOptions[`${optionType}EnabledOptions` as EnabledOptionsKey]?.includes(option),
      disabled: !configurationStarted,
      isDistroEligible:
        filteredOptions[`${optionType}DistroEligibleOptions` as DistroEligibleOptionsKey]?.includes(
          option
        ),
      label: getDisplayValue(option) ?? "",
      description:
        optionType === "bindingType" && option === "LW" ? _textLinenWrapDescription : undefined,
      value: option,
      order: customOrderForOptionType
        ? customOrderForOptionType.findIndex((o) => o.id === option)
        : 0,
      colStart: customOrderForOptionType?.find((o) => o.id === option)?.colStart,
    };
  };

  return (
    <div data-testid="book-configurator">
      <BookSizeSelect
        key={`bookSizeSelect_${productType}`}
        bookConfiguratorState={state}
        onTrimSizeChange={handleTrimSizeChange}
        onPageCountChange={handlePageCountChange}
        productType={productType}
      />
      <BindingType
        onChange={(option) =>
          dispatch({
            payload: { option: "bindingType", value: option },
            type: ConfiguratorActionType.CHANGE_POD_OPTION,
          })
        }
        activeBindingType={state.bindingType}
        values={
          availableOptions.bindingTypeOptions?.map((option: string) => ({
            ...parseOption(option, "bindingType"),
            image: imageUrlBuilder(`ProductOption/Book/lulu-book-binding-type-${option}`),
          })) ?? []
        }
      />
      <AttributeSelector
        title={_textInteriorColor}
        onChange={(option) =>
          dispatch({
            payload: { option: "interiorInk", value: option },
            type: ConfiguratorActionType.CHANGE_POD_OPTION,
          })
        }
        activeValue={state.interiorInk}
        values={
          availableOptions.interiorInkOptions?.map((option: string) => ({
            ...parseOption(option, "interiorInk"),
            image: imageUrlBuilder(`ProductOption/Book/lulu-book-interior-color-${option}`),
          })) ?? []
        }
        dataTestId="interior-ink"
      />
      <AttributeSelector
        title={_textPaperType}
        onChange={(option) =>
          dispatch({
            payload: { option: "interiorPaper", value: option },
            type: ConfiguratorActionType.CHANGE_POD_OPTION,
          })
        }
        activeValue={state.interiorPaper}
        values={
          availableOptions.interiorPaperOptions?.map((option: string) => ({
            ...parseOption(option, "interiorPaper"),
            image: imageUrlBuilder(
              `ProductOption/Book/lulu-book-paper-type-${option.replace("70", "80")}`
            ),
          })) ?? []
        }
        dataTestId="interior-paper"
      />
      <AttributeSelector
        title={_textCoverFinish}
        onChange={(option) =>
          dispatch({
            payload: { option: "coverFinish", value: option },
            type: ConfiguratorActionType.CHANGE_POD_OPTION,
          })
        }
        activeValue={state.coverFinish}
        values={
          availableOptions.coverFinishOptions?.map((option: string) => ({
            ...parseOption(option, "coverFinish"),
            image: imageUrlBuilder(`ProductOption/Book/lulu-book-cover-finish-${option}`),
          })) ?? []
        }
        dataTestId="cover-finish"
      />
      <Accordion allowMultiSelect>
        <AccordionItem
          title={_textShippingAndHandlingEstimate}
          disabled={!podPackageId || !state.pageCount}
          dataTestId="shipping-calculator"
        >
          <ShippingCalculatorAdapter />
        </AccordionItem>
        {isDeveloperPortalVersion ? (
          <AccordionItem
            title={_textAdvancedProductSpecifications}
            disabled={!podPackageId || !state.pageCount}
            dataTestId="advanced-product-specifications"
          >
            <AdvancedProductSpecsTableAdapter />
          </AccordionItem>
        ) : (
          <AccordionItem
            title={_textRevenueEstimates}
            disabled={!podPackageId || !state.pageCount}
            dataTestId="revenue-calculator"
          >
            <RevenueCalculatorAdapter />
          </AccordionItem>
        )}
      </Accordion>
    </div>
  );
};
