// replace '-' with '/' in short date strings ('YYYY-MM-DD') to ensure consistent date display for users in all timezones
export const sanitizeDateString = (date: string) =>
  date.length === 10 ? date.replace(/-/g, "/") : date;

export const formatDateAndTime = (locale: string, dateTime: string) => {
  const cleanDateString = sanitizeDateString(dateTime);

  const d = new Date(cleanDateString);
  const date = d.toLocaleDateString(locale, {
    month: "short",
    day: "numeric",
    year: "numeric",
  });
  const time = d.toLocaleTimeString(locale, {
    hour: "numeric",
    minute: "numeric",
  });

  return [date, time];
};
