import React from "react";

import { useCopy } from "../../extensions/CopyContext";
import styles from "./PaginationInfo.module.scss";

export interface PaginationInfoProps {
  dataTestId?: string;
  itemsCount: number;
  pageNumber: number;
  pageSize: number;
  pageCount: number;
  rightAligned?: boolean;
  sizeChoices?: number[];
  onPageSizeChange: (size: number) => void;
}

export interface PageSizeChoiceProps {
  pageSize: number;
  isSelected: boolean;
  onClick: () => void;
}

export const PaginationInfo: React.FC<PaginationInfoProps> = ({
  dataTestId,
  pageSize,
  itemsCount,
  pageNumber,
  pageCount,
  rightAligned,
  sizeChoices,
  onPageSizeChange,
}) => {
  const { _textOf, _textPage, _textPages, _textPerPage, _textShow } = useCopy("PaginationInfo");
  const pageLabel = pageCount > 1 ? _textPages : _textPage;
  const offsetStart = itemsCount ? (pageNumber - 1) * pageSize + 1 : 0;
  const offsetStop = Math.min(itemsCount, pageSize * pageNumber);
  const PAGE_SIZE_CHOICES = sizeChoices && sizeChoices.length > 0 ? sizeChoices : [10, 20, 50];

  return (
    <div
      data-testid={dataTestId}
      className={rightAligned ? styles.paginationInfoRightAligned : styles.paginationInfo}
    >
      {offsetStart} - {offsetStop} {_textOf} {itemsCount} ({pageCount} {pageLabel})
      <ul className={styles.sizePicker}>
        <li>{_textShow}</li>
        {PAGE_SIZE_CHOICES.map((size) => (
          <li key={size}>
            <PageSizeChoice
              isSelected={pageSize === size}
              pageSize={size}
              onClick={() => onPageSizeChange(size)}
            />
          </li>
        ))}
        <li>{_textPerPage}</li>
      </ul>
    </div>
  );
};

export const PageSizeChoice: React.FunctionComponent<PageSizeChoiceProps> = ({
  isSelected,
  pageSize,
  onClick,
}) => {
  const { _textItems } = useCopy("PageSizeChoice");
  return (
    <button
      className={isSelected ? styles.pageSizeChoiceActive : styles.pageSizeChoice}
      type="button"
      onClick={onClick}
      data-testid={`page-size-${pageSize}-items-button`}
    >
      {`${pageSize} ${_textItems}`}
    </button>
  );
};
