import classNames from "classnames";
import React, { useEffect, useState } from "react";

import { useCopy } from "../../../extensions";
import { getAmountByCurrency } from "../../../utils";
import { Button } from "../../Button/Button";
import { MoneyDisplay } from "../../MoneyDisplay/MoneyDisplay";
import { Cart, CartDrawerProps, Price } from "../CartDrawer";
import styles from "./CartSummary.module.scss";

export type AppliedVolumeDiscount = {
  discountPercentage: number;
  discountAmount: Price[];
};

export interface CartSummaryProps
  extends Pick<Cart, "subtotal">,
    Pick<CartDrawerProps, "onClose" | "onRedirect" | "currency"> {
  isCartEmpty: boolean;
  withTopBorder?: boolean;
  onCartPage?: boolean;
  loading?: boolean;
  appliedVolumeDiscounts?: AppliedVolumeDiscount[];
  totalDiscountAmount?: Price[];
  locale?: string;
}

export const CartSummary = ({
  subtotal,
  onClose,
  currency,
  onRedirect,
  isCartEmpty,
  withTopBorder = true,
  onCartPage = false,
  loading = false,
  appliedVolumeDiscounts = [],
  totalDiscountAmount = [],
  locale = "en",
}: CartSummaryProps) => {
  const {
    _textCalculatedAtCheckout,
    _textSubtotal,
    _textViewCart,
    _textContinueShopping,
    _textShippingAndTax,
    _textCheckout,
    _textAppliedDiscounts,
  } = useCopy("CartDrawer");
  const { _textBulkDiscount } = useCopy("CartItem");
  const [isLoading, setIsLoading] = useState(loading);
  const discountTotal = getAmountByCurrency(totalDiscountAmount, currency) ?? 0;
  const subtotalAmount = getAmountByCurrency(subtotal, currency);

  useEffect(() => {
    function disableLoading(event: PageTransitionEvent) {
      if (event.persisted) {
        setIsLoading(false);
      }
    }

    window.addEventListener("pageshow", disableLoading);

    return () => window.removeEventListener("pageshow", disableLoading);
  }, []);

  return (
    <div
      className={classNames(styles.footer, {
        [styles.withTopBorder]: withTopBorder,
      })}
    >
      <div className={styles.itemizedCartCosts}>
        <span className={styles.label}>{_textShippingAndTax}</span>
        <span className={styles.amount}>{_textCalculatedAtCheckout}</span>
        {discountTotal > 0 && (
          <>
            <span className={styles.label}>{_textAppliedDiscounts}</span>
            <span data-testid="total-applied-discounts" className={styles.amount}>
              -
              <MoneyDisplay
                money={{
                  amount: discountTotal,
                  currency: currency,
                }}
                locale={locale}
              />
            </span>
            {appliedVolumeDiscounts.map((discount, idx) => {
              const discountAmount = getAmountByCurrency(discount.discountAmount, currency);

              return (
                <React.Fragment key={`volumeDiscount${discount.discountPercentage}%`}>
                  <span className={classNames(styles.label, styles.bulkDiscountLabel)}>
                    {_textBulkDiscount} (-{discount.discountPercentage}%)
                  </span>
                  <span
                    data-testid={`${idx + 1}-bulk-discount-amount`}
                    className={styles.bulkDiscountAmount}
                  >
                    -
                    <MoneyDisplay
                      money={{
                        amount: discountAmount,
                        currency: currency,
                      }}
                      locale={locale}
                    />
                  </span>
                </React.Fragment>
              );
            })}
          </>
        )}
        <span className={styles.subtotal}>{_textSubtotal}</span>
        <span data-testid="subtotal-amount" className={styles.subtotal}>
          <MoneyDisplay
            money={{
              amount: subtotalAmount || 0,
              currency: currency,
            }}
            locale={locale}
          />
        </span>
      </div>
      <div className={styles.actions}>
        {!isCartEmpty && (
          <Button
            dataTestId={onCartPage ? "checkout-btn" : "view-cart-btn"}
            hierarchy="primary"
            block
            onClick={() => {
              setIsLoading(true);
              onRedirect();
            }}
            responsive={false}
            loading={isLoading}
            disabled={isLoading}
          >
            {onCartPage ? _textCheckout : _textViewCart}
          </Button>
        )}
        <Button
          dataTestId="continue-shopping-btn"
          hierarchy="secondary"
          block
          onClick={onClose}
          responsive={false}
          disabled={isLoading}
        >
          {_textContinueShopping}
        </Button>
      </div>
    </div>
  );
};
