import classNames from "classnames";
import chunk from "lodash.chunk";
import React, { useState } from "react";
import { HexColorPicker } from "react-colorful";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../extensions/CopyContext";
import { Icons } from "../../icon-types";
import { hexToRgb, RGBColor, rgbToHex } from "../../utils/colors";
import { isEnterOrSpaceKeyEvent } from "../../utils/isEnterOrSpaceKeyEvent";
import { Icon } from "../Icon/Icon";
import styles from "./ColorPicker.module.scss";
import { ColorPickerButton } from "./ColorPickerButton";
import { HelperInput } from "./HelperInput";

interface ColorBubbleProps {
  className?: string;
  style?: React.CSSProperties;
  transparent?: boolean;
}

export const ColorBubble = ({ className, style, transparent }: ColorBubbleProps) => (
  <div
    className={classNames(styles.colorBubble, className, {
      [styles.transparent]: transparent,
    })}
    style={style}
  />
);

interface AccordionProps {
  onClick: () => void;
  label: string;
  isOpen: boolean;
}

const Accordion = ({ onClick, label, isOpen }: AccordionProps) => {
  return (
    <div role="presentation" className={styles.moreOptionsContainer} onClick={onClick}>
      <span className={styles.moreOptionsLabel}>{label}</span>
      <div className={isOpen ? styles.moreOptionsButtonActive : styles.moreOptionsButton}>
        <Icon iconName={isOpen ? Icons.ArrowUpLarge : Icons.ArrowDownLarge} />
      </div>
    </div>
  );
};

interface BaseColorPickerProps {
  color: string | RGBColor;
}

interface ColorPickerWithTransparent extends BaseColorPickerProps {
  allowTransparent: true;
  onChange: (color: { hex: string; rgb: RGBColor } | "transparent") => void;
}

interface ColorPickerWithoutTransparent extends BaseColorPickerProps {
  allowTransparent?: false;
  onChange: (color: { hex: string; rgb: RGBColor }) => void;
}

type ColorPickerProps = ColorPickerWithTransparent | ColorPickerWithoutTransparent;

const DEFAULT_COLORS = [
  "#ffffff",
  "#e6e6e6",
  "#bfbfbf",
  "#808080",
  "#404040",
  "#000000",
  "#d6ccff",
  "#944dff",
  "#400080",
  "#1d3557",
  "#002e99",
  "#004dff",
  "#4dccff",
  "#cce6ff",
  "#b3e6d9",
  "#75d575",
  "#00cc00",
  "#a0ea46",
  "#fff24d",
  "#ffe600",
  "#ffcc4d",
  "#ffb300",
  "#cc7a00",
  "#804d00",
  "#ff804d",
  "#e65c00",
  "#ff4d4d",
  "#e60000",
  "#cc0000",
  "#800000",
];

type ColorHelper = "hex" | "rgb";
const _hex = "hex" as ColorHelper;
const _rgb = "rgb" as ColorHelper;

type ColorMode = "default" | "custom";

export const ColorPicker: React.FC<ColorPickerProps> = ({
  onChange,
  color: colorProp,
  allowTransparent,
}) => {
  const [currentColorMode, setCurrentColorMode] = useState<ColorMode | undefined>("default");
  const [currentColorHelper, setCurrentColorHelper] = useState(_hex);
  const { _defaultColors, _customColors, _recentlyUsedColors } = useCopy("ColorPicker");
  const [recentlyUsedColors, setRecentlyUsedColors] = useState<string[]>([]);

  const { rgb, hex } = {
    rgb: typeof colorProp === "string" ? hexToRgb(colorProp) : colorProp,
    hex: typeof colorProp === "string" ? colorProp : rgbToHex(colorProp),
  };

  const [selectedColor, setSelectedColor] = useState<string>(hex || rgbToHex(rgb));

  const transparentColorFallback = selectedColor === "transparent" ? "#ffffff" : selectedColor;

  const availableColors = allowTransparent
    ? ["transparent", ...DEFAULT_COLORS.filter((color) => color !== "#e6e6e6")]
    : DEFAULT_COLORS;

  const colorsChunks = chunk(availableColors, 6);

  const debouncedChange = useDebouncedCallback((newColor) => {
    setSelectedColor(newColor.hex);
    onChange(newColor);
  }, 300);

  const handleAccordionClick = (option: ColorMode | undefined) => {
    setCurrentColorMode((prevState) => (prevState === option ? undefined : option));
  };

  React.useEffect(() => {
    if (selectedColor === null) {
      return;
    }

    setRecentlyUsedColors((prevState) => {
      const stateCopy = prevState.filter(Boolean);
      stateCopy.unshift(selectedColor);

      const newState = Array.from(new Set(stateCopy)).slice(0, 6);
      const filler = Array(6 - newState.length).fill(null);

      return [...newState, ...filler];
    });
  }, [selectedColor]);

  const handleColorBubbleClick = (color: string) => {
    setSelectedColor(color);

    if (allowTransparent && color === "transparent") {
      // For some reason TypeScript isn't able to correctly narrow the type down,
      // but it should as the ColorPickerWithTransparent is the only type which will pass allowTransparent check.
      onChange("transparent" as any);
      return;
    }

    onChange({
      hex: color,
      rgb: hexToRgb(color),
    });
  };

  const handleColorChange = (color: string | RGBColor) => {
    let newColor: { hex: string; rgb: RGBColor };

    if (typeof color === "string") {
      newColor = {
        hex: color,
        rgb: hexToRgb(color),
      };
    } else {
      newColor = {
        hex: rgbToHex(color),
        rgb: color,
      };
    }

    debouncedChange(newColor);
  };

  return (
    <div className={styles.container}>
      <div className={styles.label}>{_recentlyUsedColors}</div>
      <div className={classNames(styles.colorsContainerRow, styles.recentlyUsedColors)}>
        {recentlyUsedColors.map((color, index) =>
          color === null ? (
            <div key={index} style={{ width: "36px" }} />
          ) : (
            <div
              tabIndex={0}
              role="button"
              aria-label={`recent ${color}`}
              className={classNames(styles.colorBubbleWrapper, {
                [styles.colorBubbleWrapperActive]: color === hex,
              })}
              onClick={() => handleColorBubbleClick(color)}
              key={color}
              onKeyDown={(e) => {
                if (isEnterOrSpaceKeyEvent(e)) {
                  handleColorBubbleClick(color);
                }
              }}
            >
              <ColorBubble
                transparent={color === "transparent"}
                style={{ backgroundColor: color }}
              />
            </div>
          )
        )}
      </div>

      <Accordion
        onClick={() => handleAccordionClick("default")}
        label={_defaultColors}
        isOpen={currentColorMode === "default"}
      />

      {currentColorMode === "default" && (
        <div className={styles.colorsContainer}>
          {colorsChunks.map((row, index) => (
            <div className={styles.colorsContainerRow} key={index}>
              {row.map((color) => {
                return (
                  <div
                    tabIndex={0}
                    role="button"
                    aria-label={color}
                    className={
                      color === selectedColor
                        ? styles.colorBubbleWrapperActive
                        : styles.colorBubbleWrapper
                    }
                    onClick={() => handleColorBubbleClick(color)}
                    key={color}
                    onKeyDown={(e) => {
                      if (isEnterOrSpaceKeyEvent(e)) {
                        handleColorBubbleClick(color);
                      }
                    }}
                  >
                    <ColorBubble
                      transparent={color === "transparent"}
                      style={{ backgroundColor: color }}
                    />
                  </div>
                );
              })}
            </div>
          ))}
        </div>
      )}

      <div className={styles.spacer} />

      <Accordion
        onClick={() => handleAccordionClick("custom")}
        label={_customColors}
        isOpen={currentColorMode === "custom"}
      />

      {currentColorMode === "custom" && (
        <>
          <div className={styles.colorPickerWrapper}>
            <HexColorPicker color={selectedColor ?? undefined} onChange={handleColorChange} />
          </div>
          <div className={styles.colorButtonsContainer}>
            <ColorPickerButton
              active={currentColorHelper === _hex}
              onClick={() => setCurrentColorHelper(_hex)}
            >
              HEX
            </ColorPickerButton>
            <ColorPickerButton
              active={currentColorHelper === _rgb}
              onClick={() => setCurrentColorHelper(_rgb)}
            >
              RGB
            </ColorPickerButton>
          </div>
          <div className={styles.helpersContainer}>
            <div className={currentColorHelper === _hex ? styles.helperHex : styles.helperRgb}>
              <ColorBubble
                style={{
                  backgroundColor: selectedColor ?? "#ffffff",
                  margin: "4px 12px 4px 0",
                }}
              />
              {currentColorHelper === _hex ? (
                <div style={{ width: "92px" }}>
                  <HelperInput
                    rgb={rgb}
                    hex={selectedColor}
                    label="hex"
                    onChange={handleColorChange}
                    color={transparentColorFallback}
                  />
                </div>
              ) : (
                <>
                  <div style={{ marginRight: "16px", width: "46px" }}>
                    <HelperInput
                      rgb={rgb}
                      hex={transparentColorFallback}
                      label="r"
                      onChange={handleColorChange}
                      color={rgb.r}
                    />
                  </div>
                  <div style={{ marginRight: "16px", width: "46px" }}>
                    <HelperInput
                      rgb={rgb}
                      hex={transparentColorFallback}
                      label="g"
                      onChange={handleColorChange}
                      color={rgb.g}
                    />
                  </div>
                  <div style={{ marginRight: "16px", width: "46px" }}>
                    <HelperInput
                      rgb={rgb}
                      hex={transparentColorFallback}
                      label="b"
                      onChange={handleColorChange}
                      color={rgb.b}
                    />
                  </div>
                </>
              )}
            </div>
          </div>
        </>
      )}
    </div>
  );
};
