import classNames from "classnames";
import React, { CSSProperties, useEffect, useState } from "react";

import { useCopy } from "../../extensions";
import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../hooks";
import { Icons } from "../../icon-types";
import { IconButton } from "../IconButton/IconButton";
import styles from "./CartDrawer.module.scss";
import { CartItem } from "./CartItem/CartItem";
import { CartSummary } from "./CartSummary/CartSummary";
import { OptimisticItemsController } from "./OptimisticItemsController/OptimisticItemsController";

export type Price = {
  amount: number;
  currency: string;
};

type ProductAttribute = {
  name: string;
  value: string | string[];
};

export type CartItemDiscount = {
  code: string;
  type: "FIXED" | "PERCENTAGE";
  value: number;
};

export type CartItemType = {
  product: {
    id: string;
    name: string;
    type: string;
    thumbnailUrl: string;
    attributes: ProductAttribute[];
  };
  quantity: number;
  unitPrice: Price[];
  totalPrice: Price[];
  discounts?: CartItemDiscount[];
  discountedTotalPrice?: Price[];
  itemRedirectURL: string;
};

export type Cart = {
  items: CartItemType[];
  lastAddedItemId?: string;
  subtotal: Price[];
};

export type CartDrawerProps = {
  visible?: boolean;
  loggedIn?: boolean;
  ignoreScroll?: boolean;
  customPageHeaderHeight?: number;
  classnames?: string;
  locale?: string;
  currency: string;
  totalItemsText: string;
  cart: Cart;
  onClose: () => void;
  onRedirect: () => void;
  onQuantityChange: (id: string, quantity: number) => Promise<any>;
};

export const CartDrawer = ({
  visible = false,
  loggedIn = false,
  customPageHeaderHeight,
  onClose,
  onQuantityChange,
  cart: { items = [], lastAddedItemId, subtotal },
  currency,
  classnames,
  onRedirect,
  totalItemsText,
  locale = "en",
  ignoreScroll = false,
}: CartDrawerProps) => {
  const isMobile = useWindowWidth() <= MEDIA_BREAKPOINTS.S;
  const { _textYourCart, _textEmptyCart } = useCopy("CartDrawer");
  const headerHeight = !!customPageHeaderHeight ? customPageHeaderHeight : loggedIn ? 91 : 57;

  const [dynamicHeaderHeight, setDynamicHeaderHeight] = useState(headerHeight);

  useEffect(() => {
    if (ignoreScroll) return;

    const handlePageScroll = () => {
      const newHeaderHeight = headerHeight - window.scrollY;
      setDynamicHeaderHeight(newHeaderHeight < 0 ? 0 : newHeaderHeight);
    };

    window.addEventListener("scroll", handlePageScroll);

    return () => window.removeEventListener("scroll", handlePageScroll);
  }, [headerHeight, ignoreScroll]);

  if (!visible) return null;

  return (
    <div
      className={classNames(classnames, isMobile ? styles.cartDrawerMobile : styles.cartDrawer)}
      style={
        {
          "--header-height": `${dynamicHeaderHeight}px`,
        } as CSSProperties
      }
    >
      <div
        data-testid="cart-drawer-wrapper"
        className={isMobile ? styles.wrapperMobile : styles.wrapper}
      >
        <div className={styles.header}>
          <div className={styles.headerText}>{_textYourCart}</div>
          <IconButton
            dataTestId="cart-drawer-close-icon"
            icon={Icons.DisplayCloseSmall}
            onClick={onClose}
            shape="naked"
            className={styles.closeButton}
          />
        </div>
        <div
          data-testid="cart-drawer-products"
          className={classNames(isMobile ? styles.itemsMobile : styles.itemsDesktop, {
            [styles.noItemsInCart]: items.length === 0,
          })}
        >
          {items.length > 0 ? (
            <OptimisticItemsController
              items={items}
              onQuantityChange={onQuantityChange}
              {...(isMobile && {
                lastAddedItemId,
                onlyDisplayLastAddedItem: true,
              })}
            >
              {(props) => (
                <CartItem
                  {...props}
                  currency={currency}
                  totalItemsText={totalItemsText}
                  key={props.item.product.id}
                  locale={locale}
                />
              )}
            </OptimisticItemsController>
          ) : (
            <p className={isMobile ? styles.emptyCartMessageMobile : styles.emptyCartMessage}>
              {_textEmptyCart}
            </p>
          )}
        </div>
        <CartSummary
          subtotal={subtotal}
          onClose={onClose}
          onRedirect={onRedirect}
          currency={currency}
          withTopBorder
          isCartEmpty={items.length === 0}
          locale={locale}
        />
      </div>
    </div>
  );
};
