import React from "react";

import { Link } from "../Link/Link";

export interface IconWithMetadataProps {
  dataTestId?: string;
  icon: string;
  iconLinkUrl?: string;
  iconLinkPage?: {
    url?: string;
  };
  iconAltText?: string;
  newTab: boolean;
  iconColor?: string;
}

export const IconWithMetadata: React.FC<IconWithMetadataProps> = ({
  dataTestId,
  icon,
  iconLinkUrl,
  iconLinkPage,
  iconAltText,
  iconColor,
  newTab,
}) => {
  const IconComponent = () => (
    <i
      className={icon}
      title={iconAltText}
      style={{ color: iconColor }}
      role="img"
      aria-label={iconAltText ?? icon}
      data-testid={dataTestId}
    />
  );

  if (!iconLinkPage && !iconLinkUrl) {
    return <IconComponent />;
  }

  return (
    <Link page={iconLinkPage} url={iconLinkUrl} newTab={newTab}>
      <IconComponent />
    </Link>
  );
};
