import React, { useContext, useEffect } from "react";

import { useCopy } from "../../../../extensions";
import { Accordion, AccordionItem } from "../../../Accordion/Accordion";
import { AdvancedProductSpecsTableAdapter } from "../../AdvancedProductSpecsTableAdapter";
import { PricingCalculatorContext } from "../../PricingCalculatorContext";
import { ConfiguratorActionType } from "../../reducer";
import { RevenueCalculatorAdapter } from "../../RevenueCalculatorAdapter";
import { ShippingCalculatorAdapter } from "../../ShippingCalculatorAdapter";
import { getAllOptionsFromPodPackages, imageUrlBuilder } from "../../utils";
import { AttributeSelector } from "../AttributeSelector/AttributeSelector";
import styles from "../PricingConfigurator.module.scss";
import { CalendarSizeSelect } from "./CalendarSizeSelect/CalendarSizeSelect";

export const CalendarConfigurator: React.FC = () => {
  const {
    podPackagesQuery: { data },
    getDisplayValue,
    state,
    dispatch,
    podPackageId,
    isDeveloperPortalVersion,
  } = useContext(PricingCalculatorContext);

  const podPackageOptions = React.useMemo(() => getAllOptionsFromPodPackages(data || []), [data]);

  useEffect(() => {
    const trimSize = podPackageOptions.trimSizes[0];
    const bindingType = podPackageOptions.bindingTypeOptions[0];
    const interiorInk = podPackageOptions.interiorInkOptions[0];
    const interiorPaper = podPackageOptions.interiorPaperOptions[0];
    const coverFinish = podPackageOptions.coverFinishOptions[0];

    dispatch({
      type: ConfiguratorActionType.RESET_CONFIGURATION,
      payload: {
        configuration: {
          trimSize,
          bindingType,
          interiorInk,
          interiorPaper,
          coverFinish,
        },
      },
    });
  }, [dispatch, podPackageOptions]);

  const {
    _textBindingTypeTitle,
    _textInteriorColor,
    _textPaperType,
    _textShippingAndHandlingEstimate,
    _textRevenueEstimates,
    _textAdvancedProductSpecifications,
  } = useCopy("PricingCalculator");

  const { bindingType, interiorInk, interiorPaper, coverFinish } = state;

  if (!bindingType || !interiorInk || !interiorPaper || !coverFinish) {
    return null;
  }

  return (
    <div data-testid="calendar-configurator">
      <CalendarSizeSelect />
      <div className={styles.configuratorLayoutRow}>
        <AttributeSelector
          title={_textBindingTypeTitle}
          activeValue={bindingType}
          values={[
            {
              image: imageUrlBuilder(
                `ProductOption/Calendar/lulu-calendar-binding-type-${bindingType}`
              ),
              label: getDisplayValue(bindingType) ?? "",
              value: bindingType,
            },
          ]}
          nonInteractive
          rowLayoutItem
          dataTestId="binding-type"
        />
        <AttributeSelector
          title={_textInteriorColor}
          activeValue={interiorInk}
          values={[
            {
              image: imageUrlBuilder(
                `ProductOption/Calendar/lulu-calendar-interior-color-${interiorInk}`
              ),
              label: getDisplayValue(interiorInk) ?? "",
              value: interiorInk,
            },
          ]}
          nonInteractive
          rowLayoutItem
          dataTestId="interior-ink"
        />
        <AttributeSelector
          title={_textPaperType}
          activeValue={interiorPaper}
          values={[
            {
              image: imageUrlBuilder(
                `ProductOption/Calendar/lulu-calendar-paper-type-${interiorPaper}`
              ),
              label: getDisplayValue(interiorPaper) ?? "",
              value: interiorPaper,
            },
          ]}
          nonInteractive
          rowLayoutItem
          dataTestId="interior-paper"
        />
      </div>
      <Accordion allowMultiSelect>
        <AccordionItem
          title={_textShippingAndHandlingEstimate}
          disabled={!podPackageId || !state.pageCount}
          dataTestId="shipping-calculator"
        >
          <ShippingCalculatorAdapter />
        </AccordionItem>
        {isDeveloperPortalVersion ? (
          <AccordionItem
            title={_textAdvancedProductSpecifications}
            disabled={!podPackageId || !state.pageCount}
            dataTestId="advanced-product-specifications"
          >
            <AdvancedProductSpecsTableAdapter />
          </AccordionItem>
        ) : (
          <AccordionItem
            title={_textRevenueEstimates}
            disabled={!podPackageId || !state.pageCount}
            dataTestId="revenue-calculator"
          >
            <RevenueCalculatorAdapter />
          </AccordionItem>
        )}
      </Accordion>
    </div>
  );
};
