import classNames from "classnames";
import React, { CSSProperties, Dispatch, SetStateAction, useEffect, useRef, useState } from "react";
import { useSwipeable } from "react-swipeable";

import { Colors } from "../../constants";
import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../hooks";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { ImageWithMetadata, ImageWithMetadataProps } from "../ImageWithMetadata/ImageWithMetadata";
import {
  ReferenceIconAndText,
  ReferenceIconAndTextProps,
} from "../ReferenceIconAndText/ReferenceIconAndText";
import { RichTextContainer, RichTextContainerProps } from "../RichTextContainer/RichTextContainer";
import { ColorSelectors } from "./ColorSelectors";
import { DesktopImageSelectors } from "./DesktopImageSelectors";
import styles from "./ImageSlider.module.scss";
import { MobileAndTabletImageSelectors } from "./MobileAndTabletImageSelectors";

export type ImageSliderOption = {
  header: RichTextContainerProps;
  description: RichTextContainerProps;
  color?: string;
  image: ImageWithMetadataProps;
  displayReferenceIcon: boolean;
};

export interface ImageSliderProps extends Partial<ReferenceIconAndTextProps> {
  richTextContainerRef?: RichTextContainerProps;
  variant: "color" | "image";
  imageAlignment?: "left" | "right";
  classes?: string;
  optionsTitle: string;
  optionsCollection: { items: ImageSliderOption[] };
  imagesContainerMinHeight?: number;
}

export interface OptionsViewProps {
  options: ImageSliderOption[];
  setActiveIndex: Dispatch<SetStateAction<number>>;
  activeIndex: number;
  handleNext?: () => void;
  handlePrevious?: () => void;
}

export const ImageSlider = ({
  classes,
  richTextContainerRef,
  referenceIcon,
  iconTextAlignment,
  referenceIconDescriptionText,
  imageAlignment = "left",
  optionsCollection: { items: options },
  imagesContainerMinHeight,
  variant,
  optionsTitle,
  ...props
}: ImageSliderProps) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const windowWidth = useWindowWidth();
  const [isDesktop, setIsDesktop] = useState<boolean>();
  const containerRef = useRef<HTMLDivElement>(null);
  const defaultContainerHeight = 490;
  const containerClientHeight = containerRef.current?.clientHeight ?? defaultContainerHeight;
  const [containerHeight, setContainerHeight] = useState(containerClientHeight);

  const handleNext = () =>
    activeIndex + 1 === options.length ? setActiveIndex(0) : setActiveIndex(activeIndex + 1);
  const handlePrevious = () =>
    activeIndex === 0 ? setActiveIndex(options.length - 1) : setActiveIndex(activeIndex - 1);

  const imageHandlers = useSwipeable({
    onSwipedLeft: () => handleNext(),
    onSwipedRight: () => handlePrevious(),
  });

  useEffect(() => {
    setIsDesktop(windowWidth >= MEDIA_BREAKPOINTS.L);
  }, [windowWidth]);

  useEffect(() => {
    setContainerHeight(containerClientHeight);
  }, [containerClientHeight]);

  return (
    <>
      {richTextContainerRef && <RichTextContainer {...richTextContainerRef} />}
      {referenceIcon && referenceIconDescriptionText && (
        <div className={classNames("max-width-container", styles.referenceIconContainer)}>
          <ReferenceIconAndText
            iconTextAlignment={iconTextAlignment}
            referenceIconDescriptionText={referenceIconDescriptionText}
            referenceIcon={referenceIcon}
          />
        </div>
      )}
      <div className={classes || "max-width-container"} ref={containerRef}>
        <div
          className={classNames(styles.container, {
            [styles.reverseRow]: imageAlignment === "right",
          })}
        >
          <div
            className={styles.imagesContainer}
            style={{
              minHeight:
                isDesktop && imagesContainerMinHeight ? `${imagesContainerMinHeight}px` : "auto",
            }}
          >
            <div
              className={styles.carousel}
              style={{ "--activeIndex": activeIndex } as CSSProperties}
              {...imageHandlers}
            >
              <div className={styles.inner}>
                <div>
                  {options.map((item, index) => (
                    <div
                      className={styles.carouselItem}
                      key={`image-${index}`}
                      data-testid="ImageSlider-carouselItem"
                      style={{
                        backgroundImage: isDesktop ? `url(${item.image.image?.url})` : undefined,
                        height: isDesktop ? `${containerHeight}px` : "100%",
                      }}
                    >
                      {!isDesktop && <ImageWithMetadata {...item.image} />}
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
          <div className={styles.infoContainer}>
            <div>
              <div className={styles.headerContainer}>
                <RichTextContainer {...options[activeIndex].header} />
                {options[activeIndex].displayReferenceIcon && (
                  <Icon iconName={Icons.DistributionYes} color={Colors.Green} size={24} />
                )}
              </div>
              <RichTextContainer {...options[activeIndex].description} />
            </div>
            <div className={styles.options}>
              {isDesktop && <div className={styles.optionsTitle}>{optionsTitle}</div>}
              {variant === "image" ? (
                isDesktop ? (
                  <DesktopImageSelectors
                    {...props}
                    options={options}
                    setActiveIndex={setActiveIndex}
                    activeIndex={activeIndex}
                  />
                ) : (
                  <MobileAndTabletImageSelectors
                    {...props}
                    options={options}
                    setActiveIndex={setActiveIndex}
                    activeIndex={activeIndex}
                    handlePrevious={handlePrevious}
                    handleNext={handleNext}
                  />
                )
              ) : (
                <ColorSelectors
                  {...props}
                  options={options}
                  activeIndex={activeIndex}
                  setActiveIndex={setActiveIndex}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
