import React, { useEffect, useMemo, useReducer } from "react";

import {
  ConfiguratorAction,
  ConfiguratorActionType,
  configuratorReducer,
  ConfiguratorState,
} from "./reducer";
import {
  ConfigurationOptions,
  Money,
  PodPackage,
  PricingCalculatorProps,
  ProductTypeEnum,
} from "./types";
import * as utils from "./utils";
import {
  getBindingTypeFromId,
  getCoverFinishFromId,
  getInteriorInkFromId,
  getInteriorPaperFromId,
  getTrimSizeFromId,
} from "./utils";

const initialState = {
  podPackages: [],
  availableOptions: {},
  bindingType: null,
  coverFinish: null,
  filteredOptions: {},
  interiorInk: null,
  interiorPaper: null,
  pageCount: null,
  podPackageId: null,
  price: "",
  priceHidden: true,
  trimSize: "",
};

type PricingCalculatorProviderProps = Pick<
  PricingCalculatorProps,
  | "availableProductTypes"
  | "podPackagesQuery"
  | "getDisplayValue"
  | "manufacturingCostQuery"
  | "getCountryName"
  | "shippingCountriesQuery"
  | "shippingMethodsQuery"
  | "totalPricesQuery"
  | "listPriceBoundsQuery"
  | "priceMapQuery"
  | "priceMapEbookQuery"
  | "podPackageDimensionsQuery"
  | "countryStatesQuery"
  | "onRequestTotalPrices"
  | "onRequestShippingMethods"
  | "onRequestPriceMap"
  | "onRequestPriceMapEbook"
  | "onRequestManufacturingCost"
  | "onRequestShippingCountries"
  | "onRequestListPriceBounds"
  | "onRequestPodPackageDimensions"
  | "onRequestCountryStates"
  | "baseCurrency"
  | "wizardUrl"
  | "locale"
  | "initialConfiguration"
  | "isDeveloperPortalVersion"
> & { children?: React.ReactNode };

interface PricingCalculatorContextType extends PricingCalculatorProviderProps {
  productType: ProductTypeEnum;
  setProductType: React.Dispatch<React.SetStateAction<ProductTypeEnum>>;
  podPackageId: string | null;
  quantity: number | null;
  trimSizes: string[];
  setQuantity: React.Dispatch<React.SetStateAction<number | null>>;
  shippingCost: Money | null;
  setShippingCost: React.Dispatch<React.SetStateAction<Money | null>>;
  initialConfiguration?: Partial<ConfiguratorState>;
  state: ConfiguratorState;
  dispatch: React.Dispatch<ConfiguratorAction>;
  availableOptions: ConfigurationOptions;
  filteredOptions: ConfigurationOptions;
}

export const PricingCalculatorContext = React.createContext<PricingCalculatorContextType>({
  productType: "PRINTED_BOOK",
  setProductType: () => {},
  podPackageId: null,
  quantity: null,
  setQuantity: () => {},
  shippingCost: null,
  setShippingCost: () => {},
  availableProductTypes: [],
  podPackagesQuery: { loading: false, data: [] },
  getDisplayValue: (rawString) => rawString,
  manufacturingCostQuery: { loading: false, data: null },
  locale: "en",
  state: initialState,
  dispatch: () => {},
  availableOptions: {},
  filteredOptions: {},
  getCountryName: () => "",
  trimSizes: [],
  wizardUrl: "https://www.lulu.com/account/wizard/draft/start",
  isDeveloperPortalVersion: false,
  shippingCountriesQuery: { loading: false, data: [] },
  shippingMethodsQuery: { loading: false, data: [] },
  totalPricesQuery: { loading: false, data: null },
  listPriceBoundsQuery: { loading: false, data: null },
  priceMapQuery: { loading: false, data: null },
  priceMapEbookQuery: { loading: false, data: null },
  podPackageDimensionsQuery: { loading: false, data: null },
  countryStatesQuery: { loading: false, data: null },
  onRequestTotalPrices: () => {},
  onRequestShippingMethods: () => {},
  onRequestPriceMap: () => {},
  onRequestPriceMapEbook: () => {},
  onRequestManufacturingCost: () => {},
  onRequestShippingCountries: () => {},
  onRequestListPriceBounds: () => {},
  onRequestPodPackageDimensions: () => {},
  onRequestCountryStates: () => {},
});

export const PricingCalculatorProvider: React.FC<PricingCalculatorProviderProps> = ({
  children,
  availableProductTypes,
  podPackagesQuery,
  getDisplayValue,
  manufacturingCostQuery,
  locale,
  initialConfiguration,
  getCountryName,
  shippingCountriesQuery,
  shippingMethodsQuery,
  totalPricesQuery,
  listPriceBoundsQuery,
  priceMapQuery,
  priceMapEbookQuery,
  podPackageDimensionsQuery,
  countryStatesQuery,
  baseCurrency,
  wizardUrl,
  isDeveloperPortalVersion,
  onRequestTotalPrices,
  onRequestShippingMethods,
  onRequestPriceMap,
  onRequestPriceMapEbook,
  onRequestManufacturingCost,
  onRequestShippingCountries,
  onRequestListPriceBounds,
  onRequestPodPackageDimensions,
  onRequestCountryStates,
}) => {
  const [productType, setProductType] = React.useState<ProductTypeEnum>("PRINTED_BOOK");
  const [quantity, setQuantity] = React.useState<number | null>(null);
  const [shippingCost, setShippingCost] = React.useState<Money | null>(null);
  const [state, dispatch] = useReducer(configuratorReducer, initialState);

  useEffect(() => {
    const podPackages = podPackagesQuery.data;
    dispatch({
      payload: { podPackages: podPackages ?? [] },
      type: ConfiguratorActionType.SET_POD_PACKAGES,
    });

    // special case: there is only one configuration (e.g. for Calendar)
    // in this case we can preselect everything
    if (podPackages?.length === 1) {
      const podId = podPackages[0].id ?? "";
      const trimSize = getTrimSizeFromId(podId);
      const bindingType = getBindingTypeFromId(podId);
      const interiorInk = getInteriorInkFromId(podId);
      const interiorPaper = getInteriorPaperFromId(podId);
      const coverFinish = getCoverFinishFromId(podId);

      dispatch({
        type: ConfiguratorActionType.RESET_CONFIGURATION,
        payload: {
          configuration: {
            trimSize,
            bindingType,
            interiorInk,
            interiorPaper,
            coverFinish,
          },
        },
      });
    }
  }, [podPackagesQuery.data]);

  // if we want a preselection (currently only used for dev purposes)
  useEffect(() => {
    dispatch({
      type: ConfiguratorActionType.RESET_CONFIGURATION,
      payload: { configuration: initialConfiguration ?? {} },
    });
  }, [initialConfiguration]);

  // here we compute values out of the reducer state (similar redux selectors)
  const podPackageId = utils.getSelectedPodPackageId(
    state.podPackages,
    state.trimSize,
    Number(state.pageCount),
    false,
    state.interiorInk,
    state.interiorPaper,
    state.bindingType,
    state.coverFinish
  );
  const availableOptions = utils.getAllOptionsFromPodPackages(state.podPackages);
  const filteredOptions = utils.getFilteredOptions(
    state.podPackages,
    state.trimSize,
    Number(state.pageCount),
    false,
    state.bindingType,
    state.interiorInk,
    state.interiorPaper
  );

  // Now we have to compare the former selected choices vs actually possible (enabled) options.
  // If a selected option isn't enabled we reset it to null.
  // Thus, we can keep any former choice if possible.
  const resetChoices = utils.getResetChoices(state, filteredOptions);

  if (Object.keys(resetChoices).length > 0 && productType !== "CALENDAR") {
    dispatch({
      type: ConfiguratorActionType.UPDATE_CONFIGURATION,
      payload: {
        options: resetChoices,
      },
    });
  }

  const trimSizes = useMemo(
    () =>
      Array.from(new Set(state.podPackages.map((pod: PodPackage) => pod.trimSize?.key))).filter(
        Boolean
      ) as string[],
    [state.podPackages]
  );

  // preselect first trim size if there is only one
  useEffect(() => {
    if (trimSizes.length === 1) {
      dispatch({
        type: ConfiguratorActionType.CHANGE_POD_OPTION,
        payload: {
          option: "trimSize",
          value: trimSizes[0],
        },
      });
    }
  }, [trimSizes]);

  // if we have a valid podPackageId and pageCount, we can calculate the price
  useEffect(() => {
    if (podPackageId && state.pageCount) {
      onRequestManufacturingCost(podPackageId, state.pageCount);
      if (isDeveloperPortalVersion) {
        onRequestPodPackageDimensions?.(podPackageId, state.pageCount);
      }
    }
  }, [
    podPackageId,
    state.pageCount,
    onRequestManufacturingCost,
    isDeveloperPortalVersion,
    onRequestPodPackageDimensions,
  ]);

  return (
    <PricingCalculatorContext.Provider
      value={{
        productType,
        setProductType,
        quantity,
        setQuantity,
        shippingCost,
        setShippingCost,
        availableProductTypes,
        podPackagesQuery,
        getDisplayValue,
        manufacturingCostQuery,
        getCountryName,
        shippingCountriesQuery,
        shippingMethodsQuery,
        totalPricesQuery,
        listPriceBoundsQuery,
        priceMapQuery,
        priceMapEbookQuery,
        podPackageDimensionsQuery,
        countryStatesQuery,
        onRequestTotalPrices,
        onRequestShippingMethods,
        onRequestPriceMap,
        onRequestPriceMapEbook,
        onRequestManufacturingCost,
        onRequestShippingCountries,
        onRequestListPriceBounds,
        onRequestPodPackageDimensions,
        onRequestCountryStates,
        baseCurrency,
        wizardUrl,
        isDeveloperPortalVersion,
        locale,
        initialConfiguration,
        state,
        dispatch,
        availableOptions,
        filteredOptions,
        podPackageId,
        trimSizes,
      }}
    >
      {children}
    </PricingCalculatorContext.Provider>
  );
};
