import classNames from "classnames";
import React, { useEffect, useState } from "react";

import { useCopy } from "../../extensions/CopyContext";
import { getRootUrl } from "../../utils";
import { ProductTypeEnum } from "../PricingCalculator/types";
import styles from "./BookTeaser.module.scss";

const checkImageIsPortraitOrSquare = (url: string): Promise<boolean> =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = ({ currentTarget }) => {
      resolve(
        currentTarget instanceof HTMLImageElement && currentTarget.height >= currentTarget.width
      );
    };
    img.onerror = reject;
    img.src = url;
  });

export type BookTeaserOrientationType = "landscape" | "portrait" | "square";

export interface BookTeaserProps {
  author?: string;
  dataTestId?: string;
  name?: string;
  orientation?: BookTeaserOrientationType;
  productUrl: string;
  thumbnailUrl: string;
  onImageLoaded?: () => void;
  type?: ProductTypeEnum;
}

export const BookTeaser: React.FC<BookTeaserProps> = ({
  author,
  dataTestId,
  thumbnailUrl,
  productUrl,
  orientation,
  name,
  onImageLoaded,
  type = "PRINTED_BOOK",
}) => {
  const { _textBy } = useCopy("BookTeaser");
  const [isPortraitOrSquareImage, setIsPortraitOrSquareImage] = useState<boolean | null>(null);
  const imageClasses = classNames(
    styles.productImage,
    { [styles[`${orientation}Image`]]: orientation },
    {
      [styles[`${isPortraitOrSquareImage ? "portrait" : "landscape"}Image`]]: !orientation,
    }
  );
  const wrapperClasses = classNames(
    styles.bookTeaser,
    {
      [styles[`${orientation}ThumbnailWrapper`]]: orientation,
    },
    {
      [styles[`${isPortraitOrSquareImage ? "portrait" : "landscape"}ThumbnailWrapper`]]:
        !orientation,
    }
  );

  useEffect(() => {
    const checkOrientation = async () => {
      setIsPortraitOrSquareImage(await checkImageIsPortraitOrSquare(thumbnailUrl));
    };
    if (thumbnailUrl) {
      checkOrientation();
    }
  }, [thumbnailUrl]);

  return (
    <div className={wrapperClasses} data-testid={dataTestId}>
      <a
        className={type !== "CALENDAR" ? styles.image : styles.calendarImage}
        href={getRootUrl(productUrl)}
        rel="noopener noreferrer"
      >
        <div>
          {type === "CALENDAR" && (
            <img
              src="https://assets.lulu.com/media/lulu-website/assets/calendar-overlay.png"
              alt="calendar overlay"
              className={styles.overlay}
            />
          )}
          <img
            className={imageClasses}
            src={thumbnailUrl}
            alt={name}
            onLoad={() => !!onImageLoaded && onImageLoaded()}
          />
        </div>
      </a>
      <a className={styles.title} href={getRootUrl(productUrl)} rel="noopener noreferrer">
        {name}
      </a>
      <div className={styles.author}>
        <span className="almost-black-text-60" style={{ opacity: !author ? 0 : 100 }}>
          {_textBy}{" "}
        </span>
        <span>{author}</span>
      </div>
    </div>
  );
};
