import classNames from "classnames";
import React from "react";

import styles from "./StepTitle.module.scss";

export interface StepTitleProps {
  dataTestId?: string;
  order?: number;
  title: string;
  description?: React.ReactNode;
  marginBottom?: boolean;
  fontSize?: "medium" | "large";
  vertical?: boolean;
  wrapperClassName?: string;
}

export const StepTitle: React.FunctionComponent<StepTitleProps> = ({
  dataTestId,
  order,
  title,
  description,
  marginBottom,
  fontSize = "medium",
  vertical,
  wrapperClassName,
}) => {
  const gridClass = classNames("grid-12", styles.grid);
  const titleClass = classNames(
    description ? "col-4" : "col-6",
    "col-m-big-12",
    order ? styles.title : styles.title_noOrder,
    {
      [styles.titleBigger]: fontSize === "large",
    }
  );
  const descriptionClass = classNames(
    "col-8",
    "col-m-big-12",
    order ? styles.description : styles.description_noOrder,
    {
      [styles.descriptionBigger]: fontSize === "large",
    }
  );
  const wrapperClass = classNames(wrapperClassName, { [styles.gutter]: marginBottom });

  return (
    <div data-testid={dataTestId} className={wrapperClass}>
      {order && (
        <div className={styles.orderBox}>
          <div className={styles.orderBar}></div>
          <div className={styles.orderNumber}>{order}</div>
          <div className={styles.orderBar}></div>
        </div>
      )}
      <div className={vertical ? styles.vertical : gridClass}>
        <h3 className={titleClass}>{title}</h3>
        {description && <div className={descriptionClass}>{description}</div>}
      </div>
    </div>
  );
};
