import React from "react";

import { useCopy } from "../../../../extensions";
import { AttributeOptionValue, AttributeSelector } from "../AttributeSelector/AttributeSelector";
import { SectionHeader } from "../SectionHeader/SectionHeader";

interface Props {
  onChange: (option: string) => void;
  activeBindingType: string | null;
  values: AttributeOptionValue[];
}

export const BindingType: React.FC<Props> = ({ onChange, activeBindingType, values }) => {
  const { _textBindingTypeTitle, _textPaperbackOptions, _textHardcoverOptions } =
    useCopy("PricingCalculator");

  const { paperbackOptions, hardcoverOptions } = values.reduce<{
    paperbackOptions: AttributeOptionValue[];
    hardcoverOptions: AttributeOptionValue[];
  }>(
    (acc, item) => {
      const key =
        item.value === "CW" || item.value === "LW" ? "hardcoverOptions" : "paperbackOptions";
      acc[key].push(item);
      // sort hardcover options deliberately: PB, CO, SS
      acc[key].sort((a, b) => {
        if (a.value === "PB") return -1;
        if (a.value === "CO" && b.value === "SS") return -1;
        return 1;
      });
      return acc;
    },
    { paperbackOptions: [], hardcoverOptions: [] }
  );

  return (
    <div data-testid="binding-type">
      <SectionHeader title={_textBindingTypeTitle} />
      {paperbackOptions.length > 0 ? (
        <AttributeSelector
          title={_textPaperbackOptions}
          onChange={onChange}
          activeValue={activeBindingType}
          values={paperbackOptions}
          isSubSection
          dataTestId="binding-paperback"
        />
      ) : null}
      {hardcoverOptions.length > 0 ? (
        <AttributeSelector
          title={_textHardcoverOptions}
          onChange={onChange}
          activeValue={activeBindingType}
          values={hardcoverOptions}
          isSubSection
          dataTestId="binding-hardcover"
        />
      ) : null}
    </div>
  );
};
