import classNames from "classnames";
import React, { InputHTMLAttributes, useState } from "react";

import { useCopy } from "../../extensions";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { Error } from "../Error/Error";
import styles from "./Input.module.scss";

export interface FloatingLabelInputProps extends InputHTMLAttributes<HTMLInputElement> {
  label: string;
  error?: string;
  onChange: (evt: React.ChangeEvent<HTMLInputElement>) => void;
  onBlur?: (evt: React.ChangeEvent<HTMLInputElement>) => void;
  wrapperClassName?: string;
  alternateDisabledStyle?: boolean;
  hideDisabledLabel?: boolean;
}

export const FloatingLabelInput: React.FC<FloatingLabelInputProps> = ({
  label,
  wrapperClassName,
  error,
  alternateDisabledStyle,
  hideDisabledLabel,
  ...props
}) => {
  const [showPassword, setShowPassword] = useState(false);
  const { _textShow, _textHide } = useCopy("Input");
  const isDisabled = !!props.disabled;
  const inputId = props.id || props.name;

  return (
    <>
      <div
        className={classNames(styles.wrapper, wrapperClassName, {
          [styles.withError]: !!error,
          [styles.isDisabled]: isDisabled && alternateDisabledStyle,
        })}
      >
        {/* Space in placeholder is required, label styles check the status of the placeholder  */}
        <input
          {...props}
          type={showPassword ? "text" : props.type}
          placeholder=" "
          disabled={isDisabled}
          id={inputId}
        />
        <label
          className={classNames(styles.label, { [styles.hideWhenDisabled]: hideDisabledLabel })}
          htmlFor={inputId}
        >
          {label}
        </label>
        {props.type === "password" ? (
          <NakedButton
            className={styles.toggleShowPassword}
            onClick={() => setShowPassword((prevState) => !prevState)}
          >
            {showPassword ? _textHide : _textShow}
          </NakedButton>
        ) : null}
      </div>
      <Error error={error} />
    </>
  );
};
