import classNames from "classnames";
import React, { useEffect, useState } from "react";

import { Colors } from "../../constants";
import { useCopy } from "../../extensions";
import { useFileUpload } from "../../hooks/useFileUpload";
import { Icons } from "../../icon-types";
import { Button } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import { CornerAction } from "./CornerAction";
import styles from "./ImageUploadBox.module.scss";

const checkImageSize = (url: string): Promise<boolean> =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = ({ currentTarget }) => {
      resolve(
        currentTarget instanceof HTMLImageElement && currentTarget.height >= currentTarget.width
      );
    };
    img.onerror = reject;
    img.src = url;
  });

// JPGs and JPEGs are the same file format (.jpg), but returned file format is `image/jpeg`
const convertJpgToJpeg = (formats?: string[]) => {
  if (!formats) return null;

  // doesn't have jpg at all
  if (!formats.includes("image/jpg")) return formats;
  // does have both jpg and jpeg
  if (formats.includes("image/jpeg")) return formats.filter((item) => item !== "image/jpg");
  // has jpg without jpeg
  return formats.map((item) => (item === "image/jpg" ? "image/jpeg" : item));
};

export interface ImageUploadBoxProps {
  dataTestId?: string;
  deleting?: boolean;
  multiple?: boolean;
  uploading?: boolean;
  fileUrl?: string;
  uploadError?: boolean;
  errorMessage?: string;
  onDrop?: (files: File[]) => void;
  fileValidation?: { maxSizeInMB?: number; formats?: string[] };
  deleteImage?: () => void;
  hasDragIcon?: boolean;
  width?: number | string;
  height?: number | string;
  displayCornerAction?: boolean;
  disable?: {
    isDisabled: boolean;
    disableCopy: string;
  };
  select?: {
    onSelect: () => void;
    isSelected: boolean;
  };
  cta?: { action: () => void; title: string };
  isUploaded?: boolean;
}

export const ImageUploadBox: React.FC<ImageUploadBoxProps> = ({
  dataTestId,
  deleting,
  onDrop,
  uploading,
  fileUrl,
  uploadError,
  deleteImage,
  multiple = false,
  hasDragIcon = true,
  width,
  height = 190,
  disable,
  displayCornerAction = true,
  select,
  errorMessage,
  cta,
  isUploaded,
  fileValidation,
}) => {
  const {
    _image,
    _textYour,
    _textIs,
    _textUploading,
    _textUploadYour,
    _textDragAndDrop,
    _textReplaceYour,
    _textSizeError,
    _textFormatError,
    _textOr,
  } = useCopy("FileUpload");
  const [error, setLocalError] = useState(false);
  const [localErrorMessage, setLocalErrorMessage] = useState<string>();
  const [isPortraitImage, setIsPortraitImage] = useState<boolean | null>(null);
  const displayedErrorMessage = errorMessage || localErrorMessage;
  const boxClasses = classNames({
    [styles.emptyBox]: !fileUrl,
    [styles.withError]: uploadError || error || !!displayedErrorMessage,
  });

  useEffect(() => {
    const checkOrientation = async () => {
      try {
        const res = await checkImageSize(fileUrl!);
        setIsPortraitImage(res);
      } catch (e) {
        setLocalError(!!e);
      }
    };
    if (fileUrl) {
      checkOrientation();
    }
  }, [fileUrl]);

  const handleOnDrop = (files: File[]) => {
    setLocalErrorMessage(undefined);
    if (onDrop && files.length) {
      onDrop(files);
    }
  };

  const defaultFileTypes = ["image/jpeg", "image/png", "image/webp"];
  const supportedFileTypes = convertJpgToJpeg(fileValidation?.formats) || defaultFileTypes;

  const { getRootProps, getInputProps } = useFileUpload({
    acceptedFileTypes: supportedFileTypes,
    multiple,
    onDrop: handleOnDrop,
    maxSize: fileValidation?.maxSizeInMB ? fileValidation.maxSizeInMB * 1000000 : undefined,
    onDropRejected: (fileRejections) => {
      const errorCode = fileRejections[0]?.errors[0]?.code;
      switch (errorCode) {
        case "file-too-large":
          if (fileValidation?.maxSizeInMB) {
            setLocalErrorMessage(`${_textSizeError} ${fileValidation.maxSizeInMB} MB.`);
          }
          return;
        case "file-invalid-type":
          const formattedFileTypesString = supportedFileTypes
            .map((type) => {
              const extension = type.split("/")[1].toUpperCase();

              return extension === "JPEG" ? "JPG" : extension;
            })
            .reduce((acc, type, index, array) =>
              index === array.length - 1 ? `${acc} ${_textOr} ${type}` : `${acc}, ${type}`
            );
          setLocalErrorMessage(
            `${_textFormatError} ${formattedFileTypesString} ${_image.toLowerCase()}.`
          );
          return;
      }
    },
  });

  const loader = (
    <div className={styles.loadingBox}>
      <div className={styles.boxContentWrapper}>
        <div className={styles.boxContent}>
          <span className={styles.uploadingText}>
            {_textYour} {_image} {_textIs} {_textUploading}
          </span>
        </div>
        <div className={styles.loadingBar}>
          <span className={styles.loadingBarContent} />
        </div>
      </div>
    </div>
  );

  const uploadActionLabel = `${isUploaded ? _textReplaceYour : _textUploadYour} ${_image}`;

  const emptyBox = (
    <div className={boxClasses}>
      {(uploadError || error || !!displayedErrorMessage) && (
        <Icon iconName={Icons.NotificationError} className={styles.errorInfo} />
      )}
      <div className={styles.boxContentWrapper}>
        <div className={styles.boxContent}>
          <Icon color={Colors.Indigo} iconName={Icons.DataUploadLarge} />
          <div>
            <span className={styles.textIndigoUnderline}>{uploadActionLabel}</span>
            <br />
            <span className={styles.textDragAndDrop}>{_textDragAndDrop}</span>
          </div>
        </div>
      </div>
    </div>
  );

  const uploadedFileBox = (
    <div className={classNames(styles.uploadedBox, { [styles.draggable]: hasDragIcon })}>
      {hasDragIcon && (
        <div className={styles.dragIcon}>
          <Icon iconName={Icons.DataMove} />
        </div>
      )}
      <div
        className={classNames(styles.image, {
          [styles.imageWithoutCornerAction]: !displayCornerAction,
        })}
        style={{
          backgroundImage: `url(${fileUrl})`,
          backgroundSize: isPortraitImage ? "auto 100%" : "cover",
        }}
      />
      {displayCornerAction && (
        <CornerAction
          dataTestId={dataTestId}
          select={select}
          deleteImage={deleteImage}
          deleting={deleting}
        />
      )}
      {cta && (
        <div className={styles.cta}>
          <Button
            dataTestId={!!dataTestId ? `${dataTestId}-cta` : undefined}
            hierarchy="tertiary-white"
            buttonSize="extra-small"
            className={styles.ctaButton}
            onClick={cta.action}
          >
            <span className={styles.ctaTitle}>{cta.title}</span>
          </Button>
        </div>
      )}
    </div>
  );

  return (
    <div data-testid={dataTestId}>
      <div
        className={disable?.isDisabled ? styles.wrapperDisabled : styles.wrapper}
        style={{ width, height }}
      >
        {disable?.isDisabled ? (
          <>
            <Icon iconName={Icons.ImagePlaceholder} color={Colors.PaleGray5} size={48} />
            <span className={styles.disableCopy}>{disable.disableCopy}</span>
          </>
        ) : uploading ? (
          loader
        ) : fileUrl ? (
          uploadedFileBox
        ) : (
          <div className={styles.dropzone} {...getRootProps()}>
            <input {...getInputProps()} aria-label={uploadActionLabel} />
            {emptyBox}
          </div>
        )}
      </div>
      {displayedErrorMessage && (
        <span className={styles.errorMessage}>{displayedErrorMessage}</span>
      )}
    </div>
  );
};
