import classNames from "classnames";
import React, { Children, ReactElement, useEffect, useState } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { Icon } from "../Icon/Icon";
import styles from "./Accordion.module.scss";

export interface AccordionProps {
  dataTestId?: string;
  items?: ReactElement<AccordionItemProps>[] | ReactElement<AccordionItemProps>;
  allowMultiSelect?: boolean;
  activeIndices?: number[] | null;
  unmountCollapsedItems?: boolean;
  children?: React.ReactNode;
}

export interface AccordionItemProps {
  dataTestId?: string;
  title: React.ReactNode;
  disabled?: boolean;
  children?: React.ReactNode;
}

interface ItemWrapperProps extends AccordionItemProps {
  isActive: boolean;
  onSetActiveIndex: () => void;
  unmountCollapsed: boolean;
}

export const Accordion: React.FC<AccordionProps> = ({
  dataTestId,
  children,
  allowMultiSelect = false,
  activeIndices,
  unmountCollapsedItems = false,
}) => {
  const [selectedIndices, setSelectedIndices] = useState<Set<number>>(
    Array.isArray(activeIndices) ? new Set(activeIndices) : new Set()
  );

  useEffect(() => {
    setSelectedIndices(Array.isArray(activeIndices) ? new Set(activeIndices) : new Set());
  }, [activeIndices]);

  const handleSetActiveIndex = (index: number) => {
    // create shallow copy of existing index
    let nextIndices = new Set(selectedIndices);

    if (selectedIndices.has(index)) {
      nextIndices.delete(index);
      setSelectedIndices(nextIndices);
      return;
    }
    if (!allowMultiSelect) {
      nextIndices = new Set();
    }
    nextIndices.add(index);
    setSelectedIndices(nextIndices);
  };

  return (
    <div data-testid={dataTestId}>
      {Children.map(children, (child, index) => {
        // as children are opaque TypeScript can't check the children type
        // so in order to prevent not allowed children, we check at runtime
        if (React.isValidElement(child) && child.type !== AccordionItem) {
          throw new Error("Only AccordionItem components are allowed as Accordion children");
        }
        // as we can be sure now this is a valid ReactElement we can assert it to make prop access easier
        const childComp = child as ReactElement;

        return (
          <ItemWrapper
            title={childComp.props.title ?? ""}
            isActive={selectedIndices.has(index)}
            onSetActiveIndex={() => handleSetActiveIndex(index)}
            disabled={childComp.props.disabled}
            unmountCollapsed={unmountCollapsedItems}
            dataTestId={`${childComp.props.dataTestId}-wrapper`}
          >
            {childComp}
          </ItemWrapper>
        );
      })}
    </div>
  );
};

const ItemWrapper: React.FC<ItemWrapperProps> = ({
  children,
  title,
  isActive,
  onSetActiveIndex,
  disabled,
  unmountCollapsed,
  dataTestId,
}) => {
  return (
    <div
      className={classNames(styles.itemWrapper, {
        [styles.itemWrapperActive]: isActive,
        [styles.itemWrapperDisabled]: disabled,
      })}
      data-testid={dataTestId}
    >
      <NakedButton
        disabled={disabled}
        className={styles.itemTitle}
        onClick={() => !disabled && onSetActiveIndex()}
      >
        {title}
        <Icon
          iconName={isActive ? Icons.ArrowUpMedium : Icons.ArrowDownMedium}
          color={Colors.Indigo}
        />
      </NakedButton>
      {unmountCollapsed ? (
        isActive && <div>{children}</div>
      ) : (
        <div style={{ display: isActive ? "initial" : "none" }}>{children}</div>
      )}
    </div>
  );
};

export const AccordionItem: React.FC<AccordionItemProps> = ({ children, dataTestId }) => {
  return <div data-testid={`accordion-item-${dataTestId}`}>{children}</div>;
};
