import classNames from "classnames";
import React, { ChangeEvent, useEffect, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../extensions";
import { InputDropdown } from "../InputDropdown/InputDropdown";
import { MoneyDisplay } from "../MoneyDisplay/MoneyDisplay";
import { NumericInput } from "../NumericInput/NumericInput";
import {
  DistributionChannelsEnum,
  EbookDistributionChannelsEnum,
  Money,
  Pricing,
  ProductTypeEnum,
  QueryLike,
  RevenueEntry,
} from "../PricingCalculator/types";
import { RevenueTable } from "../RevenueCalculator/RevenueTable/RevenueTable";
import styles from "./EbookRevenueCalculator.module.scss";
import { ExpandToggle } from "./ExpandToggle/ExpandToggle";
import { freeRevenueRows } from "./freeRevenueRows";

export interface EbookRevenueCalculatorProps {
  dataTestId?: string;
  baseCurrency: string;
  locale: string;
  listPriceBoundsQuery: QueryLike<{
    minimumPrices: Money[];
    maximumPrices: Money[];
  } | null>;
  priceMapEbookQuery: QueryLike<Pricing | null>;
  onRequestListPriceBounds: (
    podPackageId: string,
    pageCount: number,
    projectType: ProductTypeEnum
  ) => void;
  onRequestPriceMapEbook: (listPrice: Money, channels: EbookDistributionChannelsEnum[]) => void;
}

const FALLBACK_REVENUE_DATA: RevenueEntry[] = [
  { channel: "KINDLE", data: [] },
  { channel: "IBOOKSTORE", data: [] },
  { channel: "NOOK", data: [] },
  { channel: "KOBO", data: [] },
  { channel: "OTHERS", data: [] },
];

const CHANNEL_ORDER = FALLBACK_REVENUE_DATA.map((entry) => entry.channel);

const LIST_PRICE = "list-price";
const FREE = "free";

type PricingMode = typeof LIST_PRICE | typeof FREE;

const extractRevenueData = (
  localListPrice: string,
  error: string | undefined,
  priceMapEbookQuery: QueryLike<Pricing | null>
) => {
  let revenueData;
  let luluBookstoreRevenueData;
  let disabledDistroContainer = true;

  if (localListPrice && !error && priceMapEbookQuery.data?.revenue) {
    revenueData = [...priceMapEbookQuery.data.revenue].sort((a, b) => {
      const indexA = CHANNEL_ORDER.indexOf(a.channel);
      const indexB = CHANNEL_ORDER.indexOf(b.channel);
      if (indexA < indexB) {
        return -1;
      } else if (indexA > indexB) {
        return 1;
      }

      return 0;
    });
    // extract lulu bookstore data as we display it separately
    luluBookstoreRevenueData = revenueData.shift();
    disabledDistroContainer = false;
  }
  return { revenueData, luluBookstoreRevenueData, disabledDistroContainer };
};

export const EbookRevenueCalculator: React.FC<EbookRevenueCalculatorProps> = ({
  locale,
  dataTestId,
  baseCurrency,
  onRequestListPriceBounds,
  onRequestPriceMapEbook,
  listPriceBoundsQuery,
  priceMapEbookQuery,
}) => {
  const [localListPrice, setLocalListPrice] = useState<string>("");
  const [error, setError] = useState<string>();
  const [expanded, setExpanded] = useState(false);
  const [mode, setMode] = useState<PricingMode>(LIST_PRICE);

  const {
    _textMinimumListPrice,
    _textYourListPrice,
    _textEstimatedRevenue,
    _textLuluBookstore,
    _textGlobalDistribution,
    _textAmazonKindle,
    _textAppleBooks,
    _textBarnesNobleNook,
    _textKobo,
    _textOtherChannels,
    _textExpandAll,
    _textCollapseAll,
    _textListPrice,
    _textOfferForFree,
    _textEbookRevenueIntro,
    _textSetPriceBy,
    _textEbookRevenueTitle,
  } = useCopy("RevenueCalculator");

  const { _textPriceTooLow, _textPriceTooHigh } = useCopy("ListPriceForm");

  const getLabelForChannel = (channel: DistributionChannelsEnum) => {
    switch (channel) {
      case "LULU":
        return _textLuluBookstore;
      case "KINDLE":
        return _textAmazonKindle;
      case "IBOOKSTORE":
        return _textAppleBooks;
      case "NOOK":
        return _textBarnesNobleNook;
      case "KOBO":
        return _textKobo;
      case "OTHERS":
        return _textOtherChannels;
      default:
        return "";
    }
  };

  const loading = priceMapEbookQuery.loading;

  const isFreeMode = mode === FREE;

  const minimumListPrice = !isFreeMode
    ? listPriceBoundsQuery.data?.minimumPrices.find((p) => p.currency === baseCurrency)
    : { amount: 0, currency: baseCurrency };

  const maximumListPrice = !isFreeMode
    ? listPriceBoundsQuery.data?.maximumPrices.find((p) => p.currency === baseCurrency)
    : { amount: 0, currency: baseCurrency };

  useEffect(() => {
    onRequestListPriceBounds("EBOOK_PLACEHOLDER", 0, "EBOOK");
  }, [onRequestListPriceBounds]);

  const calculatePriceMap = useDebouncedCallback((newListPrice: number) => {
    if (!minimumListPrice || !maximumListPrice) return;

    if (newListPrice < minimumListPrice.amount) {
      setError(_textPriceTooLow);
      return;
    }

    if (newListPrice > maximumListPrice.amount) {
      setError(`${_textPriceTooHigh}. Max: ${maximumListPrice.amount}`);
      return;
    }

    setError(undefined);

    onRequestPriceMapEbook({ amount: newListPrice, currency: baseCurrency }, [
      "LULU",
      "GLOBALREACH",
    ]);
  }, 500);

  const handleListPriceInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    const strValue = e.target.value;
    setLocalListPrice(strValue);
    calculatePriceMap(parseFloat(strValue));
  };

  if (!minimumListPrice) {
    return null;
  }

  const { revenueData, luluBookstoreRevenueData, disabledDistroContainer } = extractRevenueData(
    localListPrice,
    error,
    priceMapEbookQuery
  );

  return (
    <div data-testid={dataTestId}>
      <div className={styles.title}>{_textEbookRevenueTitle}</div>
      <div className={styles.contentContainer}>
        <p>{_textEbookRevenueIntro}</p>
        <InputDropdown
          wrapperClassName={styles.inputDropdown}
          options={[
            { value: LIST_PRICE, label: _textListPrice },
            { value: FREE, label: _textOfferForFree },
          ]}
          value={mode}
          label={_textSetPriceBy}
          onChange={(value) => {
            setMode(value as PricingMode);
          }}
          name="mode"
        />
        <p className={classNames(styles.text, styles.minimumListPrice)}>
          {`${_textMinimumListPrice} `}
          <MoneyDisplay money={minimumListPrice} locale={locale} className={styles.price} />
        </p>
        <NumericInput
          id="listPrice"
          isMoney
          maxLength={12}
          currency={baseCurrency}
          label={_textYourListPrice}
          onChange={handleListPriceInputChange}
          wrapperClassName={styles.listPriceInput}
          error={error}
          disabled={isFreeMode}
          value={isFreeMode ? 0 : localListPrice}
          alternateDisabledStyle
          hideDisabledLabel
        />
        <div className={styles.revenueTableWrapper}>
          <RevenueTable
            revenueEntryRows={isFreeMode ? freeRevenueRows : luluBookstoreRevenueData?.data ?? []}
            summary={_textEstimatedRevenue}
            baseCurrency={baseCurrency}
            locale={locale}
            loading={loading}
            title={_textLuluBookstore}
            conversion
            showZeroValueRows={isFreeMode}
          />
        </div>

        {isFreeMode ? (
          <RevenueTable
            revenueEntryRows={freeRevenueRows}
            summary={_textEstimatedRevenue}
            baseCurrency={baseCurrency}
            locale={locale}
            loading={loading}
            title={_textGlobalDistribution}
          />
        ) : (
          <div
            className={classNames(styles.globalDistroContainer, {
              [styles.disabled]: disabledDistroContainer && !isFreeMode,
            })}
          >
            <h4 className={styles.globalDistroTitle}>{_textGlobalDistribution}</h4>
            <div className={styles.toggleContainer}>
              <ExpandToggle
                onClick={() => {
                  setExpanded((prevState) => !prevState);
                }}
                label={expanded ? _textCollapseAll : _textExpandAll}
                expanded={expanded}
              />
            </div>
            {(revenueData || FALLBACK_REVENUE_DATA).map((entry) => (
              <div key={entry.channel}>
                <RevenueTable
                  revenueEntryRows={entry.data ?? []}
                  baseCurrency={baseCurrency}
                  summary={_textEstimatedRevenue}
                  locale={locale}
                  loading={priceMapEbookQuery.loading}
                  title={entry.channel ? getLabelForChannel(entry.channel) : "Channel"}
                  theme="naked"
                  summaryOnly={!expanded}
                />
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};
