/**
 * Analyzes the given host and returns the appropriate environment host portion.
 *
 * Examples:
 * www.test.lulu.com -> test.lulu.com
 * www.sandbox.lulu.com -> sandbox.lulu.com
 * www.lulu.be -> lulu.be
 * website.lulu.com -> lulu.com
 * www.lulu.com -> lulu.com
 * dashboard.test.lulu.com -> test.lulu.com
 *
 * If no match is found, returns the original host.
 */
export function normalizeLuluRootHost(host: string): string {
  // Try to match <any>.(test|sandbox).lulu.com or www.(test|sandbox).lulu.com
  const envMatch = host.match(/(?:\w+\.)?(test|sandbox)\.lulu\.com$/);
  if (envMatch) {
    return `${envMatch[1]}.lulu.com`;
  }

  // Match any lulu.be subdomain or lulu.be itself (strip subdomain)
  const luluBeMatch = host.match(/(?:\w+\.)?lulu\.be$/);
  if (luluBeMatch) {
    return "lulu.be";
  }

  // Match lulu.com with any subdomain except test/sandbox
  const luluComMatch = host.match(/(?:\w+\.)?lulu\.com$/);
  if (luluComMatch) {
    return "lulu.com";
  }

  // Not a recognized host, return as is.
  return host;
}
