import { useCopy } from "../extensions";
import { Icons } from "../icon-types";
import { Discount, Money } from "../types";
import { StatusInfoProps } from "../ui-components";

export const getOrderNumber = (
  id: string | null | undefined,
  referenceNo: string | null | undefined
) => {
  return referenceNo || id;
};

export const getTotalProductPriceAfterDiscounts = (
  totalAmount: Money | undefined | null,
  discounts: Discount[]
) => {
  const totalDiscounts = discounts.reduce((prev, d) => prev + (d.discount?.amount || 0), 0);
  const total = (totalAmount?.amount || 0) - totalDiscounts;

  return total < 0 ? 0 : total;
};

export const useStatusIcons = (): Record<
  string,
  {
    icon: Icons;
    statusLabel: string;
    statusLevel: StatusInfoProps["statusLevel"];
  }
> => {
  const COPY = useCopy("StatusIcons");

  return {
    PROCESSING: {
      icon: Icons.StatusProcessing,
      statusLabel: COPY._textProcessingOrder,
      statusLevel: "neutral",
    },
    IN_PRODUCTION: {
      icon: Icons.StatusInProduction,
      statusLabel: COPY._textInProduction,
      statusLevel: "neutral",
    },
    CANCELED: {
      icon: Icons.StatusCanceled,
      statusLabel: COPY._textCanceled,
      statusLevel: "neutral",
    },
    COMPLETE: {
      icon: Icons.ShippingShipped,
      statusLabel: COPY._textOrderComplete,
      statusLevel: "neutral",
    },
    PARTIALLY_SHIPPED: {
      icon: Icons.ShippingPartiallyShipped,
      statusLabel: COPY._textPartiallyShipped,
      statusLevel: "neutral",
    },
    ERROR: {
      icon: Icons.NotificationError,
      statusLabel: COPY._textError,
      statusLevel: "error",
    },
    UNPAID: {
      icon: Icons.NotificationWarning,
      statusLabel: COPY._textUnpaid,
      statusLevel: "warning",
    },
  };
};
