import React from "react";

import { mapContentfulColorToStyleProp } from "../../utils/colors";
import { Column } from "../Column/Column";
import { ElementCardProps } from "../ElementCard/ElementCard";
import { Grid } from "../Grid/Grid";
import {
  ReferenceIconAndText,
  ReferenceIconAndTextProps,
} from "../ReferenceIconAndText/ReferenceIconAndText";
import { RichTextContainer, RichTextContainerProps } from "../RichTextContainer/RichTextContainer";

export interface ElementCardCollectionProps extends Partial<ReferenceIconAndTextProps> {
  elementCardsCollection: {
    items: ElementCardProps[];
  };
  richTextContainerRef?: RichTextContainerProps;
  backgroundColor?: string;
  paddingClasses?: string[];
  columnClasses?: string;
  idForQuickLink?: string;
}

export const ElementCardCollection: React.FC<ElementCardCollectionProps> = ({
  elementCardsCollection: { items },
  backgroundColor,
  paddingClasses,
  richTextContainerRef,
  referenceIcon,
  iconTextAlignment,
  referenceIconDescriptionText,
  columnClasses,
  idForQuickLink,
}) => (
  <div
    style={
      backgroundColor ? { background: mapContentfulColorToStyleProp(backgroundColor) } : undefined
    }
    className={paddingClasses?.join(" ")}
    id={idForQuickLink}
  >
    {richTextContainerRef && <RichTextContainer {...richTextContainerRef} />}
    <Grid
      dataTestId={"element-card-collection"}
      columnsCollection={{
        items: [
          ...items.map((item) => ({
            classes: columnClasses ?? "col-3 col-xl-4 col-m-big-6 col-m-12",
            isFullHeight: true,
            childrenCollection: {
              items: [{ ...item, __typename: "ElementCard" }],
            },
          })),
        ],
      }}
      gridClassNames="grid-12 max-width-container res-top-padding-8 res-bottom-padding-48"
    >
      <Column className="col-12">
        {referenceIcon && referenceIconDescriptionText && (
          <ReferenceIconAndText
            iconTextAlignment={iconTextAlignment}
            referenceIconDescriptionText={referenceIconDescriptionText}
            referenceIcon={referenceIcon}
          />
        )}
      </Column>
    </Grid>
  </div>
);
