import React from "react";

import { Icons } from "../../../icon-types";
import { Icon } from "../../Icon/Icon";
import styles from "./ExpandToggle.module.scss";
interface Props {
  onClick: (event: React.MouseEvent) => void;
  label: string;
  expanded: boolean;
}

export const ExpandToggle: React.FC<Props> = ({ onClick, label, expanded = false }) => {
  return (
    <div className={styles.toggle} onClick={onClick} role="presentation">
      {label}
      <Icon iconName={expanded ? Icons.ArrowUpMedium : Icons.ArrowDownMedium} />
    </div>
  );
};
