import classNames from "classnames";
import React from "react";
import { Link } from "react-router-dom";

import { getRootUrl, isNextJs } from "../../utils";
import { ButtonProps } from "../Button/Button";
import styles from "../Button/Button.module.scss";
import { LinkProps } from "../Link/Link";

export interface ButtonLinkProps
  extends Pick<
    ButtonProps,
    "block" | "buttonSize" | "hierarchy" | "responsive" | "disabled" | "target"
  > {
  className?: string;
  dataTestId?: string;
  url: string;
  children?: React.ReactNode;
}

export type ContentfulButtonLinkProps = Pick<ButtonProps, "buttonSize" | "hierarchy"> & {
  buttonAlignment?: "left" | "center" | "right" | "bottom-left" | "bottom-center" | "bottom-right";
  buttonText?: string;
  linkRef: LinkProps;
  linkedButtonClassNames?: string;
};

export const ButtonLink: React.FC<ButtonLinkProps> = ({
  block = false,
  buttonSize = "medium",
  children,
  className,
  hierarchy = "secondary",
  dataTestId,
  responsive = true,
  disabled = false,
  url,
  target,
}) => {
  const buttonClass = classNames(
    styles.button,
    styles[hierarchy],
    styles[buttonSize],
    {
      [styles.fullWidth]: block,
      [styles.responsive]: responsive,
    },
    className
  );
  const external = /(http(s?)):\/\//i.test(url);

  if (isNextJs) {
    return (
      <a
        aria-disabled={disabled}
        className={buttonClass}
        data-testid={dataTestId}
        href={external ? url : getRootUrl(url)}
        rel="noopener noreferrer"
        style={{ boxSizing: block ? "border-box" : undefined }}
        target={target || external ? "_blank" : "_self"}
      >
        {children}
      </a>
    );
  }

  return external ? (
    <a
      aria-disabled={disabled}
      className={buttonClass}
      data-testid={dataTestId}
      href={url}
      rel="noopener noreferrer"
      style={{ boxSizing: block ? "border-box" : undefined }}
      target={target ?? "_blank"}
    >
      {children}
    </a>
  ) : (
    <Link
      aria-disabled={disabled}
      to={getRootUrl(url)}
      className={buttonClass}
      data-testid={dataTestId}
      style={{ boxSizing: block ? "border-box" : undefined }}
      target={target ?? "_self"}
    >
      {children}
    </Link>
  );
};

// Contentful equivalent of  ButtonLink
export const LinkedButton: React.FC<ContentfulButtonLinkProps & { dataTestId?: string }> = ({
  buttonAlignment,
  buttonSize,
  buttonText,
  hierarchy,
  linkedButtonClassNames,
  linkRef,
  dataTestId,
}) => (
  <div className={classNames(linkedButtonClassNames, styles[`align_${buttonAlignment}`])}>
    <ButtonLink
      buttonSize={buttonSize}
      hierarchy={hierarchy}
      url={linkRef.page?.url || linkRef.url || ""}
      dataTestId={dataTestId}
      target={linkRef.newTab ? "_blank" : "_self"}
    >
      {buttonText || linkRef.name}
    </ButtonLink>
  </div>
);
