import classNames from "classnames";
import React from "react";
import ReactTooltip, { TooltipProps as ReactTooltipProps } from "react-tooltip";

import { Colors } from "../../constants";
import styles from "./Tooltip.module.scss";

type TooltipTheme = "discount" | "success" | "warning" | "error" | "neutral" | "default";

export interface TooltipProps extends ReactTooltipProps {
  tooltipId: string;
  closeOnOutsideClick?: boolean;
  tooltipStyle?: TooltipTheme;
  dataTestId?: string;
}

const tooltipStyleSpecs = () => ({
  default: {
    borderColor: Colors.Indigo,
    backgroundColor: Colors.White,
  },
  discount: {
    borderColor: Colors.Magenta,
    backgroundColor: Colors.MagentaBackground,
  },
  success: {
    borderColor: Colors.Green,
    backgroundColor: Colors.GreenBackground,
  },
  warning: {
    borderColor: Colors.Orange,
    backgroundColor: Colors.OrangeBackground,
  },
  error: {
    borderColor: Colors.Red,
    backgroundColor: Colors.RedBackground,
  },
  neutral: {
    borderColor: Colors.Purple,
    backgroundColor: Colors.PurpleBackground,
  },
});

export const Tooltip: React.FC<TooltipProps> = ({
  tooltipId,
  tooltipStyle = "default",
  className,
  children,
  closeOnOutsideClick,
  ...props
}) => {
  const tooltipRef = React.useRef<HTMLDivElement>(null);

  React.useEffect(() => {
    ReactTooltip.rebuild();
  }, [tooltipId]);

  React.useEffect(() => {
    if (!closeOnOutsideClick) return;

    const handleClickOutside = () => {
      if (tooltipRef?.current) {
        ReactTooltip.hide();
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [tooltipRef, closeOnOutsideClick]);

  return (
    <div role="presentation" ref={tooltipRef} onMouseDown={(e) => e.stopPropagation()}>
      <ReactTooltip
        id={tooltipId}
        effect={props.effect || "solid"}
        textColor={props.textColor || Colors.AlmostBlack80}
        border
        borderColor={props.borderColor || tooltipStyleSpecs()[tooltipStyle].borderColor}
        backgroundColor={props.backgroundColor || tooltipStyleSpecs()[tooltipStyle].backgroundColor}
        className={classNames(styles.tooltipContainer, className)}
        {...props}
      >
        {children}
      </ReactTooltip>
    </div>
  );
};
