import classNames from "classnames";
import React from "react";

import { printMoney } from "../../utils/";
import styles from "./PricingTable.module.scss";

export interface PricingTableProps {
  dataTestId?: string;
  channelSelected: boolean;
  tableCollapsed: boolean;
  currencies: string[];
  prices: PricingCategoryData[];
}

export interface PricingCategoryData {
  label: string;
  values: PricingValue[];
}

export interface PricingValue {
  amount: number;
  currency: string;
  strikethrough?: boolean;
}

export const PricingTable: React.FC<PricingTableProps> = ({
  channelSelected,
  tableCollapsed,
  currencies,
  prices,
  dataTestId,
}) => {
  const preparePriceDisplay = (values: PricingValue[]) =>
    currencies.map((currency) => values.find((price) => price.currency === currency));

  const priceDetailRows = prices?.slice(0, -1);
  const revenueRow = prices && prices[prices.length - 1];

  const revenueRowClasses = classNames(styles.revenueRow, "bold", {
    [styles.revenueRowSelected]: channelSelected,
  });

  return (
    <table className={styles.pricingTable} data-testid={dataTestId}>
      <colgroup>
        <col />
        <col className={`${styles.currencyColumn} white-background`} />
        <col className={styles.currencyColumn} />
        <col className={`${styles.currencyColumn} white-background`} />
        <col className={styles.currencyColumn} />
        <col className={`${styles.currencyColumn} white-background`} />
      </colgroup>
      <thead>
        <tr className={styles.tableHeadRow}>
          <td />
          {currencies.map((currency) => (
            <td key={currency}>{currency}</td>
          ))}
        </tr>
      </thead>
      {!tableCollapsed ? (
        <tbody>
          {priceDetailRows &&
            priceDetailRows.map((row, rowIndex) => (
              <tr className={styles.itemizedRow} key={rowIndex}>
                <td className={styles.labelCell}>{row.label}</td>
                {preparePriceDisplay(row.values ?? []).map(
                  (value) =>
                    value && (
                      <td
                        className={classNames({
                          [styles.strikethrough]: value.strikethrough,
                        })}
                        key={value.currency}
                      >
                        {printMoney(String(value.amount))}
                      </td>
                    )
                )}
              </tr>
            ))}
        </tbody>
      ) : null}
      <tfoot className={styles.pricingFooter}>
        {revenueRow && (
          <tr className={revenueRowClasses}>
            <td className={styles.labelCell}>{revenueRow.label}</td>
            {preparePriceDisplay(revenueRow.values ?? []).map(
              (value) => value && <td key={value.currency}>{printMoney(String(value.amount))}</td>
            )}
          </tr>
        )}
      </tfoot>
    </table>
  );
};
