import classNames from "classnames";
import React, { useState } from "react";
import Select, { MultiValue, Props, SingleValue, StylesConfig } from "react-select";
import AsyncSelect from "react-select/async";
import AsyncCreatableSelect from "react-select/async-creatable";
import CreatableSelect from "react-select/creatable";

import { Colors } from "../../constants";
import { useCopy } from "../../extensions";
import { ClientOnly } from "../../extensions/ClientOnly";
import { Error } from "../Error/Error";
import styles from "./CustomSelect.module.scss";

export type Option = { value: string; label: string };

export type CustomSelectProps<T = unknown> = {
  dataTestId?: string;
  error?: string;
  label?: string;
  asyncSelect?: boolean;
  disabled?: boolean;
  required?: boolean;
  isShowErrorMessage?: boolean;
  loadOptions?: (value: string) => Promise<T[]>;
  onChange?: (value: SingleValue<T> | MultiValue<T>) => void;
  onBlur?: (event: React.FocusEvent<HTMLInputElement>) => void;
  withCreate?: boolean;
  animated?: boolean;
  ariaLabel?: string;
} & Pick<
  Props<T & Option>,
  | "components"
  | "isClearable"
  | "isLoading"
  | "isSearchable"
  | "onInputChange"
  | "placeholder"
  | "formatOptionLabel"
  | "name"
  | "options"
  | "value"
  | "defaultValue"
>;

export const CustomSelect = <T extends Option>({
  asyncSelect,
  components,
  dataTestId,
  disabled,
  error,
  formatOptionLabel,
  isClearable = false,
  isLoading = false,
  isSearchable = true,
  isShowErrorMessage = true,
  loadOptions,
  onChange,
  onInputChange,
  options,
  placeholder,
  label,
  name,
  required,
  withCreate = false,
  defaultValue,
  animated = false,
  onBlur,
  ariaLabel,
}: CustomSelectProps<T>) => {
  const [isFocused, setIsFocused] = useState(false);
  const [isFilled, setIsFilled] = useState(!!defaultValue);
  const { _textNoResults, _textLoading } = useCopy("CustomSelect");
  const DefaultComponent = withCreate ? CreatableSelect : Select;
  const AsyncComponent = withCreate ? AsyncCreatableSelect : AsyncSelect;

  const animatedTextStyles = animated ? { marginLeft: "-0.5rem", color: Colors.AlmostBlack } : {};

  const customStyles: StylesConfig<React.PropsWithChildren<T & Option>> = {
    container: (provided) => ({
      ...provided,
      height: "100%",
    }),
    option: (provided, state) => ({
      ...provided,
      backgroundColor: state.isSelected || state.isFocused ? Colors.PaleGray2 : "transparent",
      color: state.isDisabled ? Colors.PaleGray4 : undefined,
      cursor: state.isDisabled ? "not-allowed" : "pointer",
      padding: 10,
      "&:hover": {
        backgroundColor: state.isDisabled ? undefined : Colors.PaleGray2,
      },
    }),
    control: (provided, state) => ({
      ...provided,
      borderColor: !!error ? Colors.RedDark : state.isFocused ? Colors.Indigo : Colors.PaleGray6,
      borderRadius: "0.125rem",
      boxShadow: !!error
        ? `0 0 0 1px ${Colors.RedDark}`
        : state.isFocused
        ? `0 0 0 1px ${Colors.Indigo}`
        : "none",
      cursor: "pointer",
      fontFamily: "museo-sans, sans-serif",
      paddingLeft: "1rem",
      minHeight: "3.125rem",
      "&:hover": {
        borderColor: !!error ? Colors.RedDark : state.isFocused ? Colors.Indigo : Colors.PaleGray6,
      },
      height: "100%",
    }),
    menu: (provided) => ({
      ...provided,
      zIndex: 2,
    }),
    menuList: (provided) => ({
      ...provided,
      ...(animated
        ? {
            borderRadius: "0.2rem",
            border: `1px solid ${Colors.PaleGray6}`,
          }
        : {}),
    }),
    input: (provided) => ({
      ...provided,
      ...animatedTextStyles,
    }),
    singleValue: (provided) => ({
      ...provided,
      ...animatedTextStyles,
    }),
    clearIndicator: (provided) => ({
      ...provided,
      color: Colors.DarkGray2,
    }),
    dropdownIndicator: (provided) => ({
      ...provided,
      color: Colors.Indigo,
      marginRight: "0.25rem",
    }),
    indicatorSeparator: (provided) => ({
      ...provided,
      margin: "1rem 0.5rem",
      display: components?.DropdownIndicator ? "none" : "unset",
    }),
    valueContainer: (provided) => ({ ...provided, paddingLeft: 0 }),
    // INFO: Styles probably used for multi value select (TagSelect in Wizard)
    // so I'm leaving them, cause we may use this component in Wizard
    multiValue: () => ({
      backgroundColor: Colors.PaleGray3,
      margin: "0.375rem 0",
      marginRight: "0.25rem",
    }),
    multiValueLabel: () => ({
      fontFamily: "museo-sans, sans-serif",
      fontSize: "0.75rem",
      lineHeight: 1.76,
      fontWeight: 500,
      padding: "0.375rem 0.5rem",
    }),
    multiValueRemove: () => ({
      color: Colors.DarkGray2,
      cursor: "pointer",
      "&:hover": {
        backgroundColor: "transparent",
        borderColor: Colors.DarkGray2,
      },
    }),
  };

  function handleOnChange(value: (T & Option) | MultiValue<T & Option> | null) {
    onChange?.(value);
    if (animated) {
      setIsFilled(true);
    }
  }

  const labelClasses = animated
    ? classNames("field__label--animated", {
        "field__label--animated--focused": isFocused || isFilled,
      })
    : "field__label field__label--visible";

  const wrapperClasses = classNames("field__input-wrapper", {
    [styles.animatedWrapper]: animated,
  });

  function onAnimatedBlur(e: React.FocusEvent<HTMLInputElement>) {
    if (!animated) return;
    if (onBlur) {
      onBlur(e);
    }
    setIsFocused(false);
  }

  function onAnimatedFocus() {
    if (!animated) return;
    setIsFocused(true);
  }

  return (
    <div className={wrapperClasses}>
      {label && (
        <label className={labelClasses} htmlFor={name}>
          {label}
          {required && "*"}
        </label>
      )}
      <ClientOnly>
        <div data-testid={dataTestId}>
          {asyncSelect ? (
            <AsyncComponent
              cacheOptions
              loadOptions={loadOptions}
              defaultOptions
              components={components}
              isDisabled={disabled}
              isClearable={isClearable}
              name={name}
              inputId={name}
              placeholder={animated ? "" : placeholder}
              formatOptionLabel={formatOptionLabel}
              styles={customStyles}
              onChange={handleOnChange}
              defaultValue={defaultValue}
              onFocus={onAnimatedFocus}
              onBlur={onAnimatedBlur}
              noOptionsMessage={() => _textNoResults}
              aria-label={ariaLabel}
            />
          ) : (
            <DefaultComponent
              components={components}
              isDisabled={disabled}
              isClearable={isClearable}
              isLoading={isLoading}
              isSearchable={isSearchable}
              name={name}
              inputId={name}
              placeholder={animated ? "" : placeholder}
              formatOptionLabel={formatOptionLabel}
              onInputChange={onInputChange}
              options={options}
              styles={customStyles}
              onChange={handleOnChange}
              defaultValue={defaultValue}
              onFocus={onAnimatedFocus}
              onBlur={onAnimatedBlur}
              noOptionsMessage={() => _textNoResults}
              loadingMessage={() => _textLoading}
              aria-label={ariaLabel}
            />
          )}
        </div>
      </ClientOnly>
      {isShowErrorMessage &&
        error &&
        //For animated use new error styling
        (animated ? (
          <Error error={error} />
        ) : (
          <p className="field__message field__message--error">{error}</p>
        ))}
    </div>
  );
};
