import classNames from "classnames";
import React, { useEffect, useState } from "react";

import { Colors } from "../../../constants";
import { Icons } from "../../../icon-types";
import { Icon } from "../../Icon/Icon";
import { FormatOption } from "../BookFormatSelectionContainer";
import styles from "./BookFormatSelectionButton.module.scss";

export interface BookFormatSelectionButtonProps {
  bookFormatButtonSize: "large" | "medium-big";
  setFormat?: (selected: FormatOption) => void;
  options: FormatOption[];
  selectedBookFormat: FormatOption | null;
}

export const BookFormatSelectionButton: React.FunctionComponent<BookFormatSelectionButtonProps> = ({
  bookFormatButtonSize,
  selectedBookFormat,
  setFormat,
  options,
  ...props
}) => {
  const [selected, setSelected] = useState(selectedBookFormat);

  const bookFormatContainerClass = (item: FormatOption) =>
    classNames(styles[`bookFormatContainer-${bookFormatButtonSize}`], {
      [styles.multipleOptions]: options.length > 1,
      [styles.selected]: selected?.value === item.value,
      [styles.singleOption]: options.length <= 1,
    });

  useEffect(() => {
    if (selected && setFormat) {
      setFormat(selected);
    }
  }, [selected, setFormat]);

  return (
    <div className={styles.container} data-value={selected?.value}>
      {options.map((item) => (
        <button
          {...props}
          key={item.value}
          onClick={() => setSelected(item)}
          className={bookFormatContainerClass(item)}
        >
          <p className={styles.bookFormatHeading}>{item.label}</p>
          <div className={styles.bookFormatPriceContainer}>
            {item.priceBeforeDiscount ? (
              <>
                <Icon
                  iconName={Icons.DiscountMediumCircle}
                  color={Colors.Magenta}
                  className={styles.discountIcon}
                />
                <p className={styles.bookFormatPricePrevious}>{item.priceBeforeDiscount}</p>
              </>
            ) : null}
            <p className={styles.bookFormatPriceCurrent}>{item.price}</p>
          </div>
        </button>
      ))}
    </div>
  );
};
