import classNames from "classnames";
import React, { CSSProperties } from "react";

import { mapContentfulColorToStyleProp } from "../../utils/colors";
import { LinkedButton } from "../ButtonLink/ButtonLink";
import { Column } from "../Column/Column";
import { ContentLinkImageCard } from "../ContentLinkImageCard/ContentLinkImageCard";
import { DownloadDropdown } from "../DownloadDropdown/DownloadDropdown";
import { ElementCard } from "../ElementCard/ElementCard";
import { EmbeddedForm } from "../EmbeddedForm/EmbeddedForm";
import { ImageTextBullet } from "../ImageTextBullet/ImageTextBullet";
import { ImageWithMetadata, ImageWithMetadataProps } from "../ImageWithMetadata/ImageWithMetadata";
import { RichTextContainer } from "../RichTextContainer/RichTextContainer";
import { Video } from "../Video/Video";
import styles from "./Grid.module.scss";

// This list should match the validation on the Column Contentful component's 'children' field
const eligibleColumnChildren = {
  ContentLinkImageCard,
  ImageTextBullet,
  ImageWithMetadata,
  RichTextContainer,
  Video,
  ElementCard,
  LinkedButton,
  EmbeddedForm,
  DownloadDropdown,
};

export interface GridProps {
  dataTestId?: string;
  backgroundColor?: string;
  gridClassNames?: string;
  columnsCollection: {
    items: Array<{
      classes: string;
      childrenCollection: { items: Array<any> };
    }>;
  };
  backgroundImage?: ImageWithMetadataProps;
  children?: React.ReactNode;
  idForQuickLink?: string;
}

export const Grid: React.FC<GridProps> = ({
  dataTestId,
  backgroundColor,
  gridClassNames,
  columnsCollection,
  children,
  backgroundImage,
  idForQuickLink,
}) => {
  const gridClasses = classNames(
    "grid-12 max-width-container",
    { "res-top-padding-48 res-bottom-padding-48": !gridClassNames },
    gridClassNames
  );
  const mapToReactComponent = (item: any, index: number) => {
    const Component = (eligibleColumnChildren as any)[item.__typename];
    return Component ? React.createElement(Component, { ...item, key: index }) : null;
  };

  const wrapperInlineStyles: Record<string, string> = {};

  if (backgroundColor) {
    wrapperInlineStyles["--bgColor"] = mapContentfulColorToStyleProp(backgroundColor);
  }

  if (backgroundImage?.image.url) {
    wrapperInlineStyles["--image"] = `url("${backgroundImage.image.url}")`;

    if (backgroundImage.image?.retina) {
      wrapperInlineStyles["--retinaImage"] = `url("${backgroundImage.image.retina}")`;
    }

    if (backgroundImage.imageForMobile?.url) {
      wrapperInlineStyles["--mobileImage"] = `url("${backgroundImage.imageForMobile.url}")`;
    }

    if (backgroundImage.imageForMobile?.retina) {
      wrapperInlineStyles[
        "--mobileRetinaImage"
      ] = `url("${backgroundImage.imageForMobile.retina}")`;
    }
  }

  return (
    <div
      style={wrapperInlineStyles as CSSProperties}
      className={
        wrapperInlineStyles["--image"] ? styles.wrapperWithBgImage : styles.wrapperWithBgColor
      }
      data-testid={dataTestId}
      id={idForQuickLink}
    >
      <div className={gridClasses}>
        {children}
        {columnsCollection?.items.map((item, i) => (
          <Column className={item.classes} key={i}>
            {item.childrenCollection.items.map((content: any, index: number) =>
              mapToReactComponent(content, index)
            )}
          </Column>
        ))}
      </div>
    </div>
  );
};
