import React, { ChangeEvent, useEffect, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { Input, InputProps } from "../Input/Input";

export type DebouncedInputProps = InputProps & {
  delay?: number;
};

export const DebouncedInput: React.FC<DebouncedInputProps> = ({
  dataTestId,
  onChange,
  value: initialValue,
  delay,
  ...rest
}) => {
  const [changeEvent, setChangeEvent] = useState<ChangeEvent<
    HTMLTextAreaElement & HTMLInputElement
  > | null>(null);

  const debouncedOnChange = useDebouncedCallback(
    (event: ChangeEvent<HTMLTextAreaElement & HTMLInputElement>) => {
      onChange?.(event);
    },
    delay ?? 800
  );

  useEffect(() => {
    if (changeEvent) {
      debouncedOnChange(changeEvent);
    }
  }, [changeEvent, debouncedOnChange]);

  return (
    <Input
      dataTestId={dataTestId}
      {...rest}
      onChange={(event: ChangeEvent<HTMLTextAreaElement & HTMLInputElement>) => {
        setChangeEvent(event);
      }}
      value={changeEvent?.target.value ?? initialValue}
    />
  );
};
