import Big from "big.js";
import React, { useEffect, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { preferredCountryCodes } from "../../constants/preferredCountries";
import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { InputDropdown, OptionProps } from "../InputDropdown/InputDropdown";
import { LoadingContainer } from "../LoadingContainer/LoadingContainer";
import { MoneyDisplay } from "../MoneyDisplay/MoneyDisplay";
import { NumericInput } from "../NumericInput/NumericInput";
import {
  Country,
  CountryState,
  QueryLike,
  ShippingMethod,
  TotalPricing,
} from "../PricingCalculator/types";
import { Money } from "../PricingCalculator/types";
import { ProjectSelect } from "../ProjectSelect/ProjectSelect";
import { ProjectFields } from "../ProjectSelect/types";
import { ProjectTileProps } from "../ProjectTile/ProjectTile";
import { BulkDiscountRanges } from "./BulkDiscountRanges";
import styles from "./ShippingCalculator.module.scss";
import {
  ShippingMethodOption,
  ShippingMethodOptionProps,
} from "./ShippingMethodOption/ShippingMethodOption";

export interface ShippingCalculatorProps {
  dataTestId?: string;
  locale: string;
  getCountryName: (countryName: string, locale: string) => string;
  shippingCountriesQuery: QueryLike<Country[]>;
  shippingMethodsQuery: QueryLike<ShippingMethod[]>;
  totalPricesQuery?: QueryLike<TotalPricing | null>;
  onRequestTotalPrices?: (podPackageId: string, pageCount: number, quantity: number) => void;
  onRequestShippingMethods: (
    podPackageId: string,
    pageCount: number,
    quantity: number,
    country: string,
    state?: string
  ) => void;
  onRequestShippingCountries: () => void;
  onRequestCountryStates?: (countryCode: string) => void;
  podPackageId: string;
  pageCount: number;
  pricePerBook?: Money | null;
  usStatesRequired?: boolean;
  countryStatesQuery?: QueryLike<CountryState[] | null>;
  projectSelect?: {
    onRequestProjects: () => void;
    projects: ProjectFields[];
    projectsLoading: boolean;
    projectTileOptions: Pick<
      ProjectTileProps,
      | "luluAppUrl"
      | "lineOfBusiness"
      | "displayVersionNumber"
      | "getDisplayValue"
      | "displayPrintCost"
    >;
  };
  shippingAndHandlingOnly?: boolean;
}

export const ShippingCalculator: React.FC<ShippingCalculatorProps> = ({
  dataTestId,
  locale,
  getCountryName,
  shippingMethodsQuery,
  shippingCountriesQuery,
  totalPricesQuery,
  onRequestTotalPrices,
  onRequestShippingMethods,
  onRequestShippingCountries,
  onRequestCountryStates,
  pageCount,
  podPackageId,
  pricePerBook,
  usStatesRequired,
  countryStatesQuery,
  projectSelect,
  shippingAndHandlingOnly = false,
}) => {
  const [quantity, setQuantity] = useState(1);
  const [country, setCountry] = useState<string | null>(null);
  const [state, setState] = useState<string | null>(null);
  const [selectedShippingMethodId, setSelectedShippingMethodId] = useState<string | null>(null);
  const [quantityError, setQuantityError] = useState(false);
  const [selectedProject, setSelectedProject] = useState<ProjectFields | null>(null);

  useEffect(() => {
    onRequestShippingCountries();
    onRequestTotalPrices && onRequestTotalPrices(podPackageId, pageCount, quantity);
  }, [onRequestShippingCountries, onRequestTotalPrices, podPackageId, pageCount, quantity]);

  useEffect(() => {
    if (!isNaN(quantity) && quantity > 0 && country) {
      const requestPayload = {
        podPackageId: selectedProject?.podPackageId || podPackageId,
        pageCount: selectedProject?.pageCount || pageCount,
        quantity,
        country,
      };

      if (usStatesRequired && country === "US") {
        if (state) {
          onRequestShippingMethods(
            requestPayload.podPackageId,
            requestPayload.pageCount,
            requestPayload.quantity,
            requestPayload.country,
            state
          );
        }
      } else {
        onRequestShippingMethods(
          requestPayload.podPackageId,
          requestPayload.pageCount,
          requestPayload.quantity,
          requestPayload.country
        );
      }
    }
  }, [
    selectedProject,
    quantity,
    country,
    state,
    podPackageId,
    pageCount,
    onRequestShippingMethods,
    usStatesRequired,
  ]);

  const {
    _textDescription,
    _textDescriptionNoAvailableBulkDiscounts,
    _textDescriptionShippingAndHandling,
    _textQuantityEstimate,
    _textQuantity,
    _textNumberOfCopies,
    _textShippingAndHandlingEstimate,
    _textBookSubtotal,
    _textShippingAndHandling,
    _textFulfillmentFee,
    _textSubtotal,
    _textSubtotalDescription,
    _textQuantityError,
    _textEstimatedDelivery1,
    _textEstimatedDelivery2,
    _textEstimatedDelivery3,
    _textShippingMethod,
    _textTrackable,
    _textNonTrackable,
    _textDestinationCountry,
    _textBulkDiscount,
    _textDestinationState,
    _textNoShippingOptions,
    _textSelectVariant,
  } = useCopy("ShippingCalculator");

  const trackableSuffix = ` | ${_textTrackable}`;
  const nonTrackableSuffix = ` | ${_textNonTrackable}`;

  const handleQuantityChange = useDebouncedCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const quantity = parseInt(event.target.value);
      if (quantity > 0) {
        onRequestTotalPrices && onRequestTotalPrices(podPackageId, pageCount, quantity);
        setQuantity(quantity);
        setQuantityError(false);
      } else {
        setQuantityError(true);
      }
    },
    500
  );

  useEffect(() => {
    if (shippingMethodsQuery.data?.length === 1) {
      setSelectedShippingMethodId(shippingMethodsQuery.data[0].id);
    }
  }, [shippingMethodsQuery.data]);

  const handleCountryChange = (value: string) => {
    setSelectedShippingMethodId(null);
    setCountry(value);

    if (value === "US") {
      onRequestCountryStates?.(value);
    }
  };

  const handleStateChange = (value: string) => {
    setSelectedShippingMethodId(null);
    setState(value);
  };

  if (!shippingCountriesQuery.data || (!shippingAndHandlingOnly && !totalPricesQuery?.data))
    return null;

  const shippingCountries = shippingCountriesQuery.data;
  const shippingMethods = shippingMethodsQuery.data;
  const {
    basePrice,
    totalPrice: bookTotal,
    totalDiscounts,
    fulfillmentFee,
  } = totalPricesQuery?.data || {};

  // (Non-Regular) Tier users special rules
  // - they don't see the bulk discount table
  // - they see the already discounted price as base price
  const isRegularUser = shippingAndHandlingOnly
    ? undefined
    : totalPricesQuery?.data?.totalDiscounts?.length === 0 ||
      totalPricesQuery?.data?.totalDiscounts?.every((item) => item.discountType === "VOLUME");

  const selectedShippingMethod = shippingMethods?.find((sm) => sm.id === selectedShippingMethodId);
  let totalAmount =
    bookTotal?.amount && selectedShippingMethod?.cost
      ? Big(bookTotal.amount).plus(Big(selectedShippingMethod.cost)).toNumber()
      : null;

  if (fulfillmentFee?.amount && totalAmount) {
    totalAmount = Big(totalAmount).plus(Big(fulfillmentFee.amount)).toNumber();
  }

  const loading = totalPricesQuery?.loading || shippingMethodsQuery.loading;

  const getDiscountLabel = (discountPercentageString?: string) => {
    const discountPercentage = parseInt(discountPercentageString ?? "0");
    return `${_textBulkDiscount} ${discountPercentage > 1 ? discountPercentage + "%" : ""}`;
  };

  const getBookTotal = (isRegularUser: boolean) => {
    if (isRegularUser) {
      return basePrice ?? { amount: undefined, currency: "" };
    }
    // in this case the discount already part of the book price
    return bookTotal ?? { amount: undefined, currency: "" };
  };

  return (
    <div className={styles.container} data-testid={dataTestId}>
      <LoadingContainer loading={loading}>
        <span className={styles.description}>
          {shippingAndHandlingOnly
            ? _textDescriptionShippingAndHandling
            : isRegularUser
            ? _textDescription
            : _textDescriptionNoAvailableBulkDiscounts}
        </span>
        {!shippingAndHandlingOnly && isRegularUser && (
          <>
            <h4 className={styles.estimateTitle}>{_textQuantityEstimate}</h4>
            {pricePerBook && <BulkDiscountRanges pricePerBook={pricePerBook} locale={locale} />}
          </>
        )}
        {!!projectSelect && (
          <ProjectSelect
            dataTestIdPrefix="shipping-calculator"
            selectedValue={selectedProject}
            onChange={setSelectedProject}
            fetchProjects={projectSelect.onRequestProjects}
            projects={projectSelect.projects}
            projectsLoading={projectSelect.projectsLoading}
            projectTileOptions={projectSelect.projectTileOptions}
            displaySelectedValue={false}
            label={_textSelectVariant}
          />
        )}
        <NumericInput
          label={shippingAndHandlingOnly ? _textQuantity : _textNumberOfCopies}
          maxLength={4}
          onChange={handleQuantityChange}
          wrapperClassName={styles.numberOfCopiesWrapper}
          error={quantityError ? _textQuantityError : ""}
          defaultValue={quantity}
          dataTestId="number-of-copies"
          style={{ marginBottom: shippingAndHandlingOnly ? "1rem" : "unset" }}
        />
        {!shippingAndHandlingOnly && (
          <h4 className={styles.estimateTitle}>{_textShippingAndHandlingEstimate}</h4>
        )}
        <InputDropdown
          options={shippingCountries.map((country) => ({
            label: getCountryName(country.code, locale),
            value: country.code,
          }))}
          name="destination-country"
          label={_textDestinationCountry}
          onChange={handleCountryChange}
          value={country}
          iconName={Icons.ContactCountry}
          wrapperClassName={styles.selectContainer}
          dataTestId="destination-country"
          preferredOptions={preferredCountryCodes}
        />
        {usStatesRequired && country === "US" && (
          <InputDropdown
            options={
              countryStatesQuery?.data?.map((state) => ({
                label: state.name,
                value: state.code,
              })) ?? []
            }
            name="destination-state"
            label={_textDestinationState}
            onChange={handleStateChange}
            value={state}
            iconName={Icons.ContactCountry}
            wrapperClassName={styles.selectContainer}
            dataTestId="destination-state"
          />
        )}
        {shippingMethods && (
          <InputDropdown<ShippingMethodOptionProps & OptionProps>
            options={shippingMethods.map((shippingMethod) => ({
              label: shippingMethod.name
                ? `${shippingMethod.name}${
                    shippingMethod.traceable ? trackableSuffix : nonTrackableSuffix
                  }`
                : "-",
              value: shippingMethod.id,
              money: {
                amount: shippingMethod.shippingCost?.amount || shippingMethod.cost,
                currency: shippingMethod.shippingCost?.currency || shippingMethod.currency,
              },
              description: `${_textEstimatedDelivery1} ${
                shippingMethod.businessDaysMin || shippingMethod.daysMin
              } ${_textEstimatedDelivery2} ${
                shippingMethod.businessDaysMax || shippingMethod.daysMax
              } ${_textEstimatedDelivery3}`,
              locale,
            }))}
            name="shipping-method"
            label={_textShippingMethod}
            value={selectedShippingMethodId}
            onChange={setSelectedShippingMethodId}
            wrapperClassName={styles.selectContainer}
            Option={ShippingMethodOption}
            dataTestId="shipping-method"
            noOptionsText={_textNoShippingOptions}
          />
        )}
        <div className={styles.summaryContainer}>
          {!shippingAndHandlingOnly && (
            <p className={styles.summaryItem} data-testid="subtotal">
              <span>{_textBookSubtotal}</span>
              <MoneyDisplay money={getBookTotal(!!isRegularUser)} locale={locale} fallback="———" />
            </p>
          )}
          {!shippingAndHandlingOnly && totalDiscounts?.length
            ? totalDiscounts.map((discount, index) => {
                // hide pricing tier and percentage discounts as they are baked in print prices
                if (discount.discountType !== "VOLUME") {
                  return null;
                }

                const moneyDiscount = discount.discount
                  ? discount.discount
                  : { amount: undefined, currency: "", type: _textBulkDiscount };
                return (
                  <p className={styles.summaryItem} key={index} data-testid="discount">
                    <span>{getDiscountLabel(discount.percentage)}</span>
                    <span>
                      -
                      <MoneyDisplay
                        money={moneyDiscount}
                        locale={locale}
                        className={styles.money}
                        fallback="———"
                      />
                    </span>
                  </p>
                );
              })
            : null}
          <p className={styles.summaryItem} data-testid="shipping-and-handling">
            <span>
              {shippingAndHandlingOnly
                ? _textShippingAndHandlingEstimate
                : _textShippingAndHandling}
            </span>
            <MoneyDisplay
              money={{
                amount:
                  (selectedShippingMethod?.shippingCost?.amount || selectedShippingMethod?.cost) ??
                  undefined,
                currency:
                  (selectedShippingMethod?.shippingCost?.currency ||
                    selectedShippingMethod?.currency) ??
                  "",
              }}
              locale={locale}
              className={styles.money}
              fallback="———"
            />
          </p>
          {!shippingAndHandlingOnly && fulfillmentFee && (
            <p className={styles.summaryItem} data-testid="fulfillment-fee">
              <span>{_textFulfillmentFee}</span>
              <MoneyDisplay
                money={fulfillmentFee ? fulfillmentFee : { amount: undefined, currency: "" }}
                locale={locale}
                className={styles.money}
                fallback="———"
              />
            </p>
          )}
          {!shippingAndHandlingOnly && (
            <div className={styles.summaryItem} data-testid="subtotal">
              <div>
                <span className={styles.subtotalExcludingFees}>{_textSubtotal} </span>
                <span className={styles.subtotalExcludingFeesDescription}>
                  ({_textSubtotalDescription})
                </span>
              </div>
              <MoneyDisplay
                money={{ amount: totalAmount ?? undefined, currency: bookTotal?.currency ?? "" }}
                locale={locale}
                className={styles.subtotalExcludingFees}
                fallback="———"
              />
            </div>
          )}
        </div>
      </LoadingContainer>
    </div>
  );
};
