import classNames from "classnames";
import isbnValidate from "is-isbn";
import React, { useCallback, useEffect, useState } from "react";

import { Colors } from "../../constants";
import { useCopy } from "../../extensions/CopyContext";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./IsbnInput.module.scss";

export interface IsbnInputProps {
  dataTestId?: string;
  onChange: (value: string, isValid: boolean) => void;
  placeholder?: string;
  disabled?: boolean;
  actions?: React.ReactNode;
  forceInitialDirty?: boolean;
  initialValue?: string;
  customError?: string;
  value?: string;
}

export const IsbnInput: React.FC<IsbnInputProps> = ({
  dataTestId,
  placeholder,
  disabled,
  onChange,
  actions,
  forceInitialDirty,
  value,
  initialValue = "",
  customError,
}) => {
  const { _textIsRequired, _textIsCorrect, _textIsIncorrect } = useCopy("IsbnInput");
  const [isbn, setIsbn] = useState(initialValue);
  const [error, setError] = useState<string>();
  const [isIsbnValid, setIsIsbnValid] = useState(false);
  const [isTouched, setIsTouched] = useState(false);

  const validateIsbn = useCallback(
    (isbnValue: string) => {
      if (!isbnValue) {
        setError(_textIsRequired);
        setIsIsbnValid(false);
        return false;
      }

      const newValue = isbnValue.replace(/-|\s/g, "");
      // is-isbn validates ISBN13 and ISBN10, but we only accept ISBN13
      const isbnValid = isbnValidate.validate(newValue) && newValue.length === 13;

      if (!isbnValid) {
        setError(_textIsIncorrect);
        setIsIsbnValid(false);
        return false;
      }

      setError(undefined);
      setIsIsbnValid(true);
      return true;
    },
    [_textIsIncorrect, _textIsRequired]
  );

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;

    setIsbn(value);

    if (!isTouched) {
      setIsTouched(true);
    }

    onChange(value, validateIsbn(value));
  };

  useEffect(() => {
    if (!forceInitialDirty) {
      return;
    }

    validateIsbn(initialValue);
  }, [forceInitialDirty, initialValue, validateIsbn]);

  return (
    <>
      <div
        className={classNames("field", {
          "field--error": error || customError,
          "field--success": isIsbnValid && !error && !customError,
        })}
      >
        <div className="field__input-wrapper field__input-wrapper--with-icon field__input-wrapper--with-isbn">
          <label className="field__label field__label--visible" htmlFor="isbnNumber">
            ISBN
          </label>
          <input
            data-testid={dataTestId}
            type="text"
            name="isbnNumber"
            className="field__input"
            onChange={handleChange}
            value={value || isbn}
            disabled={disabled}
            placeholder={placeholder}
            id="isbnNumber"
          />
          <Icon iconName={Icons.FormatsIsbn} className="input-icon" />
        </div>
        <div className={styles.footerRow}>
          <div className={styles.validationMessage}>
            {error || customError ? (
              <>
                <Icon iconName={Icons.NotificationError} color={Colors.RedDark} />
                <p>{error || customError}</p>
              </>
            ) : (
              isIsbnValid && (
                <>
                  <Icon iconName={Icons.NotificationSuccess} color={Colors.GreenDark} />
                  <p>{_textIsCorrect}</p>
                </>
              )
            )}
          </div>
          {actions}
        </div>
      </div>
    </>
  );
};
