import { useEffect, useState } from "react";

export const MEDIA_BREAKPOINTS = {
  XXL: 2560,
  XL: 1440,
  L: 1024,
  M_BIG: 840,
  M: 600,
  S: 480,
  XS: 360,
};

export const useMediaQuery = (query: string) => {
  const [matches, setMatches] = useState(false);

  useEffect(() => {
    const media = window.matchMedia(query);

    // INFO: ensure that the browser supports the "change" MediaQueryList event
    if ("onchange" in media || typeof (media as MediaQueryList).addEventListener === "function") {
      if (media.matches !== matches) {
        setMatches(media.matches);
      }
      const handleMediaChange = () => {
        setMatches(media.matches);
      };

      media.addEventListener("change", handleMediaChange);

      return () => media.removeEventListener("change", handleMediaChange);
    } else {
      return () => {};
    }
  }, [matches, query]);

  return matches;
};

export const useMediaQueryLargerThan = (breakPoint: keyof typeof MEDIA_BREAKPOINTS) =>
  useMediaQuery(`(min-width: ${MEDIA_BREAKPOINTS[breakPoint]}px)`);
export const useMediaQuerySmallerThan = (breakPoint: keyof typeof MEDIA_BREAKPOINTS) =>
  useMediaQuery(`(max-width: ${MEDIA_BREAKPOINTS[breakPoint]}px)`);
