import React from "react";
import { Link as RouterLink } from "react-router-dom";

import { getRootUrl, isNextJs } from "../../utils";

export interface LinkProps {
  className?: string;
  dataTestId?: string;
  name?: string;
  description?: string;
  newTab?: boolean;
  url?: string | null;
  page?: { url?: string } | null;
  __typename?: string;
  children?: React.ReactNode;
  onClick?: () => void;
}

export const Link: React.FC<LinkProps> = ({
  dataTestId,
  name,
  url,
  page,
  __typename,
  description,
  newTab,
  children,
  ...otherProps
}) => {
  const linkTarget = newTab ? "_blank" : "_self";
  const linkRel = newTab ? "noopener noreferrer" : undefined;
  const isInternalLink = url && !/^https?:\/\//.test(url);
  const internalUrl = page?.url || (isInternalLink ? url : undefined);

  if (!internalUrl && !url) return null;

  if (isNextJs) {
    return (
      <a
        href={url ?? getRootUrl(internalUrl ?? "")}
        target={linkTarget}
        rel={linkRel}
        data-testid={dataTestId}
        aria-label={name}
        {...otherProps}
      >
        {children || name}
      </a>
    );
  }

  return internalUrl ? (
    <RouterLink
      to={getRootUrl(internalUrl ?? "")}
      target={linkTarget}
      rel={linkRel}
      data-testid={dataTestId}
      aria-label={name}
      {...otherProps}
    >
      {children || name}
    </RouterLink>
  ) : (
    <a
      href={url!}
      target={linkTarget}
      rel={linkRel}
      data-testid={dataTestId}
      {...otherProps}
      aria-label={name}
    >
      {children || name}
    </a>
  );
};
