import classNames from "classnames";
import React, { ChangeEvent, useState } from "react";

import { Colors } from "../../../constants";
import { useCopy } from "../../../extensions";
import { Icons } from "../../../icon-types";
import { CheckboxBox } from "../../CheckboxBox/CheckboxBox";
import { Icon } from "../../Icon/Icon";
import { MoneyDisplay } from "../../MoneyDisplay/MoneyDisplay";
import { NumericInput } from "../../NumericInput/NumericInput";
import styles from "../RevenueCalculator.module.scss";
import { RevenueTable } from "../RevenueTable/RevenueTable";
import { PanelProps } from "./types";

export const DistroPanel = ({
  baseCurrency,
  minimumListPrice,
  onListPriceChange,
  initialPriceAmount,
  isGlobalDistro,
  eligibleForGlobalDistro,
  locale,
  pricing,
  error,
  loading,
}: PanelProps) => {
  const [localListPrice, setLocalListPrice] = useState<string>(
    initialPriceAmount?.toString() ?? ""
  );

  const {
    _textLBGD_header,
    _textLBGD_description,
    _textLBGD_addGlobalDistribution,
    _textYourListPrice,
    _textMinimumListPrice,
    _textEstimatedRevenue,
    _textLuluBookstore,
    _textGlobalDistribution,
  } = useCopy("RevenueCalculator");

  const handleListPriceInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;
    onListPriceChange(parseFloat(newValue), isGlobalDistro);
    setLocalListPrice(newValue);
  };

  const bookstoreRevenueEntryRows = pricing?.revenue?.find(
    ({ channel }) => channel === "LULU"
  )?.data;
  const globalDistroRevenueEntryRows = pricing?.revenue?.find(
    ({ channel }) => channel === "GLOBALREACH"
  )?.data;

  if (!minimumListPrice) return null;

  const handleCheckBoxChange = () => {
    onListPriceChange(parseFloat(localListPrice), !isGlobalDistro);
  };

  const testIdPrefix = "distro-panel";

  return (
    <div className={styles.content} data-testid={testIdPrefix}>
      <p className={classNames(styles.text, styles.contentHeader)}>{_textLBGD_header}</p>
      <div className={styles.checkboxWrapper}>
        <CheckboxBox
          title={
            <div style={{ display: "flex", alignItems: "center", gap: 16 }}>
              <span>{_textLBGD_addGlobalDistribution}</span>
              <Icon
                iconName={eligibleForGlobalDistro ? Icons.DistributionYes : Icons.DistributionNo}
                color={eligibleForGlobalDistro ? Colors.GreenDark : Colors.DarkGray1}
              />
            </div>
          }
          value={"global-distro"}
          name={"global-distro"}
          checked={!!isGlobalDistro}
          onChange={handleCheckBoxChange}
          disabled={!eligibleForGlobalDistro}
          description={_textLBGD_description}
          theme="modern"
          titleColor="black"
          dataTestId={`${testIdPrefix}-global-distro`}
        />
      </div>
      <p className={classNames(styles.text, styles.minimumListPrice)}>
        {_textMinimumListPrice}
        <MoneyDisplay money={minimumListPrice} locale={locale} className={styles.price} />
      </p>
      <NumericInput
        isMoney
        maxLength={12}
        currency={baseCurrency}
        label={_textYourListPrice}
        onChange={handleListPriceInputChange}
        wrapperClassName={styles.listPriceInput}
        value={localListPrice}
        error={error}
        dataTestId={`${testIdPrefix}-list-price-input`}
      />
      <div className={styles.revenueTableWrapper}>
        <RevenueTable
          revenueEntryRows={
            !error && localListPrice && bookstoreRevenueEntryRows ? bookstoreRevenueEntryRows : []
          }
          summary={_textEstimatedRevenue}
          baseCurrency={baseCurrency}
          locale={locale}
          loading={loading}
          title={_textLuluBookstore}
          conversion
        />
      </div>
      {isGlobalDistro && (
        <div className={styles.revenueTableWrapper}>
          <RevenueTable
            revenueEntryRows={
              !error && localListPrice && globalDistroRevenueEntryRows
                ? globalDistroRevenueEntryRows
                : []
            }
            summary={_textEstimatedRevenue}
            baseCurrency={baseCurrency}
            locale={locale}
            loading={loading}
            title={_textGlobalDistribution}
          />
        </div>
      )}
    </div>
  );
};
