import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { NotificationType } from "../Notification/Notification";
import styles from "./MiniNotifications.module.scss";

export type MiniNotificationType = Exclude<NotificationType, "marketing">;
export interface MiniNotificationsProps {
  dataTestId?: string;
  customClassNames?: string;
  noRightBorderRadius?: boolean;
  noBorderRadiusOnNotification?: boolean;
  disableClickAction?: boolean;
  notifications: {
    type: MiniNotificationType;
    message: string | React.ReactNode;
    disableClickAction?: boolean;
  }[];
  children?: React.ReactNode;
}

export const MiniNotifications: React.FC<MiniNotificationsProps> = ({
  dataTestId,
  noRightBorderRadius = false,
  noBorderRadiusOnNotification = false,
  disableClickAction = false,
  children,
  customClassNames,
  notifications,
}) => {
  const notificationIcon: Record<MiniNotificationType, Icons> = {
    warning: Icons.NotificationWarning,
    error: Icons.NotificationError,
    success: Icons.NotificationSuccess,
    neutral: Icons.NotificationNeutral,
  };

  return (
    <>
      {notifications.map(({ type, message }, index) => {
        const isFirstNotification = index === 0;
        const isLastNotification = index + 1 === notifications.length;

        return (
          <React.Fragment key={index}>
            <div
              className={classNames(
                styles.wrapper,
                styles[type],
                {
                  [styles.stacked]: index > 0,
                  [styles.lastNotificationWithoutChildren]:
                    !isFirstNotification && isLastNotification && !children,
                  [styles.oneNotificationWithChildren]:
                    isFirstNotification && isLastNotification && children,
                  [styles.lastNotificationWithChildren]:
                    !isFirstNotification && isLastNotification && children,
                  [styles.withChildren]:
                    (!!children || !isLastNotification) && !noBorderRadiusOnNotification,
                  [styles.noRightBorderRadius]: noRightBorderRadius,
                  [styles.noBorderRadiusOnNotification]: noBorderRadiusOnNotification,
                },
                customClassNames
              )}
              data-testid={dataTestId}
            >
              <div className={styles.iconWrapper}>
                <Icon iconName={notificationIcon[type]} />
              </div>
              <div
                role="presentation"
                className={styles.message}
                onClick={(e) => disableClickAction && e.stopPropagation()}
              >
                {message}
              </div>
            </div>
            {isLastNotification && children && (
              <div className={classNames(styles.border, styles[`border-${type}`])}>{children}</div>
            )}
          </React.Fragment>
        );
      })}
    </>
  );
};
