import React from "react";

import { getFlexAlignment } from "../../utils/getFlexAlignment";
import { IconWithMetadata, IconWithMetadataProps } from "../IconWithMetadata/IconWithMetadata";
import styles from "./ReferenceIconAndText.module.scss";

export type ReferenceIconAndTextProps = {
  referenceIcon: IconWithMetadataProps;
  iconTextAlignment?: "left" | "center" | "right";
  referenceIconDescriptionText: string;
};

export const ReferenceIconAndText = ({
  iconTextAlignment = "left",
  referenceIcon,
  referenceIconDescriptionText,
}: ReferenceIconAndTextProps) => (
  <div
    className={styles.iconContainer}
    style={{
      justifyContent: getFlexAlignment(iconTextAlignment),
    }}
  >
    <IconWithMetadata
      {...referenceIcon}
      icon={`${referenceIcon.icon} ${styles.iconWithMetadata}`}
    />
    <span className={styles.iconDescription}>{referenceIconDescriptionText}</span>
  </div>
);
