import * as React from "react";

import { BookRadio, BookRadioProps } from "../BookRadio/BookRadio";
import styles from "./BookRadioGroup.module.scss";

export type BookRadioGroupProps = Pick<
  BookRadioProps,
  "name" | "disabled" | "type" | "onChange" | "dataTestId"
> & { values: BookRadioProps[]; selected: string; dataTestIdCustomPrefix?: string };

export const BookRadioGroup: React.FunctionComponent<BookRadioGroupProps> = ({
  dataTestIdCustomPrefix,
  dataTestId,
  values,
  selected,
  name,
  disabled = false,
  onChange,
  type,
}) => (
  <div className={styles.bookRadioGroup} data-testid={dataTestId}>
    {values.map((value) => (
      <BookRadio
        dataTestIdCustomPrefix={dataTestIdCustomPrefix}
        name={name}
        key={value.value}
        label={value.label}
        description={value.description}
        value={value.value}
        icon={value.icon}
        image={value.image}
        imageSet={value.imageSet}
        disabled={disabled || value.disabled}
        onChange={onChange}
        selected={selected === value.value}
        type={type}
        CustomImageComponent={value.CustomImageComponent}
      />
    ))}
  </div>
);
