import classNames from "classnames";
import React, { useEffect, useRef, useState } from "react";

import { Icons } from "../../../icon-types";
import { Icon } from "../../Icon/Icon";
import buttonStyles from "../BookFormatSelectionButton/BookFormatSelectionButton.module.scss";
import { FormatOption } from "../BookFormatSelectionContainer";
import styles from "./BookFormatSelectionDropdown.module.scss";

export interface BookFormatSelectionDropdownProps {
  options: FormatOption[];
  setFormat?: (selected: FormatOption) => void;
  selectedBookFormat: FormatOption | null;
  bookFormatDropdownSize: "medium-small" | "extra-small";
}

export const BookFormatSelectionDropdown: React.FunctionComponent<
  BookFormatSelectionDropdownProps
> = ({ bookFormatDropdownSize, selectedBookFormat, setFormat, options }) => {
  const [selected, onChange] = useState(selectedBookFormat);
  const [dropdownHeight, updateDropdownHeight] = useState(0);
  const [isOpen, setIsOpen] = useState(false);

  const dropdownEl = useRef<HTMLDivElement>(null);
  const maxItemsInDropdown = options.length;

  const bookFormatDropdownClass = (isOpen: boolean) =>
    classNames(
      styles.bookFormatDropdown,
      styles.selectedFormat,
      buttonStyles[`bookFormatContainer-${bookFormatDropdownSize}`],
      {
        [styles.dropdownOpen]: isOpen,
      }
    );

  useEffect(() => {
    if (dropdownEl.current === null) {
      return;
    }
    updateDropdownHeight(
      dropdownEl.current.children[0].clientHeight * maxItemsInDropdown + maxItemsInDropdown - 1
    );
  }, [updateDropdownHeight, maxItemsInDropdown]);

  useEffect(() => {
    if (selected && setFormat) {
      setFormat(selected);
    }
  }, [selected, setFormat]);

  return (
    <div
      onBlur={(e) => {
        if (e.relatedTarget?.id === "dropdownItem") {
          return;
        }
        setIsOpen(false);
      }}
    >
      <button
        role="combobox"
        aria-controls="dropdownEl"
        aria-expanded={isOpen}
        onClick={() => setIsOpen((prevState) => !prevState)}
        className={bookFormatDropdownClass(isOpen)}
        style={{
          maxWidth: bookFormatDropdownSize === "medium-small" ? 400 : 150,
        }}
      >
        {bookFormatDropdownSize === "medium-small" ? (
          <div className={styles.bookFormatDropdownColumnFormat}>
            <Icon iconName={(selected && selected.iconName) as Icons} />
          </div>
        ) : null}
        <div className={styles.bookFormatDropdownColumn}>
          <p className={buttonStyles.bookFormatHeading}>{selected && selected.label}</p>
          <div>
            {selected && selected.priceBeforeDiscount ? (
              <p className={buttonStyles.bookFormatPricePrevious}>
                {selected && selected.priceBeforeDiscount}
              </p>
            ) : null}
            <p className={buttonStyles.bookFormatPriceCurrent}>{selected && selected.price}</p>
          </div>
        </div>
        <div className={styles.bookFormatDropdownColumnArrow}>
          <Icon
            iconName={isOpen ? Icons.ArrowUpLarge : Icons.ArrowDownLarge}
            className={styles.bookFormatDropdownColumnArrow}
          />
        </div>
      </button>
      <div
        id="dropdownEl"
        className={`${styles.dropdown} ${!isOpen ? " invisible" : ""}`}
        ref={dropdownEl}
        style={{
          maxHeight: dropdownHeight,
          maxWidth: bookFormatDropdownSize === "medium-small" ? 400 : 150,
        }}
      >
        {options.map((item, index) => (
          <button
            id="dropdownItem"
            key={item.value || index}
            onClick={() => {
              setIsOpen((prevState) => !prevState);
              onChange(item);
            }}
            className={`${styles.dropDownItem} ${
              buttonStyles[`bookFormatContainer-${bookFormatDropdownSize}`]
            }`}
          >
            {bookFormatDropdownSize === "medium-small" ? (
              <div className={styles.bookFormatDropdownColumnFormat}>
                <Icon iconName={item.iconName as Icons} />
              </div>
            ) : null}
            <div className={styles.bookFormatDropdownColumn}>
              <p className={buttonStyles.bookFormatHeading}>{item.label}</p>
              <div>
                {item.priceBeforeDiscount ? (
                  <p className={buttonStyles.bookFormatPricePrevious}>{item.priceBeforeDiscount}</p>
                ) : null}
                <p className={buttonStyles.bookFormatPriceCurrent}>{item.price}</p>
              </div>
            </div>
          </button>
        ))}
      </div>
    </div>
  );
};
