import classNames from "classnames";
import React from "react";

import { Colors } from "../../../../constants";
import { useCopy } from "../../../../extensions";
import { useMediaQueryLargerThan, useMediaQuerySmallerThan } from "../../../../hooks";
import { Icons } from "../../../../icon-types";
import { NakedButton } from "../../../../shared/components/NakedButton/NakedButton";
import { Icon } from "../../../Icon/Icon";
import styles from "./AttributeSelector.module.scss";

interface Props {
  image?: string;
  label: string;
  description?: string;
  selected?: boolean;
  value: string;
  onChange?: (option: string) => void;
  disabled?: boolean;
  unavailable?: boolean;
  nonInteractive?: boolean;
  isDistroEligible?: boolean;
  order?: number;
  colStart?: number;
}

export const AttributeOption: React.FC<Props> = ({
  image,
  label,
  description,
  selected,
  onChange,
  value,
  disabled,
  unavailable,
  nonInteractive,
  isDistroEligible,
  order,
  colStart,
}) => {
  const { _textOptionNotAvailable } = useCopy("PricingCalculator");
  const largerThanM = useMediaQueryLargerThan("M");
  const smallerThanMBig = useMediaQuerySmallerThan("M_BIG");
  const largerThanXL = useMediaQueryLargerThan("XL");
  const isTablet = largerThanM && smallerThanMBig;

  const testIdPrefix = `attribute-option-${value}`;
  const isAttributeAvailable = onChange && !disabled && !nonInteractive && !unavailable;

  return (
    <NakedButton
      className={classNames(styles.option, {
        [styles.selected]: selected && !disabled && !nonInteractive,
        [styles.disabled]: disabled || unavailable,
        [styles.nonInteractive]: nonInteractive,
      })}
      style={{
        order: order ?? 0,
        gridColumnStart: !isTablet && !largerThanXL && colStart ? colStart : "auto",
      }}
      onClick={() => {
        if (isAttributeAvailable) onChange(value);
      }}
      disabled={!isAttributeAvailable}
      data-testid={testIdPrefix}
    >
      <div className={styles.imageContainer}>
        <picture>
          <source type="image/webp" srcSet={`${image}-1x.webp, ${image}-2x.webp 2x`} />
          <img
            className={styles.image}
            alt={label}
            src={image + "-1x.jpg"}
            srcSet={`${image}-1x.jpg, ${image}-2x.jpg 2x`}
          />
        </picture>
        {disabled || unavailable ? (
          <>
            <div className={styles.disabledFog} />
            {unavailable && !disabled && (
              <div className={styles.disabledInfo}>
                <span>{_textOptionNotAvailable}</span>
              </div>
            )}
          </>
        ) : null}
      </div>
      <div className={styles.optionContentContainer}>
        {!nonInteractive && (
          <div
            className={classNames(styles.radio, {
              [styles.selected]: selected,
            })}
          />
        )}
        <div className={classNames("h4", styles.label)}>
          {label}
          {description && <div className={styles.description}>{description}</div>}
        </div>
        {isDistroEligible && (
          <Icon
            className={styles.distroIcon}
            iconName={Icons.DistributionYes}
            color={Colors.GreenDark}
            size={20}
            dataTestId={`${testIdPrefix}-global-distro-icon`}
          />
        )}
        {disabled ? <div className={styles.disabledFog} /> : null}
      </div>
    </NakedButton>
  );
};
