import { PodPackage } from "./types";

export enum ConfiguratorActionType {
  RESET_CONFIGURATION = "RESET_CONFIGURATION", // nullify the configurator, keeps the pod packages, enables initial data
  CHANGE_POD_OPTION = "CHANGE_POD_OPTION",
  CHANGE_PAGE_COUNT = "CHANGE_PAGE_COUNT",
  SET_POD_PACKAGES = "SET_POD_PACKAGES",
  UPDATE_CONFIGURATION = "UPDATE_CONFIGURATION", // does *not* nullify, just overrides what's given
}

export interface ConfiguratorState {
  trimSize: string | null;
  bindingType: string | null;
  coverFinish: string | null;
  interiorInk: string | null;
  interiorPaper: string | null;
  pageCount: number | null;
  podPackages: PodPackage[];
}

type PodOption = Extract<
  keyof ConfiguratorState,
  "trimSize" | "bindingType" | "coverFinish" | "interiorInk" | "interiorPaper"
>;

export type ConfiguratorAction =
  | {
      type: ConfiguratorActionType.RESET_CONFIGURATION;
      payload?: { configuration: Partial<ConfiguratorState> };
    }
  | {
      type: ConfiguratorActionType.CHANGE_POD_OPTION;
      payload: { option: PodOption; value: string };
    }
  | {
      type: ConfiguratorActionType.CHANGE_PAGE_COUNT;
      payload: { value: number | null };
    }
  | {
      type: ConfiguratorActionType.SET_POD_PACKAGES;
      payload: { podPackages: PodPackage[] };
    }
  | {
      type: ConfiguratorActionType.UPDATE_CONFIGURATION;
      payload: { options: Partial<ConfiguratorState> };
    };

export const configuratorReducer = (
  state: ConfiguratorState,
  action: ConfiguratorAction
): ConfiguratorState => {
  switch (action.type) {
    case ConfiguratorActionType.RESET_CONFIGURATION:
      return {
        bindingType: null,
        coverFinish: null,
        interiorInk: null,
        interiorPaper: null,
        pageCount: null,
        podPackages: state.podPackages,
        trimSize: null,
        ...(action.payload?.configuration ?? {}),
      };
    case ConfiguratorActionType.SET_POD_PACKAGES:
      return {
        ...state,
        bindingType: null,
        coverFinish: null,
        interiorInk: null,
        interiorPaper: null,
        pageCount: null,
        trimSize: null,
        podPackages: action.payload.podPackages,
      };
    case ConfiguratorActionType.CHANGE_POD_OPTION:
      return {
        ...state,
        [action.payload.option]: action.payload.value,
      };
    case ConfiguratorActionType.CHANGE_PAGE_COUNT:
      return {
        ...state,
        pageCount: action.payload.value,
      };
    case ConfiguratorActionType.UPDATE_CONFIGURATION:
      return {
        ...state,
        ...action.payload.options,
      };
    default:
      return state;
  }
};
