import classNames from "classnames";
import React, { CSSProperties } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { isEnterOrSpaceKeyEvent } from "../../utils/isEnterOrSpaceKeyEvent";

export interface IconProps {
  dataTestId?: string;
  iconName: Icons;
  color?: Colors;
  className?: string;
  style?: CSSProperties;
  size?: number;
  onClick?: (event: React.MouseEvent | React.KeyboardEvent) => void;
  ariaLabel?: string;
  ariaRole?: string;
  children?: React.ReactNode;
}

export const Icon: React.FunctionComponent<IconProps> = ({
  iconName,
  className,
  color,
  onClick,
  style,
  size,
  dataTestId,
  children,
  ariaLabel,
  ariaRole,
}) => {
  const defaultStyle: React.CSSProperties = {};

  if (size) {
    defaultStyle.fontSize = size;
  }

  if (color) {
    defaultStyle.color = color;
  }

  return (
    <i
      data-testid={dataTestId}
      className={classNames(`icon-${iconName}`, className)}
      style={{
        ...defaultStyle,
        ...style,
      }}
      onClick={onClick}
      role={ariaRole ?? "img"}
      aria-label={ariaLabel ?? iconName}
      onKeyDown={(e) => {
        if (onClick && isEnterOrSpaceKeyEvent(e)) {
          onClick(e);
        }
      }}
    >
      {children}
    </i>
  );
};
