import classNames from "classnames";
import React from "react";

import styles from "./MoneyDisplay.module.scss";

export interface MoneyDisplayProps {
  money?: {
    amount?: number | null;
    currency?: string | null;
  } | null;
  onlyAmount?: boolean;
  locale?: string;
  className?: string;
  fallback?: string;
}

export const MoneyDisplay: React.FC<MoneyDisplayProps> = ({
  money,
  className,
  fallback = "-",
  locale = "en",
  onlyAmount,
}) => {
  const nf = new Intl.NumberFormat(locale, {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });

  const amount = money?.amount || money?.amount === 0 ? nf.format(money.amount) : null;

  if (onlyAmount && amount) return <>{amount}</>;

  const value = money?.currency && amount ? `${amount} ${money.currency}` : `${fallback}`;

  return (
    <React.Fragment>
      {className ? <span className={classNames(className, styles.money)}>{value}</span> : value}
    </React.Fragment>
  );
};
