import React from "react";

import { Colors } from "../../constants";
import { AccordionForm } from "../AccordionForm/AccordionForm";
import { Link, LinkProps } from "../Link/Link";
import { PricingCategoryData, PricingTable } from "../PricingTable/PricingTable";
import styles from "./PricingTableAccordionForm.module.scss";

export interface PricingTableAccordionFormProps {
  dataTestId?: string;
  title: string;
  description?: string;
  logo?: string;
  condensedLogo?: boolean;
  footnote?: React.ReactNode | React.ReactNode[];
  helperLink?: LinkProps;
  withCheckbox: boolean;
  value?: string;
  name?: string;
  currencies: string[];
  selected: boolean;
  disabled?: boolean;
  initiallyExpanded?: boolean;
  pricingInfo: PricingData;
  onChange?: () => void;
}

export interface PricingData {
  channel: string;
  data?: PricingCategoryData[];
}

export const PricingTableAccordionForm: React.FC<PricingTableAccordionFormProps> = ({
  title,
  description,
  logo,
  condensedLogo,
  footnote,
  helperLink,
  value,
  name,
  selected,
  disabled,
  initiallyExpanded = true,
  withCheckbox,
  pricingInfo,
  currencies,
  onChange,
  dataTestId,
}) => {
  const [expanded, setIsExpanded] = React.useState(initiallyExpanded && selected);

  const handleClick = () => {
    if (!disabled && withCheckbox && onChange) {
      onChange();
    }

    setIsExpanded(!expanded);
  };

  React.useEffect(() => {
    if (disabled && initiallyExpanded) {
      setIsExpanded(false);
    }
  }, [disabled, initiallyExpanded]);

  return (
    <div className={styles.container}>
      <AccordionForm
        type={withCheckbox ? "checkbox" : "noCheck"}
        title={title}
        labelStyle={{ color: Colors.AlmostBlack }}
        value={value}
        name={name}
        checked={selected}
        onClick={() => setIsExpanded(!expanded)}
        onChange={handleClick}
        collapsed={!expanded}
        dataTestId={dataTestId}
        disabled={disabled}
        variant="pricing"
        description={description}
        logo={logo}
        condensedLogo={condensedLogo}
      >
        {pricingInfo.data && (
          <div className={styles.dropdown}>
            <PricingTable
              channelSelected={!!selected}
              tableCollapsed={!expanded}
              currencies={currencies}
              prices={pricingInfo.data}
            />
          </div>
        )}
      </AccordionForm>
      {footnote &&
        (Array.isArray(footnote) ? (
          footnote.map((text, index) => {
            const starCharacterWidth = 5.11;
            const baseStarSpace = -8;

            return (
              <div key={index} className={styles.footnote}>
                <span
                  className={styles.text}
                  style={{
                    left: baseStarSpace + -starCharacterWidth * index,
                  }}
                >
                  {"*".repeat(index + 1)}
                </span>
                {text}
              </div>
            );
          })
        ) : (
          <div className={styles.footnote}>
            <span>*</span>
            {footnote}
          </div>
        ))}
      {helperLink && <Link className={styles.helperLink} {...helperLink} />}
    </div>
  );
};
