import classNames from "classnames";
import React, { useEffect, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../extensions";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import {
  DistributionChannelsEnum,
  Money,
  Pricing,
  ProductTypeEnum,
  QueryLike,
} from "../PricingCalculator/types";
import { DistroPanel } from "./Panels/DistroPanel";
import { LuluDirectPanel } from "./Panels/LuluDirectPanel";
import styles from "./RevenueCalculator.module.scss";
import { usePriceCalculation } from "./usePriceCalculation";

export type PanelId = "distro" | "luluDirect";

export interface RevenueCalculatorProps {
  dataTestId?: string;
  locale: string;
  baseCurrency: string;
  podPackageId: string;
  podPackageIsEligibleForGlobalDistro: boolean;
  pageCount: number;
  productType: ProductTypeEnum;
  priceMapQuery: QueryLike<Pricing | null>;
  manufacturingCostQuery: QueryLike<Money | null>;
  listPriceBoundsQuery: QueryLike<{
    minimumPrices: Money[];
    maximumPrices: Money[];
  } | null>;
  onRequestPriceMap: (
    podPackageId: string,
    pageCount: number,
    listPrice?: Money,
    channels?: DistributionChannelsEnum[],
    currency?: string
  ) => void;
  onRequestListPriceBounds: (
    podPackageId: string,
    pageCount: number,
    projectType: ProductTypeEnum
  ) => void;
}

const getPanelComponentByPanelId = (panelId: PanelId) => {
  switch (panelId) {
    case "distro":
      return DistroPanel;
    case "luluDirect":
      return LuluDirectPanel;
  }
};

const PanelTab = ({
  name,
  id,
  active,
  onChange,
  dataTestId,
}: {
  id: PanelId;
  name: string;
  active: boolean;
  onChange: (id: PanelId) => void;
  dataTestId?: string;
}) => (
  <NakedButton
    className={classNames(styles.panelItem, {
      [styles.activePanelItem]: active,
    })}
    onClick={() => onChange(id)}
    data-testid={dataTestId}
  >
    {name}
  </NakedButton>
);

export const RevenueCalculator: React.FC<RevenueCalculatorProps> = ({
  locale,
  podPackageId,
  podPackageIsEligibleForGlobalDistro,
  pageCount,
  productType,
  baseCurrency,
  priceMapQuery,
  listPriceBoundsQuery,
  manufacturingCostQuery,
  onRequestListPriceBounds,
  onRequestPriceMap,
}) => {
  const { _textDistro, _textLuluDirect } = useCopy("RevenueCalculator");
  const [selectedPanel, setSelectedPanel] = useState<PanelId>("distro");
  const [isGlobalDistro, setIsGlobalDistro] = useState(false);
  const [directPrice, setDirectPrice] = useState<number>();
  const [initialDirectPricing, setInitialDirectPricing] = useState<Pricing>();

  const { error, distroPrice, setDistroPrice, validatePrice, calculateMinMax } =
    usePriceCalculation(baseCurrency, listPriceBoundsQuery, manufacturingCostQuery);

  const isDirect = selectedPanel === "luluDirect";

  useEffect(
    function requestData() {
      // request min/max. Only way to get max price. min price is global distro.
      onRequestListPriceBounds(podPackageId, pageCount, productType);

      const channels: DistributionChannelsEnum[] =
        isGlobalDistro && podPackageIsEligibleForGlobalDistro
          ? ["LULU", "GLOBALREACH", "LULU_DIRECT"]
          : ["LULU", "LULU_DIRECT"];

      const listPrice = distroPrice
        ? {
            currency: baseCurrency,
            amount: distroPrice,
          }
        : undefined;

      onRequestPriceMap(podPackageId, pageCount, listPrice, channels);
    },
    [
      onRequestListPriceBounds,
      onRequestPriceMap,
      podPackageId,
      pageCount,
      productType,
      isGlobalDistro,
      distroPrice,
      baseCurrency,
      podPackageIsEligibleForGlobalDistro,
    ]
  );

  useEffect(
    function initDirectPricing() {
      if (
        priceMapQuery.data &&
        priceMapQuery.data.revenue?.find((r) => r.channel === "LULU_DIRECT")
      ) {
        setInitialDirectPricing(priceMapQuery.data);
      }
    },
    [priceMapQuery.data]
  );

  useEffect(
    // if the pod packages changes, we re-calculate the price map
    // as the currently entered price could be outside the min/max bounds we need to validate again
    function revalidateInput() {
      if (
        priceMapQuery.data &&
        listPriceBoundsQuery.data &&
        selectedPanel !== "luluDirect" &&
        distroPrice
      ) {
        if (!validatePrice(distroPrice, isGlobalDistro)) {
          return;
        }
      }
    },
    [
      priceMapQuery.data,
      listPriceBoundsQuery.data,
      selectedPanel,
      distroPrice,
      isGlobalDistro,
      validatePrice,
    ]
  );

  useEffect(() => {
    if (isGlobalDistro && !podPackageIsEligibleForGlobalDistro) {
      setIsGlobalDistro(false);
    }
  }, [podPackageIsEligibleForGlobalDistro, isGlobalDistro]);

  const handleListPriceChange = useDebouncedCallback(
    (newListPrice: number, includeGlobalDistro?: boolean) => {
      if (!isDirect) {
        // keep distro selection if e.g. podPackage changes, so we need to re-fetch the initial price map
        setIsGlobalDistro(!!includeGlobalDistro);

        if (!validatePrice(newListPrice, includeGlobalDistro)) {
          return;
        }
      }

      const isValidPrice = !isNaN(newListPrice);
      // keep entered price in local state to re-populate tabs when switched
      if (isDirect) {
        // direct revenue is calculated on the client - no further requests needed
        isValidPrice && setDirectPrice(newListPrice);
        return;
      }

      isValidPrice && setDistroPrice(newListPrice);
    },
    500
  );

  const { distroMinimumListPrice, distroMaximumListPrice } = calculateMinMax(
    isGlobalDistro && podPackageIsEligibleForGlobalDistro
  );

  if (!priceMapQuery.data || !distroMaximumListPrice) return null;

  const Panel = getPanelComponentByPanelId(selectedPanel);

  const testIdPrefix = "revenue-calculator";

  return (
    <div data-testid={testIdPrefix}>
      <div className={styles.panelSelection}>
        <PanelTab
          id="distro"
          name={_textDistro}
          active={selectedPanel === "distro"}
          onChange={setSelectedPanel}
          dataTestId={`${testIdPrefix}-toggle-distro`}
        />
        <PanelTab
          id="luluDirect"
          name={_textLuluDirect}
          active={selectedPanel === "luluDirect"}
          onChange={setSelectedPanel}
          dataTestId={`${testIdPrefix}-toggle-direct`}
        />
      </div>
      <Panel
        baseCurrency={baseCurrency}
        minimumListPrice={distroMinimumListPrice}
        pricing={isDirect ? initialDirectPricing : priceMapQuery.data}
        loading={priceMapQuery.loading}
        onListPriceChange={handleListPriceChange}
        locale={locale}
        error={error}
        initialPriceAmount={isDirect ? directPrice : distroPrice}
        isGlobalDistro={isGlobalDistro}
        eligibleForGlobalDistro={podPackageIsEligibleForGlobalDistro}
      />
    </div>
  );
};
