import React, { CSSProperties } from "react";

import { Link } from "../Link/Link";
import styles from "./Breadcrumbs.module.scss";

export type BreadcrumbsProps = {
  dataTestId?: string;
  containerStyles?: CSSProperties;
  backLinks: { name: string; url: string }[];
  currentPage: string;
};

export const Breadcrumbs: React.FC<BreadcrumbsProps> = ({
  dataTestId,
  containerStyles,
  backLinks,
  currentPage,
}) => (
  <div data-testid={dataTestId} className={styles.breadcrumbsContainer} style={containerStyles}>
    {backLinks.map((link) => (
      <div className={styles.breadcrumb} key={link.name}>
        <Link className={styles.link} name={link.name} url={link.url} />
        <span className={styles.slash}>/</span>
      </div>
    ))}
    <span className={styles.currentPage}>{currentPage}</span>
  </div>
);
