import classNames from "classnames";
import React from "react";

import styles from "./Badge.module.scss";

export type BadgeProps = {
  dataTestId?: string;
  className?: string;
  style?: React.CSSProperties;
} & (
  | {
      number?: number;
      bookBadge?: boolean;
      text?: never;
      variant?: never;
    }
  | {
      text?: string;
      variant?: "info" | "success" | "error" | "neutral" | "muted" | "outlined";
      number?: never;
      bookBadge?: never;
    }
);

export const Badge: React.FC<BadgeProps> = ({ dataTestId, className, style, ...props }) => {
  if ("number" in props) {
    const { number, bookBadge } = props;
    const badgeClass = classNames(
      styles.number,
      { [styles.bookBadge]: bookBadge, [styles.cart]: !bookBadge },
      className
    );
    return (
      <span data-testid={dataTestId} className={badgeClass} style={style}>
        {number}
      </span>
    );
  }

  if ("text" in props) {
    const { variant = "neutral" } = props;
    const badgeClass = classNames(styles.text, styles[variant], className);
    return (
      <span data-testid={dataTestId} className={badgeClass} style={style}>
        {props.text}
      </span>
    );
  }

  return <div data-testid={dataTestId} className={styles.dot} style={style} />;
};
