import classNames from "classnames";
import React from "react";

import { Icon } from "../../amber/components/Icon/Icon";
import { Colors } from "../../amber/constants/colors";
import { Icons } from "../../amber/icon-types";
import styles from "./AmberButton.module.scss";

export type AmberButtonProps = {
  additionalHtmlProps?: Omit<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    "className" | "style" | "disabled" | "onClick" | "color"
  >;
  children?: React.ReactNode;
  dataTestId?: string;
  variant?: "primary" | "secondary" | "outline" | "destructive" | "contrast" | "ghost";
  size?: "small" | "medium" | "large";
  iconLeft?: Icons;
  iconRight?: Icons;
  iconColor?: Colors;
  className?: string;
  style?: React.CSSProperties;
  disabled?: boolean;
  loading?: boolean;
  onClick?: () => void;
};

export const AmberButton = React.forwardRef<HTMLButtonElement, AmberButtonProps>(
  (
    {
      additionalHtmlProps,
      children,
      dataTestId,
      variant = "primary",
      size = "medium",
      iconLeft,
      iconRight,
      iconColor,
      className,
      loading,
      disabled,
      ...props
    },
    ref
  ) => {
    const buttonClass = classNames(styles.button, styles[variant], styles[size], className);

    const spinnerIcon = (
      <Icon
        dataTestId={dataTestId && `${dataTestId}-spinner-icon`}
        className={classNames(styles.spinner, styles.icon, styles[variant])}
        iconName={Icons.Spinner}
        color={Colors.ContentAccentGreen}
      />
    );

    // Icon button
    if (iconLeft || iconRight) {
      const iconButtonClass = classNames(buttonClass, {
        [styles.iconLeft]: iconLeft,
        [styles.iconRight]: iconRight,
      });

      const getIconColor = () => {
        if (!disabled) return iconColor;

        switch (variant) {
          case "primary":
            return Colors.SurfacePrimaryContrast;
          case "secondary":
          case "outline":
          case "ghost":
            return Colors.ContentPrimaryDisabled;
          case "destructive":
            return Colors.ContentPrimaryContrast;
        }
      };

      const renderIcon = (icon?: Icons) =>
        icon ? (
          loading ? (
            spinnerIcon
          ) : (
            <Icon
              dataTestId={dataTestId && `${dataTestId}-icon`}
              className={styles.icon}
              color={getIconColor()}
              iconName={icon}
            />
          )
        ) : (
          ""
        );

      return (
        <button
          ref={ref}
          data-testid={dataTestId}
          className={iconButtonClass}
          disabled={disabled || loading}
          {...props}
          {...additionalHtmlProps}
        >
          {renderIcon(iconLeft)}
          <span className={styles.text}>{children}</span>
          {renderIcon(iconRight)}
        </button>
      );
    }

    return (
      <button
        ref={ref}
        data-testid={dataTestId}
        className={buttonClass}
        disabled={disabled || loading}
        {...props}
        {...additionalHtmlProps}
      >
        {loading && <div className={styles.spinnerContainer}>{spinnerIcon}</div>}
        <span className={classNames(styles.text, { [styles.hidden]: loading })}>{children}</span>
      </button>
    );
  }
);
