import { useContext } from "react";

import { CopyContext } from "../extensions";
import { Option } from "../ui-components";
import { IMAGES_BASE_URL } from "../utils/images";

export interface TrimSizeType extends Option {
  previewImage: string;
  previewRetinaImage: string;
  mobilePreviewImage: string;
  comparisonImage: string;
  mobileComparisonImage: string;
  mobilePreviewRetinaImage: string;
  comparisonRetinaImage: string;
  mobileComparisonRetinaImage: string;
  description: string;
  availableForGlobalDistribution: boolean;
}

export const useTrimSizes: () => TrimSizeType[] = () => {
  const { BookSizeSelector: translations } = useContext(CopyContext);
  const trimSizes = [
    "PocketBook",
    "Novella",
    "Digest",
    "A5",
    "Us Trade",
    "Royal",
    "Comic Book",
    "Executive",
    "Crown Quarto",
    "A4",
    "Us Letter",
    "Small Square",
    "Square",
    "Small Landscape",
    "Us Letter Landscape",
    "A4 Landscape",
  ];

  const globalDistributionTrimSizes = [
    "PocketBook",
    "Novella",
    "Digest",
    "A5",
    "Us Trade",
    "Royal",
    "Comic Book",
    "Executive",
    "Crown Quarto",
    "A4",
    "Us Letter",
    "Small Square",
    "Square",
  ];

  return trimSizes.map((trimSize) => {
    const trimSizeStartingWithLowerCase =
      trimSize[0].toLowerCase() + trimSize.slice(1).split(" ").join("");
    const trimSizeFileNamePart = trimSize.toLowerCase().split(" ").join("-");

    return {
      label: translations[`_${trimSizeStartingWithLowerCase}Label` as keyof typeof translations],
      value: translations[`_${trimSizeStartingWithLowerCase}Value` as keyof typeof translations],
      previewImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Desktop/White/1x/desktop-white-${trimSizeFileNamePart}-1x.webp`,
      previewRetinaImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Desktop/White/2x/desktop-white-${trimSizeFileNamePart}-2x.webp`,
      mobilePreviewImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Mobile/White/1x/mobile-white-${trimSizeFileNamePart}-1x.webp`,
      comparisonImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Desktop/Gray/1x/desktop-gray-${trimSizeFileNamePart}-1x.webp`,
      mobileComparisonImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Mobile/Gray/1x/mobile-gray-${trimSizeFileNamePart}-1x.webp`,
      mobilePreviewRetinaImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Mobile/White/2x/mobile-white-${trimSizeFileNamePart}-2x.webp`,
      comparisonRetinaImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Desktop/Gray/2x/desktop-gray-${trimSizeFileNamePart}-2x.webp`,
      mobileComparisonRetinaImage: `${IMAGES_BASE_URL}BookSizeSelector/Covers/Mobile/Gray/2x/mobile-gray-${trimSizeFileNamePart}-2x.webp`,
      description:
        translations[`_${trimSizeStartingWithLowerCase}Description` as keyof typeof translations],
      disabled: false,
      availableForGlobalDistribution: globalDistributionTrimSizes.includes(trimSize),
    };
  });
};
