import "./reactDatePicker.scss";

import classNames from "classnames";
import { de, es, fr, it } from "date-fns/locale";
import React, { forwardRef, Ref, useEffect, useRef } from "react";
import ReactDatePicker, { ReactDatePickerProps, registerLocale } from "react-datepicker";

import { useCopy } from "../../extensions/CopyContext";
import { Icons } from "../../icon-types";
import { Locale } from "../../utils/locale";
import { Icon } from "../Icon/Icon";
import styles from "./DatePicker.module.scss";

export interface DatePickerProps extends Omit<ReactDatePickerProps, "onChange"> {
  dataTestId?: string;
  name?: string;
  className?: string;
  icon?: Icons;
  date: Date | null;
  label: string;
  locale: Locale;
  maxDate?: Date;
  minDate?: Date;
  onChange(arg: Date): void;
}

type InputProps = {
  value: string;
  onClick: (event: React.MouseEvent<HTMLInputElement, MouseEvent>) => void;
};

export const DatePicker: React.FC<DatePickerProps> = ({
  dataTestId,
  name,
  className,
  icon,
  date,
  label,
  locale,
  maxDate,
  minDate,
  onChange,
}) => {
  const ref = useRef(null);
  const { _selectDate } = useCopy("DatePicker");
  const wrapperClassNames = classNames(
    "field__input-wrapper",
    "field__input-wrapper--select",
    className,
    {
      "field__input-wrapper--with-icon": icon,
    }
  );

  const identifier = name ?? label;

  const Input = forwardRef(({ value, onClick }: InputProps, ref: Ref<HTMLInputElement>) => (
    <div data-testid={dataTestId}>
      <label className="field__label field__label--visible" htmlFor={identifier}>
        {label}
      </label>
      <input
        className="field__input field__input--select"
        name={identifier}
        id={identifier}
        onClick={onClick}
        onChange={() => null}
        value={value ? new Date(value).toLocaleDateString(locale) : ""}
        autoComplete="off"
        ref={ref}
        placeholder={_selectDate}
      />
      {icon && <Icon iconName={icon} className="input-icon" size={20} />}
      <div className="field__caret" />
    </div>
  ));
  Input.displayName = "Input";

  return (
    <ReactDatePicker
      wrapperClassName={wrapperClassNames}
      locale={locale}
      selected={date}
      maxDate={maxDate}
      minDate={minDate}
      onChange={(date) => {
        if (date instanceof Date) {
          onChange(date);
        }
      }}
      customInput={
        <Input value={date?.toLocaleDateString(locale) ?? ""} onClick={() => null} ref={ref} />
      }
      renderCustomHeader={({ date, decreaseMonth, increaseMonth }) => (
        <>
          <div className={styles.header}>
            <div className={styles.headerYear}>{date.getFullYear()}</div>
            <div className={styles.headerDate}>{`${date.toLocaleDateString(locale, {
              weekday: "short",
            })} ${date.toLocaleDateString(locale, {
              day: "numeric",
              month: "short",
            })}`}</div>
          </div>
          <div className={styles.headerMonthSelect}>
            <Icon
              iconName={Icons.ArrowLinkLeftLarge}
              className={styles.arrow}
              onClick={decreaseMonth}
              style={{
                visibility: !minDate || (minDate && date > minDate) ? "visible" : "hidden",
              }}
            />
            {date.toLocaleDateString(locale, {
              year: "numeric",
              month: "long",
            })}
            <Icon
              iconName={Icons.ArrowLinkRightLarge}
              className={styles.arrow}
              onClick={increaseMonth}
            />
          </div>
        </>
      )}
    />
  );
};

export const MonthPicker: React.FC<DatePickerProps> = ({
  dataTestId,
  name,
  className,
  icon,
  date,
  label,
  locale = "en",
  onChange,
  maxDate,
  minDate,
  placeholderText,
}) => {
  const ref = useRef(null);
  const wrapperClassNames = classNames(
    "field__input-wrapper",
    "field__input-wrapper--select",
    className,
    {
      "field__input-wrapper--with-icon": icon,
    }
  );

  useEffect(() => {
    const loadLocale = async (locale: Locale) => {
      try {
        const dateLocale = {
          de,
          fr,
          it,
          es,
        };
        registerLocale(`${locale}`, dateLocale[locale as keyof typeof dateLocale]);
      } catch (error) {
        throw Error(error as string);
      }
    };

    if (locale !== "en") {
      loadLocale(locale);
    }
  }, [locale]);

  const identifier = name ?? label;

  const Input = forwardRef(({ value, onClick }: InputProps, ref: Ref<HTMLInputElement>) => (
    <div data-testid={dataTestId}>
      <label className="field__label field__label--visible" htmlFor={identifier}>
        {label}
      </label>
      <input
        className="field__input field__input--select"
        id={identifier}
        name={identifier}
        onClick={onClick}
        onChange={() => null}
        value={
          date
            ? new Date(value).toLocaleDateString(locale, {
                year: "numeric",
                month: "long",
              })
            : ""
        }
        autoComplete="off"
        ref={ref}
        placeholder={placeholderText}
      />
      {icon && <Icon iconName={icon} className="input-icon" size={20} />}
      <div className="field__caret" />
    </div>
  ));
  Input.displayName = "Input";

  return (
    <ReactDatePicker
      wrapperClassName={wrapperClassNames}
      showMonthYearPicker={true}
      locale={locale}
      selected={date}
      maxDate={maxDate}
      minDate={minDate}
      onChange={(date) => {
        if (date instanceof Date) {
          onChange(date);
        }
      }}
      customInput={<Input value={date?.toString() ?? ""} onClick={() => null} ref={ref} />}
      renderCustomHeader={({ date, decreaseYear, increaseYear }) => (
        <>
          <div className={styles.monthPickerHeader}>
            <Icon
              iconName={Icons.ArrowLinkLeftLarge}
              className={styles.arrow}
              onClick={decreaseYear}
              style={{
                visibility:
                  !minDate || date.getFullYear() > minDate.getFullYear() ? "visible" : "hidden",
              }}
            />

            {date.toLocaleDateString(locale, { year: "numeric" })}
            <Icon
              iconName={Icons.ArrowLinkRightLarge}
              className={styles.arrow}
              onClick={increaseYear}
            />
          </div>
        </>
      )}
    />
  );
};
