import React from "react";

import { ReactComponent as Logo } from "../../styles/assets/logo.svg";
import { Button } from "../Button/Button";
import styles from "./ErrorPage.module.scss";

export interface ErrorPageProps {
  header?: string;
  description?: string;
  actionText?: string;
  errorId?: string;
  actionFunction?: () => void;
}

export const ErrorPage: React.FC<ErrorPageProps> = ({
  header = "Internal Server Error",
  description = "Someone tripped over a cord. We're working on getting everything plugged in again.",
  actionText = "Back to Lulu",
  errorId,
  actionFunction = () => window.location.assign("/"),
}) => (
  <div className={styles.errorPage}>
    <div className={styles.container}>
      <Logo className={styles.logo} />
      <h1 className={`large ${styles.header}`}>{header}</h1>
      <h2 className={styles.description}>{description}</h2>
      <Button buttonSize="large" onClick={actionFunction}>
        {actionText}
      </Button>
      {errorId && <p className="almost-black-text-70">Error ID: {errorId}</p>}
    </div>
  </div>
);
