import { documentToReactComponents, Options } from "@contentful/rich-text-react-renderer";
import { BLOCKS, Document } from "@contentful/rich-text-types";
import classNames from "classnames";
import React from "react";

import { mapContentfulColorToStyleProp } from "../../utils/colors";

export interface RichTextFieldProps {
  textBlockContainerClassNames?: string;
  textAlignment: "center" | "right" | "left" | "justify";
  richTextContent: {
    json: Document;
  };
  headingTextColor?: string;
  headingClasses?: string;
  normalTextClasses?: string;
}

export interface RichTextContainerProps {
  dataTestId?: string;
  richTextContainerClassNames?: string;
  standaloneEntry: boolean;
  richTextFieldsCollection: { items: RichTextFieldProps[] };
}

const setRichTextRenderOptions = (
  headingClasses?: string,
  headingTextColor?: string,
  normalTextClasses?: string
) => {
  const textColor = headingTextColor
    ? { color: mapContentfulColorToStyleProp(headingTextColor) }
    : undefined;

  const richTextRenderOptions: Options = {
    renderNode: {
      [BLOCKS.HEADING_1]: (_node, children) => (
        <h1 className={classNames("contentful-h1", headingClasses)} style={textColor}>
          {children}
        </h1>
      ),
      [BLOCKS.HEADING_2]: (_node, children) => (
        <h2 className={classNames("contentful-h2", headingClasses)} style={textColor}>
          {children}
        </h2>
      ),
      [BLOCKS.HEADING_3]: (_node, children) => (
        <h3 className={classNames("contentful-h3", headingClasses)} style={textColor}>
          {children}
        </h3>
      ),
      [BLOCKS.HEADING_4]: (_node, children) => (
        <h4 className={classNames("contentful-h4", headingClasses)} style={textColor}>
          {children}
        </h4>
      ),
      [BLOCKS.HEADING_5]: (_node, children) => (
        <h5 className={classNames("contentful-h5", headingClasses)} style={textColor}>
          {children}
        </h5>
      ),
      // Prevents Contentful's rich-text-react-renderer from rendering empty paragraphs
      [BLOCKS.PARAGRAPH]: (node, children) =>
        "value" in node?.content[0] &&
        node.content[0].value && (
          <p className={classNames("contentful-body", normalTextClasses)}>{children}</p>
        ),
    },
  };
  return richTextRenderOptions;
};

export const RichTextContainer: React.FC<RichTextContainerProps> = ({
  dataTestId,
  standaloneEntry,
  richTextContainerClassNames,
  richTextFieldsCollection: { items },
}) => {
  const richTextContainerClasses = classNames(
    {
      "max-width-container res-top-padding-48 res-bottom-padding-48": standaloneEntry,
    },
    richTextContainerClassNames
  );

  return (
    <section className={richTextContainerClasses} data-testid={dataTestId}>
      {items.map((field, i) => {
        const textBlockContainerClasses = classNames(
          "rich-text-container",
          {
            [`text-${field.textAlignment}`]: field.textAlignment,
          },
          field.textBlockContainerClassNames
        );

        return (
          <div className={textBlockContainerClasses} key={`field-${i}`}>
            {documentToReactComponents(
              field.richTextContent.json,
              setRichTextRenderOptions(
                field.headingClasses,
                field.headingTextColor,
                field.normalTextClasses
              )
            )}
          </div>
        );
      })}
    </section>
  );
};
