import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { mapContentfulColorToStyleProp } from "../../utils";
import { ContentfulButtonLinkProps, LinkedButton } from "../ButtonLink/ButtonLink";
import { ImageWithMetadata, ImageWithMetadataProps } from "../ImageWithMetadata/ImageWithMetadata";
import styles from "./ImageBanner.module.scss";

export interface ImageBannerProps {
  contentBlockClassNames?: string;
  dataTestId?: string;
  headingText?: string;
  imageBannerClassNames?: string;
  subheadingText?: string;
  backgroundColor?: string;
  whiteText: boolean;
  withMaxWidthContainer?: boolean;
  linkedButtonRef?: ContentfulButtonLinkProps;
  backgroundImageRef: ImageWithMetadataProps;
  gridClassNames?: string;
  borderRadius?: number;
}

export const ImageBanner: React.FC<ImageBannerProps> = ({
  backgroundImageRef,
  contentBlockClassNames,
  dataTestId,
  headingText,
  imageBannerClassNames,
  linkedButtonRef,
  subheadingText,
  whiteText,
  backgroundColor,
  withMaxWidthContainer = true,
  gridClassNames,
  borderRadius = 0,
}) => {
  const defaultImageBannerClassNames = "res-vertical-margins-96";
  const imageBannerClasses = classNames(
    imageBannerClassNames || defaultImageBannerClassNames,
    backgroundImageRef?.imageClasses,
    {
      "grid-12": !!gridClassNames,
    }
  );
  const defaultContentBlockClassNames =
    "res-vertical-padding-48 res-left-padding-48 col-4 col-xl-6 col-l-8";
  const contentBlockClasses = classNames(
    styles.contentContainer,
    contentBlockClassNames || defaultContentBlockClassNames
  );
  const headingClasses = classNames("contentful-h2", "large", "top", {
    "white-text": whiteText,
  });
  const subheadingClasses = classNames("contentful-h2", "top", "subhead", {
    "white-text": whiteText,
  });
  const defaultLinkedButtonClassNames = "res-vertical-margins-16";
  const buttonLinkContainerClasses = classNames(
    {
      [`text-${linkedButtonRef?.buttonAlignment}`]: linkedButtonRef?.buttonAlignment,
    },
    linkedButtonRef?.linkedButtonClassNames || defaultLinkedButtonClassNames
  );

  const ImageBannerContent = (
    <div
      className={`${styles.imageBannerWrapper} grid-12 ${
        !withMaxWidthContainer && gridClassNames ? gridClassNames : ""
      }`}
      style={{ borderRadius, background: Colors.DarkGray6 }}
    >
      <ImageWithMetadata {...backgroundImageRef} />
      <div className={contentBlockClasses}>
        {headingText && <h2 className={headingClasses}>{headingText}</h2>}
        {subheadingText && <h2 className={subheadingClasses}>{subheadingText}</h2>}
        {linkedButtonRef && (
          <div className={buttonLinkContainerClasses}>
            <LinkedButton
              dataTestId={dataTestId && `${dataTestId}-button`}
              buttonSize={linkedButtonRef.buttonSize}
              hierarchy={linkedButtonRef.hierarchy}
              linkRef={linkedButtonRef.linkRef.page || linkedButtonRef.linkRef}
              buttonAlignment={linkedButtonRef.buttonAlignment}
              buttonText={linkedButtonRef.buttonText}
            />
          </div>
        )}
      </div>
    </div>
  );
  return (
    <section
      className={imageBannerClasses}
      data-testid={dataTestId}
      style={
        backgroundColor
          ? {
              background: mapContentfulColorToStyleProp(backgroundColor),
            }
          : undefined
      }
    >
      {withMaxWidthContainer ? (
        <div className={`max-width-container ${gridClassNames}`}>{ImageBannerContent}</div>
      ) : (
        ImageBannerContent
      )}
    </section>
  );
};
