import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./Checkbox.module.scss";

export interface CheckboxProps {
  dataTestId?: string;
  checked?: boolean;
  disabled?: boolean;
  lightTheme?: boolean;
  id?: string;
  label?: string | React.ReactNode;
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
  value?: number | string;
  name?: string;
  className?: string;
  resetLabelStyles?: boolean;
}

export const Checkbox: React.FC<CheckboxProps> = ({
  label,
  checked = false,
  disabled = false,
  id,
  lightTheme = false,
  dataTestId,
  onChange,
  value,
  name,
  className,
  resetLabelStyles = false,
}) => {
  const checkboxClass = classNames(styles.checkbox, className, {
    [styles.lightTheme]: lightTheme,
    [styles.disabled]: disabled,
  });

  const iconWrapperClasses = classNames(styles.iconWrapper, {
    [styles.noInputMargin]: resetLabelStyles,
  });

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (disabled) return;

    onChange?.(event);
  };

  return (
    <div className={checkboxClass} data-testid={dataTestId}>
      <span className={styles.inputWrapper}>
        <label className={resetLabelStyles ? styles.noLabelPadding : undefined}>
          <input
            tabIndex={onChange ? 0 : -1}
            aria-checked={checked}
            checked={checked}
            disabled={disabled}
            id={id}
            onChange={(event) => handleChange(event)}
            type="checkbox"
            value={value}
            name={name}
            data-testid={dataTestId ? `${dataTestId}-checkbox` : undefined}
          />
          <span className={iconWrapperClasses}>
            <Icon iconName={Icons.GeneralCheckMediumBold} />
          </span>
          {label && label}
        </label>
      </span>
    </div>
  );
};
