import React, { useContext } from "react";

import { AdvancedProductSpecsTable } from "../AdvancedProductSpecsTable/AdvancedProductSpecsTable";
import { PricingCalculatorContext } from "./PricingCalculatorContext";

export const AdvancedProductSpecsTableAdapter: React.FC = () => {
  const { podPackageDimensionsQuery, podPackageId, state } = useContext(PricingCalculatorContext);

  const podPackage = state.podPackages.find((pod) => pod.id === podPackageId);
  const dimensions = podPackageDimensionsQuery?.data;
  const dimensionsIn = dimensions?.find((d) => d.unit === "INCH");
  const dimensionMm = dimensions?.find((d) => d.unit === "MILLIMETER");

  return (
    <AdvancedProductSpecsTable
      dataTestId="advanced-product-specs-table"
      dimensionsIn={dimensionsIn}
      dimensionsMm={dimensionMm}
      minPages={podPackage?.minPages ?? undefined}
      maxPages={podPackage?.maxPages ?? undefined}
      podPackageId={podPackageId ?? undefined}
    />
  );
};
