import React, { useEffect } from "react";

import { usePrevious } from "../../hooks";
import { Icons } from "../../icon-types";
import { DatePicker, DatePickerProps } from "../DatePicker/DatePicker";

export type DateRange = {
  from: Date;
  to: Date;
};

export type DateRangePickerProps = Pick<DatePickerProps, "locale"> & {
  startDate?: Date;
  endDate?: Date;
  fromLabel?: string;
  toLabel?: string;
  onChange: (range: DateRange) => void;
  pickerClassname?: string;
  dataTestId?: string;
  maxDays?: number;
};

export const DateRangePicker: React.FC<DateRangePickerProps> = ({
  startDate,
  endDate,
  fromLabel = "From",
  toLabel = "To",
  locale = "en",
  onChange,
  pickerClassname,
  dataTestId,
  maxDays,
}) => {
  const [from, setFrom] = React.useState(startDate);
  const [to, setTo] = React.useState(endDate);
  const prevFrom = usePrevious(from);
  const prevTo = usePrevious(to);
  const [maxDate, setMaxDate] = React.useState<Date>();
  const [minDate, setMinDate] = React.useState<Date>();

  useEffect(() => {
    if (maxDays) {
      if (from && from !== prevFrom) {
        const maxDate = new Date(from);
        maxDate.setDate(from.getDate() + maxDays);
        setMaxDate(maxDate);
      }
      if (to && to !== prevTo) {
        const minDate = new Date(to);
        minDate.setDate(to.getDate() - maxDays);
        setMinDate(minDate);
      }
    }

    // we only want to call onChange if the date range has changed
    if (from && to && (from !== prevFrom || to !== prevTo)) {
      onChange({ from, to });
    }
  }, [from, to, prevFrom, prevTo, onChange, maxDays]);

  return (
    <>
      <div data-testid={dataTestId}>
        <DatePicker
          name="fromDate"
          date={from ?? null}
          minDate={minDate}
          maxDate={to ?? maxDate}
          locale={locale}
          label={fromLabel}
          icon={Icons.GeneralCalendar}
          className={pickerClassname}
          onChange={(date) => {
            setFrom(date);
          }}
        />
      </div>
      <div>
        <DatePicker
          name="toDate"
          date={to ?? null}
          minDate={from ?? minDate}
          maxDate={maxDate}
          locale={locale}
          label={toLabel}
          icon={Icons.GeneralCalendar}
          className={pickerClassname}
          onChange={(date) => {
            setTo(date);
          }}
        />
      </div>
    </>
  );
};
