import classNames from "classnames";
import React, { useContext, useEffect } from "react";
import { useState } from "react";

import { Colors } from "../../constants";
import { CopyContext } from "../../extensions";
import { TrimSizeType, useTrimSizes } from "../../hooks/useTrimSizes";
import { Icons } from "../../icon-types";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { IMAGES_BASE_URL } from "../../utils";
import { CustomSelect } from "../CustomSelect/CustomSelect";
import { Icon } from "../Icon/Icon";
import { ImageWithMetadata } from "../ImageWithMetadata/ImageWithMetadata";
import { ReferenceIconAndText } from "../ReferenceIconAndText/ReferenceIconAndText";
import { Toggle } from "../Toggle/Toggle";
import styles from "./BookSizeSelector.module.scss";

export type BookSizeSelectorProps = {
  currentLanguage: "en" | "de" | "es" | "fr" | "it";
};

export const BookSizeSelector = ({ currentLanguage }: BookSizeSelectorProps) => {
  const trimSizes = useTrimSizes();
  const {
    _availableForGlobalDistribution,
    _bookSize,
    _compareWithAnotherSize,
    _units,
    _rulers,
    _mm,
    _in,
  } = useContext(CopyContext).BookSizeSelector;

  const [unitInMillimeters, setUnitInMillimeters] = useState(false);
  const [isComparisonContainerExpanded, setIsComparisonContainerExpanded] = useState(false);
  const [currentTrimSize, setCurrentTrimSize] = useState<TrimSizeType>(trimSizes[0]);
  const [trimSizeForComparison, setTrimSizeForComparison] = useState<TrimSizeType>(trimSizes[1]);

  const getSubheadingText = (trimSizeOption: TrimSizeType) =>
    trimSizeOption.label.replace(trimSizeOption.value, "").replace("(", "").replace(")", "");
  const currentUnit = unitInMillimeters ? "Millimeters" : "Inches";
  const languagesFullNames = {
    en: "English",
    de: "German",
    it: "Italian",
    fr: "French",
    es: "Spanish",
  };

  useEffect(() => {
    if (currentTrimSize.value === trimSizeForComparison.value)
      setTrimSizeForComparison(
        trimSizes.filter((trimSize) => trimSize.value !== currentTrimSize.value)[0]
      );
  }, [currentTrimSize.value, trimSizeForComparison.value, trimSizes]);

  const generateImageUrl = (deviceType: "desktop" | "mobile") => {
    const language = languagesFullNames[currentLanguage];
    const unit = currentUnit.toLowerCase();
    return {
      url: `${IMAGES_BASE_URL}BookSizeSelector/Rulers/1x/${currentUnit}/${language}/book-size-window-${deviceType}-${unit}-${currentLanguage}-1x.webp`,
      retina: `${IMAGES_BASE_URL}BookSizeSelector/Rulers/2x/${currentUnit}/${language}/book-size-window-${deviceType}-${unit}-${currentLanguage}-2x.webp`,
    };
  };

  return (
    <>
      <div className="max-width-container">
        <div className={styles.referenceIconContainer}>
          <ReferenceIconAndText
            referenceIcon={{
              icon: "icon-distribution-yes",
              iconColor: Colors.Green,
              newTab: false,
            }}
            iconTextAlignment="right"
            referenceIconDescriptionText={_availableForGlobalDistribution}
          />
        </div>
        <div className={styles.wrapper}>
          <div className={styles.previewContainer}>
            <ImageWithMetadata
              image={generateImageUrl("desktop")}
              imageForMobile={generateImageUrl("mobile")}
              newTab={false}
              imageAltText={_rulers}
              highPriority
            />
            <ImageWithMetadata
              image={{
                url: currentTrimSize.previewImage,
                retina: currentTrimSize.previewRetinaImage,
              }}
              imageForMobile={{
                url: currentTrimSize.mobilePreviewImage,
                retina: currentTrimSize.mobilePreviewRetinaImage,
              }}
              newTab={false}
              imageAltText={currentTrimSize.label}
            />
            {isComparisonContainerExpanded && (
              <ImageWithMetadata
                image={{
                  url: trimSizeForComparison.comparisonImage,
                  retina: trimSizeForComparison.comparisonRetinaImage,
                }}
                imageForMobile={{
                  url: trimSizeForComparison.mobileComparisonImage,
                  retina: trimSizeForComparison.mobileComparisonRetinaImage,
                }}
                newTab={false}
                imageAltText={trimSizeForComparison.label}
                imageClasses={styles.comparisonImage}
              />
            )}
            <Toggle
              label={_units}
              type={"all"}
              active={unitInMillimeters}
              onChange={setUnitInMillimeters}
              activeStateLabel={_mm}
              inactiveStateLabel={_in}
              customClassNames={styles.toggle}
            />
          </div>
          <div className={styles.detailsContainer}>
            <div className={styles.infoContainer}>
              <div className={styles.header}>
                <h3 className="contentful-h3">{currentTrimSize.value}</h3>
                <h3 className="contentful-h3 subhead">{getSubheadingText(currentTrimSize)}</h3>
                {currentTrimSize.availableForGlobalDistribution && (
                  <Icon iconName={Icons.DistributionYes} color={Colors.Green} size={24} />
                )}
              </div>
              <div className={classNames("contentful-body", styles.description)}>
                {currentTrimSize.description}
              </div>
              <div className={styles.dropdownContainer}>
                <div className="field__label field__label--visible">{_bookSize}</div>
                <CustomSelect<TrimSizeType>
                  options={trimSizes.map((trimSize) => ({
                    ...trimSize,
                    isDisabled: isComparisonContainerExpanded
                      ? trimSize.value === trimSizeForComparison.value
                      : false,
                  }))}
                  defaultValue={currentTrimSize}
                  onChange={(value) => value && setCurrentTrimSize(value as TrimSizeType)}
                  ariaLabel={_bookSize}
                />
              </div>
            </div>
            <div className={styles.comparisonContainer}>
              <NakedButton
                className={styles.header}
                onClick={() => setIsComparisonContainerExpanded((v) => !v)}
              >
                <h4 className="contentful-h4">{_compareWithAnotherSize}</h4>
                <Icon
                  iconName={
                    isComparisonContainerExpanded ? Icons.DisplayCloseMedium : Icons.ArrowDownMedium
                  }
                  color={Colors.CyanBackground10}
                  size={24}
                />
              </NakedButton>
              {isComparisonContainerExpanded && (
                <div className={styles.details}>
                  <div className={styles.comparisonHeader}>
                    <h3 className="contentful-h3">{trimSizeForComparison.value}</h3>
                    <h3 className="contentful-h3 subhead">
                      {getSubheadingText(trimSizeForComparison)}
                    </h3>
                    {trimSizeForComparison.availableForGlobalDistribution && (
                      <Icon iconName={Icons.DistributionYes} color={Colors.Green} size={24} />
                    )}
                  </div>
                  <div className={styles.dropdownContainer}>
                    <label className="field__label field__label--visible">{_bookSize}</label>
                    <CustomSelect<TrimSizeType>
                      options={trimSizes.map((trimSize) => ({
                        ...trimSize,
                        isDisabled: trimSize.value === currentTrimSize.value,
                      }))}
                      defaultValue={trimSizeForComparison}
                      onChange={(value) => value && setTrimSizeForComparison(value as TrimSizeType)}
                    />
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
