import classNames from "classnames";
import React from "react";

import { Icon } from "../../amber/components/Icon/Icon";
import { Colors } from "../../amber/constants/colors";
import { Icons } from "../../amber/icon-types";
import { Badge } from "../Badge/Badge";
import styles from "./AmberIconButton.module.scss";

export type AmberIconButtonProps = {
  additionalHtmlProps?: Omit<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    "className" | "style" | "disabled" | "onClick" | "color"
  >;
  dataTestId?: string;
  iconName: Icons;
  variant?: "default" | "outline" | "ghost";
  shift?: "right" | "left";
  size?: "large" | "medium" | "small";
  badge?: number;
  selected?: boolean;
  iconColor?: Colors;
  className?: string;
  style?: React.CSSProperties;
  disabled?: boolean;
  onClick?: () => void;
} & (
  | { variant?: "default" | "outline"; ghostAccent?: never }
  | {
      variant: "ghost";
      ghostAccent?: Colors;
    }
);

export const AmberIconButton = React.forwardRef<HTMLButtonElement, AmberIconButtonProps>(
  (
    {
      additionalHtmlProps,
      dataTestId,
      iconName,
      variant = "default",
      shift,
      size = "large",
      badge,
      selected,
      iconColor = Colors.ContentPrimaryEmphasized,
      className,
      style,
      ghostAccent,
      ...props
    },
    ref
  ) => {
    const iconButtonClass = classNames(
      styles.button,
      styles[variant],
      styles[size],
      selected ? styles.selected : undefined,
      {
        [styles.shiftLeft]: shift === "left",
        [styles.shiftRight]: shift === "right",
      },
      className
    );

    const customColors = {
      "--color": iconColor,
      "--ghostAccent": ghostAccent,
    } as React.CSSProperties;

    return (
      <button
        ref={ref}
        className={classNames(styles.button, iconButtonClass)}
        style={{ ...(customColors || {}), ...style }}
        data-testid={dataTestId}
        {...props}
        {...additionalHtmlProps}
      >
        <Icon
          dataTestId={`${dataTestId}-icon`}
          className={styles.icon}
          iconName={iconName}
          size={24}
        />
        {(badge ?? 0) > 0 && (
          <Badge dataTestId={`${dataTestId}-badge`} className={styles.badge} number={badge} />
        )}
      </button>
    );
  }
);
