import React, { useEffect } from "react";
import { Helmet } from "react-helmet";

import styles from "./EmbeddedForm.module.scss";

export interface EmbeddedFormProps {
  html: string;
}

export const EmbeddedForm: React.FC<EmbeddedFormProps> = ({ html }) => {
  const isKlaviyoForm = html.includes("klaviyo-form");
  const isClickUpForm = html.includes("clickup");
  const isFreshsalesForm = html.includes("lulupressinc-org.myfreshworks.com");
  const includesScriptTag =
    !isKlaviyoForm && !isClickUpForm && !isFreshsalesForm && html.includes("<script");

  const formProvider = isClickUpForm ? "ClickUp" : isFreshsalesForm ? "Freshsales" : "Klaviyo";

  useEffect(() => {
    const formContainer = document.getElementById("formContainer");
    const scriptElement = document.createElement("script");
    let styleElement: HTMLStyleElement | null = document.createElement("style");

    if (isFreshsalesForm) {
      const parser = new DOMParser();
      const doc = parser.parseFromString(html, "text/html");
      const parsedScript = doc.querySelector("script");
      styleElement = doc.querySelector("style");

      const id = parsedScript?.getAttribute("id");
      const scriptUrl =
        parsedScript?.getAttribute("src")?.replace(".html", "").replace(".js", "") ?? ""; // removing the extension fixes a MIME type error

      if (scriptUrl) scriptElement.src = scriptUrl;
      if (id) scriptElement.id = id;
      scriptElement.crossOrigin = "anonymous";
      scriptElement.type = "text/javascript";

      formContainer?.appendChild(scriptElement);
      if (styleElement) document.body.appendChild(styleElement);
    }

    return () => {
      if (isFreshsalesForm) {
        formContainer?.removeChild(scriptElement);
        if (styleElement) document.body.removeChild(styleElement);
      }
    };
  }, [html, isFreshsalesForm]);

  const changeIframeHeight = () => {
    const iframe = document.getElementById("formIframe");
    if (iframe) {
      (iframe as HTMLIFrameElement).height =
        ((iframe as HTMLIFrameElement).contentWindow?.document.body.scrollHeight ?? 0) +
        45 + // add all extra spacing from the iframe to remove the scroll bar
        "px";
    }
  };

  useEffect(() => {
    if (includesScriptTag) window.addEventListener("resize", changeIframeHeight);

    return () => {
      if (includesScriptTag) window.removeEventListener("resize", changeIframeHeight);
    };
  }, [includesScriptTag]);

  return (
    <div className={styles.container}>
      <Helmet>
        {isKlaviyoForm && (
          <script
            type="text/javascript"
            src="https://static.klaviyo.com/onsite/js/klaviyo.js?company_id=QUUSRr"
          />
        )}
        {isClickUpForm && (
          <script src="https://app-cdn.clickup.com/assets/js/forms-embed/v1.js">
            window.iFrameResize();
          </script>
        )}
      </Helmet>
      {includesScriptTag ? (
        <iframe
          id="formIframe"
          srcDoc={html}
          onLoad={changeIframeHeight}
          className={styles.iframe}
          data-testid={`embeddedScriptForm`}
          title="embeddedForm"
        />
      ) : (
        <div
          dangerouslySetInnerHTML={isFreshsalesForm ? undefined : { __html: html }}
          data-testid={`embedded${formProvider}Form`}
          id="formContainer"
        />
      )}
    </div>
  );
};
