import classNames from "classnames";
import React, { useState } from "react";

import { useCopy } from "../../extensions";
import { Button } from "../Button/Button";
import { Checkbox } from "../Checkbox/Checkbox";
import styles from "./PurchaseOrderNumberInput.module.scss";

const btnStyles = {
  fontSize: 16,
  fontWeight: 600,
  padding: "1.13rem",
};

const inputBtnContainerClasses = classNames(
  "field field__input-btn-wrapper",
  styles.inputBtnContainer
);

export interface PurchaseOrderNumberInputProps {
  onAdd: (orderNumber: string) => void;
}

export const PurchaseOrderNumberInput: React.FC<PurchaseOrderNumberInputProps> = ({ onAdd }) => {
  const { _textYourNumber, _textAddOptionalNumber } = useCopy("PurchaseOrderNumberInput");

  const [isShowInput, setIsShowInput] = useState(false);
  const [isNumberAdded, setIsNumberAdded] = useState(false);
  const [number, setNumber] = useState("");

  const handleCheckboxChange = () => {
    setIsShowInput((show) => !show);
    if (isShowInput) {
      setIsNumberAdded(false);
      setNumber("");
      onAdd("");
    }
  };

  const handleBtnClick = () => {
    setIsNumberAdded(true);
    onAdd(number);
  };

  return (
    <div className={styles.card}>
      <Checkbox
        dataTestId="purchase-order-number-checkbox"
        checked={isShowInput}
        label={<span style={{ marginLeft: 8 }}>{_textAddOptionalNumber}</span>}
        onChange={handleCheckboxChange}
      />
      {isShowInput &&
        (isNumberAdded ? (
          <div className={styles.addedNumber}>{number}</div>
        ) : (
          <div className={inputBtnContainerClasses}>
            <input
              aria-label={_textYourNumber}
              data-testid="purchase-order-number-input"
              className="field__input"
              placeholder={_textYourNumber}
              value={number}
              onChange={({ target }) => setNumber(target.value)}
            />
            <Button
              dataTestId="purchase-order-number-add"
              disabled={!number}
              style={btnStyles}
              onClick={handleBtnClick}
            >
              Add
            </Button>
          </div>
        ))}
    </div>
  );
};
