import { useCallback, useState } from "react";

import { useCopy } from "../../extensions";
import { Money, QueryLike } from "../PricingCalculator/types";

export const usePriceCalculation = (
  baseCurrency: string,
  listPriceBoundsQuery: QueryLike<{
    minimumPrices: Money[];
    maximumPrices: Money[];
  } | null>,
  manufacturingCostQuery: QueryLike<Money | null>
) => {
  const { _textPriceTooLow, _textPriceTooHigh } = useCopy("ListPriceForm");
  const [error, setError] = useState<string>();
  const [distroPrice, setDistroPrice] = useState<number>();

  const calculateMinMax = useCallback(
    (isGlobalDistro?: boolean) => {
      const distroMinimumListPrice = isGlobalDistro
        ? listPriceBoundsQuery.data?.minimumPrices?.find(
            (minimumPrice) => minimumPrice.currency === baseCurrency
          )
        : manufacturingCostQuery.data ?? undefined; // w/o global distro the manufacturing costs mark the minimum

      const distroMaximumListPrice = listPriceBoundsQuery.data?.maximumPrices?.find(
        (maximumPrice) => maximumPrice.currency === baseCurrency
      );

      return { distroMinimumListPrice, distroMaximumListPrice };
    },
    [listPriceBoundsQuery, manufacturingCostQuery, baseCurrency]
  );

  const validatePrice = useCallback(
    (price: number, isGlobalDistro?: boolean) => {
      const { distroMinimumListPrice: min, distroMaximumListPrice: max } =
        calculateMinMax(isGlobalDistro);

      if (!min || !max) {
        return false;
      }
      if (price < min.amount) {
        setError(_textPriceTooLow);
        return false;
      }
      if (price > max.amount) {
        setError(`${_textPriceTooHigh}. Max: ${max.amount}`);
        return false;
      }

      setError(undefined);
      return true;
    },
    [calculateMinMax, _textPriceTooLow, _textPriceTooHigh]
  );

  return {
    error,
    distroPrice,
    setDistroPrice,
    validatePrice,
    calculateMinMax,
  };
};
