import classNames from "classnames";
import React, { useState } from "react";
import { SingleValue } from "react-select";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { DownloadDropdownOption } from "../BannerTextOnly/BannerTextOnly";
import { Button, ButtonProps } from "../Button/Button";
import { CustomSelect, Option } from "../CustomSelect/CustomSelect";
import { Icon } from "../Icon/Icon";
import { Tooltip } from "../Tooltip/Tooltip";
import styles from "./DownloadDropdown.module.scss";

export interface DownloadDropdownProps {
  label: string;
  placeholder: string;
  errorText?: string;
  buttonText?: string;
  helpText?: string;
  hasDownloadIcon?: boolean;
  hasTooltip?: boolean;
  isDisabled?: boolean;
  buttonHierarchy?: ButtonProps["hierarchy"];
  downloadContentfulAssetCollection: { items: DownloadDropdownOption[] };
  downloadExternalAssetCollection: { items: DownloadDropdownOption[] };
  onChange?: (event: SingleValue<Option>) => void;
}

export const DownloadDropdown: React.FC<DownloadDropdownProps> = ({
  buttonText,
  label,
  helpText,
  hasTooltip = false,
  placeholder,
  downloadContentfulAssetCollection: { items: contentfulOptions } = {
    items: [],
  },
  downloadExternalAssetCollection: { items: externalOptions } = {
    items: [],
  },
  errorText,
  isDisabled = false,
  hasDownloadIcon = false,
  onChange,
  buttonHierarchy,
}) => {
  const [selectedOption, setSelectedOption] = useState<SingleValue<Option>>();
  const options = [...contentfulOptions, ...externalOptions];

  return (
    <>
      <div className={styles.labelAndHelp}>
        {label && (
          <span className={classNames(styles.label, "field__label field__label--visible")}>
            {label}
          </span>
        )}
        {hasTooltip ? (
          <div className={styles.tooltip}>
            <div data-tip data-for={label}>
              <Icon iconName={Icons.HelpQuestionmarkCircleFilled} size={15} color={Colors.Indigo} />
            </div>
            <Tooltip tooltipStyle="default" tooltipId={label}>
              {helpText || "Please add help text"}
            </Tooltip>
          </div>
        ) : (
          !!helpText && <span className={styles.helpText}>{helpText}</span>
        )}
      </div>
      <div className={styles.selectAndButton}>
        <CustomSelect
          options={options.map((option) => ({
            label: option.title,
            value: option.url,
          }))}
          onChange={(evt) => {
            onChange && onChange(evt as SingleValue<Option>);
            setSelectedOption(evt as SingleValue<Option>);
          }}
          placeholder={placeholder}
          disabled={isDisabled}
          error={errorText}
          isShowErrorMessage={false}
          ariaLabel={label}
        />
        <Button
          disabled={isDisabled || !!errorText || !selectedOption}
          hierarchy={buttonHierarchy}
          download
          hasDownloadIcon={hasDownloadIcon}
          downloadHref={selectedOption?.value}
          downloadTitle={selectedOption?.label}
          isVerticallyStretched
        >
          {buttonText && buttonText}
        </Button>
      </div>
      {errorText && <p className="field__message field__message--error">{errorText}</p>}
    </>
  );
};
