import classNames from "classnames";
import React, { useEffect, useRef } from "react";
import { createPortal } from "react-dom";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { Button, ButtonProps } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import styles from "./Modal.module.scss";

export type ModalCTAProps = {
  action: () => void;
  title: string;
} & Pick<ButtonProps, "disabled" | "hierarchy" | "loading">;

export interface ModalProps {
  dataTestId?: string;
  cta?: ModalCTAProps[];
  customHeaderIcon?: {
    height?: string | number;
    width?: string | number;
    src: string;
    alt: string;
  };
  headerIcon?: Icons;
  headerIconColor?: Colors;
  headerIconSize?: number;
  isOpen: boolean;
  modalStatus?: "info" | "warning" | "success" | "error";
  onClose?: () => void;
  title?: string;
  titlePosition?: "center" | "left";
  isTitleResponsiveHorizontalAlign?: boolean;
  increasedMinimumWidth?: boolean;
  width?: number | string;
  portalContainer?: HTMLElement;
  children?: React.ReactNode;
}

export const Modal: React.FC<ModalProps> = ({
  dataTestId,
  children,
  cta,
  headerIcon,
  customHeaderIcon,
  modalStatus = "info",
  onClose,
  title,
  titlePosition = "center",
  isTitleResponsiveHorizontalAlign,
  width,
  increasedMinimumWidth,
  portalContainer,
  headerIconColor,
  headerIconSize,
  ...props
}) => {
  const backdrop = useRef<HTMLDivElement>(null);

  useEffect(() => {
    document.body.style.position = "initial";
    document.body.style.overflow = "hidden";

    return () => {
      document.body.style.removeProperty("position");
      document.body.style.removeProperty("overflow");
    };
  }, []);

  const onOutsideClick = (event: React.MouseEvent) => {
    if (event.target === backdrop.current && onClose) {
      onClose();
    }
  };

  if (typeof document === "undefined") return null;

  const portal = createPortal(
    <div
      role="presentation"
      data-testid={dataTestId}
      className={styles.backdrop}
      ref={backdrop}
      onMouseDown={onOutsideClick}
    >
      <div
        className={classNames(styles.dialog, {
          [styles.widerDialog]: increasedMinimumWidth,
        })}
        style={{ width: width }}
        role="dialog"
      >
        {onClose && (
          <NakedButton role="button" aria-label="close" className={styles.close} onClick={onClose}>
            <Icon iconName={Icons.DisplayCloseLarge} color={Colors.Indigo} />
          </NakedButton>
        )}
        <div
          className={
            isTitleResponsiveHorizontalAlign
              ? styles.headerResponsiveHorizontalAlign
              : titlePosition === "center"
              ? styles.header
              : styles.headerLeft
          }
        >
          {customHeaderIcon && (
            <img
              src={customHeaderIcon.src}
              alt={customHeaderIcon.alt}
              height={customHeaderIcon.height}
              width={customHeaderIcon.width}
            />
          )}
          {headerIcon && (
            <Icon
              iconName={headerIcon}
              className={`icon-${modalStatus}`}
              color={headerIconColor}
              size={headerIconSize}
            />
          )}
          {title && <h2>{title}</h2>}
        </div>
        <div
          className={classNames(styles.body, {
            [styles.noMarginBottom]: !cta?.length,
          })}
        >
          {children}
        </div>
        {cta && (
          <div className={styles.footer}>
            {cta.map((el, index) => (
              <Button
                key={index}
                hierarchy={el.hierarchy ?? "secondary"}
                onClick={el.action}
                loading={el.loading}
                disabled={el.disabled}
                responsive={false}
                hasModalStyles
                dataTestId={dataTestId ? `${dataTestId}-cta-${index}` : undefined}
              >
                {el.title}
              </Button>
            ))}
          </div>
        )}
      </div>
    </div>,
    portalContainer ?? document.body
  );

  return props.isOpen ? portal : null;
};
