import React, { ReactNode, useContext, useRef } from "react";

import { Colors } from "../../../constants";
import { useCopy } from "../../../extensions";
import { Icons } from "../../../icon-types";
import { Icon } from "../../Icon/Icon";
import { Loading } from "../../Loading/Loading";
import { PricingCalculatorContext } from "../PricingCalculatorContext";
import { ProductSpecsPanel } from "../ProductSpecsPanel/ProductSpecsPanel";
import { ProductTypeEnum } from "../types";
import { BookConfigurator } from "./BookConfigurator/BookConfigurator";
import { CalendarConfigurator } from "./CalendarConfigurator/CalendarConfigurator";
import { EbookConfigurator } from "./EbookConfigurator/EbookConfigurator";
import styles from "./PricingConfigurator.module.scss";

const getConfiguratorComponentByProductType = (productType: ProductTypeEnum) => {
  switch (productType) {
    case "EBOOK":
      return EbookConfigurator;
    case "CALENDAR":
      return CalendarConfigurator;
    default:
      return BookConfigurator;
  }
};

interface Props {
  mainColumnAddons?: ReactNode;
}

export const PricingConfigurator: React.FC<Props> = ({ mainColumnAddons }) => {
  const configuratorContentRef = useRef<HTMLDivElement>(null);
  const specsPanelContainerRef = useRef<HTMLDivElement>(null);
  const { podPackagesQuery, productType, getDisplayValue, isDeveloperPortalVersion } =
    useContext(PricingCalculatorContext);
  const { _textGlobalDistroHint } = useCopy("PricingCalculator");

  if (!productType) {
    return null;
  }

  if (podPackagesQuery.loading || !podPackagesQuery?.data || podPackagesQuery.data.length === 0) {
    return (
      <div className={styles.loadingContainer}>
        <Loading />
      </div>
    );
  }

  const Configurator = getConfiguratorComponentByProductType(productType);

  const testIdPrefix = "pricing-configurator";

  return (
    <>
      <div ref={configuratorContentRef} data-testid={`${testIdPrefix}-content`}>
        <div className={styles.configuratorHeader} data-testid={`${testIdPrefix}-header`}>
          <h2 className="text-no-margin">{getDisplayValue(productType)}</h2>
          {!isDeveloperPortalVersion && (
            <div className={styles.globalDistroHint}>
              <Icon
                iconName={Icons.DistributionYes}
                size={24}
                color={Colors.GreenDark}
                data-testid={`${testIdPrefix}-global-distro-icon`}
              />
              {_textGlobalDistroHint}
            </div>
          )}
        </div>
        <Configurator />
        {mainColumnAddons}
      </div>
      <div data-testid={`${testIdPrefix}-specs-panel`}>
        <div className={styles.specsPanelContainer} ref={specsPanelContainerRef}>
          <ProductSpecsPanel />
        </div>
      </div>
    </>
  );
};
