import React, { useContext, useEffect } from "react";

import { ShippingCalculator } from "../ShippingCalculator/ShippingCalculator";
import { PricingCalculatorContext } from "./PricingCalculatorContext";

export const ShippingCalculatorAdapter = () => {
  const {
    podPackageId,
    state,
    manufacturingCostQuery,
    shippingCountriesQuery,
    totalPricesQuery,
    shippingMethodsQuery,
    countryStatesQuery,
    onRequestShippingMethods,
    onRequestTotalPrices,
    onRequestShippingCountries,
    onRequestCountryStates,
    getCountryName,
    locale,
    isDeveloperPortalVersion,
  } = useContext(PricingCalculatorContext);

  useEffect(() => {
    onRequestShippingCountries();
  }, [onRequestShippingCountries]);

  if (!podPackageId || !state.pageCount) return null;

  return (
    <ShippingCalculator
      dataTestId="shipping-calculator"
      podPackageId={podPackageId}
      pageCount={state.pageCount}
      locale={locale}
      pricePerBook={manufacturingCostQuery.data ?? null}
      shippingCountriesQuery={shippingCountriesQuery}
      totalPricesQuery={totalPricesQuery}
      shippingMethodsQuery={shippingMethodsQuery}
      onRequestShippingMethods={onRequestShippingMethods}
      onRequestTotalPrices={onRequestTotalPrices}
      onRequestShippingCountries={onRequestShippingCountries}
      getCountryName={getCountryName}
      usStatesRequired={isDeveloperPortalVersion}
      onRequestCountryStates={onRequestCountryStates}
      countryStatesQuery={countryStatesQuery}
    />
  );
};
