import { Price } from "../ui-components";

const MONEY_REGEXP = /^[0-9]+(\.)?([0-9]{1,2})?$/;
const DOT_CENTS_REGEXP = /^\.[0-9]{1,2}$/;

export const validInput = (input: string) => {
  return input === "" || input === "." || validAmount(input);
};

export const validAmount = (amount: string) => {
  return MONEY_REGEXP.test(amount) || DOT_CENTS_REGEXP.test(amount);
};

export const printMoney = (amount?: string | number, locale = "en") => {
  if (amount === null || amount === undefined) {
    return null;
  }

  const nf = new Intl.NumberFormat(locale, {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });
  return nf.format(Number(amount));
};

export const getAmountByCurrency = (prices: Price[], currency: string) =>
  prices.find((item) => item.currency === currency)?.amount;
