import classNames from "classnames";
import React, { useContext, useMemo } from "react";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../../../../extensions";
import { InputDropdown } from "../../../../InputDropdown/InputDropdown";
import { NumericInput } from "../../../../NumericInput/NumericInput";
import { PricingCalculatorContext } from "../../../PricingCalculatorContext";
import { ConfiguratorState } from "../../../reducer";
import { ProductTypeEnum } from "../../../types";
import {
  getMinPageCountByProductType,
  getPagesLimitByProductType,
  maxPageCountLimit,
} from "../../../utils";
import { SectionHeader } from "../../SectionHeader/SectionHeader";
import styles from "./BookSizeSelect.module.scss";

interface BookSizeSelectProps {
  productType: ProductTypeEnum;
  bookConfiguratorState: ConfiguratorState;
  onTrimSizeChange: (value: string) => void;
  onPageCountChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
}

export const BookSizeSelect = ({
  productType,
  bookConfiguratorState,
  onTrimSizeChange,
  onPageCountChange,
}: BookSizeSelectProps) => {
  const { getDisplayValue, trimSizes } = useContext(PricingCalculatorContext);

  const {
    _textBookSizeTitle,
    _textSelectBookSize,
    _textPageCount,
    _textPageCountMinMax,
    _textInvalidPageCount,
  } = useCopy("PricingCalculator");

  const handlePageCountChange = useDebouncedCallback((event) => {
    onPageCountChange(event);
  }, 500);

  const options = useMemo(
    () =>
      trimSizes.map((trim) => {
        return {
          value: trim,
          label: getDisplayValue(trim) || "",
        };
      }),
    [trimSizes, getDisplayValue]
  );

  const testIdPrefix = "book-size-select";

  const shouldDisplayPageCountErrorMessage =
    !!bookConfiguratorState.trimSize &&
    !!bookConfiguratorState.pageCount &&
    (bookConfiguratorState.pageCount < getMinPageCountByProductType(productType) ||
      bookConfiguratorState.pageCount > maxPageCountLimit);

  return (
    <div className={styles.container} data-testid={testIdPrefix}>
      <SectionHeader title={_textBookSizeTitle} />
      <div className={styles.selectorsContainer}>
        <div className={styles.inputDropdownContainer}>
          <InputDropdown
            options={options}
            label={_textSelectBookSize}
            onChange={onTrimSizeChange}
            value={bookConfiguratorState.trimSize ? bookConfiguratorState.trimSize : ""}
            wrapperClassName={classNames(styles.inputDropdownWrapper, {
              [styles.withBottomMargin]: !!shouldDisplayPageCountErrorMessage,
            })}
            dataTestId={`${testIdPrefix}-input`}
            name={testIdPrefix}
            inputProps={{ autoComplete: "off" }}
          />
        </div>

        <div className={styles.pageCountContainer}>
          <NumericInput
            label={_textPageCount}
            onChange={handlePageCountChange}
            wrapperClassName={styles.pageCountWrapper}
            defaultValue={bookConfiguratorState.pageCount ?? undefined}
            maxLength={4}
            onClick={(event: React.MouseEvent) => (event.target as HTMLInputElement).select()}
            dataTestId={`${testIdPrefix}-page-count-input`}
            helperText={
              <>
                {_textPageCountMinMax}{" "}
                <b>
                  {!!bookConfiguratorState.trimSize ? getPagesLimitByProductType(productType) : "-"}
                </b>
              </>
            }
            error={shouldDisplayPageCountErrorMessage ? _textInvalidPageCount : ""}
            id={`${testIdPrefix}-page-count`}
          />
        </div>
      </div>
    </div>
  );
};
