import classNames from "classnames";
import React from "react";

import { mapContentfulColorToStyleProp } from "../../utils/colors";
import {
  ContentLinkImageCard,
  ContentLinkImageCardProps,
} from "../ContentLinkImageCard/ContentLinkImageCard";
import {
  ContentLinkTextBlock,
  ContentLinkTextBlockProps,
} from "../ContentLinkTextBlock/ContentLinkTextBlock";

export interface ContentLinkImageCardCollectionProps {
  dataTestId?: string;
  backgroundColor?: string;
  classes?: string;
  contentLinkTextBlockRef?: ContentLinkTextBlockProps;
  contentLinkImageCardsCollection?: { items: ContentLinkImageCardProps[] };
}

export const ContentLinkImageCardCollection: React.FC<ContentLinkImageCardCollectionProps> = ({
  dataTestId,
  backgroundColor,
  classes,
  contentLinkTextBlockRef,
  contentLinkImageCardsCollection: { items } = {},
}) => {
  const collectionClasses = classNames("max-width-container", classes);
  return (
    <section
      style={
        backgroundColor ? { background: mapContentfulColorToStyleProp(backgroundColor) } : undefined
      }
      data-testid={dataTestId}
    >
      {contentLinkTextBlockRef && <ContentLinkTextBlock {...contentLinkTextBlockRef} />}
      <div className={collectionClasses}>
        <div className="grid-default">
          {items &&
            items.map((contentLinkImageCard: ContentLinkImageCardProps, index: number) => (
              <ContentLinkImageCard {...contentLinkImageCard} key={`card-${index}`} />
            ))}
        </div>
      </div>
    </section>
  );
};
