export const displayCalendarTotalPageCount = (projectInteriorPageCount: number | null) => {
  if (!projectInteriorPageCount) return;

  const coverPageCount = 4;

  return projectInteriorPageCount + coverPageCount;
};

export const displayCalendarMonthCount = (projectInteriorPageCount: number | null) => {
  if (!projectInteriorPageCount) return;

  return (projectInteriorPageCount + 2) / 2;
};

export const getRootUrl = (url: string) => "/" + url.replace(/^\/*/, "");
// Used to prevent react-router-dom to load on next js pages with different built-in routing
export const isNextJs = typeof window === "undefined" || !!document.getElementById("__next");
