import classNames from "classnames";
import React from "react";
import { Link } from "react-router-dom";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { isNextJs } from "../../utils";
import { Icon } from "../Icon/Icon";
import styles from "./StatusInfo.module.scss";

type StatusType = "neutral" | "success" | "warning" | "error" | "retired";

export interface StatusInfoProps {
  dataTestId?: string;
  icon: Icons;
  productLink?: string;
  statusLabel: string;
  statusLevel?: StatusType;
  hasNoMargin?: boolean;
  asExternalLink?: boolean;
}

export const StatusInfo: React.FC<StatusInfoProps> = ({
  dataTestId,
  icon,
  productLink,
  statusLabel,
  statusLevel = "neutral",
  hasNoMargin,
  asExternalLink,
}) => {
  const iconColor: Record<StatusType, Colors> = {
    error: Colors.RedDark,
    neutral: Colors.PurpleDark,
    retired: Colors.AlmostBlack60,
    success: Colors.GreenDark,
    warning: Colors.OrangeDark,
  };

  const linkContent = (
    <>
      <span className={`${styles.iconDescriptor} ${statusLevel}`}>{statusLabel}</span>
      <Icon iconName={Icons.ArrowLinkRightSmall} />
    </>
  );

  return (
    <div
      role="presentation"
      data-testid={dataTestId}
      className={classNames(styles.statusInfo, {
        [styles.hasNoMargin]: hasNoMargin,
      })}
      onClick={(e) => productLink && e.stopPropagation()}
    >
      <Icon iconName={icon} className={`${statusLevel}`} color={iconColor[statusLevel]} size={24} />
      {productLink ? (
        <>
          {isNextJs || asExternalLink ? (
            <a
              className={styles.productShareLink}
              href={productLink}
              target="_blank"
              rel="noopener noreferrer"
            >
              {linkContent}
            </a>
          ) : (
            <Link className={styles.productShareLink} to={productLink} target="_blank">
              {linkContent}
            </Link>
          )}
        </>
      ) : (
        <span className={`${styles.iconDescriptor} ${statusLevel}`}>{statusLabel}</span>
      )}
    </div>
  );
};
