import classNames from "classnames";
import React from "react";

import styles from "./NakedButton.module.scss";

export interface NakedButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  dataTestId?: string;
}

export const NakedButton = React.forwardRef<HTMLButtonElement, NakedButtonProps>(
  ({ dataTestId, className, children, ...rest }, ref) => {
    return (
      <button
        ref={ref}
        data-testid={dataTestId}
        className={classNames(styles.nakedButton, className)}
        {...rest}
      >
        {children}
      </button>
    );
  }
);
