import classNames from "classnames";
import React, { CSSProperties, HTMLProps, useEffect, useRef, useState } from "react";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { Link, LinkProps } from "../Link/Link";
import { LinkedIconAndText, LinkedIconAndTextProps } from "../LinkedIconAndText/LinkedIconAndText";
import { RichTextContainer, RichTextContainerProps } from "../RichTextContainer/RichTextContainer";
import styles from "./CollapsibleContent.module.scss";

export interface CollapsibleContentProps extends HTMLProps<HTMLDivElement> {
  dataTestId?: string;
  maxHeight: number;
  contentLink?: LinkedIconAndTextProps;
  gradientColor?: string;
}
export interface ContentfulCollapsibleContentProps {
  dataTestId?: string;
  classes?: string;
  showMoreText: string;
  showLessText: string;
  maxHeight?: number;
  linkRef?: LinkProps;
  gradientColor?: string;
  richTextContainerRef: RichTextContainerProps;
}

export const CollapsibleContent: React.FC<CollapsibleContentProps> = ({
  dataTestId,
  maxHeight,
  contentLink,
  gradientColor,
  className,
  children,
  ...props
}) => {
  const [contentHeight, setContentHeight] = useState(maxHeight);
  const [isCollapsed, setIsCollapsed] = useState(true);

  const { _show, _hide } = useCopy("CollapsibleContent");
  const contentRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (contentRef.current) {
      const contentHeight = contentRef.current.offsetHeight;
      setIsCollapsed(contentHeight > maxHeight);
      setContentHeight(contentHeight);
    }
  }, [contentRef, maxHeight]);

  const isContentOverflow = contentHeight > maxHeight;
  const shouldSomeContentHide = isContentOverflow && isCollapsed;

  const contentStyle = shouldSomeContentHide
    ? {
        height: maxHeight,
      }
    : undefined;

  const contentClassNames = shouldSomeContentHide ? styles.contentCollapsed : styles.content;

  return (
    <div className={className} data-testid={dataTestId} {...props}>
      <div
        style={{ ...contentStyle, "--color": gradientColor } as CSSProperties}
        className={contentClassNames}
        ref={contentRef}
      >
        {children}
      </div>
      <div
        className={classNames(styles.actions, {
          [styles.multipleActions]: contentLink,
        })}
      >
        {!!contentLink && <LinkedIconAndText {...contentLink} className={styles.collapseLink} />}
        {isContentOverflow && (
          <div className={styles.collapseToggle}>
            <button
              className={classNames("link", styles.collapseLink)}
              onClick={() => setIsCollapsed(!isCollapsed)}
            >
              {isCollapsed ? _show : _hide}
            </button>
            <Icon iconName={isCollapsed ? Icons.ArrowDownMedium : Icons.ArrowUpMedium} />
          </div>
        )}
      </div>
    </div>
  );
};

export const ContentfulCollapsibleContent: React.FC<ContentfulCollapsibleContentProps> = ({
  maxHeight = 100,
  classes,
  gradientColor,
  richTextContainerRef,
  linkRef,
  showMoreText,
  showLessText,
}) => {
  const [contentHeight, setContentHeight] = useState(maxHeight);
  const [isCollapsed, setIsCollapsed] = useState(true);

  const contentRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (contentRef.current) {
      const contentHeight = contentRef.current.offsetHeight;
      setIsCollapsed(contentHeight > maxHeight);
      setContentHeight(contentHeight);
    }
  }, [contentRef, maxHeight]);

  const isContentOverflow = contentHeight > maxHeight;
  const shouldSomeContentHide = isContentOverflow && isCollapsed;

  const contentStyle = shouldSomeContentHide
    ? {
        height: maxHeight,
      }
    : undefined;

  const contentClassNames = shouldSomeContentHide ? styles.contentCollapsed : styles.content;

  return (
    <div className={classes}>
      <div
        style={{ ...contentStyle, "--color": gradientColor } as CSSProperties}
        className={contentClassNames}
        ref={contentRef}
      >
        <RichTextContainer
          {...richTextContainerRef}
          richTextContainerClassNames={styles.richTextContainer}
        />
      </div>
      <div
        className={classNames(styles.actions, {
          [styles.multipleActions]: linkRef,
        })}
      >
        {!!linkRef && <Link {...linkRef} className={styles.collapseLink} />}
        {isContentOverflow && (
          <div className={styles.collapseToggle}>
            <button
              className={classNames("link", styles.collapseLink)}
              onClick={() => setIsCollapsed(!isCollapsed)}
            >
              {isCollapsed ? showMoreText : showLessText}
            </button>
            <Icon iconName={isCollapsed ? Icons.ArrowDownMedium : Icons.ArrowUpMedium} />
          </div>
        )}
      </div>
    </div>
  );
};
