import classNames from "classnames";
import React from "react";

import { useCopy } from "../../../extensions";
import { getLocaleUrlPrefix, Locale } from "../../../utils/locale";
import { Button } from "../../Button/Button";
import { Heading } from "../../Heading/Heading";
import styles from "./Header.module.scss";

interface Props {
  productsUrl: string;
  isDeveloperPortalVersion?: boolean;
  locale: Locale;
}

export const Header: React.FC<Props> = ({ productsUrl, isDeveloperPortalVersion, locale }) => {
  const {
    _textPricingCalculator,
    _textHeaderDescription,
    _textHeaderDescriptionDeveloperPortal,
    _textHeaderMoreInformation,
    _textHeaderMoreInformationDeveloperPortal,
    _textProductsPage,
    _textHeaderMoreInformationBulkPricing,
    _textHeaderBulkPricingPage,
    _textProductsPageDeveloperPortal,
  } = useCopy("PricingCalculator");

  const testIdPrefix = "header";

  return (
    <div
      className={classNames(styles.container, {
        [styles.developerPortal]: isDeveloperPortalVersion,
      })}
      data-testid={testIdPrefix}
    >
      <div>
        <Heading
          title={_textPricingCalculator}
          description={
            isDeveloperPortalVersion
              ? _textHeaderDescriptionDeveloperPortal
              : _textHeaderDescription
          }
          noPaddingTop
          dataTestId={`${testIdPrefix}-heading`}
        />
      </div>
      {isDeveloperPortalVersion ? (
        <div className={styles.informationContainerDevPortal}>
          <div className={styles.imageContainer}>
            <img
              src="https://assets.lulu.com/media/pricing-calculator/lulu-icon-publish.svg"
              alt="Small book illustration"
              data-testid={`${testIdPrefix}-image`}
            />
          </div>
          <h2
            className={classNames(styles.information, styles.devPotal)}
            data-testid={`${testIdPrefix}-information`}
          >
            {_textHeaderMoreInformationDeveloperPortal}{" "}
            <Button
              className={styles.downloadButton}
              download
              hierarchy="primary"
              buttonSize="extra-small"
              downloadHref={productsUrl}
            >
              {_textProductsPageDeveloperPortal}
            </Button>
          </h2>
        </div>
      ) : (
        <div className={styles.informationContainer}>
          <div className={styles.imageContainer}>
            <img
              src="https://assets.lulu.com/media/pricing-calculator/lulu-icon-publish.svg"
              alt="Small book illustration"
              data-testid={`${testIdPrefix}-image`}
            />
          </div>
          <div className={styles.information} data-testid={`${testIdPrefix}-information`}>
            <p>
              {_textHeaderMoreInformation}{" "}
              <a
                href={productsUrl}
                target="_blank"
                rel="noopener noreferrer"
                data-testid={`${testIdPrefix}-productUrl`}
              >
                {_textProductsPage}
              </a>
              .
            </p>
            <p>
              {_textHeaderMoreInformationBulkPricing}{" "}
              <a
                href={`https://www.lulu.com${getLocaleUrlPrefix(locale)}/pricing/volume-discounts`}
              >
                {_textHeaderBulkPricingPage}
              </a>
              .
            </p>
          </div>
        </div>
      )}
    </div>
  );
};
