import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { IconButton } from "../IconButton/IconButton";
import { RadioInput } from "../RadioInput/RadioInput";
import styles from "./BookRadio.module.scss";

export interface BookRadioProps {
  dataTestIdCustomPrefix?: string;
  dataTestId?: string;
  disabled?: boolean;
  image?: string;
  imageSet?: string;
  label: string;
  description?: string;
  value: string;
  selected?: boolean;
  name?: string;
  hasDropdown?: boolean;
  icon?: boolean;
  type?: "product_type" | "configurator";
  onChange?: (value: string) => void;
  CustomImageComponent?: JSX.Element;
}

export const BookRadio: React.FC<BookRadioProps> = ({
  dataTestIdCustomPrefix,
  dataTestId,
  disabled,
  image,
  imageSet,
  label,
  description,
  value,
  selected,
  name,
  icon,
  type,
  hasDropdown,
  onChange,
  CustomImageComponent,
}) => {
  const { _textOptionNotAvailable } = useCopy("BookRadio");

  const [open, setOpen] = React.useState(false);

  const bookRadioClass = classNames(styles.bookRadio, {
    [styles.bookRadioDisabled]: disabled,
    [styles.bookRadioHasIcon]: icon,
    [styles.bookRadioSelected]: selected,
  });

  const bookRadioContainerClass = classNames(styles.container, {
    [styles.containerConfigurator]: type === "configurator",
    [styles.containerHasDropdown]: hasDropdown,
    [styles.containerOpened]: description && open,
  });

  return (
    <div
      className={bookRadioContainerClass}
      data-testid={
        dataTestId ?? `${dataTestIdCustomPrefix || "card_radio_button"}-${name}-${value}`
      }
    >
      <div
        role="presentation"
        className={bookRadioClass}
        onClick={() => !disabled && onChange?.(value)}
      >
        {(CustomImageComponent || image || imageSet) && (
          <div className={styles.bookRadioImage}>
            {CustomImageComponent ?? <img src={image} srcSet={imageSet} alt={name} />}
            {disabled ? (
              <div className={styles.bookRadioDisabledContent}>
                <Icon iconName={Icons.DataLockClosedLarge} />
                <span>{_textOptionNotAvailable}</span>
              </div>
            ) : null}
          </div>
        )}
        <div className={styles.bookRadioFooter}>
          <RadioInput
            aria-label={label}
            spacing={8}
            name={name}
            value={value}
            checked={selected}
            readOnly={true}
            dataTestId={`${value}-radio`}
          >
            <div className={styles.bookRadioContent}>
              <label className={styles.bookRadioContentLabel} htmlFor={name}>
                {label}
              </label>
              {icon && (
                <Icon iconName={Icons.DistributionYes} className={styles.bookRadioContentIcon} />
              )}
              {description && !hasDropdown && (
                <p className={styles.bookRadioContentDescription}>{description}</p>
              )}
            </div>
          </RadioInput>
        </div>
        {hasDropdown && <p className={styles.bookRadioContentDescription}>{description}</p>}
      </div>
      {hasDropdown && (
        <IconButton
          buttonSize="large"
          hierarchy="primary"
          icon={Icons.ArrowDownMedium}
          shape="square"
          onClick={() => setOpen(!open)}
          className={styles.bookRadioButton}
          aria-label={"Show more"}
        />
      )}
    </div>
  );
};
