import classNames from "classnames";
import React from "react";

import { Button } from "../Button/Button";
import { Link } from "../Link/Link";
import styles from "./NextStepButton.module.scss";

export interface NextStepButtonProps {
  dataTestId?: string;
  to?: string;
  onClick?: any;
  disabled?: boolean;
  nextStepInfo?: string | JSX.Element;
  primary?: boolean;
  loading?: boolean;
  progress?: number;
  buttonStyle?: React.CSSProperties;
  style?: React.CSSProperties;
  children?: React.ReactNode;
}

export const NextStepButton: React.FC<NextStepButtonProps> = ({
  dataTestId,
  to,
  onClick,
  disabled = false,
  nextStepInfo,
  primary,
  loading,
  progress,
  buttonStyle,
  style,
  children,
}) => {
  const buttonHierarchy = primary ? "primary" : "secondary";

  const ButtonComponent = () => (
    <div className={styles.buttonWrapper}>
      <Button
        disabled={disabled}
        onClick={onClick}
        buttonSize="large"
        hierarchy={buttonHierarchy}
        loading={loading}
        style={buttonStyle}
        progress={progress}
      >
        {children}
      </Button>
    </div>
  );

  const classes = classNames(styles.nextStep, { [styles.primary]: primary });

  return (
    <div className={classes} style={style} data-test-id={dataTestId}>
      {nextStepInfo && typeof nextStepInfo === "string" ? (
        <h2 className="h1 subhead">{nextStepInfo}</h2>
      ) : (
        nextStepInfo
      )}
      {to ? (
        <Link url={to}>
          <ButtonComponent />
        </Link>
      ) : (
        <ButtonComponent />
      )}
    </div>
  );
};
