import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { mapContentfulColorToStyleProp } from "../../utils";
import { OptionsViewProps } from "./ImageSlider";
import styles from "./ImageSlider.module.scss";

export const ColorSelectors = ({ options, setActiveIndex, activeIndex }: OptionsViewProps) => {
  const { _previewColorAriaLabel } = useCopy("ImageSlider");

  return (
    <div className={styles.previewColorsContainer}>
      {options.map((item, index) => (
        <NakedButton
          key={item.color}
          onClick={() => setActiveIndex(index)}
          className={classNames(styles.colorSelector, {
            [styles.selectedColor]: index === activeIndex,
          })}
          data-testid="ImageSlider-optionItem"
          aria-label={`${_previewColorAriaLabel} ${index + 1}`}
        >
          <div
            style={{
              background: mapContentfulColorToStyleProp(item.color || ""),
            }}
          />
        </NakedButton>
      ))}
    </div>
  );
};
