import React, { ComponentPropsWithoutRef } from "react";

import { SectionHeader } from "../SectionHeader/SectionHeader";
import { AttributeOption } from "./AttributeOption";
import styles from "./AttributeSelector.module.scss";

export type AttributeOptionValue = Omit<
  ComponentPropsWithoutRef<typeof AttributeOption>,
  "onChange" | "nonInteractive" | "selected"
>;

interface Props {
  title: string;
  isSubSection?: boolean;
  onChange?: (option: string) => void;
  activeValue: string | null;
  values: AttributeOptionValue[];
  nonInteractive?: boolean;
  rowLayoutItem?: boolean;
  dataTestId?: string;
}

export const AttributeSelector: React.FC<Props> = ({
  title,
  isSubSection,
  onChange,
  activeValue,
  values,
  nonInteractive,
  rowLayoutItem,
  dataTestId,
}) => (
  <div className={styles.selector} data-testid={`attribute-selector-${dataTestId}`}>
    {isSubSection ? (
      <h4 className={styles.subSectionTitle}>{title}</h4>
    ) : (
      <SectionHeader title={title} />
    )}
    <div className={rowLayoutItem ? styles.rowLayoutItem : styles.radioGroup}>
      {values.map((item) => (
        <AttributeOption
          key={item.value}
          label={item.label}
          description={item.description}
          image={item.image}
          selected={activeValue === item.value}
          onChange={onChange}
          value={item.value}
          disabled={item.disabled}
          unavailable={item.unavailable}
          nonInteractive={nonInteractive}
          isDistroEligible={item.isDistroEligible}
          order={item.order}
          colStart={item.colStart}
        />
      ))}
    </div>
  </div>
);
