import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { MoneyDisplay } from "../MoneyDisplay/MoneyDisplay";
import { Money } from "../PricingCalculator/types";
import { Tooltip } from "../Tooltip/Tooltip";
import styles from "./ShippingCalculator.module.scss";

interface BulkDiscountRangesProps {
  pricePerBook: Money;
  locale: string;
}

export const BulkDiscountRanges = ({ pricePerBook, locale }: BulkDiscountRangesProps) => {
  const {
    _textDiscountRanges,
    _textOff,
    _textPricePerBook,
    _textBookQuantity,
    _textTooltipDescription,
    _textTooltipHeader,
    _textTooltipReachOut,
  } = useCopy("ShippingCalculator");

  const discount = pricePerBook.amount * 0.05;
  const pricePerBookDiscount5 = {
    ...pricePerBook,
    amount: pricePerBook.amount - discount,
  };
  const pricePerBookDiscount10 = {
    ...pricePerBook,
    amount: pricePerBook.amount - discount * 2,
  };
  const pricePerBookDiscount15 = {
    ...pricePerBook,
    amount: pricePerBook.amount - discount * 3,
  };

  return (
    <div data-testid="bulk-discount-table">
      <h6 className={styles.bulkDiscountRangesTitle}>{_textDiscountRanges}</h6>
      <div className={styles.bulkDiscountRangesTable}>
        <div className={classNames(styles.tableRow, styles.splitter)}>
          <span className={classNames(styles.tableRecord, styles.header)}>
            {_textBookQuantity}
            <div data-tip data-for="quantity-tooltip" className={styles.tooltipIconWrapper}>
              <Icon
                iconName={Icons.NotificationNeutral}
                color={Colors.PurpleDark}
                className={styles.icon}
              />
            </div>
            <Tooltip
              tooltipStyle="discount"
              tooltipId="quantity-tooltip"
              place="top"
              delayHide={500}
              delayUpdate={500}
              className={styles.tooltipWrapper}
            >
              <div className={styles.tooltip}>
                <p className={styles.tooltipHeader}>{_textTooltipHeader}?</p>
                <span className={styles.tooltipDescription}>
                  {_textTooltipDescription}.{" "}
                  <a href="mailto:bulkquote@lulu.com" className={styles.tooltipLink}>
                    {_textTooltipReachOut}
                  </a>
                  !
                </span>
              </div>
            </Tooltip>
          </span>
          <span className={classNames(styles.tableRecord, styles.header)}>% {_textOff}</span>
          <span className={classNames(styles.tableRecord, styles.header)}>{_textPricePerBook}</span>
        </div>
        <div className={classNames(styles.tableRow, styles.firstDataRow)}>
          <span className={styles.tableRecord}>100-499</span>
          <span className={styles.tableRecord}>5% {_textOff.toUpperCase()}</span>
          <span className={styles.tableRecord}>
            <MoneyDisplay money={pricePerBookDiscount5} locale={locale} />
          </span>
        </div>
        <div className={styles.tableRow}>
          <span className={styles.tableRecord}>500-999</span>
          <span className={styles.tableRecord}>10% {_textOff.toUpperCase()}</span>
          <span className={styles.tableRecord}>
            <MoneyDisplay money={pricePerBookDiscount10} locale={locale} />
          </span>
        </div>
        <div className={styles.tableRow}>
          <span className={styles.tableRecord}>1000+</span>
          <span className={styles.tableRecord}>15% {_textOff.toUpperCase()}</span>
          <span className={styles.tableRecord}>
            <MoneyDisplay money={pricePerBookDiscount15} locale={locale} />
          </span>
        </div>
      </div>
    </div>
  );
};
