import React, { useContext } from "react";

import { EbookRevenueCalculator } from "../EbookRevenueCalculator/EbookRevenueCalculator";
import { PricingCalculatorContext } from "./PricingCalculatorContext";

export const EbookRevenueCalculatorAdapter = () => {
  const {
    locale,
    priceMapEbookQuery,
    listPriceBoundsQuery,
    onRequestListPriceBounds,
    onRequestPriceMapEbook,
    baseCurrency,
  } = useContext(PricingCalculatorContext);

  return (
    <EbookRevenueCalculator
      locale={locale}
      baseCurrency={baseCurrency || "USD"}
      priceMapEbookQuery={priceMapEbookQuery}
      listPriceBoundsQuery={listPriceBoundsQuery}
      onRequestListPriceBounds={onRequestListPriceBounds}
      onRequestPriceMapEbook={onRequestPriceMapEbook}
    />
  );
};
