import classNames from "classnames";
import React, {
  ChangeEvent,
  InputHTMLAttributes,
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";

import { Colors } from "../../constants";
import { useCopy } from "../../extensions";
import { Icons } from "../../icon-types";
import { Error } from "../Error/Error";
import { Icon } from "../Icon/Icon";
import styles from "./InputDropdown.module.scss";

export interface OptionProps {
  value: string;
  label: string;
  [key: string]: any;
}

export interface InputDropdownProps<T extends OptionProps> {
  dataTestId?: string;
  wrapperClassName?: string;
  inputClassName?: string;
  options: Array<T>;
  optionsValue?: Array<T>;
  name?: string;
  label: string;
  onChange: (value: string, reset?: boolean) => void;
  onTextChange?: (value: string) => void;
  error?: string;
  value?: string | null;
  defaultValue?: string | null;
  defaultText?: string | undefined | null;
  onBlur?: (value?: string) => void;
  preferredOptions?: Array<string>;
  optionsClassname?: string;
  disabled?: boolean;
  iconName?: Icons;
  Option?: ({ label, ...rest }: OptionProps) => ReactElement | null;
  selectOnly?: boolean;
  disableOptionBorder?: boolean;
  shortInputOnComma?: boolean;
  search?: boolean;
  FooterElement?: () => ReactElement;
  dropdownText?: string;
  hideNoResults?: boolean;
  noOptionsText?: string;
  inputProps?: InputHTMLAttributes<HTMLInputElement>;
}

export const InputDropdown = <T extends OptionProps>({
  options,
  name,
  label,
  onChange,
  error,
  value: controlledValue,
  onBlur,
  preferredOptions,
  optionsClassname,
  wrapperClassName,
  inputClassName,
  disabled,
  iconName,
  Option,
  dataTestId,
  defaultValue,
  defaultText,
  selectOnly,
  disableOptionBorder,
  onTextChange,
  optionsValue,
  shortInputOnComma,
  search,
  FooterElement,
  dropdownText,
  hideNoResults,
  noOptionsText,
  inputProps,
}: InputDropdownProps<T>) => {
  const actualOptions = optionsValue || options;
  const {
    valuesDictionary,
    labelsDictionary,
  }: {
    valuesDictionary: Record<string, string>;
    labelsDictionary: Record<string, string>;
  } = useMemo(
    () =>
      actualOptions.reduce(
        (acc, { value, label }) => {
          const valuesDictionary = {
            ...acc.valuesDictionary,
            [value]: label,
          };
          const labelsDictionary = {
            ...acc.labelsDictionary,
            [label]: value,
          };
          return { valuesDictionary, labelsDictionary };
        },
        { valuesDictionary: {}, labelsDictionary: {} }
      ),
    [actualOptions]
  );

  const { actualPreferredOptions, actualNonPreferredOptions } = useMemo(
    () =>
      actualOptions.reduce<{
        actualPreferredOptions: Array<T>;
        actualNonPreferredOptions: Array<T>;
      }>(
        (acc, option) => {
          preferredOptions?.includes(option.value)
            ? acc.actualPreferredOptions.push(option)
            : acc.actualNonPreferredOptions.push(option);
          return acc;
        },
        { actualPreferredOptions: [], actualNonPreferredOptions: [] }
      ),
    [actualOptions, preferredOptions]
  );

  const sortedActualPreferredOptions = useMemo(() => {
    return (
      preferredOptions?.reduce<Array<T>>((acc, preferredOption) => {
        const OPTION = actualPreferredOptions.find(({ value }) => value === preferredOption);
        return !!OPTION ? [...acc, OPTION] : acc;
      }, []) || []
    );
  }, [preferredOptions, actualPreferredOptions]);

  const filteredOptions = useRef([...sortedActualPreferredOptions, ...actualNonPreferredOptions]);

  const initialValue = useMemo(() => {
    return actualOptions.length === 1
      ? actualOptions[0].value
      : controlledValue ?? defaultValue ?? "";
  }, [actualOptions, controlledValue, defaultValue]);

  const initialInput = useMemo(() => {
    if (defaultText) return defaultText;
    return actualOptions.length === 1
      ? actualOptions[0].label
      : initialValue
      ? valuesDictionary[initialValue]
      : "";
  }, [actualOptions, defaultText, initialValue, valuesDictionary]);

  const [value, setValue] = useState(initialValue);
  const [input, setInput] = useState(initialInput);
  const [focusValue, setFocusValue] = useState(
    initialValue || filteredOptions.current[0]?.value || ""
  );
  const [isFocused, setIsFocused] = useState(false);
  const isFocusedRef = useRef(false);
  const focusedOptionRef = useRef<HTMLDivElement>(null);
  const firstOptionRef = useRef<HTMLDivElement>(null);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  const { _textNoResults } = useCopy("CustomSelect");

  useEffect(() => {
    if (typeof controlledValue === "string" || actualOptions.length === 1) {
      setValue(controlledValue ?? (actualOptions.length === 1 ? actualOptions[0].value : ""));
    }
  }, [controlledValue, actualOptions]);

  /** This resets input value onBlur **/
  useEffect(() => {
    if (!isFocused) {
      setInput(defaultText || valuesDictionary[value] || "");
      setFocusValue(
        actualOptions.find(({ value: optionValue }) => optionValue === value)?.value ||
          filteredOptions.current[0]?.value ||
          ""
      );
      filteredOptions.current = [...sortedActualPreferredOptions, ...actualNonPreferredOptions];
    } else {
      focusedOptionRef?.current?.scrollIntoView({
        behavior: "auto",
        block: "nearest",
      });
    }
  }, [
    defaultText,
    isFocused,
    value,
    valuesDictionary,
    actualOptions,
    actualNonPreferredOptions,
    sortedActualPreferredOptions,
    initialInput,
  ]);

  const setFocus = useCallback(
    (isFocus: boolean, valueChange?: string) => {
      if (isFocusedRef.current && onBlur) {
        onBlur(valueChange);
      }
      setIsFocused(isFocus);
      isFocusedRef.current = isFocus;
    },
    [onBlur]
  );

  useEffect(() => {
    function clickHandler(event: MouseEvent) {
      if (!wrapperRef?.current?.contains(event?.target as Node | null)) {
        setFocus(false);
      }
    }

    function arrowPressHandle(event: KeyboardEvent) {
      if (!(isFocusedRef.current && ["ArrowUp", "ArrowDown"].includes(event.code))) return;

      if (event.code === "ArrowDown") {
        setFocusValue((prevState) => {
          const previousIndex = filteredOptions.current.findIndex(
            ({ value: optionValue }) => prevState === optionValue
          );
          return (
            filteredOptions.current[previousIndex + 1]?.value ||
            filteredOptions.current[previousIndex]?.value ||
            ""
          );
        });
        focusedOptionRef?.current?.scrollIntoView({
          behavior: "smooth",
          block: "nearest",
        });
      } else if (event.code === "ArrowUp") {
        setFocusValue((prevState) => {
          const previousIndex = filteredOptions.current.findIndex(
            ({ value: optionValue }) => prevState === optionValue
          );
          return (
            filteredOptions.current[previousIndex - 1]?.value ||
            filteredOptions.current[previousIndex]?.value ||
            ""
          );
        });
        focusedOptionRef?.current?.scrollIntoView({
          behavior: "smooth",
          block: "nearest",
        });
      }
    }

    document.addEventListener("mousedown", clickHandler);
    document.addEventListener("keydown", arrowPressHandle);

    return () => {
      document.removeEventListener("mousedown", clickHandler);
      document.removeEventListener("keydown", arrowPressHandle);
    };
  }, [actualOptions.length, isFocused, setFocus]);

  function getFilteredActualOptions(input: string) {
    return [...sortedActualPreferredOptions, ...actualNonPreferredOptions].filter((option) =>
      option?.label?.toLowerCase().includes(input.toLowerCase())
    );
  }

  function findAutofillValue(newInput: string) {
    return (
      [...sortedActualPreferredOptions, ...actualNonPreferredOptions].find(
        (option) => option?.label?.toLowerCase() === newInput.toLowerCase()
      )?.value || ""
    );
  }

  function handleAutofill(value: string) {
    const autofillValue = valuesDictionary[value] ? value : labelsDictionary[value];
    const newAutofillValue = autofillValue || findAutofillValue(value);
    if (newAutofillValue) {
      setValue(newAutofillValue);
      onChange(newAutofillValue);
      setInput(valuesDictionary[value] ?? "");
      setFocus(false, newAutofillValue);
    } else {
      setInput(value);
      filteredOptions.current = getFilteredActualOptions(value);
    }
  }

  function clearDropdown(reset: boolean) {
    setValue("");
    onChange("", reset);
    setInput("");
    if (isFocused) {
      inputRef?.current?.focus();
    }
    filteredOptions.current = getFilteredActualOptions("");
  }

  function handleOnInput({ target: { value } }: ChangeEvent<HTMLInputElement>) {
    onTextChange && onTextChange(value);
    if (value === "") {
      /** Reset field when user completely deletes input **/
      clearDropdown(false);
      const newFilteredActualOptions = getFilteredActualOptions("");
      filteredOptions.current = newFilteredActualOptions;
      setFocusValue(newFilteredActualOptions[0]?.value || "");
      firstOptionRef?.current?.scrollIntoView({
        behavior: "smooth",
        block: "nearest",
      });
    } else if (!input && value.length >= 2) {
      handleAutofill(value);
    } else if (value.length >= 2 && !value.includes(input) && !input.includes(value)) {
      handleAutofill(value);
    } else {
      setInput(value);
      const newFilteredActualOptions = getFilteredActualOptions(value);
      filteredOptions.current = newFilteredActualOptions;
      setFocusValue(newFilteredActualOptions[0]?.value || "");
      firstOptionRef?.current?.scrollIntoView({
        behavior: "smooth",
        block: "nearest",
      });
    }
  }

  //The optionsValue parameter dynamically controls the list of options based on its parent, so the component should remain enabled when it's available.
  const isDisabled = disabled || (!optionsValue && options.length === 1);
  const wrapperClassnames = classNames(styles.wrapper, wrapperClassName, {
    [styles.withError]: !!error && !isFocused,
    [styles.focus]: isFocused,
    [styles.disabled]: isDisabled,
  });
  const isAnimated = (!selectOnly && isFocused) || value || input;

  const OptionComponent = ({ value: optionValue, label, index, ...rest }: OptionProps) => (
    // eslint-disable-next-line jsx-a11y/click-events-have-key-events
    <div
      className={classNames(styles.option, {
        [styles.active]: optionValue === value,
        [styles.optionFocus]: optionValue === focusValue,
        [styles.disableBorderTop]: disableOptionBorder,
      })}
      onClick={() => {
        setValue(optionValue);
        onChange(optionValue);
        setInput(valuesDictionary[optionValue] ?? "");
        setFocus(false, optionValue);
      }}
      role="option"
      aria-selected={optionValue === value}
      tabIndex={0}
      data-testid={`option-${index}`}
      ref={optionValue === focusValue ? focusedOptionRef : index === 0 ? firstOptionRef : undefined}
    >
      {Option ? (
        <Option label={label} active={optionValue === value} value={optionValue} {...rest} />
      ) : (
        label
      )}
    </div>
  );
  const filteredActualOptions = optionsValue ? optionsValue : filteredOptions.current;
  return (
    <>
      <div className={wrapperClassnames} ref={wrapperRef}>
        {iconName && (
          <Icon iconName={iconName} size={20} className={styles.icon} color={Colors.DarkGray2} />
        )}
        <input
          ref={inputRef}
          onFocus={() => {
            setFocus(true);
            if (!selectOnly && !search) setInput("");
          }}
          autoComplete={name}
          className={classNames(styles.input, inputClassName, {
            [styles.withIcon]: !!iconName && !isAnimated,
            [styles.disabled]: isDisabled,
            [styles.cursorPointer]: selectOnly,
            [styles.fade]: !isFocusedRef.current,
            [styles.activeInputColor]: isFocusedRef.current,
          })}
          value={shortInputOnComma ? input.split(",")[0] : input}
          onChange={handleOnInput}
          onKeyDown={(e) => {
            if (e.key === "Enter" && !focusValue) {
              setFocus(false);
              inputRef?.current?.blur();
            } else if (e.key === "Enter" && focusValue) {
              setValue(focusValue);
              onChange(focusValue);
              setInput(valuesDictionary[focusValue] ?? "");
              setFocus(false, focusValue);
              inputRef?.current?.blur();
            } else if (e.key === "Tab") {
              setFocus(false);
            } else if (e.key === "Escape") {
              inputRef?.current?.blur();
              setFocus(false);
            } else if (e.key === "ArrowUp" || e.key === "ArrowDown") {
              e.preventDefault();
            }
          }}
          id={name}
          name={name}
          disabled={isDisabled}
          data-testid={dataTestId}
          readOnly={selectOnly}
          aria-labelledby={`${name}-label`}
          {...inputProps}
        />
        <label
          htmlFor={name}
          className={classNames(styles.label, {
            [styles.animated]: isAnimated,
            [styles.withIcon]: !!iconName && !isAnimated,
          })}
          id={`${name}-label`}
        >
          {label}
        </label>
        {search && (
          <>
            {input && (
              <Icon
                iconName={Icons.DisplayCloseSmall}
                className={styles.icons}
                color={Colors.Indigo}
                onClick={() => clearDropdown(true)}
              />
            )}
            <Icon
              iconName={Icons.NavigationSearch}
              className={classNames(styles.searchIcon, styles.icons)}
              color={Colors.Indigo}
              onClick={() => setFocus(true)}
            />
          </>
        )}
        {!search && !isDisabled && (
          <div
            className={classNames("field__caret", styles.cursorPointer, {
              "field__caret--rotate": isFocused,
            })}
          >
            <div
              className={styles.caretBody}
              onClick={() => {
                if (isFocused) {
                  setFocus(false);
                } else {
                  setFocus(true);
                  inputRef?.current?.focus();
                  if (!selectOnly) setInput("");
                }
              }}
              role="presentation"
            />
          </div>
        )}
        {((isFocused && !hideNoResults) ||
          (isFocused && hideNoResults && (filteredActualOptions.length || !input))) && (
          <div className={classNames(styles.options, optionsClassname)}>
            {filteredActualOptions.length ? (
              <>
                {filteredActualOptions.map(({ value, label, ...rest }, index) => (
                  <OptionComponent
                    {...rest}
                    label={label}
                    value={value}
                    index={index}
                    key={value}
                  />
                ))}
                {FooterElement && <FooterElement />}
              </>
            ) : (
              <div className={classNames(styles.option, styles.disabled)}>
                {options.length === 0 ? noOptionsText : dropdownText || _textNoResults}
              </div>
            )}
          </div>
        )}
      </div>
      <Error error={error} />
    </>
  );
};
