import React from "react";

import { mapContentfulColorToStyleProp } from "../../utils/colors";
import { Button } from "../Button/Button";
import { Link as SmartLink, LinkProps } from "../Link/Link";
import { Select } from "../Select/Select";
import styles from "./BannerTextOnly.module.scss";

export interface BannerTextOnlyProps {
  dataTestId?: string;
  headingCopy: string;
  subheadingCopy: string;
  linkedButtonRef?: LinkProps;
  linkButtonCopy?: string;
  buttonOnClick?: () => void;
  downloadDropdown?: boolean;
  downloadDropdownFieldLabel?: string;
  downloadDropdownOptionsCollection?: { items: DownloadDropdownOption[] };
  downloadButtonCopy?: string;
  backgroundColor: string;
}

export interface DownloadDropdownOption {
  title: string;
  url: string;
}

export const BannerTextOnly: React.FC<BannerTextOnlyProps> = ({
  dataTestId,
  headingCopy,
  subheadingCopy,
  linkedButtonRef,
  linkButtonCopy,
  buttonOnClick,
  downloadDropdown,
  downloadDropdownFieldLabel,
  downloadDropdownOptionsCollection: { items } = { items: [] },
  downloadButtonCopy,
  backgroundColor,
}) => {
  const [selectedUrl, setDownloadUrl] = React.useState(
    items && items.length > 0 ? items[0].url : undefined
  );

  const handleUrlChange = ({ target: { value } }: React.ChangeEvent<HTMLSelectElement>) => {
    setDownloadUrl(value);
  };

  return (
    <section
      className="res-vertical-padding-48"
      style={{
        background: mapContentfulColorToStyleProp(backgroundColor),
      }}
      data-testid={dataTestId}
    >
      <div className="max-width-container centered-text">
        <div className="grid-12">
          <div className="col-8 col-start-3 col-l-10 col-start-l-2">
            <h2 className="large top white-text">{headingCopy}</h2>
            <h2 className={`text-line-height-125 white-text ${styles.subheading}`}>
              {subheadingCopy}
            </h2>
          </div>
        </div>
        {linkedButtonRef ? (
          <SmartLink {...linkedButtonRef}>
            <Button buttonSize="large">{linkButtonCopy}</Button>
          </SmartLink>
        ) : (
          buttonOnClick && (
            <Button buttonSize="large" onClick={buttonOnClick}>
              {linkButtonCopy}
            </Button>
          )
        )}
        {downloadDropdown && downloadDropdownFieldLabel && items && (
          <div className={styles.downloadFieldContainer}>
            <div className={styles.downloadField}>
              <Select
                className="field__input-wrapper--dark field__input-btn-wrapper"
                value={selectedUrl}
                label={downloadDropdownFieldLabel}
                options={items.map((item) => {
                  return { label: item.title, value: item.url };
                })}
                onChange={(selection) => handleUrlChange(selection)}
              />
              <a href={selectedUrl} target="_blank" rel="noreferrer" download>
                <Button buttonSize="medium" hierarchy="secondary" className={styles.downloadButton}>
                  {downloadButtonCopy}
                </Button>
              </a>
            </div>
          </div>
        )}
      </div>
    </section>
  );
};
