import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import { Icon } from "../Icon/Icon";
import styles from "./SearchFilterBar.module.scss";

export interface SearchFilterBarProps {
  actions?: React.ReactNode;
  className?: string;
  dataTestId?: string;
  filters?: React.ReactNode;
  open?: boolean;
  onOpenToggle?(open: boolean): void;
  search?: React.ReactNode;
  style?: React.CSSProperties;
}

export const SearchFilterBar: React.FC<SearchFilterBarProps> = ({
  dataTestId,
  search,
  filters,
  actions,
  className,
  style,
  open,
  onOpenToggle,
}) => {
  const [_collapsed, _setCollapsed] = React.useState(false);

  const collapsed = open || _collapsed;
  const setCollapsed = () => {
    if (open !== undefined) {
      onOpenToggle?.(!collapsed);
    } else {
      _setCollapsed(!collapsed);
    }
  };

  return (
    <div data-testid={dataTestId} className={classNames(styles.filterBar, className)} style={style}>
      <div className={styles.header}>
        <div className={styles.content}>
          {search}
          {search ? collapsed && filters : filters}
          {actions && <div className={styles.actions}>{actions}</div>}
        </div>
        {search && filters && (
          <NakedButton className={styles.toggle} onClick={setCollapsed} aria-label="toggle">
            <Icon
              iconName={collapsed ? Icons.ArrowUpLarge : Icons.ArrowDownLarge}
              color={Colors.White}
            />
          </NakedButton>
        )}
      </div>
    </div>
  );
};
