import classNames from "classnames";
import React from "react";

import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import styles from "./ProductTypeSelector.module.scss";

interface Props {
  isSelected?: boolean;
  image: string;
  label: string;
  onClick: (value: string) => void;
  value: string;
}

export const ProductTypeOption: React.FC<Props> = ({
  isSelected,
  image,
  label,
  onClick,
  value,
}) => (
  <div className={styles.option} data-testid={`product-type-option-${value}`}>
    <NakedButton
      className={classNames(styles.radioContainer, {
        [styles.active]: isSelected,
      })}
      onClick={() => onClick(value)}
    >
      <picture className={styles.imageContainer}>
        <source type="image/webp" srcSet={`${image}-1x.webp, ${image}-2x.webp 2x`} />
        <img
          className={styles.image}
          alt={label}
          src={image + "-1x.jpg"}
          srcSet={`${image}-1x.jpg, ${image}-2x.jpg 2x`}
        />
      </picture>
      <h4 className={styles.label}>{label}</h4>
    </NakedButton>
    {isSelected && <div className={styles.indicator} />}
  </div>
);
