import React from "react";

import { useCopy } from "../../extensions";
import styles from "./OrderDetails.module.scss";
import { Order, PaymentMethod } from "./types";

interface PaymentMethodProps {
  order: Order;
}

export const PaymentMethodDetails: React.FC<PaymentMethodProps> = ({ order }) => {
  const { _textCreditCard, _textCreditedOrder, _textPaymentMethod } = useCopy("OrderDetails");

  const PaymentName: Record<PaymentMethod, string> = {
    GOOGLEPAY: "Google Pay",
    APPLEPAY: "Apple Pay",
    PAYPAL: "PayPal",
    CREDITED_ORDER: _textCreditedOrder,
    CREDIT_CARD: _textCreditCard,
  };

  return (
    <div>
      <div className={styles.infoColumn}>
        <p>{_textPaymentMethod}</p>
      </div>
      {/** order.payment is deprecated shopify payment **/}
      {order.payment ? (
        <div className={styles.infoColumn}>
          <p>{PaymentName.CREDIT_CARD}</p>
          <p>{order.payment?.creditCardCompany}</p>
          <p>{order.payment?.creditCardNumber}</p>
          <p>{order.payment?.cardHolderName}</p>
        </div>
      ) : (
        order?.payments?.map((payment, index) => (
          <div className={styles.infoColumn} key={index}>
            {payment.method && <p>{PaymentName[payment.method]}</p>}
            {payment.method !== "GOOGLEPAY" && payment.method !== "APPLEPAY" && (
              <>
                {payment.creditCard?.creditCardCompany && (
                  <p>{payment.creditCard.creditCardCompany}</p>
                )}
                {payment.creditCard?.creditCardNumber && (
                  <p>{payment.creditCard.creditCardNumber}</p>
                )}
                {payment.creditCard?.cardHolderName && <p>{payment.creditCard.cardHolderName}</p>}
              </>
            )}
          </div>
        ))
      )}
    </div>
  );
};
