import classNames from "classnames";
import React, { CSSProperties } from "react";

import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../hooks";
import { isNextJs } from "../../utils";
import { LuluColor, mapContentfulColorToStyleProp } from "../../utils/colors";
import { ContentfulButtonLinkProps, LinkedButton } from "../ButtonLink/ButtonLink";
import {
  ContentfulCollapsibleContent,
  ContentfulCollapsibleContentProps,
} from "../CollapsibleContent/CollapsibleContent";
import { DownloadDropdown, DownloadDropdownProps } from "../DownloadDropdown/DownloadDropdown";
import { ImageWithMetadata, ImageWithMetadataProps } from "../ImageWithMetadata/ImageWithMetadata";
import { Link, LinkProps } from "../Link/Link";
import { LinkedIconAndText, LinkedIconAndTextProps } from "../LinkedIconAndText/LinkedIconAndText";
import { RichTextContainer, RichTextContainerProps } from "../RichTextContainer/RichTextContainer";
import { Video, VideoProps } from "../Video/Video";
import defaultStyles from "./DefaultElementCard.module.scss";
import newStyles from "./NewElementCard.module.scss";

export interface ElementCardProps {
  dataTestId?: string;
  borderColor?: string;
  elementCardClassNames?: string;
  backgroundImageColor?: string;
  imageWidth?: number;
  isFullHeight?: boolean;
  isHorizontallyAligned?: boolean;
  borderLocation?: "Top" | "Right" | "Bottom" | "Left";
  imageVerticalAlignment?: "Top" | "Center" | "Bottom";
  buttonLinkRef?: ContentfulButtonLinkProps;
  imageWithMetadataRef?: ImageWithMetadataProps;
  linksCollection?: { items: LinkedIconAndTextProps[] };
  richTextContainerRef?: RichTextContainerProps;
  collapsibleContentRef?: ContentfulCollapsibleContentProps;
  videoRef?: VideoProps;
  downloadDropdownRef?: DownloadDropdownProps;
  containerEffect?: "Drop Shadow" | "Stroke" | "None";
  containerHoverEffect?: "Drop Shadow" | "Stroke" | "Only Image Animation" | null;
  backgroundColor?: LuluColor;
  borderRadius?: number;
  linkCardRef?: LinkProps;
  contentCollection?: {
    items: (
      | VideoProps
      | ImageWithMetadataProps
      | ContentfulCollapsibleContentProps
      | DownloadDropdownProps
      | ContentfulButtonLinkProps
      | LinkedIconAndTextProps
      | RichTextContainerProps
    )[];
  };
}

const DefaultElementCard: React.FC<ElementCardProps> = ({
  dataTestId,
  borderColor,
  borderLocation,
  buttonLinkRef,
  elementCardClassNames,
  imageWithMetadataRef,
  isFullHeight = false,
  linksCollection: { items } = {},
  richTextContainerRef,
  collapsibleContentRef,
  videoRef,
  downloadDropdownRef,
  isHorizontallyAligned,
  backgroundImageColor,
  imageVerticalAlignment,
  imageWidth,
  containerEffect,
  backgroundColor = "White",
  borderRadius,
  linkCardRef,
  containerHoverEffect,
}) => {
  const windowWidth = useWindowWidth();
  const isTabletOrDesktop = windowWidth > MEDIA_BREAKPOINTS.M;

  const isRenderedInSSR = isNextJs && windowWidth === 0;
  const shouldRenderHorizontally =
    isHorizontallyAligned && (isRenderedInSSR ? true : isTabletOrDesktop); // isRenderedInSSR checked to fix react hydration error

  const canRenderDownloadDropdown =
    !!downloadDropdownRef && Object.keys(downloadDropdownRef).length > 0;

  const elementCardClasses = classNames(
    defaultStyles.elementCard,
    {
      [defaultStyles.elementCardIsFullHeight]: isFullHeight,
      [defaultStyles.visibleOverflow]: canRenderDownloadDropdown,
      [defaultStyles.isHorizontallyAligned]: shouldRenderHorizontally,
      [defaultStyles.dropShadow]: containerEffect === "Drop Shadow",
      [defaultStyles.stroke]: containerEffect === "Stroke" || !containerEffect, // For some reason, the default value is not set and the null value is accepted from Contentful
      [defaultStyles.dropShadowOnHover]: containerHoverEffect === "Drop Shadow",
      [defaultStyles.strokeOnHover]: containerHoverEffect === "Stroke",
      [defaultStyles.zoomImageOnHover]: !!containerHoverEffect,
    },
    elementCardClassNames
  );

  const imageContainerClasses = classNames(defaultStyles.imageContainer, {
    [defaultStyles.horizontalImageContainer]: shouldRenderHorizontally,
    [defaultStyles.imageAlignTop]: shouldRenderHorizontally && imageVerticalAlignment === "Top",
    [defaultStyles.imageAlignBottom]:
      shouldRenderHorizontally && imageVerticalAlignment === "Bottom",
  });

  const borderStyle =
    borderLocation && borderColor
      ? { [`border${borderLocation}`]: `10px solid ${borderColor}` }
      : undefined;

  const contentContainerClasses = classNames({
    [defaultStyles.contentContainer]: buttonLinkRef || richTextContainerRef || items?.length,
    [defaultStyles.contentContainerIsFullHeight]: isFullHeight,
  });

  const buttonLinkContainerClasses = classNames(
    buttonLinkRef?.linkedButtonClassNames || "res-vertical-margins-16",
    {
      [defaultStyles.buttonBottomAlignment]:
        isFullHeight && buttonLinkRef?.buttonAlignment?.includes("bottom"),
    }
  );

  const getContentContainerWidth = () => {
    if (shouldRenderHorizontally && !!imageWidth) return `calc(100% - ${imageWidth}px)`;

    return "auto";
  };

  return (
    <div
      className={elementCardClasses}
      style={{
        ...borderStyle,
        borderRadius: `${borderRadius ?? 4}px`, // For some reason, the default value is not set and the null value is accepted from Contentful
        backgroundColor: mapContentfulColorToStyleProp(backgroundColor),
      }}
      data-testid={dataTestId}
    >
      {imageWithMetadataRef && (
        <div
          className={classNames(imageContainerClasses, defaultStyles.image)}
          style={{
            backgroundColor: backgroundImageColor,
            width: (shouldRenderHorizontally && imageWidth && `${imageWidth}px`) || "auto",
          }}
        >
          <ImageWithMetadata
            {...imageWithMetadataRef}
            // <a> cannot appear as a descendant of <a>
            imageLinkPage={!linkCardRef ? imageWithMetadataRef.imageLinkPage : undefined}
            // <a> cannot appear as a descendant of <a>
            imageLinkUrl={!linkCardRef ? imageWithMetadataRef.imageLinkUrl : undefined}
          />
        </div>
      )}
      {videoRef && <Video {...videoRef} />}
      <div
        className={contentContainerClasses}
        style={{
          width: getContentContainerWidth(),
        }}
      >
        {richTextContainerRef && (
          <RichTextContainer
            standaloneEntry={false}
            richTextContainerClassNames={richTextContainerRef.richTextContainerClassNames}
            richTextFieldsCollection={richTextContainerRef.richTextFieldsCollection}
          />
        )}
        {collapsibleContentRef && <ContentfulCollapsibleContent {...collapsibleContentRef} />}
        {/* <a> cannot appear as a descendant of <a> */}
        {!linkCardRef &&
          items &&
          items.map((link: LinkedIconAndTextProps, index: number) => (
            <div className={defaultStyles.linkedIconAndText} key={`link-${index}`}>
              <LinkedIconAndText {...link} />
            </div>
          ))}
        {/* <a> cannot appear as a descendant of <a> */}
        {!linkCardRef && buttonLinkRef && (
          <div className={buttonLinkContainerClasses}>
            <LinkedButton
              buttonSize={buttonLinkRef.buttonSize}
              hierarchy={buttonLinkRef.hierarchy}
              linkRef={buttonLinkRef.linkRef.page || buttonLinkRef.linkRef}
              buttonAlignment={buttonLinkRef.buttonAlignment}
              buttonText={buttonLinkRef.buttonText}
            />
          </div>
        )}
        {/* The download button is an Anchor tag, <a> cannot appear as a descendant of <a> */}
        {!linkCardRef && canRenderDownloadDropdown && (
          <div className="res-vertical-margins-16">
            <DownloadDropdown {...(downloadDropdownRef as DownloadDropdownProps)} />
          </div>
        )}
      </div>
    </div>
  );
};

const eligibleComponents = {
  Video,
  ImageWithMetadata,
  CollapsibleContent: ContentfulCollapsibleContent,
  DownloadDropdown,
  LinkedButton,
  LinkedIconAndText,
  RichTextContainer,
};

const mapToReactComponent = (item: any, index: number) => {
  const Component = (eligibleComponents as any)[item.__typename];

  return Component ? React.createElement(Component, { ...item, key: index }) : null;
};

const NewElementCard = ({
  dataTestId,
  borderColor,
  borderLocation,
  buttonLinkRef,
  elementCardClassNames,
  imageWithMetadataRef,
  isFullHeight = false,
  linksCollection,
  richTextContainerRef,
  collapsibleContentRef,
  videoRef,
  downloadDropdownRef,
  isHorizontallyAligned,
  backgroundImageColor,
  imageVerticalAlignment,
  imageWidth,
  containerEffect,
  backgroundColor = "White",
  borderRadius,
  linkCardRef,
  containerHoverEffect,
  contentCollection,
}: ElementCardProps) => {
  const windowWidth = useWindowWidth();

  const isTabletOrDesktop = windowWidth > MEDIA_BREAKPOINTS.M;

  const isRenderedInSSR = isNextJs && windowWidth === 0;
  const shouldRenderHorizontally =
    isHorizontallyAligned && (isRenderedInSSR ? true : isTabletOrDesktop); // isRenderedInSSR checked to fix react hydration error

  const canRenderDownloadDropdown =
    !!downloadDropdownRef && Object.keys(downloadDropdownRef).length > 0;

  const borderStyle =
    borderLocation && borderColor
      ? { [`border${borderLocation}`]: `10px solid ${borderColor}` }
      : undefined;

  const firstItem = contentCollection?.items?.[0];
  const lastItem = contentCollection?.items?.at(-1);
  const isFirstItemAnImageOrVideo = isImageOrVideoComponent(firstItem);
  const isLastItemAnImageOrVideo = isImageOrVideoComponent(lastItem);
  const hasBorderTop = borderStyle && borderLocation === "Top";
  const hasBorderBottom = borderStyle && borderLocation === "Bottom";

  const elementCardClasses = classNames(
    newStyles.elementCard,
    {
      [newStyles.elementCardIsFullHeight]: isFullHeight,
      [newStyles.visibleOverflow]: canRenderDownloadDropdown,
      [newStyles.isHorizontallyAligned]: shouldRenderHorizontally,
      [newStyles.dropShadow]: containerEffect === "Drop Shadow",
      [newStyles.stroke]: containerEffect === "Stroke" || !containerEffect, // For some reason, the default value is not set and the null value is accepted from Contentful
      [newStyles.dropShadowOnHover]: containerHoverEffect === "Drop Shadow",
      [newStyles.strokeOnHover]: containerHoverEffect === "Stroke",
      [newStyles.zoomImageOnHover]: !!containerHoverEffect,
      [newStyles.lastItemWithBorderRadius]: isLastItemAnImageOrVideo && !hasBorderBottom,
    },
    elementCardClassNames
  );

  const imageContainerClasses = classNames(newStyles.imageContainer, {
    [newStyles.horizontalImageContainer]: shouldRenderHorizontally,
    [newStyles.imageAlignTop]: shouldRenderHorizontally && imageVerticalAlignment === "Top",
    [newStyles.imageAlignBottom]: shouldRenderHorizontally && imageVerticalAlignment === "Bottom",
  });

  if ((contentCollection?.items.length || 0) === 0)
    return (
      <DefaultElementCard
        dataTestId={dataTestId}
        borderColor={borderColor}
        borderLocation={borderLocation}
        buttonLinkRef={buttonLinkRef}
        elementCardClassNames={elementCardClassNames}
        imageWithMetadataRef={imageWithMetadataRef}
        isFullHeight={isFullHeight}
        linksCollection={linksCollection}
        richTextContainerRef={richTextContainerRef}
        collapsibleContentRef={collapsibleContentRef}
        videoRef={videoRef}
        downloadDropdownRef={downloadDropdownRef}
        isHorizontallyAligned={isHorizontallyAligned}
        backgroundImageColor={backgroundImageColor}
        imageVerticalAlignment={imageVerticalAlignment}
        imageWidth={imageWidth}
        containerEffect={containerEffect}
        backgroundColor={backgroundColor}
        borderRadius={borderRadius}
        linkCardRef={linkCardRef}
        containerHoverEffect={containerHoverEffect}
      />
    );

  return (
    <div
      style={
        {
          ...borderStyle,
          borderRadius: `${borderRadius ?? 4}px`, // For some reason, the default value is not set and the null value is accepted from Contentful
          backgroundColor: mapContentfulColorToStyleProp(backgroundColor),
          "--border-radius": `${borderRadius ?? 4}px`,
        } as CSSProperties
      }
      data-testid={dataTestId}
      className={elementCardClasses}
    >
      {shouldRenderHorizontally ? (
        <>
          <div
            className={imageContainerClasses}
            style={
              {
                ...(backgroundImageColor && { "--imageContainerBgColor": backgroundImageColor }),
                ...(shouldRenderHorizontally && imageWidth && { "--width": `${imageWidth}px` }),
              } as CSSProperties
            }
          >
            {mapToReactComponent(contentCollection?.items?.[0], 0)}
          </div>
          <div>
            {contentCollection?.items.map((content, index) =>
              index !== 0 ? mapToReactComponent(content, index) : null
            )}
          </div>
        </>
      ) : (
        <>
          {isFirstItemAnImageOrVideo && (
            <div
              className={newStyles.imageAndVideoWrapper}
              style={{
                borderTopLeftRadius:
                  isFirstItemAnImageOrVideo && !hasBorderTop ? `${borderRadius ?? 4}px` : 0, // For some reason, the default value is not set and the null value is accepted from Contentful
                borderTopRightRadius:
                  isFirstItemAnImageOrVideo && !hasBorderTop ? `${borderRadius ?? 4}px` : 0,
              }}
            >
              {mapToReactComponent(contentCollection?.items?.[0], 0)}
            </div>
          )}
          {contentCollection?.items.map((content, index) =>
            isFirstItemAnImageOrVideo && index === 0 ? null : mapToReactComponent(content, index)
          )}
        </>
      )}
    </div>
  );
};

export const ElementCard: React.FC<ElementCardProps> = ({ linkCardRef, ...props }) =>
  linkCardRef ? (
    <Link
      {...linkCardRef}
      className={classNames(props.elementCardClassNames, newStyles.asLink)}
      dataTestId="ElementCard-LinkCard"
    >
      <NewElementCard {...props} linkCardRef={linkCardRef} />
    </Link>
  ) : (
    <NewElementCard {...props} />
  );

const isImageOrVideoComponent = (item: any) => {
  return (
    typeof item === "object" &&
    "__typename" in item &&
    (item.__typename === "ImageWithMetadata" || item.__typename === "Video")
  );
};
