type Copy = typeof DEFAULT_COPY;
type Key = keyof Copy;

type DeepPartial<T> = {
  [P in keyof T]?: T[P] extends object ? DeepPartial<T[P]> : T[P];
};

export const setCopy = (copy: DeepPartial<Copy>): Copy =>
  Object.keys(copy).reduce(
    (copyObj, key) => ({
      ...copyObj,
      [key]: {
        ...copyObj[key as Key],
        ...copy[key as Key],
      },
    }),
    { ...DEFAULT_COPY }
  );
export const CopyContext = React.createContext<Copy>(DEFAULT_COPY);

export function useCopy(): Copy;
export function useCopy<K extends keyof Copy>(key: K): Copy[K];
export function useCopy(key?: keyof Copy) {
  const data = React.useContext(CopyContext);
  return key ? data[key] : data;
}
