import { ProductTypeEnum } from "./types";

const bookTemplatePath = "https://assets.lulu.com/media/templates/book/lulu-book-template-";
const calendarTemplatePath =
  "https://assets.lulu.com/media/templates/calendar/lulu-calendar-template-us-letter-landscape-";
const guidePath = "https://assets.lulu.com/media/guides/";

const trimSizeFileMap: Record<string, string> = {
  "0425X0687": `${bookTemplatePath}pocket-book.zip`,
  "0550X0850": `${bookTemplatePath}digest.zip`,
  "0583X0827": `${bookTemplatePath}a5.zip`,
  "0600X0900": `${bookTemplatePath}us-trade.zip`,
  "0614X0921": `${bookTemplatePath}royal.zip`,
  "0663X1025": `${bookTemplatePath}comic-book.zip`,
  "0700X1000": `${bookTemplatePath}executive.zip`,
  "0744X0968": `${bookTemplatePath}crown-quarto.zip`,
  "0750X0750": `${bookTemplatePath}small-square.zip`,
  "0827X1169": `${bookTemplatePath}a4.zip`,
  "0850X0850": `${bookTemplatePath}square.zip`,
  "0850X1100": `${bookTemplatePath}us-letter.zip`,
  "0900X0700": `${bookTemplatePath}small-landscape.zip`,
  "1100X0850": `${bookTemplatePath}us-letter-landscape.zip`,
  "1169X0827": `${bookTemplatePath}a4-landscape.zip`,
  "0500X0800": `${bookTemplatePath}novella.zip`,
};

const magazineTrimSizeFileMap: Record<string, string> = {
  "0850X1100": `${bookTemplatePath}us-letter-magazine.zip`,
  "0827X1169": `${bookTemplatePath}a4-magazine.zip`,
};

export const getTemplateFilename = (
  productType: ProductTypeEnum,
  trimSize: string | null,
  pageCount: number | null,
  locale: string = "en"
) => {
  const monthsNumber = !pageCount || (pageCount + 2) / 2 <= 12 ? 12 : 18;

  if (productType === "EBOOK" && !trimSize) {
    return `${guidePath}${locale}/lulu-ebook-creation-guide.pdf`;
  }

  switch (productType) {
    case "COMIC_BOOK":
      return `${bookTemplatePath}comic-book.zip`;
    case "CALENDAR":
      return `${calendarTemplatePath}${monthsNumber}-month.zip`;
    case "MAGAZINE":
      // need to use non-null assertion as TS can't narrow down for whatever reason
      return magazineTrimSizeFileMap[trimSize!];
    default:
      // same here
      return trimSizeFileMap[trimSize!];
  }
};
