import { Icons } from "../../icon-types";
import { Order } from "./types";

export const getProductTypeIcon = (type?: string) => {
  switch (type) {
    case "CALENDAR":
      return Icons.FormatsCalendar;
    case "EBOOK":
      return Icons.FormatsEbook;
    default:
      return Icons.FormatsBook;
  }
};

const isLuluDotComOrderReference = (orderRef?: string | null) => {
  if (!orderRef) return false;

  return /(AUD|CAD|EUR|GBP|USD)-C[0-9]+/.test(orderRef);
};

/**
 * Gift receipts feature is active only at Christmas and previous month, till 30.12 without 31.12 https://app.clickup.com/t/8587959/LU-6063
 * User is able to download gift receipt
 */
const areGiftReceiptsEnabled = () => {
  const today = new Date();
  const month = today.getMonth() + 1; // getMonth() returns 0-11, so we add 1
  const day = today.getDate();

  return month === 11 || (month === 12 && day <= 30);
};

export const showGiftReceipt = (order: Order, ignoreDate?: boolean) => {
  return (
    (ignoreDate || areGiftReceiptsEnabled()) &&
    (order.status === "COMPLETE" || order.status === "IN_PRODUCTION") &&
    order.lineItems.some(({ product }) => product?.type !== "EBOOK") &&
    isLuluDotComOrderReference(order.referenceNo)
  );
};
