import classNames from "classnames";
import React from "react";

import { useCopy } from "../../extensions";
import { validAmount, validInput } from "../../utils";
import styles from "./RevenueGoalForm.module.scss";

export interface RevenueGoalFormProps {
  dataTestId?: string;
  currencies: string[];
  revenueGoal: RevenueGoal;
  onChange: (revenueGoal: RevenueGoal) => void;
  error?: string;
  disabled?: boolean;
}

export interface RevenueGoal {
  amount: string;
  currency: string;
}

export const RevenueGoalForm: React.FC<RevenueGoalFormProps> = ({
  dataTestId,
  revenueGoal,
  currencies,
  onChange,
  error,
  disabled = false,
}) => {
  const { _textCurrency, _textRevenueGoal } = useCopy("RevenueGoalForm");
  const [selectedRevenueGoal, setSelectedRevenueGoal] = React.useState(revenueGoal);

  React.useEffect(() => {
    if (validAmount(selectedRevenueGoal.amount)) {
      onChange(selectedRevenueGoal);
    }
  }, [selectedRevenueGoal, onChange]);

  const handleAmountChange = ({ target: { value } }: React.ChangeEvent<HTMLInputElement>) => {
    if (validInput(value)) {
      setSelectedRevenueGoal((previous) => ({ ...previous, amount: value }));
    }
  };

  const handleCurrencyChange = ({ target: { value } }: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedRevenueGoal((previous) => ({ ...previous, currency: value }));
  };

  const fieldClass = classNames("field", { "field--error": error });

  return (
    <section data-test-id={dataTestId}>
      <div className={styles.revenueGoalForm}>
        <div className={styles.fieldContainer}>
          <div className={fieldClass}>
            <div className="field__input-wrapper field__input-wrapper--with-icon">
              <label className="field__label field__label--visible" htmlFor="revenueGoal">
                {_textRevenueGoal}
              </label>
              <input
                disabled={disabled}
                className="field__input"
                value={selectedRevenueGoal.amount}
                placeholder="_.__"
                onChange={handleAmountChange}
                id="revenueGoal"
              />
              <i className="input-icon icon-wizard-revenue-goal" />
            </div>
            {error && <p className="field__message field__message--error">{error}</p>}
          </div>
        </div>
        <div className={styles.fieldContainer}>
          <div className="field">
            <div className="field__input-wrapper field__input-wrapper--select field__input-wrapper--with-icon">
              <label className="field__label field__label--visible" htmlFor="currency">
                {_textCurrency}
              </label>
              <select
                id="currency"
                disabled={disabled}
                className="field__input field__input--select"
                value={selectedRevenueGoal.currency}
                onChange={handleCurrencyChange}
              >
                {currencies.map((currency) => (
                  <option key={currency} value={currency}>
                    {currency}
                  </option>
                ))}
              </select>
              <i className="input-icon icon-contact-currency" />
              <div className="field__caret" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
