import React from "react";

import { useCopy } from "../../extensions";
import { DocumentDimension } from "../PricingCalculator/types";
import styles from "./AdvancedProductSpecsTable.module.scss";

export interface AdvancedProductSpecsTableProps {
  dataTestId?: string;
  podPackageId?: string;
  minPages?: number;
  maxPages?: number;
  dimensionsIn?: DocumentDimension;
  dimensionsMm?: DocumentDimension;
}

export const AdvancedProductSpecsTable: React.FC<AdvancedProductSpecsTableProps> = ({
  dataTestId,
  podPackageId,
  minPages,
  maxPages,
  dimensionsIn,
  dimensionsMm,
}) => {
  const { _textLuluSku, _textPageSize, _textMinMaxPageCount, _textCoverSize, _textSpineWidth } =
    useCopy("AdvancedProductSpecsTable");

  return (
    <div className={styles.container} data-testid={dataTestId}>
      <dl className={styles.specsTable}>
        <div>
          <dt>{_textLuluSku}</dt>
          <dd>{podPackageId ?? "---"}</dd>
        </div>
        <div>
          <dt>{_textPageSize}</dt>
          <dd>
            {dimensionsIn && dimensionsMm
              ? `${dimensionsIn.interiorWidth ?? "-"} x ${
                  dimensionsIn.interiorHeight ?? "-"
                } in / ${dimensionsMm.interiorWidth ?? "-"} x ${
                  dimensionsMm.interiorHeight ?? "-"
                } mm`
              : "---"}
          </dd>
        </div>
        <div>
          <dt>{_textMinMaxPageCount}</dt>
          <dd>{minPages && maxPages ? `${minPages} - ${maxPages}` : "---"}</dd>
        </div>
        <div>
          <dt>{_textCoverSize}</dt>
          <dd>
            {dimensionsIn && dimensionsMm
              ? `${dimensionsIn.coverWidth ?? "-"} x ${dimensionsIn.coverHeight ?? "-"} in / ${
                  dimensionsMm.coverWidth ?? "-"
                } x ${dimensionsMm.coverHeight ?? "-"} mm`
              : "---"}
          </dd>
        </div>
        <div>
          <dt>{_textSpineWidth}</dt>
          <dd>
            {dimensionsIn && dimensionsMm
              ? `${dimensionsIn.spineWidth ?? "-"} in / ${dimensionsMm.spineWidth ?? "-"} mm`
              : "---"}
          </dd>
        </div>
      </dl>
    </div>
  );
};
