import classNames from "classnames";
import React, { useEffect, useRef } from "react";
import { useDebouncedCallback } from "use-debounce";

import { Colors } from "../../../constants";
import { useCopy } from "../../../extensions";
import { MEDIA_BREAKPOINTS, useWindowWidth } from "../../../hooks";
import { Icons } from "../../../icon-types";
import { getAmountByCurrency } from "../../../utils";
import { Icon } from "../../Icon/Icon";
import { IconButton } from "../../IconButton/IconButton";
import { Loading } from "../../Loading/Loading";
import { MoneyDisplay } from "../../MoneyDisplay/MoneyDisplay";
import { Tooltip } from "../../Tooltip/Tooltip";
import { CartDrawerProps, CartItemType } from "../CartDrawer";
import styles from "./CartItem.module.scss";

const MAX_ITEM_QUANTITY = 1000000;

export interface CartItemProps {
  item: CartItemType;
  onQuantityChange?: CartDrawerProps["onQuantityChange"];
  isLoading?: boolean;
  isRemoved?: boolean;
  currency: string;
  totalItemsText?: string;
  locale?: string;
}

interface QuantityPickerProps extends Pick<CartDrawerProps, "onQuantityChange"> {
  productId: string;
  quantity: number;
}

const getProductTypeIcon = (type?: string) => {
  switch (type) {
    case "CALENDAR":
      return Icons.FormatsCalendar;
    case "EBOOK":
      return Icons.FormatsEbook;
    default:
      return Icons.FormatsBook;
  }
};

const QuantityPicker = ({
  quantity: savedQuantity,
  onQuantityChange,
  productId,
}: QuantityPickerProps) => {
  const { _ariaLabel } = useCopy("QuantityPicker");
  const inputRef = useRef<HTMLInputElement>(null);
  const spanRef = useRef<HTMLSpanElement>(null);
  const [quantity, setQuantity] = React.useState<number | undefined>(savedQuantity);

  const handleQuantityChange = useDebouncedCallback((newQuantity: number) => {
    onQuantityChange(productId, newQuantity);
  }, 200);

  useEffect(() => {
    setQuantity(savedQuantity);
  }, [savedQuantity]);

  useEffect(() => {
    if (spanRef.current && inputRef.current) {
      if (quantity) {
        inputRef.current.value = String(quantity);
      }

      spanRef.current.innerText = inputRef.current.value;
      const width = Math.ceil(spanRef.current.getBoundingClientRect().width);
      const initialWidth = 30;

      // This logic prevents input width fluctuation on number width change
      inputRef.current.style.width =
        width > initialWidth ? `${width * 1.1}px` : `${initialWidth}px`;
    }
  }, [quantity]);

  const testIdPrefix = `product-quantity-picker`;

  return (
    <div className={styles.quantityPicker} data-testid={testIdPrefix}>
      <Icon
        dataTestId={`${testIdPrefix}-minus-icon`}
        iconName={Icons.DisplayCollapseMinusSmall}
        onClick={() => {
          if (!quantity || quantity <= 0) return;

          setQuantity(quantity - 1);
          handleQuantityChange(quantity - 1);
        }}
        color={Colors.PaleGray6}
        className={styles.button}
        size={16}
      />
      {/* it cannot be display: none, as it wouldn't have render width  */}
      <span
        ref={spanRef}
        style={{
          visibility: "hidden",
          position: "absolute",
        }}
      />
      <input
        aria-label={_ariaLabel}
        ref={inputRef}
        data-testid={`${testIdPrefix}-quantity-input`}
        type="number"
        onBlur={({ target }) => {
          if (!target.value) {
            target.value = String(quantity ?? savedQuantity);
            setQuantity(savedQuantity);
          }
        }}
        onChange={({ target }) => {
          if (!target.value) {
            setQuantity(undefined);
            return;
          }

          const newQuantity = Number(target.value);

          if (newQuantity < 0 || newQuantity > MAX_ITEM_QUANTITY) return;

          setQuantity(Math.floor(newQuantity));

          if (
            newQuantity !== savedQuantity // to avoid infinite loading state
          ) {
            if (target.value.startsWith("0"))
              // replace with the most recent value without 0 at the beginning
              target.value = String(newQuantity);

            handleQuantityChange(Math.floor(newQuantity));
          }
        }}
        min={0}
        onKeyDown={(e) => ["e", "E", "+", "-", ",", "."].includes(e.key) && e.preventDefault()}
        max={MAX_ITEM_QUANTITY}
        defaultValue={quantity}
      />
      <Icon
        dataTestId={`${testIdPrefix}-plus-icon`}
        iconName={Icons.DisplayCollapsePlusSmall}
        onClick={() => {
          if (!quantity || quantity > MAX_ITEM_QUANTITY) return;

          setQuantity(quantity + 1);
          handleQuantityChange(quantity + 1);
        }}
        color={Colors.PaleGray6}
        className={styles.button}
        size={16}
      />
    </div>
  );
};

export const CartItem = ({
  isLoading = false,
  item,
  currency,
  onQuantityChange,
  isRemoved = false,
  totalItemsText,
  locale = "en",
}: CartItemProps) => {
  const {
    _textCALENDAR,
    _textEBOOK,
    _textCW,
    _textLW,
    _textWO,
    _textSS,
    _textCO,
    _textPB,
    _textBulkDiscount,
    _textVolume5DiscountDescription,
    _textVolume10DiscountDescription,
    _textVolume15DiscountDescription,
    _textNoCover,
    _textQTY,
    _textItemRemoved,
  } = useCopy("CartItem");

  const isItemEditable = !!onQuantityChange;
  const isMobile = useWindowWidth() <= MEDIA_BREAKPOINTS.S;

  const {
    product,
    unitPrice,
    totalPrice,
    quantity,
    discounts,
    discountedTotalPrice,
    itemRedirectURL,
  } = item;

  const testIdPrefix = `${product.id ? "" : "removed-"}product`;

  const discountDescription: () => Record<string, string> = () => ({
    VOLUME_5: _textVolume5DiscountDescription,
    VOLUME_10: _textVolume10DiscountDescription,
    VOLUME_15: _textVolume15DiscountDescription,
  });

  const productLabelMap = new Map([
    ["CALENDAR", _textCALENDAR],
    ["EBOOK", _textEBOOK],
    ["CW", _textCW],
    ["LW", _textLW],
    ["WO", _textWO],
    ["SS", _textSS],
    ["CO", _textCO],
    ["PB", _textPB],
  ]);

  if (isRemoved)
    return (
      <div data-testid={`${testIdPrefix}-removed-info`} className={styles.removedItem}>
        <div>{_textItemRemoved}</div>
        <div>{totalItemsText}</div>
      </div>
    );

  const unitAmount = getAmountByCurrency(unitPrice, currency);
  const totalAmount = getAmountByCurrency(totalPrice, currency);
  const discountedTotalAmount = discountedTotalPrice
    ? getAmountByCurrency(discountedTotalPrice, currency)
    : undefined;

  return (
    <div className={styles.wrapper} aria-label="CartItem" data-testid={`${testIdPrefix}-cart-item`}>
      <div
        className={classNames(styles.productInfo, {
          [styles.productInfoLoading]: isLoading,
        })}
      >
        <div className={styles.productThumbnail}>
          <a rel="noopener noreferrer" target="_blank" href={itemRedirectURL}>
            {product.thumbnailUrl ? (
              <img src={product.thumbnailUrl} alt={product.name} />
            ) : (
              <div className={styles.noCover}>{_textNoCover}</div>
            )}
          </a>
        </div>
        <div data-testid={`${testIdPrefix}-name`} className={styles.productName}>
          <a rel="noopener noreferrer" target="_blank" href={itemRedirectURL}>
            {product.name}
          </a>
        </div>
        <div className={styles.productType}>
          <Icon iconName={getProductTypeIcon(product.type)} color={Colors.PaleGray6} size={22} />
          {productLabelMap.get(product.type) ||
            productLabelMap.get(
              product.attributes.find(
                (item: Record<string, string | string[]>) => item.name === "bindingTypeName"
              )?.value[0] as string
            )}
        </div>
        <div className={styles.productUnitPrice}>
          <span data-testid={`${testIdPrefix}-unit-price-amount`}>
            <MoneyDisplay money={{ amount: unitAmount, currency: currency }} locale={locale} />
          </span>
          {!!discounts?.length && (
            <div>
              <div data-testid={`${testIdPrefix}-discount-value`} className={styles.saleIcon}>
                <span data-tip data-for={`${product.id}-discount-tooltip`}>
                  <Icon
                    iconName={Icons.DiscountMediumCircleOutline}
                    color={Colors.Magenta}
                    size={15}
                  />
                </span>{" "}
                -{discounts[0].value}%
              </div>
              <div className={styles.bulkDiscount}>
                {discounts[0].code.startsWith("VOLUME_") ? _textBulkDiscount : discounts[0].code}
              </div>
              {isItemEditable && (
                <Tooltip
                  tooltipStyle="discount"
                  tooltipId={`${product.id}-discount-tooltip`}
                  overridePosition={({ left, top }) => ({
                    top,
                    left: isMobile ? 107 : left,
                  })}
                  place="right"
                >
                  <div className={styles.discountsTooltipContent}>
                    {discounts?.map((discount, id) => (
                      <span key={id}>
                        {discount?.code && discountDescription()[discount?.code]}
                      </span>
                    ))}
                  </div>
                </Tooltip>
              )}
            </div>
          )}
        </div>
      </div>
      <div
        className={classNames(styles.quantityAndPrice, {
          [styles.quantityAndPriceLoading]: isLoading,
        })}
      >
        {isItemEditable ? (
          <div className={styles.removeButton}>
            <IconButton
              dataTestId={`${testIdPrefix}-remove-btn`}
              icon={Icons.DataDeleteSmall}
              onClick={() => {
                onQuantityChange!(product.id, 0);
              }}
              color={Colors.PaleGray6}
              size={24}
              shape="naked"
            />
          </div>
        ) : (
          <div data-testid={`${testIdPrefix}-quantity`} className={styles.quantity}>
            {_textQTY} {quantity}
          </div>
        )}
        <div className={styles.quantityPickerWrapper}>
          {isItemEditable && (
            <QuantityPicker
              onQuantityChange={onQuantityChange!}
              quantity={quantity}
              productId={product.id}
            />
          )}
          <div className={styles.total}>
            <div>
              {!!discounts?.length && (
                <s data-testid={`${testIdPrefix}-total-price-amount`}>
                  <MoneyDisplay
                    money={{
                      amount: totalAmount,
                      currency: currency,
                    }}
                    locale={locale}
                  />
                </s>
              )}
            </div>
            <div
              data-testid={`${testIdPrefix}-${
                !!discountedTotalAmount ? "total-discounted-price-amount" : "total-price-amount"
              }`}
            >
              <MoneyDisplay
                money={{
                  amount: discountedTotalAmount ?? totalAmount,
                  currency: currency,
                }}
                locale={locale}
              />
            </div>
          </div>
        </div>
      </div>
      {isLoading && (
        <div className={styles.loader}>
          <Loading dataTestId={`${testIdPrefix}-loader`} />
        </div>
      )}
    </div>
  );
};
