import classNames from "classnames";
import React from "react";

import { NakedButton } from "../../shared/components/NakedButton/NakedButton";
import styles from "./Toggle.module.scss";

export interface ToggleProps {
  dataTestId?: string;
  alignLabel?: "left" | "right";
  type: "bleed" | "margin" | "wrap" | "folds" | "trim" | "holePunch" | "all";
  active: boolean;
  label: string;
  onChange: (value: boolean) => void;
  /**
   * The width of the toggle container is not dynamic.
   * More details [here](https://app.clickup.com/t/8678vw7zm).
   */
  activeStateLabel?: string;
  inactiveStateLabel?: string;
  customClassNames?: string;
}

export const Toggle: React.FC<ToggleProps> = ({
  dataTestId,
  alignLabel = "right",
  type,
  label,
  active,
  onChange,
  activeStateLabel = "on",
  inactiveStateLabel = "off",
  customClassNames,
}) => {
  const toggleClasses = classNames(
    active ? styles.toggle : styles.toggleInactive,
    styles[`labelAlign-${alignLabel}`],
    customClassNames
  );

  const inputClasses = classNames(
    active ? styles.input : styles.inputInactive,
    styles[`inputType-${type}`]
  );

  const stateLabel = active ? activeStateLabel : inactiveStateLabel;
  return (
    <div className={toggleClasses} data-testid={dataTestId}>
      <NakedButton
        className={inputClasses}
        role="switch"
        aria-checked={active}
        onClick={() => onChange(!active)}
      >
        <span className={styles.inputCircle} />
        <span className={styles.inputText} aria-label={stateLabel}>
          {stateLabel}
        </span>
      </NakedButton>
      <span className={styles.label}>{label}</span>
    </div>
  );
};
