import classNames from "classnames";
import React from "react";
import { NumericFormat, NumericFormatProps } from "react-number-format";

import { FloatingLabelInput } from "../Input/FloatingLabelInput";
import styles from "./NumericInput.module.scss";

export interface NumericInputProps extends NumericFormatProps {
  dataTestId?: string;
  label: string;
  isMoney?: boolean;
  currency?: string;
  error?: string;
  wrapperClassName?: string;
  alternateDisabledStyle?: boolean;
  hideDisabledLabel?: boolean;
  helperText?: React.ReactNode;
}

export const NumericInput: React.FC<NumericInputProps> = ({
  dataTestId,
  label,
  currency = "USD",
  isMoney = false,
  allowNegative = false,
  decimalScale = 0,
  maxLength,
  onChange,
  onClick,
  defaultValue,
  error,
  wrapperClassName,
  id,
  name,
  value,
  style = {},
  disabled,
  alternateDisabledStyle,
  hideDisabledLabel,
  helperText,
}) => {
  return (
    <>
      <div className={classNames(styles.container, wrapperClassName)} style={style}>
        {helperText && (
          <span className={classNames(styles.helperText, "field__label__helper")}>
            {helperText}
          </span>
        )}
        <NumericFormat
          data-testid={dataTestId}
          decimalScale={isMoney ? 2 : decimalScale}
          type="tel"
          customInput={FloatingLabelInput}
          fixedDecimalScale
          allowNegative={allowNegative}
          label={label}
          style={{ paddingRight: 56 }}
          maxLength={maxLength}
          onChange={onChange}
          onClick={onClick}
          defaultValue={defaultValue}
          error={error}
          id={id}
          name={name}
          value={value}
          disabled={disabled}
          alternateDisabledStyle={alternateDisabledStyle}
          hideDisabledLabel={hideDisabledLabel}
          aria-labelledby={id || name}
        />
        {isMoney && <span className={styles.currencyContainer}>{currency}</span>}
      </div>
    </>
  );
};
