import React, { useContext } from "react";

import { useCopy } from "../../../../extensions";
import { Accordion, AccordionItem } from "../../../Accordion/Accordion";
import { MoneyDisplay } from "../../../MoneyDisplay/MoneyDisplay";
import { EbookRevenueCalculatorAdapter } from "../../EbookRevenueCalculatorAdapter";
import { PricingCalculatorContext } from "../../PricingCalculatorContext";
import { imageUrlBuilder } from "../../utils";
import { AttributeSelector } from "../AttributeSelector/AttributeSelector";
import styles from "./EbookConfigurator.module.scss";

// add localized article urls once available
const getSupportArticleUrl = (locale: string) => {
  switch (locale) {
    case "de":
    case "fr":
    case "es":
    case "it":
    default:
      return "https://help.lulu.com/en/support/solutions/articles/64000255460-how-do-i-set-the-retail-price-for-my-ebook";
  }
};

export const EbookConfigurator: React.FC = () => {
  const {
    _textEbookInfo1,
    _textEbookInfo2,
    _textEbookInfo3,
    _textEbookInfo4,
    _textEbookInfo5,
    _textEbookInfo5Link,
    _textEbookFileFormat,
    _textRevenueEstimates,
  } = useCopy("PricingCalculator");

  const { locale, baseCurrency } = useContext(PricingCalculatorContext);

  return (
    <div data-testid="ebook-configurator">
      <AttributeSelector
        title={_textEbookFileFormat}
        activeValue={null}
        values={[
          {
            image: imageUrlBuilder(`ProductOption/Ebook/lulu-ebook-format-PDF`),
            label: "PDF",
            value: "PDF",
          },
          {
            image: imageUrlBuilder(`ProductOption/Ebook/lulu-ebook-format-EPUB`),
            label: "EPUB",
            value: "EPUB",
            isDistroEligible: true,
          },
        ]}
        nonInteractive
        dataTestId="ebook-file-format"
      />
      <div className={styles.description}>
        <p className="text-weight-300">{_textEbookInfo1}</p>
        <p className="text-weight-300">
          {`${_textEbookInfo2} ${_textEbookInfo5} `}
          <a href={getSupportArticleUrl(locale)} target="_blank" rel="noopener noreferrer">
            {_textEbookInfo5Link}
          </a>
          .
        </p>
        <div className={styles.summary}>
          <p>{_textEbookInfo3}</p>
          <p>
            {_textEbookInfo4}{" "}
            <MoneyDisplay
              money={{ amount: 4.99, currency: baseCurrency ?? "USD" }}
              locale={locale}
            />
            .
          </p>
        </div>
      </div>
      <Accordion>
        <AccordionItem title={_textRevenueEstimates}>
          <EbookRevenueCalculatorAdapter />
        </AccordionItem>
      </Accordion>
    </div>
  );
};
