import React from "react";
import { useEffect, useState } from "react";

import { CartDrawerProps, CartItemType } from "../CartDrawer";

export type OptimisticItemType = {
  item: CartItemType;
  isLoading: boolean;
  isRemoved: boolean;
};

interface ChildrenInterface extends OptimisticItemType {
  onQuantityChange: (id: string, quantity: number) => Promise<any>;
}

const getUpdatedOptimisticItems = (
  items: CartItemType[],
  currentOptimisticItems: OptimisticItemType[]
) => {
  const updatedItems: OptimisticItemType[] = [];

  // Go through current items
  currentOptimisticItems.forEach((currentItem) => {
    const matchingItemFromProp = items.find(
      (item) => item.product.id === currentItem.item.product.id
    );

    if (matchingItemFromProp) {
      // If there is the same item in updated prop -> merge and update loading state
      updatedItems.push({
        item: {
          ...currentItem.item,
          ...matchingItemFromProp,
        },
        isLoading: false,
        isRemoved: false,
      });
    } else {
      // If there is no such item in prop items -> isRemoved = true
      updatedItems.push({
        ...currentItem,
        isRemoved: true,
      });
    }
  });

  // At the end append all prop items which are not yet in the updated items
  items.forEach((item) => {
    const matchingItemFromCurrent = updatedItems.find(
      (currentItem) => item.product.id === currentItem.item.product.id
    );

    if (!matchingItemFromCurrent) {
      updatedItems.push({ item, isLoading: false, isRemoved: false });
    }
  });

  return updatedItems;
};

export interface OptimisticItemsControllerProps {
  items: CartItemType[];
  lastAddedItemId?: string;
  onQuantityChange: CartDrawerProps["onQuantityChange"];
  children: ({ item, onQuantityChange, isRemoved, isLoading }: ChildrenInterface) => JSX.Element;
  hideRemovedItems?: boolean;
  onlyDisplayLastAddedItem?: boolean;
}

export const OptimisticItemsController = ({
  items,
  onQuantityChange,
  children,
  hideRemovedItems,
  lastAddedItemId,
  onlyDisplayLastAddedItem,
}: OptimisticItemsControllerProps) => {
  const [optimisticItems, setOptimisticItems] = useState(
    items.map((item) => ({
      item,
      isLoading: false,
      isRemoved: false,
    }))
  );

  useEffect(() => {
    setOptimisticItems((currentOptimisticItems) =>
      getUpdatedOptimisticItems(items, currentOptimisticItems)
    );
  }, [items]);

  const handleQuantityChange = async (productId: string, newQuantity: number) => {
    const affectedInternalItem = optimisticItems.find(({ item }) => item?.product.id === productId);

    if (affectedInternalItem) {
      affectedInternalItem.item.quantity = newQuantity;
      affectedInternalItem.isLoading = true;
      setOptimisticItems((values) =>
        values.map((item) =>
          item.item.product.id === affectedInternalItem.item.product.id
            ? affectedInternalItem
            : item
        )
      );
    }

    await onQuantityChange(productId, newQuantity);
  };

  let shouldDisplayRemovedItemMessage = true;

  const itemsToDisplay = hideRemovedItems
    ? optimisticItems.filter((item) => !item.isRemoved)
    : optimisticItems
        .filter((item) => {
          if (item.isRemoved && shouldDisplayRemovedItemMessage) {
            shouldDisplayRemovedItemMessage = false;
            return item;
          } else if (!item.isRemoved) {
            return item;
          } else return null;
        })
        .filter((item) =>
          onlyDisplayLastAddedItem ? item.item.product.id === lastAddedItemId : true
        );

  return (
    <>
      {itemsToDisplay.map((props) =>
        children({
          ...props,
          onQuantityChange: handleQuantityChange,
        })
      )}
    </>
  );
};
